import type { ChangeEvent } from 'react';
import type { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';

import { useAppDispatch, useAppSelector } from 'store/hooks';
import { useEffect, useState } from 'react';
import { format } from 'date-fns';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';

import {
  CircularProgress,
  InputLabel,
  MenuItem,
  Select,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core';
import { Grid } from '@mui/material';
import { Button } from '@development/ui-kit';
import { Refresh, GetApp } from '@mui/icons-material';

import {
  getStatsParamTitle,
  StatsItemFilterParam,
} from 'old-enums/referral-group';

import {
  getReferralsStats,
  setFilter,
  setStatsListPagination,
  setFilterPublishers,
} from 'old-reducer/referrals/thunk';

import type { Referral, ReferralStatsItem } from 'old-reducer/referrals/types';
import { Pagination } from 'components/pagination';
import * as Styled from './styles';

export const ReferralStats = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(setFilterPublishers());
    dispatch(getReferralsStats());
  }, [dispatch]);

  const {
    stats,
    filter,
    filterPublishersList,
    statsPagination: pagination,
    isLoading,
  } = useAppSelector((state) => state.referralsPage);

  const [isGroupByReferrer, setIsGroupByReferrer] = useState(false);

  const handleResetFilters = () => {
    dispatch(getReferralsStats());
    setIsGroupByReferrer(false);
  };

  const handleStartDateChange = (date: MaterialUiPickersDate) => {
    if (date !== null) {
      dispatch(
        setFilter({ ...filter, start_date: format(date, 'yyyy-MM-dd') }),
      );
    }
  };

  const handleFinishDateChange = (date: MaterialUiPickersDate) => {
    if (date !== null) {
      dispatch(
        setFilter({ ...filter, finish_date: format(date, 'yyyy-MM-dd') }),
      );
    }
  };

  const handlePublisherChange = (e: ChangeEvent<{ value: unknown }>) =>
    dispatch(setFilter({ ...filter, publisher_id: e.target.value as number }));
  const handleGroupByChange = (e: ChangeEvent<{ value: unknown }>) =>
    dispatch(
      setFilter({
        ...filter,
        group_by: e.target.value as StatsItemFilterParam,
      }),
    );

  const handleApplyFilters = () => {
    dispatch(getReferralsStats(filter));

    if (filter.group_by === StatsItemFilterParam.REFERRAL_USER) {
      setIsGroupByReferrer(true);
    } else {
      setIsGroupByReferrer(false);
    }
  };

  const handlePagination = (page: number) => {
    dispatch(setStatsListPagination(page));
  };

  const handleDownloadCsvFile = () => dispatch(getReferralsStats(filter, true));

  const getTotalAmount = () =>
    stats.reduce((acc, cur) => acc + Number(cur.amount), 0).toFixed(2);

  return (
    <>
      <Styled.TitleReferrals>Referrals&apos; statistics</Styled.TitleReferrals>

      <div>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Styled.FiltersBlockReferrals>
            <DatePicker
              label="Start date"
              variant="inline"
              value={new Date(filter.start_date)}
              onChange={handleStartDateChange}
              data-testid="start-date-picker"
            />
            <DatePicker
              label="Finish date"
              variant="inline"
              value={new Date(filter.finish_date)}
              onChange={handleFinishDateChange}
              data-testid="finish-date-picker"
            />
            <Styled.FilterInputFieldReferrals>
              <InputLabel id="publisher-label">Publisher</InputLabel>
              <Select
                labelId="publisher-label"
                id="publisher"
                value={filter.publisher_id}
                onChange={handlePublisherChange}
                data-testid="publisher-select"
              >
                <MenuItem value={0}>Any</MenuItem>
                {filterPublishersList.map((referral: Referral) => (
                  <MenuItem key={referral.id} value={referral.id}>
                    {referral.login}
                  </MenuItem>
                ))}
              </Select>
            </Styled.FilterInputFieldReferrals>
            <Styled.FilterInputFieldReferrals>
              <InputLabel id="group-by-label">Group by</InputLabel>
              <Select
                value={filter.group_by}
                onChange={handleGroupByChange}
                data-testid="group-by-select"
              >
                {Object.values(StatsItemFilterParam)
                  .filter((value) => !Number.isNaN(Number(value)))
                  .map((id) => (
                    <MenuItem key={id} value={id}>
                      {getStatsParamTitle(Number(id))}
                    </MenuItem>
                  ))}
              </Select>
            </Styled.FilterInputFieldReferrals>
          </Styled.FiltersBlockReferrals>
        </MuiPickersUtilsProvider>
        <Grid
          container
          rowSpacing={1}
          justifyContent="space-between"
          wrap="nowrap"
        >
          <Grid item>
            <Button
              startIcon={<GetApp />}
              onClick={handleDownloadCsvFile}
              dataTestId="get-csv-filter-button"
            >
              Get CSV
            </Button>
          </Grid>
          <Grid item>
            <Grid container columnSpacing={2}>
              <Grid item>
                <Button
                  startIcon={<Refresh />}
                  onClick={handleResetFilters}
                  dataTestId="reset-filter-button"
                >
                  Reset
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  onClick={handleApplyFilters}
                  dataTestId="apply-filter-button"
                >
                  Apply
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </div>

      {isLoading ? (
        <CircularProgress />
      ) : (
        <Styled.StatReferrals>
          <Styled.StatTotalReferrals>
            <span>Total</span>
            <Styled.StatTotalAmountReferrals>
              ${getTotalAmount()}
            </Styled.StatTotalAmountReferrals>
          </Styled.StatTotalReferrals>
          <Styled.TableStyledStatList>
            <TableHead>
              <TableRow>
                <TableCell>
                  {isGroupByReferrer ? 'Referral user' : 'Date'}
                </TableCell>
                <TableCell>Amount</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {stats.length > 0 && (
                <>
                  {pagination.paginatedStats.map((item: ReferralStatsItem) => (
                    <TableRow key={item.id}>
                      <TableCell>
                        {isGroupByReferrer ? item.login : item.date}
                      </TableCell>
                      <TableCell>${item.amount.toFixed(2)}</TableCell>
                    </TableRow>
                  ))}
                </>
              )}
            </TableBody>
          </Styled.TableStyledStatList>
          {pagination.total > 1 && (
            <Pagination
              active={pagination.active}
              total={pagination.total}
              onTap={handlePagination}
            />
          )}
        </Styled.StatReferrals>
      )}
    </>
  );
};
