import type { Document } from 'old-reducer/payments/types';
import { DocumentStatus } from 'old-enums/document-status';
import { DocumentType } from 'old-enums/document-type';
import { Grid } from '@material-ui/core';
import { IconButton } from 'old-component';
import { GetApp } from '@material-ui/icons';
import { getDocumentTypeById } from '../utils';
import { SignDocument } from './sign-document';

export const DocumentControlButtons = ({
  id,
  type,
  status,
  path,
}: Document) => {
  const docType = getDocumentTypeById(type);
  const isPendingSignature = status === DocumentStatus.PENDING_SIGNATURE;
  const titleText = `Download ${docType}_${id}`;
  const isAllowedToDownloadAgreement =
    type === DocumentType.AGREEMENT &&
    [
      DocumentStatus.VERIFICATION,
      DocumentStatus.VALID,
      DocumentStatus.PENDING_SIGNATURE,
    ].includes(status);
  const isAllowedToDownloadInsertionOrder =
    type === DocumentType.INSERTION_ORDER && status === DocumentStatus.VALID;

  const isAllowedToDownloadDocument =
    isAllowedToDownloadAgreement || isAllowedToDownloadInsertionOrder;

  return (
    <Grid container justifyContent="flex-end" spacing={1}>
      {isPendingSignature && (
        <Grid item xs={6}>
          <SignDocument id={id} />
        </Grid>
      )}
      {isAllowedToDownloadDocument && path !== null && (
        <Grid item xs={6}>
          <IconButton
            dataTestId="payments__download-document-button"
            href={path}
            title={titleText}
            edge="end"
          >
            <GetApp />
          </IconButton>
        </Grid>
      )}
    </Grid>
  );
};
