/* eslint-disable @typescript-eslint/no-shadow */
/* eslint-disable react/destructuring-assignment */

import { Component } from 'react';
import { connect } from 'react-redux';
import { Popover } from '@material-ui/core';
import { grey } from '@material-ui/core/colors';
import { Info } from '@material-ui/icons';

import { RadioButton, RadioButtonGroup } from '@development/ui-kit';
import { CustomSlider } from 'old-component/custom-slider';
import { PortalWrap } from 'old-component/portal-wrap';
import { Tooltip } from 'old-component/tooltip';
import { AdUnits } from 'old-component/dialog-common/ad-units';
import { Alert } from 'old-component/dialog-common/alert-wrap';
import { DialogWrap } from 'old-component/dialog-common/dialog-wrap';
import { Feeds } from 'old-component/dialog-common/feeds';
import { SiteCategory } from 'old-component/dialog-common/site-category';
import { Title } from 'old-component/dialog-common/title';

import { getAdUnitTitle, getMultiformatAdUnits } from 'old-enums/ad-units';
import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';
import { FeedEnum, getFeedTitle } from 'old-enums/feed';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { WebsitesTipTypesEnum } from 'old-enums/websites-tip-types-enum';
import { ZoneCategoryEnum } from 'old-enums/zone-category';
import { WebsitesMultiformatType } from 'old-enums/websites-multiformat-type';
import { TooltipPosition } from 'old-enums/tooltip-position';

import {
  addSite,
  editPlacement,
  editSite,
  getCodes,
  hideDialog,
  hideSite,
  hideTip,
  hideTooltip,
  requestAntiAdblock,
  setPlacementTitle,
  setTypeCodes,
  showTip,
  showTooltip,
  siteAddData,
} from 'old-reducer/site-page';

import type { MapDispatchToProps, MapStateToProps } from 'react-redux';
import type { AppState } from 'store/types';
import type { ChangeEvent } from 'react';
import type { AdUnitEnum } from 'old-enums/ad-units';

import * as Styled from './styles';

type Props = AppState['sitePage'];

interface Dispatchers {
  addSite(): void;
  editPlacement(type: string, id: number): void;
  editSite(): void;
  getCodes(): void;
  hideDialog(): void;
  hideSite(): void;
  hideTip(): void;
  hideTooltip(): void;
  requestAntiAdblock(): void;
  setPlacementTitle(title: string): void;
  setTypeCodes(type: string): void;
  showTooltip(anchorId: string, content: string): void;
  showTip(element: any, id: number, type: WebsitesTipTypesEnum): void;
  siteAddData(data: any, type: string): void;
}

// tslint:disable-next-line:no-empty
const noop = () => {};

const anchorId = 'cpmTooltip';

const tooltipContent = `We'll add suitable ad formats to get the most from your traffic based on your expected income and available offers`;

class PureFormWebsite extends Component<Props & Dispatchers> {
  // eslint-disable-next-line react/sort-comp
  private readonly dialogs = {
    [WebsitesDialogTypesEnum.EMPTY]: {
      onClick: noop,
      title: '',
      width: 'auto',
    },
    [WebsitesDialogTypesEnum.ADD_AD_UNIT]: {
      onClick: this.props.addSite,
      title: 'Add new code',
      width: 'auto',
    },
    [WebsitesDialogTypesEnum.ADD_SITE]: {
      onClick: this.props.addSite,
      title: 'Add new website',
      width: 'auto',
    },
    [WebsitesDialogTypesEnum.EDIT_PLACEMENT]: {
      onClick: () =>
        this.props.editPlacement('dialog', this.props.action.data.id),
      title: 'Edit placement',
      width: 512,
    },
    [WebsitesDialogTypesEnum.EDIT_SITE]: {
      onClick: this.props.editSite,
      title: 'Edit website',
      width: 512,
    },
    [WebsitesDialogTypesEnum.EXPORT_CODES]: {
      onClick: this.props.getCodes,
      submit: 'Export',
      title: 'Export codes',
      width: 512,
    },
    [WebsitesDialogTypesEnum.HIDE_CONFIRM]: {
      onClick: this.props.hideSite,
      submit: 'Yes',
      title: 'Hide from statistics',
      width: 392,
    },
  };

  // tslint:disable-next-line:member-ordering
  private readonly alerts = {
    [WebsitesDialogTypesEnum.VIEW_SITE]: {
      title: 'Pending website',
    },
    [WebsitesDialogTypesEnum.ADD_SITE]: {
      title: this.dialogs[WebsitesDialogTypesEnum.ADD_SITE].title,
    },
    [WebsitesDialogTypesEnum.ADD_AD_UNIT]: {
      title: this.dialogs[WebsitesDialogTypesEnum.ADD_AD_UNIT].title,
    },
  };

  private readonly getExportCodesDialog = () => {
    const {
      action: { general_error, step },
    } = this.props;

    const { hideDialog } = this.props;

    return (
      <DialogWrap
        data={this.dialogs[WebsitesDialogTypesEnum.EXPORT_CODES]}
        hideDialog={hideDialog}
        step={step}
      >
        {general_error && <Styled.Error>{general_error}</Styled.Error>}
        <p>Here you can download your codes</p>
        <RadioButtonGroup
          dataTestId="dialog__export-code-type"
          defaultValue="txt"
          name="exportType"
          row
          onChange={this.onExportTypeChange}
        >
          <RadioButton value="txt" label="TXT" dataTestId="radiobutton-txt" />
          <RadioButton value="csv" label="CSV" dataTestId="radiobutton-csv" />
        </RadioButtonGroup>
      </DialogWrap>
    );
  };

  private readonly getHideConfirmDialog = () => {
    const {
      action: { step },
    } = this.props;

    const { hideDialog } = this.props;

    return (
      <DialogWrap
        data={this.dialogs[WebsitesDialogTypesEnum.HIDE_CONFIRM]}
        hideDialog={hideDialog}
        step={step}
      >
        <p>
          If you hide your website it won&apos;t be visible and counted in
          statistics anymore
        </p>
      </DialogWrap>
    );
  };

  private readonly getViewAdUnitOrSiteDialog = (
    dialogType: WebsitesDialogTypesEnum,
  ) => {
    const {
      action: { data, step },
    } = this.props;

    const { hideDialog } = this.props;

    return (
      <Alert data={this.alerts[dialogType]} hideDialog={hideDialog} step={step}>
        <Styled.View>
          <Styled.CaptionLight>Website</Styled.CaptionLight>
          {data.zone}
        </Styled.View>
        {data.ad_units && data.ad_units.length > 0 && (
          <Styled.View>
            <Styled.CaptionLight>Ad units</Styled.CaptionLight>
            {data.ad_units.map(getAdUnitTitle).join(', ')}
          </Styled.View>
        )}
        {data.na_feed_category_exclude &&
          data.na_feed_category_exclude.length > 0 && (
            <Styled.View>
              <Styled.CaptionLight>
                <Styled.CaptionHighlight>Exclude</Styled.CaptionHighlight>{' '}
                campaigns with
              </Styled.CaptionLight>
              {data.na_feed_category_exclude.map(getFeedTitle).join(', ')}
            </Styled.View>
          )}
      </Alert>
    );
  };

  private readonly hasAdUnit = (id: AdUnitEnum) => {
    const {
      action: {
        data: { ad_units },
      },
    } = this.props;

    return ad_units && ad_units.includes(id);
  };

  private readonly hasCategory = (id: FeedEnum) => {
    const {
      action: {
        data: { na_feed_category_exclude },
      },
    } = this.props;

    return na_feed_category_exclude && na_feed_category_exclude.includes(id);
  };

  private readonly hasNewAdUnit = (id: AdUnitEnum) => {
    const {
      action: {
        data: { new_ad_units },
      },
    } = this.props;

    return new_ad_units && new_ad_units.includes(id);
  };

  // eslint-disable-next-line react/sort-comp
  private readonly onAdUnitChange = (
    { currentTarget }: ChangeEvent<HTMLInputElement>,
    id: AdUnitEnum,
  ) => {
    const { showTip, siteAddData } = this.props;

    if (this.hasAdUnit(id)) {
      showTip(currentTarget, id, WebsitesTipTypesEnum.REQUEST_AD_UNIT);

      return;
    }

    siteAddData([id], 'new_ad_units');
  };

  private readonly onExportTypeChange = (_: unknown, value: string) => {
    const { setTypeCodes } = this.props;

    setTypeCodes(value);
  };

  private readonly onFeedCategoryChange = (id: FeedEnum, checked: boolean) => {
    const {
      action: {
        data: { na_feed_category_exclude },
      },
    } = this.props;

    const { siteAddData } = this.props;

    const data = na_feed_category_exclude
      ? na_feed_category_exclude.slice()
      : [];
    const index = data.indexOf(id);
    const found = index !== -1;

    if (checked && !found) {
      data.push(id);
    } else if (!checked && found) {
      data.splice(index, 1);
    }

    siteAddData(data, 'na_feed_category_exclude');
  };

  private readonly onPlacementTitleChange = (
    event: ChangeEvent<{ value: string }>,
  ) => {
    // eslint-disable-next-line
    const { setPlacementTitle } = this.props;

    setPlacementTitle(event.target.value);
  };

  private readonly onZoneCategoryChange = (
    event: ChangeEvent<{ value: ZoneCategoryEnum | null }>,
  ) => {
    const { value } = event.target;
    const toCheck = !!value && value !== ZoneCategoryEnum.ADULT;

    /* eslint-disable no-shadow */
    const { siteAddData } = this.props;
    /* eslint-enable no-shadow */

    this.onFeedCategoryChange(FeedEnum.EROTIC_ADS, toCheck);
    siteAddData(value, 'category_id');
  };

  private readonly onZoneNameChange = (
    event: ChangeEvent<{ value: string }>,
  ) => {
    const { siteAddData } = this.props;

    siteAddData(event.target.value, 'zone');
  };

  private readonly handleShowTooltip = () => {
    const { showTooltip: show } = this.props;

    show(anchorId, tooltipContent);
  };

  private readonly handleHideTooltip = () => {
    const { hideTooltip: hide } = this.props;

    hide();
  };

  private readonly handleSliderChange = (
    _: unknown,
    value: number | number[],
  ) => {
    const { siteAddData: addSiteValue } = this.props;

    addSiteValue(value, 'multiformat');
  };

  render() {
    const {
      action: { action, data, general_error, step },
      hideDialog: hideDialogFunc,
      tip,
      tooltip,
      isAbTest,
    } = this.props;

    const { action: topAction } = this.props;

    const { hideTip } = this.props;

    const adUnitId =
      action === WebsitesDialogTypesEnum.EDIT_PLACEMENT
        ? data.ad_unit
        : data.new_ad_units[0];

    const shouldShowMoneySlider =
      action === WebsitesDialogTypesEnum.EDIT_PLACEMENT
        ? isAbTest && data.money_scale_position !== 0
        : isAbTest && getMultiformatAdUnits().includes(adUnitId);

    const defaultSliderValue =
      data.money_scale_position ||
      data.multiformat ||
      WebsitesMultiformatType.MAX;

    const isExistingSite = action !== WebsitesDialogTypesEnum.ADD_SITE;

    switch (action) {
      case WebsitesDialogTypesEnum.EXPORT_CODES:
        return this.getExportCodesDialog();

      case WebsitesDialogTypesEnum.VIEW_SITE:
        return this.getViewAdUnitOrSiteDialog(action);

      case WebsitesDialogTypesEnum.HIDE_CONFIRM:
        return this.getHideConfirmDialog();

      case WebsitesDialogTypesEnum.EMPTY:
        return null;

      default: {
        if (step === DialogStepsEnum.FINISHED) {
          return (
            <Alert
              data={this.alerts[action]}
              step={step}
              hideDialog={hideDialogFunc}
            >
              {isExistingSite ? (
                <p>
                  Your request has been submitted.
                  <br />
                  You can paste your codes once they are approved.
                </p>
              ) : (
                <p>
                  Your website has been sent for approval. We&apos;ll send you
                  an e-mail when it&apos;s done.
                </p>
              )}
            </Alert>
          );
        }

        const isEditSiteOrPlacement =
          action === WebsitesDialogTypesEnum.EDIT_SITE ||
          action === WebsitesDialogTypesEnum.EDIT_PLACEMENT;

        return (
          <DialogWrap
            step={step}
            data={this.dialogs[action]}
            hideDialog={this.props.hideDialog}
          >
            {general_error && <Styled.Error>{general_error}</Styled.Error>}
            {action === WebsitesDialogTypesEnum.EDIT_SITE && (
              <Styled.Note>
                Changes will only apply to newly requested codes.
              </Styled.Note>
            )}
            <Title
              onZoneNameChange={this.onZoneNameChange}
              onPlacementTitleChange={this.onPlacementTitleChange}
              props={topAction}
            />
            <div>
              <Styled.Caption>
                {action === WebsitesDialogTypesEnum.EDIT_PLACEMENT
                  ? 'Placement'
                  : 'Website'}{' '}
                category:
              </Styled.Caption>
              <SiteCategory
                onZoneCategoryChange={this.onZoneCategoryChange}
                isExistingSite={isExistingSite}
                categoryId={data.category_id}
              />
            </div>
            <Styled.Parameters>
              {!isEditSiteOrPlacement && (
                <AdUnits
                  findAdUnit={this.hasAdUnit}
                  findNewAdUnit={this.hasNewAdUnit}
                  onChangeAdUnit={this.onAdUnitChange}
                />
              )}
              <Styled.DialogWrap id="dialogWrap">
                <Feeds
                  findCategory={this.hasCategory}
                  onChangeCategory={this.onFeedCategoryChange}
                  shouldShowNote={!shouldShowMoneySlider}
                />
                {shouldShowMoneySlider &&
                  action !== WebsitesDialogTypesEnum.EDIT_SITE && (
                    <Styled.SliderNoteWrapper>
                      <Styled.SliderNote>
                        Make my CPM higher by allowing more ads
                        <Info
                          htmlColor={grey[600]}
                          onMouseEnter={this.handleShowTooltip}
                          onMouseLeave={this.handleHideTooltip}
                          id={anchorId}
                        />
                      </Styled.SliderNote>
                      <PortalWrap root={document.body}>
                        <Tooltip
                          anchorId={tooltip.anchorId}
                          content={tooltip.content}
                          position={TooltipPosition.TOP}
                        />
                      </PortalWrap>
                      <CustomSlider
                        defaultValue={defaultSliderValue}
                        max={3}
                        min={1}
                        onChange={this.handleSliderChange}
                      />
                    </Styled.SliderNoteWrapper>
                  )}
              </Styled.DialogWrap>
            </Styled.Parameters>
            <Popover
              anchorEl={tip.anchorEl}
              anchorOrigin={{
                horizontal: 'left',
                vertical: 'bottom',
              }}
              open={tip.type === WebsitesTipTypesEnum.REQUEST_AD_UNIT}
              onClose={() => hideTip()}
            >
              <Styled.PopoverParagraphStyles>
                This ad unit has already been approved.
              </Styled.PopoverParagraphStyles>
            </Popover>
          </DialogWrap>
        );
      }
    }
  }
}

const mapStateToProps: MapStateToProps<Props, {}, AppState> = (state) =>
  state.sitePage;

const mapDispatchToProps: MapDispatchToProps<Dispatchers, {}> = {
  addSite,
  editPlacement,
  editSite,
  getCodes,
  hideDialog,
  hideSite,
  hideTip,
  hideTooltip,
  requestAntiAdblock,
  setPlacementTitle,
  setTypeCodes,
  showTip,
  showTooltip,
  siteAddData,
};

export const FormWebsite = connect(
  mapStateToProps,
  mapDispatchToProps,
)(PureFormWebsite);
