import {
  getPublisherMessengers,
  getAffiliateMessengers,
} from 'old-enums/messenger';
import { isAffiliateDomain } from 'constants/index';
import { SignUpFormField } from 'old-enums/sign-up-form-field';
import { getAdvertiserName } from 'old-reducer/signup/utils';
import type { FormFieldsSignup } from './types';

export const emptySelectOptionID = '';
export const emptySelectOption = [emptySelectOptionID, 'Please, select...'];

export const initialFormFields: FormFieldsSignup = {
  [SignUpFormField.EMAIL]: '',
  [SignUpFormField.LOGIN]: '',
  [SignUpFormField.MESSENGER]: emptySelectOptionID,
  [SignUpFormField.MESSENGER_ACCOUNT]: '',
  [SignUpFormField.NAME]: getAdvertiserName(),
  [SignUpFormField.PASSWORD]: '',
  [SignUpFormField.PAYSYSTEM_ID]: emptySelectOptionID,
  [SignUpFormField.ARE_TERMS_ACCEPTED]: false,
};

const messengers = isAffiliateDomain
  ? getAffiliateMessengers()
  : getPublisherMessengers();

export const messengerList = () => [emptySelectOption, ...messengers];
