import type { DataGridTableProps } from '@development/ui-kit';
import type {
  FilteredField,
  PossibleFieldType,
  TableField,
  TableFields,
} from './types';

export const isFilteredField = (
  field: FilteredField | TableField,
): field is FilteredField => {
  const currentField = field as FilteredField;

  if (
    !currentField ||
    !currentField.availableValues ||
    currentField.currentValue === undefined ||
    !currentField.onChange
  ) {
    return false;
  }

  return true;
};

export const isFieldType = (value: any): value is PossibleFieldType => {
  return typeof value === 'string' || typeof value === 'number';
};

export const prepareColumnsData = <T>(
  tableFields: TableFields<T>,
): DataGridTableProps['columnsArray'] => {
  return Object.keys(tableFields).map((fieldKey) => {
    const field = tableFields[fieldKey] as TableField;

    return {
      dataField: fieldKey,
      caption: field.caption,
      cellRender: field.cellRender,
      minWidth: field.minWidth,
      width: field.width,
      alignment: 'left',
    };
  });
};
