export enum ZoneCategoryEnum {
  EMPTY = 0,
  MOVIES = 1,
  ADULT_SOCIAL = 2,
  OTHER = 3,
  ADULT = 16,
  FILEHOSTS = 17,
  TORRENTS = 18,
  STREAMING = 19,
  MP3 = 20,
  DOWNLOAD = 21,
  ANIME = 22,
  XTESTS = 24,
  BOOKS = 27,
  SOCIAL = 28,
  XXLPTESTS = 29,
  ADWARE = 30,
  SOCIAL_BLACK = 31,
  ADULT_MEDIABUY = 32,
  MAINSTREAM_MEDIABUY = 33,
  URLSHORTENER = 34,
  CONVERTER = 35,
  FAUCET = 36,
  VIDEOHOST = 37,
  ADULT_SOCIALBLACK = 38,
}

export const getZoneCategoriesForNewZone = () => [
  ZoneCategoryEnum.SOCIAL,
  ZoneCategoryEnum.ADULT_SOCIAL,
  ZoneCategoryEnum.MOVIES,
  ZoneCategoryEnum.ADULT,
  ZoneCategoryEnum.URLSHORTENER,
  ZoneCategoryEnum.MP3,
  ZoneCategoryEnum.STREAMING,
  ZoneCategoryEnum.VIDEOHOST,
  ZoneCategoryEnum.CONVERTER,
  ZoneCategoryEnum.FILEHOSTS,
  ZoneCategoryEnum.TORRENTS,
  ZoneCategoryEnum.ANIME,
  ZoneCategoryEnum.DOWNLOAD,
  ZoneCategoryEnum.FAUCET,
  ZoneCategoryEnum.BOOKS,
  ZoneCategoryEnum.OTHER,
];

export const getAllZoneCategories = () => [
  ...getZoneCategoriesForNewZone(),
  ZoneCategoryEnum.SOCIAL_BLACK,
  ZoneCategoryEnum.ADULT_MEDIABUY,
  ZoneCategoryEnum.MAINSTREAM_MEDIABUY,
  ZoneCategoryEnum.ADULT_SOCIALBLACK,
  ZoneCategoryEnum.ADWARE,
  ZoneCategoryEnum.XTESTS,
  ZoneCategoryEnum.XXLPTESTS,
];

export const getZoneCategoryTitle = (categoryId: ZoneCategoryEnum) => {
  switch (categoryId) {
    case ZoneCategoryEnum.SOCIAL:
    case ZoneCategoryEnum.SOCIAL_BLACK:
      return 'Social';
    case ZoneCategoryEnum.ADULT_SOCIAL:
      return 'Adult Social';
    case ZoneCategoryEnum.MOVIES:
      return 'Movies';
    case ZoneCategoryEnum.ADULT_SOCIALBLACK:
    case ZoneCategoryEnum.ADULT:
      return 'Adult';
    case ZoneCategoryEnum.URLSHORTENER:
      return 'URLShortener';
    case ZoneCategoryEnum.MP3:
      return 'MP3';
    case ZoneCategoryEnum.STREAMING:
      return 'Sport Streaming';
    case ZoneCategoryEnum.VIDEOHOST:
      return 'Videohosts';
    case ZoneCategoryEnum.CONVERTER:
      return 'Converter';
    case ZoneCategoryEnum.FILEHOSTS:
      return 'Filehosts';
    case ZoneCategoryEnum.TORRENTS:
      return 'Torrents';
    case ZoneCategoryEnum.ANIME:
      return 'Anime';
    case ZoneCategoryEnum.DOWNLOAD:
      return 'Downloads';
    case ZoneCategoryEnum.FAUCET:
      return 'Faucet';
    case ZoneCategoryEnum.BOOKS:
      return 'Books';
    case ZoneCategoryEnum.ADULT_MEDIABUY:
      return 'Adult Mediabuy';
    case ZoneCategoryEnum.MAINSTREAM_MEDIABUY:
      return 'Mainstream Mediabuy';
    default:
      return 'Other';
  }
};
