export enum PublisherStatusEnum {
  ACTIVE = 1,
  BANNED = 2,
  DECLINED = 3,
  NOT_CONFIRMED = 4,
  BLACKLISTED = 5,
}

export const getPublisherStatusTitle = (status: PublisherStatusEnum) => {
  switch (status) {
    case PublisherStatusEnum.BANNED:
      return 'Banned';
    case PublisherStatusEnum.BLACKLISTED:
      return 'Blacklisted';
    case PublisherStatusEnum.DECLINED:
      return 'Declined';
    case PublisherStatusEnum.ACTIVE:
      return 'Active';
    case PublisherStatusEnum.NOT_CONFIRMED:
      return 'Not confirmed';
    default:
      return '';
  }
};
