import type { AppState } from 'store/types';

import { SignInActionsEnum } from 'old-enums/sign-in-actions-enum';
import { Component } from 'react';
import { connect } from 'react-redux';
import FingerprintJS from '@fingerprintjs/fingerprintjs';
import { ConfirmStep } from 'components/confirm-step';

import { Grid } from '@mui/material';

import { getSignIn, setAction, setUser } from 'old-reducer/sign-in';
import { checkAuth } from 'old-reducer/common/thunk';

import { DialogChooseRole } from 'features/dialog-role/dialog-choose-role';
import { Resend } from 'features/resend';
import type { State, Dispatchers } from './types';
import { ForgotPassword } from './components/forgot-password';
import { LogIn } from './components/log-in';

import * as Styled from './styles';

const setFingerprint = (setUserFingerprint: Dispatchers['setUser']) => {
  const excludeParams = [
    'audio',
    'availableScreenResolution',
    'languages',
    'plugins',
    'screenResolution',
    'touchSupport',
    'vendor',
  ];

  FingerprintJS.load().then((fp) => {
    fp.get().then((result) => {
      const { components } = result;
      const shortComponentsList = {};

      Object.keys(components).forEach((key) => {
        if (key in components && !excludeParams.includes(key)) {
          shortComponentsList[key] = components[key];
        }
      });

      setUserFingerprint(
        'fingerprint',
        FingerprintJS.hashComponents(shortComponentsList),
      );
    });
  });
};

class SignInComponent extends Component<State & Dispatchers, {}> {
  componentDidMount() {
    const { props } = this;
    const { setUser: setUserFingerprint } = props;

    props.checkAuth();
    setFingerprint(setUserFingerprint);
  }

  private readonly getCurrentForm = () => {
    const {
      signIn: {
        action,
        forgotPassword,
        form: { form_error, general_error, loading, message },
        user,
      },
    } = this.props;

    switch (action) {
      case SignInActionsEnum.SIGN_IN:
        return (
          <Styled.MainContainer
            container
            flexDirection="column"
            alignItems="center"
          >
            <LogIn
              formError={form_error}
              generalError={general_error}
              loading={loading}
              user={user}
            />
            <DialogChooseRole login />
          </Styled.MainContainer>
        );

      case SignInActionsEnum.CONFIRM:
        return (
          <Styled.CheckEmailContainer>
            <ConfirmStep login />
          </Styled.CheckEmailContainer>
        );

      case SignInActionsEnum.RESEND:
        return (
          <Styled.MainContainer
            container
            flexDirection="column"
            alignItems="center"
          >
            <p>You have not confirmed your account yet.</p>
            <p>Please check your inbox or request a new code.</p>
            <Resend />
          </Styled.MainContainer>
        );

      default:
        return (
          <Styled.MainContainer
            container
            flexDirection="column"
            alignItems="center"
          >
            <ForgotPassword
              formError={form_error}
              loading={loading}
              forgotPassword={forgotPassword.email}
              generalError={general_error}
              message={message}
            />
          </Styled.MainContainer>
        );
    }
  };

  render() {
    return (
      <Styled.Wrapper>
        <Styled.GridContainer container>
          <Styled.DummyGrid item lg={4} md={3} sm={2} />
          <Grid item lg={4} md={6} sm={8} xs={12}>
            <Grid container justifyContent="center">
              {this.getCurrentForm()}
            </Grid>
          </Grid>
          <Styled.BackgroundImageGrid item lg={4} md={3} sm={2} />
        </Styled.GridContainer>
      </Styled.Wrapper>
    );
  }
}

export const SignIn = connect<State, Dispatchers, {}, AppState>(
  (state) => ({ signIn: state.signIn }),
  {
    checkAuth,
    getSignIn,
    setAction,
    setUser,
  },
)(SignInComponent);
