import { Table, TableBody, TableRow } from '@material-ui/core';

import { getAdUnitTitle } from 'old-enums/ad-units';
import * as Styled from './styles';

import type { Props } from './types';

export const PendingRequestRow = ({ adUnits }: Props) => (
  <Table>
    <TableBody>
      {adUnits.map((adUnit) => (
        <TableRow key={adUnit}>
          <Styled.AdUnitCell>{getAdUnitTitle(adUnit)}</Styled.AdUnitCell>
          <Styled.StatusCell>Pending</Styled.StatusCell>
        </TableRow>
      ))}
    </TableBody>
  </Table>
);
