import { FormControlLabel } from '@mui/material';
import { green, red, yellow } from '@material-ui/core/colors';
import {
  FormControl,
  Grid,
  Paper,
  Select,
  TableCell,
  TableRow,
} from '@material-ui/core';
import { Button, IconButton } from '@development/ui-kit';
import { KeyboardArrowDown, KeyboardArrowUp, Sync } from '@mui/icons-material';
import styled, { css, keyframes } from 'styled-components';
import { SiteStatusEnum } from 'old-enums/site-status';
import { themeColors } from 'styles/theme/constants';
import type {
  AllCodesIconProps,
  DeclinedStatusIconProps,
  StatusCellProps,
  WebsiteTableRowProps,
} from './types';

const statusColor = {
  [SiteStatusEnum.PENDING]: yellow[800],
  [SiteStatusEnum.QUEUED_FOR_APPROVAL]: yellow[800],
  [SiteStatusEnum.APPROVED]: green[500],
  [SiteStatusEnum.DECLINED]: red[500],
};

const rotation = keyframes`
   from {
     transform: rotate(0deg);
   }
   to {
     transform: rotate(359deg);
   }
`;

const loadingAnimation = css`
  animation: ${rotation} 2s infinite linear;
`;

export const Container = styled(Paper)`
  margin-bottom: ${({ theme }) => theme.spacing(2)};
`;

export const ExportCodesButton = styled(Button)`
  margin-right: ${({ theme }) => theme.spacing(3)};
`;

export const Filter = styled(Paper)`
  margin-bottom: ${({ theme }) => theme.spacing(2)};
`;

export const FilterButtonsGrid = styled(Grid)`
  justify-content: flex-end;
  padding: ${({ theme }) => theme.spacing(3, 3, 3, 0)};
`;

export const NoWebsitesFoundText = styled.p`
  padding: ${({ theme }) => theme.spacing(3)};
  color: ${({ theme }) => theme.palette.tertiary.light};
`;

export const PaginationContainer = styled(Paper)`
  padding-top: ${({ theme }) => theme.spacing(2)};
`;

export const VisibilityFilter = styled(Select)`
  margin-top: ${({ theme }) => theme.spacing(2)};
  width: 150px;
`;

export const FilterFormControl = styled(FormControl)`
  margin-top: ${({ theme }) => theme.spacing(2)};
  margin-left: ${({ theme }) => theme.spacing(3)};
`;

export const DomainSearchIconButton = styled(IconButton)`
  padding: 0;
`;

export const ResultsCount = styled.p`
  padding: ${({ theme }) => theme.spacing(2, 3)};
`;

export const ResultsCountLabel = styled.span`
  color: ${({ theme }) => theme.palette.tertiary.light};
`;

export const ResetButtonContainer = styled.div`
  margin: ${({ theme }) => theme.spacing(1, 0)};
`;

export const WebsiteTableRow = styled(TableRow)<WebsiteTableRowProps>`
  height: 56px;
  background-color: ${({ $isActive }) =>
    $isActive ? themeColors.borderTable : themeColors.bgPaper};
  transition: ${({ $isActive }) =>
    $isActive
      ? 'background-color 200ms cubic-bezier(0.4, 0, 0.2, 1)'
      : 'background-color 1s cubic-bezier(0.4, 0, 0.2, 1) 300ms'};
`;

const BaseTableCell = styled(TableCell)`
  padding: ${({ theme }) => theme.spacing(0, 0, 0, 1)};
`;

export const ReSubmitCell = styled(BaseTableCell)`
  width: 128px;
`;

export const CollapserCell = styled(TableCell)`
  padding: 0;
  width: 56px;
  text-align: center;
`;

export const ExpandedIcon = styled(KeyboardArrowUp)<AllCodesIconProps>`
  ${({ $isLoading }) => $isLoading && loadingAnimation};
`;

export const CollapsedIcon = styled(KeyboardArrowDown)<AllCodesIconProps>`
  ${({ $isLoading }) => $isLoading && loadingAnimation};
`;

export const StatisticsCell = styled(BaseTableCell)`
  width: 138px;
`;

export const AddCodeCell = styled(BaseTableCell)`
  width: 128px;
`;

export const IdCell = styled(TableCell)`
  padding: 0;
  width: 104px;
`;

export const DomainCell = styled(BaseTableCell)`
  width: 306px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
`;

export const EditCell = styled(BaseTableCell)`
  width: 100px;
`;

export const HideStatisticsCell = styled(TableCell)`
  padding: 0;
  width: 208px;
`;

export const HideStatisticsLabel = styled(FormControlLabel)`
  margin: 0;
`;

export const MenuCell = styled(TableCell)`
  padding: 0;
  width: 56px;
  text-align: center;
`;

export const ReasonCell = styled(BaseTableCell)`
  padding-left: ${({ theme }) => theme.spacing(2)};
  color: ${red[500]};
`;

export const StatusCell = styled(BaseTableCell)<StatusCellProps>`
  color: ${({ $status }) => statusColor[$status]};
  width: 104px;
`;

export const DeclinedStatusIcon = styled(Sync)<DeclinedStatusIconProps>`
  ${({ $isLoading }) => $isLoading && loadingAnimation};
`;
