import { useState } from 'react';
import type { MouseEvent } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Menu, MenuItem } from '@mui/material';
import { MoreVert } from '@mui/icons-material';
import { useAppDispatch } from 'store/hooks';
import { showSnackbar } from 'old-reducer/snackbar';
import { copyIdSnackbarData } from '../../constants';
import * as Styled from './styles';
import type { Props } from './types';

export const WebsiteMenuButton = ({ id }: Props) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const dispatch = useAppDispatch();
  const isOpen = Boolean(anchorEl);

  const handleClick = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleCopyClick = () => {
    dispatch(showSnackbar(copyIdSnackbarData));
    setAnchorEl(null);
  };

  return (
    <>
      <Styled.MenuButton
        onClick={handleClick}
        size="small"
        dataTestId={`sites-list__menu-button-${id}`}
      >
        <MoreVert />
      </Styled.MenuButton>
      <Menu
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={isOpen}
        onClose={handleClose}
      >
        <CopyToClipboard text={String(id)} onCopy={handleCopyClick}>
          <MenuItem data-testid={`sites-list__menu-copy-${id}`}>
            Copy ID
          </MenuItem>
        </CopyToClipboard>
      </Menu>
    </>
  );
};
