import { memo } from 'react';
import { InputLabel, MenuItem, Select } from '@material-ui/core';

import type { ChangeEvent } from 'react';
import type {
  OfferFilterItems,
  OffersFilterValues,
} from 'old-view/offers/types';
import * as Styled from './styles';

interface FilterProps {
  items: OfferFilterItems;
  label: string;
  value: OffersFilterValues;
  onFilterChange(event: ChangeEvent<{ value: OffersFilterValues }>): void;
  dataTestID: string;
  minWidthFormControl?: number;
}

export const OffersFilter = memo(
  ({
    items,
    label,
    onFilterChange,
    minWidthFormControl,
    value,
    dataTestID,
  }: FilterProps) => (
    <Styled.Container>
      <Styled.FormControlStyled minWidth={minWidthFormControl}>
        <InputLabel id="select-label">{label}</InputLabel>
        <Select
          data-testid={dataTestID}
          multiple
          labelId="select-label"
          id="demo-simple-select"
          value={value}
          //  d.ts "event: React.ChangeEvent<{ name?: string; value: unknown }>" -   .
          onChange={(event) =>
            onFilterChange(event as ChangeEvent<{ value: OffersFilterValues }>)
          }
        >
          {items.map(({ id, name }) => (
            <MenuItem key={name} value={id}>
              {name}
            </MenuItem>
          ))}
        </Select>
      </Styled.FormControlStyled>
    </Styled.Container>
  ),
);
