export enum EntityActiveEnum {
  NON_ACTIVE = 1,
  NON_ACTIVE_BY_LIMITS = 2,
  ACTIVE = 3,
  NOT_IN_USE = 4,
}

export const getPlacementActiveTitle = (placementActive: EntityActiveEnum) => {
  switch (placementActive) {
    case EntityActiveEnum.ACTIVE:
      return 'Active';
    case EntityActiveEnum.NOT_IN_USE:
      return 'Inactive';
    default:
      return placementActive;
  }
};

export const getOfferActivityStatusTitleById = (statusId: EntityActiveEnum) => {
  switch (statusId) {
    case EntityActiveEnum.NON_ACTIVE:
      return 'Inactive';
    case EntityActiveEnum.NON_ACTIVE_BY_LIMITS:
      return 'Inactive by limits';
    case EntityActiveEnum.ACTIVE:
      return 'Active';
    case EntityActiveEnum.NOT_IN_USE:
      return 'Not in use';
    default:
      throw new Error(`Unknown offer activity status id: ${statusId}`);
  }
};
