import { memo } from 'react';

import { useAppDispatch } from 'store/hooks';
import { editPlacement } from 'old-reducer/site-page';

import * as Styled from './styles';

import type { Props } from './types';

export const NotInUseStatus = memo(
  ({ activatePlacementId, editPlacement: editPlacementParam, id }: Props) => {
    const isLoading = activatePlacementId === id;

    const dispatch = useAppDispatch();
    const handleReactivate = editPlacementParam
      ? () => editPlacementParam('activate', id)
      : () => dispatch(editPlacement('activate', id));

    return (
      <Styled.StatusButton
        dataTestId="not-in-use-status__button"
        disabled={isLoading}
        startIcon={<Styled.StatusIcon $isLoading={isLoading} />}
        onClick={handleReactivate}
      >
        Reactivate
      </Styled.StatusButton>
    );
  },
);
