import { MINUTE, HOUR, DAY, SECOND } from './constants';

export function formatCountdown(count: number) {
  const seconds = count % MINUTE;
  let left = count - seconds;
  const minutes = left % HOUR;
  left -= minutes;
  const hours = left % DAY;

  const parts = [minutes / MINUTE, seconds / SECOND];
  if (hours > 0) parts.unshift(hours / HOUR);

  return parts.map((t) => `${Math.floor(t)}`.padStart(2, '0')).join(':');
}
