import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { thunkTicketStatuses } from './thunk';
import { selectTicketStatuses, selectStatusTicketStatuses } from './selectors';

export function useGetTicketStatusesQuery() {
  const dispatch = useAppDispatch();

  const status = useAppSelector(selectStatusTicketStatuses);
  const data = useAppSelector(selectTicketStatuses);

  useEffect(() => {
    dispatch(thunkTicketStatuses());
  }, [dispatch]);

  const isUninitialized = status === undefined;
  const isLoading = status === 'pending' || status === undefined;
  const isError = status === 'rejected';
  const isSuccess = status === 'fulfilled';

  return { data, isUninitialized, isLoading, isError, isSuccess };
}
