import { createSlice } from '@reduxjs/toolkit';

import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { getHeaders } from 'utils/http';

import { AlertSeverity } from 'old-enums/alert-severity';
import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';
import { EntityActiveEnum } from 'old-enums/entity-active';
import { GoogleTagManagerEventsTypesEnum } from 'old-enums/google-tag-manager-events-type';
import { SiteStatusEnum } from 'old-enums/site-status';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { WebsitesLoadingTypesEnum } from 'old-enums/websites-loading-types-enum';
import { WebsitesTipTypesEnum } from 'old-enums/websites-tip-types-enum';
import { ZoneCategoryEnum } from 'old-enums/zone-category';
import { getDescriptionOpenState } from 'old-reducer/common';

import type { PayloadAction } from '@reduxjs/toolkit';
import type { PendingRequest } from 'types/pending-request';
import type { Site } from 'types/site';
import type { Thunk } from 'store/types';
import type {
  Dialog,
  DialogData,
  DialogDataKeys,
  DialogDataValues,
  DialogKeys,
  DialogValues,
} from 'old-view/direct-links/form-website/types';
import type { Placement } from 'old-view/direct-links/types';
import { showSnackbar } from './snackbar';
import { snackbarCommonError } from './snackbar/constants';
import type { TipData } from './types';

type DirectLinkPageState = {
  activate_placement_id_loading: number;
  descriptionOpen: boolean;
  dialogData: DialogData;
  loading: WebsitesLoadingTypesEnum;
  site: Site;
  tip: TipData;
};

const initialState: DirectLinkPageState = {
  site: {
    ad_units: [],
    b_hidden: 'false',
    category_id: 0,
    domain: '',
    id: 0,
    n_decline_reason_type: 0,
    na_feed_category_exclude: [],
    pending_request: {
      ad_units: [],
      domain: '',
      id: 0,
      na_feed_category_exclude: [],
    },
    placements: [],
    status: 0,
    t_decline_reason_description: '',
    type: 0,
  },
  activate_placement_id_loading: 0,
  loading: WebsitesLoadingTypesEnum.EMPTY,
  dialogData: {
    action: WebsitesDialogTypesEnum.EMPTY,
    data: {
      ad_unit: 0,
      ad_units: [],
      b_hidden: 'false',
      category_id: null,
      id: 0,
      na_feed_category_exclude: [],
      new_ad_units: [],
      status: 0,
      t_alias: '',
      type: 0,
      zone: '',
    },
    export_type: 'txt',
    form_error: {
      zone: '',
    },
    general_error: '',
    step: DialogStepsEnum.WAIT,
  },
  tip: {
    adunit_id: null,
    anchorEl: null,
    type: WebsitesTipTypesEnum.EMPTY,
  },
  descriptionOpen: getDescriptionOpenState('directlinkDescrOpen'),
};

const {
  actions: {
    setPageLoading,
    setDialogDataData,
    setDialogData,
    setDialogDataProp,
    setDialogProp,
    setSiteData,
    setTipState,
    adUnitAddSuccess,
    setPlacement,
    toggleDescription,
    placementActivateLoading,
  },
  reducer: directLinksPageReducer,
} = createSlice({
  name: 'directLinksPage',
  initialState,
  reducers: {
    placementActivateLoading: (
      state,
      { payload: id }: PayloadAction<number>,
    ) => {
      state.activate_placement_id_loading = id;
    },
    setPageLoading: {
      prepare: (loading: WebsitesLoadingTypesEnum) => ({
        payload: { loading },
      }),
      reducer: (
        state,
        {
          payload: { loading },
        }: PayloadAction<{ loading: WebsitesLoadingTypesEnum }>,
      ) => {
        state.loading = loading;
      },
    },
    setDialogDataData: {
      prepare: (dialogType: WebsitesDialogTypesEnum, dialogData: Dialog) => ({
        payload: { dialogType, dialogData },
      }),
      reducer: (
        state,
        {
          payload: { dialogType, dialogData },
        }: PayloadAction<{
          dialogType: WebsitesDialogTypesEnum;
          dialogData: Dialog;
        }>,
      ) => {
        state.dialogData.action = dialogType;
        state.dialogData.step = DialogStepsEnum.START;
        state.dialogData.data = dialogData;
      },
    },
    setDialogData: {
      prepare: (dialogData: DialogData) => ({ payload: { dialogData } }),
      reducer: (
        state,
        { payload: { dialogData } }: PayloadAction<{ dialogData: DialogData }>,
      ) => {
        state.dialogData = dialogData;
      },
    },
    setDialogDataProp: {
      prepare: (prop: DialogKeys, value: DialogValues) => ({
        payload: { prop, value },
      }),
      reducer: (
        state,
        {
          payload: { prop, value },
        }: PayloadAction<{ prop: DialogKeys; value: DialogValues }>,
      ) => {
        state.dialogData.data = {
          ...state.dialogData.data,
          [prop]: value,
        };
      },
    },
    setDialogProp: {
      prepare: (prop: DialogDataKeys, value: DialogDataValues) => ({
        payload: { prop, value },
      }),
      reducer: (
        state,
        {
          payload: { prop, value },
        }: PayloadAction<{ prop: DialogDataKeys; value: DialogDataValues }>,
      ) => {
        state.dialogData = {
          ...state.dialogData,
          [prop]: value,
        };
      },
    },
    setSiteData: {
      prepare: (site: Site) => ({ payload: { site } }),
      reducer: (
        state,
        { payload: { site } }: PayloadAction<{ site: Site }>,
      ) => {
        state.site = site;
        state.site.placements = site.placements || initialState.site.placements;
      },
    },
    setTipState: {
      prepare: (tipData: TipData) => ({ payload: { tipData } }),
      reducer: (
        state,
        { payload: { tipData } }: PayloadAction<{ tipData: TipData }>,
      ) => {
        state.tip = tipData;
      },
    },
    adUnitAddSuccess: {
      prepare: (request: PendingRequest) => ({ payload: { request } }),
      reducer: (
        state,
        { payload: { request } }: PayloadAction<{ request: PendingRequest }>,
      ) => {
        state.site.pending_request = {
          ...request,
        };
        state.dialogData = {
          ...initialState.dialogData,
          data: {
            ...state.dialogData.data,
          },
          action: WebsitesDialogTypesEnum.ADD_AD_UNIT,
          step: DialogStepsEnum.FINISHED,
        };
      },
    },
    setPlacement: (state, { payload: placement }: PayloadAction<Placement>) => {
      const {
        site: { placements },
      } = state;
      const index = placements.findIndex(
        (statePlacement) => statePlacement.id === placement.id,
      );

      if (index >= 0) {
        placements[index] = placement;
      }

      state.activate_placement_id_loading =
        initialState.activate_placement_id_loading;
      state.loading = initialState.loading;
    },
    toggleDescription: (state, { payload: isOpen }: PayloadAction<boolean>) => {
      state.descriptionOpen = isOpen;
    },
  },
});

const getSitesData = async (token: string) =>
  window.fetch('/api/direct-link/zone', {
    headers: getHeaders(token),
  });

export const getPlacementList = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
    directLinksPage: { site },
  } = getState();

  if (site.placements.length !== 0) {
    return;
  }

  dispatch(setPageLoading(WebsitesLoadingTypesEnum.GET_PLACEMENT_LIST));

  const siteResponse = await getSitesData(token);

  if (siteResponse.status !== 200) {
    dispatch(setPageLoading(WebsitesLoadingTypesEnum.EMPTY));
    dispatch(showSnackbar(snackbarCommonError));

    return;
  }

  const { data: siteData } = (await siteResponse.json()) as {
    data: { items: Site[] };
  };

  if (siteData.items.length !== 1) {
    dispatch(setPageLoading(WebsitesLoadingTypesEnum.EMPTY));

    return;
  }

  const [responseSiteData] = siteData.items;
  const isPendingRequest = [
    SiteStatusEnum.QUEUED_FOR_APPROVAL,
    SiteStatusEnum.PENDING,
  ].includes(responseSiteData.status);

  if (isPendingRequest) {
    dispatch(
      setSiteData({
        ...site,
        pending_request: {
          ...responseSiteData,
        },
      }),
    );
  } else if (responseSiteData.id !== 0) {
    const placementsResponse = await window.fetch(
      `/api/placements/${responseSiteData.id}/extended`,
      {
        headers: getHeaders(token),
      },
    );

    if (placementsResponse.status === 200) {
      const { data: placements } = (await placementsResponse.json()) as {
        data: Placement[];
      };

      dispatch(
        setSiteData({
          ...responseSiteData,
          placements,
        }),
      );
    }
  } else {
    dispatch(
      setSiteData({
        ...responseSiteData,
      }),
    );
  }

  dispatch(setPageLoading(WebsitesLoadingTypesEnum.EMPTY));
};

export const showDialog =
  (
    dialogType: WebsitesDialogTypesEnum,
    data?: PendingRequest | Partial<Placement>,
  ): Thunk =>
  async (dispatch, getState) => {
    const currentState = getState().directLinksPage;
    const isAddingAdUnit = dialogType === WebsitesDialogTypesEnum.ADD_AD_UNIT;

    if (
      isAddingAdUnit &&
      !Array.isArray(currentState.site.pending_request) &&
      currentState.site.pending_request.id !== 0
    ) {
      return;
    }

    let dialogData = {
      ...initialState.dialogData.data,
    };

    if (data && !isAddingAdUnit) {
      dialogData = {
        ...dialogData,
        ...data,
        zone:
          'domain' in data ? data.domain : currentState.dialogData.data.zone,
      };
    }

    if (isAddingAdUnit) {
      dialogData.category_id = ZoneCategoryEnum.ADULT_SOCIALBLACK;
    }

    dispatch(setDialogDataData(dialogType, dialogData));
  };

export const showTip =
  (anchorEl: any, adUnit: number, type: WebsitesTipTypesEnum): Thunk =>
  async (dispatch) => {
    dispatch(
      setTipState({
        adunit_id: adUnit,
        anchorEl,
        type,
      }),
    );
  };

export const getCodes = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
    directLinksPage: {
      dialogData: { export_type: exportType },
    },
  } = getState();

  dispatch(setDialogProp('step', DialogStepsEnum.LOADING));

  setTimeout(async () => {
    const response = await window.fetch('/api/direct-link/codes', {
      headers: getHeaders(token),
    });

    if (response.status !== 200) {
      return;
    }

    const { data } = await response.json();

    if (data !== null) {
      dispatch(setDialogData(initialState.dialogData));
      window.location.assign(data[exportType]);
      dispatch(
        showSnackbar({
          message: SnackbarMessage.DOWNLOAD_FILE,
          severity: AlertSeverity.SUCCESS,
        }),
      );
    } else {
      dispatch(
        setDialogProp('general_error', "You haven't requested any codes."),
      );
    }
  }, 200);
};

export const hideDialog = (): Thunk => async (dispatch) => {
  dispatch(setDialogData(initialState.dialogData));
};

export const setPlacementTitle =
  (title: string): Thunk =>
  async (dispatch) => {
    dispatch(setDialogDataProp('t_alias', title));
  };

export const hideTip = (): Thunk => async (dispatch) => {
  dispatch(setTipState(initialState.tip));
};

export const addPlacement =
  (dialogDataProp?: DialogValues, keyProp?: DialogKeys): Thunk =>
  async (dispatch, getState) => {
    if (dialogDataProp !== undefined && keyProp !== undefined) {
      dispatch(setDialogDataProp(keyProp, dialogDataProp));

      return;
    }

    dispatch(setDialogProp('step', DialogStepsEnum.LOADING));
    const {
      directLinksPage: currentState,
      user: { token },
    } = getState();
    const dialogSiteData = currentState.dialogData.data;

    const placementRequest = {
      na_feed_category_exclude: dialogSiteData.na_feed_category_exclude,
      category_id: dialogSiteData.category_id as number,
    };

    const response = await window.fetch('/api/direct-link', {
      body: JSON.stringify(placementRequest),
      headers: getHeaders(token, { hasBody: true }),
      method: 'POST',
    });

    if (response.status === 200) {
      const {
        data: { item: pendingRequestData },
      } = await response.json();
      pushToGoogleTagManager({
        event: GoogleTagManagerEventsTypesEnum.PUB_ADD_DIRECTLINK_SUCCESS,
      });

      dispatch(
        adUnitAddSuccess({
          ad_units: pendingRequestData.ad_units,
          domain: pendingRequestData.zone,
          id: pendingRequestData.id,
          na_feed_category_exclude: placementRequest.na_feed_category_exclude,
        }),
      );

      return;
    }

    if (response.status === 422) {
      const { data } = await response.json();
      let formError;

      if (data.zone) {
        formError = data.zone;
      } else {
        formError = Object.values(data).join(', ');
      }

      dispatch(
        setDialogProp('form_error', {
          ...currentState.dialogData.form_error,
          zone: formError,
        }),
      );
      dispatch(setDialogProp('step', DialogStepsEnum.START));

      return;
    }

    const { message } = await response.json();

    if (message) {
      dispatch(setDialogProp('step', DialogStepsEnum.START));
      dispatch(setDialogProp('general_error', message));
    }
  };

export const setTypeCodes =
  (exportType: string): Thunk =>
  async (dispatch) => {
    dispatch(setDialogProp('export_type', exportType));
  };

export const editPlacement =
  (type: string, id: number): Thunk =>
  async (dispatch, getState) => {
    if (type === 'dialog') {
      dispatch(setDialogProp('step', DialogStepsEnum.LOADING));
    } else {
      dispatch(placementActivateLoading(id));
    }

    const state = getState();

    const { data: dialogDataData } = state.directLinksPage.dialogData;
    const formErrorData = state.directLinksPage.dialogData.form_error;

    if (!dialogDataData.t_alias && type === 'dialog') {
      dispatch(
        setDialogProp('form_error', {
          ...formErrorData,
          zone: 'Placement Alias is required',
        }),
      );
      dispatch(setDialogProp('step', DialogStepsEnum.START));

      return;
    }

    let placementData;

    if (type === 'dialog') {
      placementData = {
        na_feed_category_exclude: dialogDataData.na_feed_category_exclude,
        t_alias: dialogDataData.t_alias,
      };
    } else {
      placementData = {
        active: EntityActiveEnum.ACTIVE,
      };
    }

    const response = await window.fetch(`/api/placement/${id}`, {
      body: JSON.stringify(placementData),
      headers: getHeaders(state.user.token, { hasBody: true }),
      method: 'PATCH',
    });

    if (response.status === 200) {
      const { data: responsePlacementData } = await response.json();
      dispatch(setPlacement(responsePlacementData));
      dispatch(hideDialog());

      return;
    }

    if (response.status === 422) {
      const { data } = await response.json();

      if (data.t_alias) {
        dispatch(
          setDialogProp('form_error', {
            ...formErrorData,
            zone: 'Placement Alias must not exceed 100 latin characters',
          }),
        );
      }

      dispatch(setDialogProp('step', DialogStepsEnum.START));
    }
  };

export const toggleDescriptionState =
  (isOpen: boolean): Thunk =>
  (dispatch) => {
    localStorage.setItem('directlinkDescrOpen', JSON.stringify(isOpen));
    dispatch(toggleDescription(isOpen));
  };

export { directLinksPageReducer };
