import { CONTENT_TYPE, X_ACCESS_TOKEN } from './constants';
import type { ExtraHeaders, HeadersOptions, Token } from './types';

/**
 *    HTTP-
 */
export const getHeaders = (
  token: Token = null,
  { hasBody }: HeadersOptions = { hasBody: false },
  extraHeaders: ExtraHeaders = {},
) => {
  const headerObject = {
    ...extraHeaders,
  };

  if (token != null) {
    headerObject[X_ACCESS_TOKEN] = token;
  }

  if (hasBody) {
    headerObject[CONTENT_TYPE] = 'application/json; charset=utf-8';
  }

  return headerObject;
};
