import { getHeaders } from 'utils/http';
import type { Placement } from 'old-view/direct-links/types';

interface Response {
  data: boolean;
}

export const saveTemplateParams = async (
  token: string,
  id: number,
  values: Placement['template_values'],
): Promise<boolean> => {
  try {
    const res = await window.fetch(`/api/placement/${id}/template`, {
      body: JSON.stringify(values),
      headers: getHeaders(token, { hasBody: true }),
      method: 'PATCH',
    });

    if (res.status === 200) {
      const { data }: Response = await res.json();

      return data;
    }

    return false;
  } catch (e) {
    // eslint-disable-next-line no-console
    console.warn('Unable to save template params', e);

    return false;
  }
};
