import { DocumentStatus } from 'old-enums/document-status';
import { DocumentType } from 'old-enums/document-type';
import { themeColors } from 'styles/theme/constants';

export const getDocumentStatusById = (id: DocumentStatus) => {
  switch (id) {
    case DocumentStatus.VERIFICATION:
      return 'Verification (1-2 business days)';
    case DocumentStatus.VALID:
      return 'Valid';
    case DocumentStatus.INVALID:
      return 'Invalid';
    case DocumentStatus.PENDING_SIGNATURE:
      return 'Pending signature';
    default:
      return '';
  }
};

export const getDocumentTypeById = (id: DocumentType) => {
  switch (id) {
    case DocumentType.ID_PHOTO_SCAN:
      return 'ID Photo/Scan';
    case DocumentType.AGREEMENT:
      return 'Agreement';
    case DocumentType.INSERTION_ORDER:
      return 'Insertion Order';
    case DocumentType.OTHER_PHOTO_SCAN:
      return 'Other Photo/Scan';
    case DocumentType.INVOICE:
      return 'Invoice';
    default:
      return '';
  }
};

export const getStatusTextColor = (id: DocumentStatus) => {
  switch (id) {
    case DocumentStatus.VERIFICATION:
    case DocumentStatus.INVALID:
      return themeColors.tertiaryLight;
    case DocumentStatus.VALID:
      return themeColors.successDark;
    case DocumentStatus.PENDING_SIGNATURE:
      return themeColors.warningDark;
    default:
      return '';
  }
};
