import type { FC, ReactElement } from 'react';
import React, { memo, useState } from 'react';
import { Dialog, DialogTitle } from '@material-ui/core';
import { Button } from '@development/ui-kit';
import CloseIcon from '@material-ui/icons/Close';

import { PreviewImage } from '../preview-image';
import * as Styled from './styles';

type Props = {
  buttonClassName: string;
  buttonLabel: string;
  buttonIcon?: ReactElement | null;
  url: string;
};

export const PreviewDialog: FC<Props> = memo(
  ({ buttonClassName, buttonLabel, buttonIcon, url }) => {
    const [open, setOpen] = useState(false);

    const toggle = () => {
      setOpen(!open);
    };

    return (
      <>
        <Button
          startIcon={buttonIcon}
          dataTestId="preview-dialog__button"
          className={buttonClassName}
          onClick={toggle}
        >
          {buttonLabel}
        </Button>

        <Dialog open={open} onClose={toggle}>
          <DialogTitle>
            <Styled.CloseButton onClick={toggle}>
              <CloseIcon />
            </Styled.CloseButton>
          </DialogTitle>
          <Styled.DialogContentStyled>
            <PreviewImage height={300} url={url} />
          </Styled.DialogContentStyled>
        </Dialog>
      </>
    );
  },
);
