import { Component } from 'react';

import { CircularProgress, Grid } from '@material-ui/core';
import { connect } from 'react-redux';

import { requestAntiAdblock } from 'old-reducer/site-page';

import type { AppState } from 'store/types';
import { Typography, Dialog } from 'old-component';
import { Button } from '@development/ui-kit';

import { showSnackbar } from 'old-reducer/snackbar';
import { DialogStatus } from 'old-enums/dialog-status';
import { AlertSeverity } from 'old-enums/alert-severity';
import { SnackbarMessage } from 'old-enums/snackbar-message';

import type { Dispatchers, Props, State } from './types';

import * as Styled from './styles';
import { AntiAdblockIcon } from './icon';
import { dialogProps, dialogText } from './constants';

class RequestAntiAdblock extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      isLoading: false,
      isAntiAdblockDialogOpen: DialogStatus.HIDE,
    };
  }

  private onRequestAntiAdblock = () => {
    const { isLoading } = this.state;
    if (isLoading) return;

    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { requestAntiAdblock } = this.props;

    const start = Date.now();
    this.setState({ isLoading: true }, async () => {
      await requestAntiAdblock();

      const diff = Date.now() - start;

      if (diff < 1000) {
        setTimeout(() => this.requestAntiAdblockCallback(), 1000 - diff);
      } else {
        this.requestAntiAdblockCallback();
      }
    });
  };

  private onDialogOpen = () => {
    this.setState({ isAntiAdblockDialogOpen: DialogStatus.SHOW });
  };

  private onDialogClose = () => {
    this.setState({ isAntiAdblockDialogOpen: DialogStatus.HIDE });
  };

  private onDialogSubmit = () => {
    this.onDialogClose();
    this.onRequestAntiAdblock();
  };

  private hasAntiAdblockStatus = () => {
    const {
      account_info: { aa_request_status },
    } = this.props;

    return !!aa_request_status;
  };

  private requestAntiAdblockCallback = () => {
    this.setState({ isLoading: false });

    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { showSnackbar } = this.props;

    let showSnackbarData = {
      message: SnackbarMessage.SUCCESS_ANTIADBLOCK_REQUEST,
      severity: AlertSeverity.SUCCESS,
    };

    if (!this.hasAntiAdblockStatus()) {
      showSnackbarData = {
        message: SnackbarMessage.ERROR,
        severity: AlertSeverity.ERROR,
      };
    }

    showSnackbar(showSnackbarData);
  };

  render() {
    const { isLoading, isAntiAdblockDialogOpen } = this.state;
    const { submitButtonText, cancelButtonText, title, width, dataTestId } =
      dialogProps;

    return (
      <>
        <Styled.GridContainer
          container
          spacing={2}
          wrap="nowrap"
          direction="row"
          justifyContent="space-between"
          alignItems="flex-start"
        >
          <Grid item>
            <Typography variant="body1">
              Monetize traffic from adblock users with our custom domains.
              <br />
              They work as regular ads, no additional code required. Support all
              formats except video (VAST)
            </Typography>
          </Grid>
          <Grid item>
            <Grid
              container
              spacing={1}
              direction="column"
              justifyContent="flex-start"
              alignItems="center"
            >
              <Grid item>
                <Button
                  dataTestId="anti-adblock__request-button"
                  color="primary"
                  disabled={this.hasAntiAdblockStatus()}
                  startIcon={<AntiAdblockIcon />}
                  variant="contained"
                  onClick={this.onDialogOpen}
                >
                  Request anti-adblock
                </Button>
              </Grid>
              <Grid item>
                <Typography variant="caption" color="textSecondary">
                  Request once to apply everywhere
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Styled.GridContainer>
        <Dialog
          submitButtonText={submitButtonText}
          cancelButtonText={cancelButtonText}
          title={title}
          width={width}
          dataTestId={dataTestId}
          onSubmit={this.onDialogSubmit}
          onCancel={this.onDialogClose}
          status={isAntiAdblockDialogOpen}
        >
          {dialogText}
        </Dialog>
        {isLoading && (
          <Styled.Loader>
            <CircularProgress />
          </Styled.Loader>
        )}
      </>
    );
  }
}

// eslint-disable-next-line
export default connect<AppState['user'], Dispatchers, {}, AppState>(
  (state) => state.user,
  {
    requestAntiAdblock,
    showSnackbar,
  },
)(RequestAntiAdblock);
