import { memo } from 'react';

import { AdUnitEnum } from 'old-enums/ad-units';
import { EntityActiveEnum } from 'old-enums/entity-active';

import { WebPushStatus } from './web-push-status';
import { NotInUseStatus } from './not-in-use-status';
import { CommonStatus } from './common-status';

import type { Props } from './types';

export const PlacementStatus = memo(
  ({ activity, activatePlacementId, adUnit, editPlacement, id }: Props) => {
    if (adUnit === AdUnitEnum.WEB_PUSH) {
      return <WebPushStatus />;
    }

    if (activity === EntityActiveEnum.NOT_IN_USE) {
      return (
        <NotInUseStatus
          activatePlacementId={activatePlacementId}
          editPlacement={editPlacement}
          id={id}
        />
      );
    }

    return <CommonStatus activity={activity} id={id} />;
  },
);
