import { Checkbox, FormControlLabel } from '@material-ui/core';
import { IndeterminateCheckBox } from '@material-ui/icons';

import { FeedEnum, getFeedTitle } from 'old-enums/feed';

import type { ChangeEvent } from 'react';
import type { Props } from './types';

import * as Styled from './styles';

const feeds = [
  [FeedEnum.EROTIC_ADS, FeedEnum.SOFTWARE_ADS, FeedEnum.ALERT_ADS],
  [FeedEnum.ADS_WITH_SOUND, FeedEnum.GAMBLING_ADS],
];

export const Feeds = ({
  findCategory,
  onChangeCategory,
  shouldShowNote = true,
}: Props) => (
  <div>
    <Styled.Caption>
      <Styled.CaptionAction>Remove</Styled.CaptionAction> campaigns with
    </Styled.Caption>
    <Styled.Columns>
      {feeds.map((column) => (
        <Styled.Column key={column.join('-')}>
          {column.map((item) => {
            const isChecked = findCategory(item);

            return (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={isChecked}
                    checkedIcon={<IndeterminateCheckBox />}
                    color="secondary"
                    value={isChecked ? 'on' : 'off'}
                    onChange={(event: ChangeEvent<HTMLInputElement>) =>
                      onChangeCategory(item, event.target.checked)
                    }
                  />
                }
                data-testid="dialog__feed-list"
                key={item}
                label={getFeedTitle(item)}
              />
            );
          })}
        </Styled.Column>
      ))}
    </Styled.Columns>
    {shouldShowNote && (
      <Styled.Note>
        Your CPM value will be higher if you allow more ads
      </Styled.Note>
    )}
  </div>
);
