export const sign = (num: number): '-' | '+' => (num < 0 ? '-' : '+');

export const formatAmount = (num: number, dropCurrency?: boolean): string => {
  const arr = num.toFixed(2).split('.');
  arr[0] = arr[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, ',');

  return `${dropCurrency ? '' : '$'}${arr.join('.')}`;
};
