import type { FC } from 'react';
import React, { memo } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button } from '@development/ui-kit';

import * as Styled from './styles';

type Props = {
  label?: string;
  textToCopy: string;
  onCopy(): void;
  className?: string;
};

const getButton = (label?: string) => {
  if (label != null) {
    return (
      <Button
        dataTestId="copy-to-clipboard-button__button"
        endIcon={<Styled.FileCopyOutlinedStyled />}
        size="small"
      >
        {label}
      </Button>
    );
  }

  return (
    <Styled.IconButtonStyled>
      <Styled.FileCopyOutlinedStyled />
    </Styled.IconButtonStyled>
  );
};

export const CopyToClipboardButton: FC<Props> = memo(
  ({ label, textToCopy, onCopy, className }) => (
    <Styled.Container className={className}>
      <CopyToClipboard text={textToCopy} onCopy={onCopy}>
        {getButton(label)}
      </CopyToClipboard>
    </Styled.Container>
  ),
);
