import type { SummaryProps } from '@development/ui-kit';
import { SummaryProcess } from 'old-enums/summary-process';
import { PayoutType } from 'old-enums/payout-type';
import { TransactionType } from 'old-enums/transaction-type';
import { ColumnNamesForPaymentHistory } from './column-names';

const excludedTypes = [PayoutType.NEXT_PAYOUT, PayoutType.PENDING_PAYOUT];

export const calculatePaymentRows = (options: SummaryProps['options']) => {
  const isSummaryColumn = [
    ColumnNamesForPaymentHistory.PAYMENT_AMOUNT,
    ColumnNamesForPaymentHistory.FEE_AMOUNT,
  ].includes(options.name as ColumnNamesForPaymentHistory);

  if (!isSummaryColumn) {
    return;
  }

  if (options.summaryProcess === SummaryProcess.START) {
    options.totalValue = 0;
  } else if (
    options.summaryProcess === SummaryProcess.CALCULATE &&
    options.name &&
    !excludedTypes.includes(options.value.type) &&
    options.value.transaction_type !== TransactionType.REFERRAL
  ) {
    options.totalValue += options.value[options.name];
  }
};
