import { memo } from 'react';
import { Link } from 'react-router-dom';
import { Button } from '@development/ui-kit';
import { Grid } from '@mui/material';

import { ClassNameForTesting } from 'old-enums/class-name-for-testing';
import { OffersTabEnum } from 'old-enums/offers-tab';
import type { OfferItem } from 'old-view/offer/types';
import * as Styled from './styles';

type Props = {
  name: OfferItem['t_name'];
  id: OfferItem['id_offer'];
  tabId?: OffersTabEnum;
};

const getOfferNameContent = (id: number, name: string, tabId?: number) =>
  tabId === OffersTabEnum.MY_OFFERS ? (
    <Styled.LinkTitle data-testid="offer-name-link">
      <Link to={`/offer/${id}`}>{name}</Link>
    </Styled.LinkTitle>
  ) : (
    <Styled.Title>{name}</Styled.Title>
  );

export const OfferCell = memo(({ id, name, tabId }: Props) => {
  const isTabMyOffers = tabId === OffersTabEnum.MY_OFFERS;

  return (
    <Grid container rowSpacing={1} direction="column">
      <Grid item>{getOfferNameContent(id, name, tabId)}</Grid>
      {!isTabMyOffers && (
        <Grid item>
          <Button
            dataTestId="offers__get-offer-button"
            variant="contained"
            className={ClassNameForTesting.GET_LINK_BUTTON}
            href={`/offer/${id}`}
            size="small"
          >
            Get offer
          </Button>
        </Grid>
      )}
    </Grid>
  );
});
