import { useContext } from 'react';
import { GetApp } from '@mui/icons-material';

import { Button } from '@development/ui-kit';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { AdUnitEnum } from 'old-enums/ad-units';
import { SiteStatusEnum } from 'old-enums/site-status';
import { SiteType } from 'old-enums/site-type';

import type { Site } from 'types/site';
import { SitesTableContext } from '../sites-table-context';

import * as Styled from './styles';

const areActiveSitesAndPlacementsExist = (sitesInfo: Site[]) => {
  const neededAdUnits = [
    AdUnitEnum.POPUNDER,
    AdUnitEnum.DIRECT_LINK,
    AdUnitEnum.SOCIAL_BAR,
  ];

  const isDirectLinkFakeZone =
    sitesInfo[0].n_type !== undefined &&
    sitesInfo[0].n_type === SiteType.DIRECT_LINK;
  const hasDirectLinkActivePlacements =
    isDirectLinkFakeZone && sitesInfo[0].placements.length !== 0;

  if (hasDirectLinkActivePlacements) {
    return true;
  }

  return sitesInfo.some(({ status, ad_units }) => {
    const isApproved = status === SiteStatusEnum.APPROVED;
    const isNeededAdUnit = neededAdUnits.some((adUnit) =>
      ad_units.includes(adUnit),
    );

    return isApproved && isNeededAdUnit;
  });
};

export const TitleButtonsContainer = () => {
  const { sites, showDialogHandler } = useContext(SitesTableContext);

  return (
    <Styled.ButtonsContainer container>
      {areActiveSitesAndPlacementsExist(sites) && (
        <Styled.ExportCodesItem item>
          <Button
            dataTestId="direct-link__export-codes-button"
            startIcon={<GetApp />}
            variant="outlined"
            onClick={() =>
              showDialogHandler(WebsitesDialogTypesEnum.EXPORT_CODES)
            }
          >
            Export codes
          </Button>
        </Styled.ExportCodesItem>
      )}
    </Styled.ButtonsContainer>
  );
};
