import { IconButton } from '@material-ui/core';
import type { IconButtonControlProps } from './types';

export const IconButtonControl = ({
  ariaLabel = '',
  dataTestId,
  href,
  target,
  children,
  onClick,
  color,
  size,
  title,
  className,
  edge,
  disabled,
}: IconButtonControlProps) => {
  if (href !== undefined) {
    return (
      <IconButton
        aria-label={ariaLabel}
        href={href}
        target={target}
        data-testid={dataTestId}
        title={title}
        className={className}
        edge={edge}
        disabled={disabled}
      >
        {children}
      </IconButton>
    );
  }

  return (
    <IconButton
      aria-label={ariaLabel}
      data-testid={dataTestId}
      onClick={onClick}
      color={color}
      size={size}
      title={title}
      className={className}
      edge={edge}
      disabled={disabled}
    >
      {children}
    </IconButton>
  );
};
