import React, { memo } from 'react';
import { TableCell } from '@material-ui/core';

import { OffersTabEnum } from 'old-enums/offers-tab';
import * as Styled from '../styles';

export const Header = memo(({ tabId }: { tabId: OffersTabEnum }) => {
  if (tabId === OffersTabEnum.MY_OFFERS) {
    return (
      <Styled.Header>
        <Styled.Id>ID</Styled.Id>
        <Styled.UserPreview />
        <TableCell>Offer</TableCell>
        <Styled.Links>Offer links</Styled.Links>
        <Styled.UserVertical>Vertical</Styled.UserVertical>
        <Styled.UserPayout>Payout</Styled.UserPayout>
        <Styled.UserConversionType>Conversion type</Styled.UserConversionType>
        <TableCell>Geo</TableCell>
        <Styled.Stats />
      </Styled.Header>
    );
  }

  return (
    <Styled.Header>
      <Styled.Id>ID</Styled.Id>
      <Styled.OfferPreview />
      <TableCell>Offer</TableCell>
      <TableCell>Tags</TableCell>
      <Styled.OfferVertical>Vertical</Styled.OfferVertical>
      <Styled.OfferPayout>Payout</Styled.OfferPayout>
      <Styled.OfferConversionType>Conversion type</Styled.OfferConversionType>
      <TableCell>Geo</TableCell>
    </Styled.Header>
  );
});
