import { Alert } from '@development/ui-kit';
import type { Props } from './types';

export const NextPayoutAlert = ({
  payoutDate,
  payoutAmount,
  prefix,
}: Props) => {
  const preparedAmount = Math.abs(Number(payoutAmount)).toFixed(2);
  const alertMessage = `${payoutDate} ${prefix + preparedAmount}`;

  return (
    <Alert
      dataTestId="payments-next-payout-info"
      message={alertMessage}
      severity="info"
      title="Next Payout:"
    />
  );
};
