import { baseDomain } from 'constants/index';
import adsterraLogo from 'assets/images/adsterra-logo.svg';
import * as Styled from './styles';

export const HeaderSign = () => {
  const contactUsLink = window.location.hostname.includes('beta.affiliates.')
    ? 'mailto:affiliate.support@adsterra.com'
    : 'mailto:publishers@adsterra.com';

  return (
    <Styled.Header>
      <div className="contentSign">
        <Styled.LogoLink href={`https://${baseDomain}`}>
          <Styled.Logo src={adsterraLogo} alt="Adsterra Network" />
        </Styled.LogoLink>
        <Styled.Nav>
          <Styled.HeaderButton
            dataTestId="sign-in__contact-button"
            href={contactUsLink}
          >
            Contact us
          </Styled.HeaderButton>
        </Styled.Nav>
      </div>
    </Styled.Header>
  );
};
