import { isAffiliateDomain } from 'constants/index';
import { getRecaptchaToken, getRecaptchaTokenV2 } from 'lib/google-recaptcha';
import type { FormFieldsSignup } from 'features/signup/components/form/types';
import { emptySelectOption } from 'features/signup/components/form/constants';
import type { SelectOption } from './types';

type PaysystemsApi = {
  [key: number]: { title: string };
};

const getURLSearchParams = () => new URLSearchParams(window.location.search);

export const preparePaysystemsForSelect = (data: PaysystemsApi) => {
  const paySystemsList = Object.keys(data).map(
    (id): SelectOption => [Number(id), data[id].title],
  );

  return [emptySelectOption, ...paySystemsList];
};

export const getAdvertiserName = () => {
  const urlSearchParams = getURLSearchParams();

  return urlSearchParams.get('name') ?? '';
};

export const getAdvertiserId = () => {
  const urlSearchParams = getURLSearchParams();

  return urlSearchParams.has('id') ? Number(urlSearchParams.get('id')) : null;
};

const advertiserId = getAdvertiserId();
export const isSignUpAfterShowingAffiliatePopup = getAdvertiserId() !== null;

const getContext = () => {
  const generalFields = {
    isAffiliateDomain,
    pageUrl: window.location.href,
  };

  if (!isSignUpAfterShowingAffiliatePopup) {
    return generalFields;
  }

  return { ...generalFields, advertiserID: advertiserId };
};

const getToken = async (wasRecaptchaRendered: boolean) => {
  const result = wasRecaptchaRendered
    ? await getRecaptchaTokenV2()
    : await getRecaptchaToken('sign_up_pub');

  return result;
};

export const prepareFormDataForBack = async ({
  fields,
  recaptcha,
}: {
  fields: FormFieldsSignup;
  recaptcha: any;
}) => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { areTermsAccepted, ...formData } = fields;

  return {
    ...formData,
    token: await getToken(recaptcha.wasRendered),
    context: getContext(),
    isRecaptchaV2: recaptcha.wasRendered,
  };
};
