import type { PayloadAction } from '@reduxjs/toolkit';
import type { OfferTag } from 'old-enums/offer-tag';
import type { ConversionTypeEnum } from 'old-enums/conversion-type';
import type { OfferExclusivityTypeEnum } from 'old-enums/offer-exclusivity-type';
import type { OfferVerticalEnum } from 'old-enums/offer-vertical';
import type { Maybe } from 'types/utils';
import type { Thunk } from 'store/types';

import { createSlice } from '@reduxjs/toolkit';
import { getHeaders } from 'utils/http';
import { OffersTabEnum } from 'old-enums/offers-tab';
import type { OfferItem } from 'old-view/offer/types';
import type { OffersFilterValues, SearchResult } from 'old-view/offers/types';

import type { Country } from 'old-view/types';
import { showSnackbar } from './snackbar';
import { snackbarCommonError } from './snackbar/constants';

const ITEMS_PER_PAGE = 20;
const LOCAL_STORAGE_KEY = 'isOffersDescriptionExpanded';

const getOffersDescriptionExpandState = (): boolean => {
  const value = localStorage.getItem(LOCAL_STORAGE_KEY);

  if (value !== null) {
    return JSON.parse(value);
  }

  return true;
};

const isFilterValueNotEmpty = (value: OffersFilterValues | null) => {
  const isNumber = typeof value === 'number';
  const isNotEmptyStringOrArray =
    (typeof value === 'string' || Array.isArray(value)) && value.length !== 0;

  return isNumber || isNotEmptyStringOrArray;
};

interface OffersListRequestBody {
  items_per_page: number;
  page: number;
  b_my_offers?: boolean;
  b_top?: boolean;
  id?: string;
  ida_advertiser_category?: OfferVerticalEnum[];
  ida_country?: Country['id'][];
  is_new?: boolean;
  n_exclusivity_type?: OfferExclusivityTypeEnum;
  na_conversion_type?: ConversionTypeEnum[];
  search?: string;
  tags?: OfferTag[];
}

export interface OffersPageState {
  activeTab: OffersTabEnum;
  filters: {
    id: number | null;
    ida_country: Country['id'][];
    ida_advertiser_category: OfferVerticalEnum[];
    na_conversion_type: ConversionTypeEnum[];
    search: string;
    tags: OfferTag[];
  };
  isDescriptionExpanded: boolean;
  isLoading: boolean;
  list: {
    currentPage: number;
    pagesCount: number;
    items: OfferItem[];
    allOffersCount: number;
    myOffersCount: number;
  };
  search: {
    inputText: string;
    resultList: SearchResult[];
  };
  tooltip: {
    anchorId: Maybe<string>;
    content: Maybe<string>;
  };
  countries: Country[];
}

export const searchFieldDefaultValue = {
  id: undefined,
  search: '',
};

const initialState: OffersPageState = {
  activeTab: OffersTabEnum.ALL,
  filters: {
    na_conversion_type: [],
    id: null,
    ida_country: [],
    ida_advertiser_category: [],
    tags: [],
    search: '',
  },
  isDescriptionExpanded: getOffersDescriptionExpandState(),
  isLoading: false,
  list: {
    currentPage: 1,
    items: [],
    pagesCount: 1,
    allOffersCount: 0,
    myOffersCount: 0,
  },
  search: {
    inputText: '',
    resultList: [],
  },
  tooltip: {
    anchorId: null,
    content: null,
  },
  countries: [],
};

const {
  actions: {
    expandDescription,
    hideTooltip,
    setActiveTab,
    setDefaultFilters,
    setFilter,
    setLoadingState,
    setOffersListData,
    setResultListSearchData,
    setCountries,
    setSearchInputText,
    showTooltip,
  },
  reducer: offersPageReducer,
} = createSlice({
  name: 'offersPage',
  initialState,
  reducers: {
    expandDescription: (state, { payload: isOpen }: PayloadAction<boolean>) => {
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(isOpen));
      state.isDescriptionExpanded = isOpen;
    },
    setActiveTab: (
      state,
      { payload: newActiveTab }: PayloadAction<OffersTabEnum>,
    ) => {
      state.activeTab = newActiveTab;
      state.filters = initialState.filters;
      state.search.inputText = initialState.search.inputText;
    },
    setDefaultFilters: (state) => {
      state.filters = initialState.filters;
    },
    setFilter: {
      prepare: (name: string, value: OffersFilterValues) => ({
        payload: { name, value },
      }),
      reducer: (
        state,
        {
          payload: { name, value },
        }: PayloadAction<{ name: string; value: OffersFilterValues }>,
      ) => {
        state.filters[name] = value;
      },
    },
    setLoadingState: (
      state,
      { payload: isLoading }: PayloadAction<boolean>,
    ) => {
      state.isLoading = isLoading;
    },
    setOffersListData: {
      prepare: (
        items: OfferItem[],
        currentPage: number,
        pagesCount: number,
        allOffersCount: number,
        myOffersCount: number,
      ) => ({
        payload: {
          currentPage,
          items,
          pagesCount,
          allOffersCount,
          myOffersCount,
        },
      }),
      reducer: (state, { payload }: PayloadAction<OffersPageState['list']>) => {
        state.isLoading = false;
        state.list = payload;
      },
    },
    setResultListSearchData: {
      prepare: (items: SearchResult[]) => ({ payload: { items } }),
      reducer: (
        state,
        { payload: { items } }: PayloadAction<{ items: SearchResult[] }>,
      ) => {
        state.search.resultList = items;
      },
    },
    setCountries: (state, { payload }: PayloadAction<Country[]>) => {
      state.countries = payload;
    },
    setSearchInputText: (state, { payload }) => {
      state.search.inputText = payload;
    },
    showTooltip: {
      prepare: (anchorId: string, content: string) => ({
        payload: { anchorId, content },
      }),
      reducer: (
        state,
        { payload }: PayloadAction<OffersPageState['tooltip']>,
      ) => {
        state.tooltip = payload;
      },
    },
    hideTooltip: (state) => {
      state.tooltip = {
        anchorId: null,
        content: null,
      };
    },
  },
});

const getOffersListData =
  (pageNumber = 1): Thunk =>
  async (dispatch, getState) => {
    const {
      offersPage: { activeTab, filters },
      user: { token },
    } = getState();

    dispatch(setLoadingState(true));

    try {
      const data: OffersListRequestBody = {
        items_per_page: ITEMS_PER_PAGE,
        page: pageNumber,
      };

      if (activeTab === OffersTabEnum.MY_OFFERS) {
        data.b_my_offers = true;
      }

      for (const [filter, value] of Object.entries(filters)) {
        if (isFilterValueNotEmpty(value)) {
          data[filter] = value;
        }
      }

      const response = await window.fetch('api/offers-list', {
        body: JSON.stringify(data),
        headers: getHeaders(token, { hasBody: true }),
        method: 'POST',
      });
      const {
        data: {
          items,
          page,
          pages,
          all_offers_count: allOffersCount,
          my_offers_count: myOffersCount,
        },
      } = await response.json();

      dispatch(
        setOffersListData(items, page, pages, allOffersCount, myOffersCount),
      );
    } catch {
      dispatch(setLoadingState(false));
    }
  };

const getCountries = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  try {
    const response = await window.fetch('/api/countries', {
      headers: getHeaders(token),
    });

    if (response.status === 200) {
      const { data }: { data: Country[] } = await response.json();

      dispatch(setCountries(data));
    } else {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  }
};

const getOffersListSearchData = (): Thunk => async (dispatch, getState) => {
  const {
    offersPage: {
      filters: { search },
    },
    user: { token },
  } = getState();

  try {
    const data = { search };

    const response = await window.fetch('api/simple-offers-list', {
      body: JSON.stringify(data),
      headers: getHeaders(token, { hasBody: true }),
      method: 'POST',
    });

    if (response.status !== 200) {
      dispatch(showSnackbar(snackbarCommonError));

      return;
    }

    const { data: items } = await response.json();

    dispatch(setResultListSearchData(items));
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  }
};

const resetFilters = (): Thunk => async (dispatch) => {
  dispatch(setDefaultFilters());
  dispatch(getOffersListData());
};

const setFilterData =
  (filterName: string, value: OffersFilterValues): Thunk =>
  async (dispatch) => {
    dispatch(setFilter(filterName, value));
    dispatch(getOffersListData());
  };

const setSearchFilterData =
  (filterName: string, value: string): Thunk =>
  async (dispatch) => {
    dispatch(setFilter(filterName, value));
    dispatch(getOffersListSearchData());
  };

export {
  expandDescription,
  hideTooltip,
  getOffersListData,
  getCountries,
  resetFilters,
  setActiveTab,
  setDefaultFilters,
  setFilter,
  setFilterData,
  setLoadingState,
  setOffersListData,
  setResultListSearchData,
  setSearchFilterData,
  setSearchInputText,
  showTooltip,
  offersPageReducer,
};
