import type { ChangeEvent, KeyboardEvent } from 'react';
import { useCallback, useEffect, useState } from 'react';

import { getPassword } from 'old-reducer/common/thunk';
import { setUser, setAction } from 'old-reducer/sign-in';
import { useAppDispatch } from 'store/hooks';

import { string } from 'yup';
import * as Styled from 'pages/signin/styles';
import { SignInActionsEnum } from 'old-enums/sign-in-actions-enum';

import { Typography, Link } from '@development/ui-kit';

import { ENTER_KEY_CODE } from 'pages/signin/constants';

import type { ForgotPasswordProps } from './types';

export const ForgotPassword = ({
  formError,
  loading,
  forgotPassword,
  generalError,
  message,
}: ForgotPasswordProps) => {
  const dispatch = useAppDispatch();
  const [emailValid, setEmailValid] = useState(false);

  const validateEmail = useCallback(async () => {
    const validationSchema = string().required().email();

    try {
      await validationSchema.validate(forgotPassword);
      setEmailValid(true);
    } catch {
      setEmailValid(false);
    }
  }, [forgotPassword]);

  useEffect(() => {
    validateEmail();
  }, [validateEmail]);

  const onGoBackToLoginButtonClick = () => {
    dispatch(setAction(SignInActionsEnum.SIGN_IN));
  };

  const onEmailChange = (event: ChangeEvent<HTMLInputElement>) => {
    const emailValue = event.target.value.trim();
    dispatch(setUser('email', emailValue));
  };

  const onForgotPasswordSubmit = () => {
    dispatch(getPassword());
  };

  const onForgotPasswordKeyDown = ({
    code,
  }: KeyboardEvent<HTMLImageElement>) => {
    if (code === ENTER_KEY_CODE) {
      onForgotPasswordSubmit();
    }
  };

  return (
    <>
      <Styled.LeftSideHeader>
        <Typography variant="h2">Reset your password</Typography>
      </Styled.LeftSideHeader>
      {generalError && !message && (
        <Styled.ErrorText>{generalError}</Styled.ErrorText>
      )}
      {message && <Styled.Message>{message}</Styled.Message>}
      <Styled.ForgotPasswordTextWrapper>
        <Typography>
          Enter email associated with your account, and we will send you reset
          instructions
        </Typography>
      </Styled.ForgotPasswordTextWrapper>
      <Styled.ForgotPasswordField
        dataTestId="forgot-password__email"
        error={formError.email}
        helperText={formError.email}
        key="email"
        label="Email"
        required
        value={forgotPassword}
        onChange={onEmailChange}
        onKeyDown={onForgotPasswordKeyDown}
        variant="outlined"
      />
      <Styled.LoginPageButton
        dataTestId="sign-in__get-instructions-button"
        disabled={loading || !emailValid}
        color="tertiary"
        variant="contained"
        onClick={onForgotPasswordSubmit}
        isLoading={loading}
      >
        Get instructions
      </Styled.LoginPageButton>
      <Styled.ContactNote>
        <Typography variant="caption">
          If you cant receive email,&nbsp;
          <Styled.LinkSolidUnderline
            href="mailto:publishers@adsterra.com"
            text="contact us"
            color="secondary"
            dataTestId="sign-in__contact-us"
          />
        </Typography>
      </Styled.ContactNote>
      <Styled.Note>
        <Typography variant="caption">
          Remember password?&nbsp;
          <Link
            text="Go back to log-in page"
            dataTestId="signin__remember-password"
            onClick={onGoBackToLoginButtonClick}
            color="textSecondary"
          />
        </Typography>
      </Styled.Note>
      <div>
        <div className="inner" id="recaptchav2" />
      </div>
    </>
  );
};
