import { Button } from '@mui/material';
import styled from 'styled-components';
import type { PaginationItemProps } from './types';

const BasicItem = styled(Button)`
  height: 40px;
  min-width: 35px;
`;

export const Pagination = styled.div`
  margin-left: ${({ theme }) => theme.spacing(1)};
  padding-bottom: ${({ theme }) => theme.spacing(2)};
`;

export const PaginationItem = styled(BasicItem)<PaginationItemProps>`
  border: ${({ $isActive, theme }) =>
    $isActive ? `2px solid ${theme.palette.secondary.main}` : 'none'};
  color: ${({ $isActive, theme }) =>
    $isActive ? theme.palette.text.primary : theme.palette.grey[500]};
`;

export const PaginationArrow = styled(BasicItem)`
  color: ${({ theme }) => theme.palette.grey[900]};
`;

export const Spacer = styled(BasicItem)`
  color: ${({ theme }) => theme.palette.grey[500]};
`;
