const MAX_STEP = 1000;

/**
 *    Amplitude    
 */
export const setAmplitudeInstanceData = (
  userId: string | null,
  userProps?: object,
) => {
  let step = 0;
  let isLoaded = false;

  const intl = setInterval(() => {
    if (window.amplitude != null) {
      const instance = window.amplitude.getInstance();

      if (userId !== null) {
        instance.setUserId(userId);
      }

      if (userProps != null) {
        instance.setUserProperties(userProps);
      }
      isLoaded = true;
    } else {
      step += 1;
    }

    if (isLoaded || step > MAX_STEP) {
      clearInterval(intl);
    }
  }, 16);
};
