import { PaysystemEnum } from 'old-enums/paysystem';

import paxum from './assets/paxum.svg';
import paypal from './assets/paypal.svg';
import paypalHw from './assets/paypal-hw.svg';
import webmoney from './assets/webmoney.svg';
import wiretransfer from './assets/wiretransfer.svg';
import bitcoin from './assets/bitcoin.svg';
import localBankTransfer from './assets/local-bank-transfer.svg';
import tether from './assets/tether.svg';

export const getPaysystemLogo = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.PAXUM:
      return paxum;
    case PaysystemEnum.PAYPAL:
      return paypal;
    case PaysystemEnum.PAYPAL_HW:
      return paypalHw;
    case PaysystemEnum.WEBMONEY:
      return webmoney;
    case PaysystemEnum.WIRETRANSFER:
      return wiretransfer;
    case PaysystemEnum.BITCOIN:
      return bitcoin;
    case PaysystemEnum.LBT_HW:
      return localBankTransfer;
    case PaysystemEnum.TETHER:
      return tether;
    default:
      return paysystemId;
  }
};

export const getPaysystemDeliveryTime = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.PAXUM:
    case PaysystemEnum.PAYPAL:
    case PaysystemEnum.PAYPAL_HW:
    case PaysystemEnum.WEBMONEY:
    case PaysystemEnum.BITCOIN:
    case PaysystemEnum.TETHER:
      return 'Instant';
    case PaysystemEnum.WIRETRANSFER:
      return '3-4 days';
    case PaysystemEnum.LBT_HW:
      return '1-3 days';
    default:
      return '';
  }
};

export const getPaySystemMinAmount = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.PAXUM:
    case PaysystemEnum.WEBMONEY:
      return '$5';
    case PaysystemEnum.PAYPAL:
    case PaysystemEnum.PAYPAL_HW:
    case PaysystemEnum.BITCOIN:
    case PaysystemEnum.TETHER:
    case PaysystemEnum.LBT_HW:
      return '$100';
    case PaysystemEnum.WIRETRANSFER:
      return '$1000';
    default:
      return '';
  }
};

export const getPaysystemCurrency = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.PAXUM:
    case PaysystemEnum.PAYPAL:
    case PaysystemEnum.PAYPAL_HW:
    case PaysystemEnum.WEBMONEY:
      return 'USD';
    case PaysystemEnum.WIRETRANSFER:
      return 'USD/EUR';
    case PaysystemEnum.BITCOIN:
      return 'Bitcoin';
    case PaysystemEnum.TETHER:
      return 'USD(T)';
    default:
      return '';
  }
};

export const getPaysystemFee = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.PAXUM:
      return '$1';
    case PaysystemEnum.PAYPAL:
      return 'Up to 5.4% of payout sum';
    case PaysystemEnum.PAYPAL_HW:
      return '$0.65';
    case PaysystemEnum.WEBMONEY:
      return '1% of payout sum';
    case PaysystemEnum.BITCOIN:
      return 'Network fee is included';
    case PaysystemEnum.LBT_HW:
      return '$7 + FX fee';
    case PaysystemEnum.TETHER:
      return '2% plus network fee';
    default:
      return '';
  }
};
