import { useState, useEffect } from 'react';

import { PublisherStatusEnum } from 'old-enums/publisher-status';
import { UserPermissionEnum } from 'old-enums/user-permission';

import { AVATAR } from '../../constants';

import type { Props } from '../../types';

import * as Style from './style';
import { ManagerContent } from '../manager-content';

export const PureManagerComponent = ({
  props: {
    data: { ida_permissions },
    account_info: { news_counter: newsCounter },
    data: { status: pubStatus },
    manager: { avatar, email, name, skype, status },
    getManager,
  },
}: {
  props: Props;
}) => {
  const [isActive, setIsActive] = useState(false);

  useEffect(() => {
    if (
      !name &&
      ida_permissions.includes(UserPermissionEnum.SHOW_MANAGER_CONTACTS)
    ) {
      getManager();
    }
  }, [getManager, ida_permissions, name]);

  const toggleActive = () => setIsActive((prevState) => !prevState);
  const isStatusConfirm = pubStatus === PublisherStatusEnum.NOT_CONFIRMED;

  return (
    <Style.Wrapper>
      <Style.IconWrapper
        isActive={isActive}
        isShow={Boolean(name)}
        isHaveBadge={Boolean(newsCounter)}
        isStatusConfirm={isStatusConfirm}
        data-badge={newsCounter}
        onClick={toggleActive}
        data-testid="personal-manager__icon-button"
      >
        <Style.ProfileIcon
          isActive={!isActive}
          className={`mdi ${isStatusConfirm ? 'mdi-timetable' : 'mdi-account'}`}
          data-testid="personal-manager__profile-icon"
        />
        <Style.CloseIcon
          isActive={isActive}
          className="mdi mdi-window-close"
          data-testid="personal-manager__close-icon"
        />
      </Style.IconWrapper>
      <ManagerContent
        isStatusConfirm={isStatusConfirm}
        isActive={isActive}
        newsCounter={newsCounter}
        avatar={avatar || AVATAR}
        email={email}
        name={name}
        skype={skype}
        status={status}
      />
    </Style.Wrapper>
  );
};
