import { baseDomain } from 'constants/index';
import { isPureAffiliate, isPureWebmaster } from 'old-enums/user-permission';
import adsterraLogo from 'assets/images/adsterra-logo.svg';
import forPublishers from './assets/for-publishers.svg';
import forAffiliates from './assets/for-affiliates.svg';
import forPubsAffiliates from './assets/for-pubs-affiliates.svg';
import * as Styled from '../../styles';

export const SidebarLogo = (props: {
  permissions: number[];
  menuOpen: boolean;
}) => {
  const { permissions, menuOpen } = props;
  if (permissions.length === 0) return null;
  let logoSrc = '';

  if (isPureWebmaster(permissions)) {
    logoSrc = forPublishers;
  } else if (isPureAffiliate(permissions)) {
    logoSrc = forAffiliates;
  } else if (permissions.length > 0) {
    logoSrc = forPubsAffiliates;
  }

  return (
    <Styled.Logo href={`//${baseDomain}`}>
      <Styled.LogoImage src={adsterraLogo} alt="Adsterra Network" />

      {menuOpen && logoSrc && (
        <Styled.LogoText
          src={logoSrc}
          alt="title"
          data-testid="menu-logo-text"
        />
      )}
    </Styled.Logo>
  );
};
