import { RECAPTCHA_SITE_KEY, RECAPTCHA_SITE_KEY_V2 } from './constants';

export const renderRecaptchaV2 = () =>
  new Promise((resolve) => {
    const script = document.createElement('script');
    script.src = `//www.google.com/recaptcha/api.js?render=${RECAPTCHA_SITE_KEY}`;

    script.onload = () => {
      window.grecaptcha?.ready(async () => {
        window.grecaptcha?.render('recaptchav2', {
          sitekey: RECAPTCHA_SITE_KEY_V2,
        });
      });
    };

    document.head.appendChild(script);
    resolve(true);
  });
