import React, { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import type { RouteComponentProps } from 'react-router-dom';
import { Redirect, withRouter } from 'react-router-dom';

import { CircularProgress, ImageList, ImageListItem } from '@material-ui/core';

import {
  setPassword,
  submitForm,
  validateHash,
} from 'old-reducer/reset-password';

import type { ChangeEvent, FC, KeyboardEvent } from 'react';

import signIn from 'assets/images/signup/sign-in.svg';
import * as Styled from './style';

const ResetPasswordComponent: FC<RouteComponentProps<{ hash?: string }>> = ({
  match,
}) => {
  const isLoggedIn = useAppSelector((state) => state.user.token !== '');

  const dispatch = useAppDispatch();
  const {
    error,
    form: {
      hash: { value: hash },
      password,
    },
    isLoading,
    isSuccess,
  } = useAppSelector((state) => state.resetPassword);

  useEffect(() => {
    dispatch(validateHash(match.params.hash ?? ''));
  }, [dispatch, match.params.hash]);

  if (isLoggedIn) {
    return <Redirect to="/websites" />;
  }

  if (error === '' && hash === '') {
    return <Styled.CircularProgressStyled size={50} />;
  }

  const form = (
    <div>
      {error !== '' && <Styled.ErrorMessage>{error}</Styled.ErrorMessage>}
      <Styled.ResetPasswordField
        dataTestId="reset-password__new-password"
        disabled={isLoading || hash === ''}
        error={password.error}
        helperText={password.error}
        label="New password"
        onChange={(event: ChangeEvent<HTMLInputElement>) =>
          dispatch(setPassword(event.target.value))
        }
        inputProps={{
          onKeyDown: (
            event: KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>,
          ) => event.keyCode === 13 && dispatch(submitForm()),
        }}
        type="password"
        value={password.value}
        variant="standard"
      />
      <Styled.ImageListItemStyledButtonContainer>
        <Styled.ButtonControl
          disabled={isLoading || hash === ''}
          variant="contained"
          dataTestId="reset-password__continue-button"
          onClick={() => dispatch(submitForm())}
        >
          {isLoading ? <CircularProgress size={25} /> : <span>Continue</span>}
        </Styled.ButtonControl>
      </Styled.ImageListItemStyledButtonContainer>
    </div>
  );

  const successMessage = (
    <Styled.Message>
      Password changed successfully!
      <br />
      <br />
      Wait until you are redirected to the Login page
      <br />
      or follow{' '}
      <Styled.Link href="/login" title="Sign in">
        this&nbsp;link
      </Styled.Link>
    </Styled.Message>
  );

  return (
    <div className="contentSign">
      <Styled.PaperStyled>
        <ImageList cellHeight={550} cols={2}>
          <ImageListItem>
            <div>
              <img src={signIn} alt="" />
            </div>
          </ImageListItem>
          <Styled.ImageListItemStyledWrapper>
            <div>
              <h2>Reset password</h2>
              {isSuccess ? successMessage : form}
            </div>
          </Styled.ImageListItemStyledWrapper>
        </ImageList>
      </Styled.PaperStyled>
    </div>
  );
};

export const ResetPassword = withRouter(ResetPasswordComponent);
