import { useAppSelector } from 'store/hooks';
import { SignUpProgressEnum } from 'old-enums/sign-up-progress';
import { ConfirmStep } from 'components/confirm-step';
import * as Styled from './styles';
import { SignupStep } from '../signup-step';

export const MainContent = () => {
  const { step } = useAppSelector((state) => state.signUp);

  const currentStep =
    step === SignUpProgressEnum.SIGNUP_STEP ? <SignupStep /> : <ConfirmStep />;

  return (
    <Styled.Wrapper>
      <Styled.Content>{currentStep}</Styled.Content>
    </Styled.Wrapper>
  );
};
