import { customizeImage, renderTextWithTooltip } from '@development/ui-kit';
import { Grid } from '@material-ui/core';
import type { DataGridTableProps, CellRenderProps } from '@development/ui-kit';
import { ColumnNamesForPaymentRules } from './column-names';

export const paySystemColumns: DataGridTableProps['columnsArray'] = [
  {
    alignment: 'center',
    dataField: ColumnNamesForPaymentRules.LOGO_NAME,
    caption: '',
    width: 120,
    cellRender: (cellData: CellRenderProps['cellData']) =>
      customizeImage(cellData, 'title', { altFromRowData: true }),
  },
  {
    dataField: ColumnNamesForPaymentRules.DELIVERY_TIME,
    caption: '',
    width: 120,
    cellRender: (cellData: CellRenderProps['cellData']) =>
      renderTextWithTooltip(cellData.value, cellData.data.deliveryTimeTooltip),
  },
  {
    dataField: ColumnNamesForPaymentRules.MIN_AMOUNT,
    caption: 'Minimum amount',
    cellRender: (cellData: CellRenderProps['cellData']) =>
      renderTextWithTooltip(cellData.value, cellData.data.minAmountTooltip),
  },
  {
    dataField: ColumnNamesForPaymentRules.CURRENCY,
    caption: 'Currency',
    cellRender: (cellData: CellRenderProps['cellData']) =>
      renderTextWithTooltip(
        cellData.value,
        cellData.data.currencyTooltip,
        'flex-end',
      ),
  },
  {
    dataField: ColumnNamesForPaymentRules.FEE,
    caption: 'Fee',
    width: 220,
    cellRender: (cellData: CellRenderProps['cellData']) => {
      return (
        <Grid container direction="column">
          {renderTextWithTooltip(cellData.value, cellData.data.feeTooltip)}
        </Grid>
      );
    },
  },
  {
    dataField: ColumnNamesForPaymentRules.INFO,
    caption: 'Required info',
    width: 350,
    cellRender: (cellData: CellRenderProps['cellData']) => {
      return cellData.value;
    },
  },
];
