import { useCallback, memo } from 'react';
import { useAppDispatch } from 'store/hooks';

import { CopyToClipboardButton } from 'old-component/offers/copy-to-clipboard-button';
import { ClassNameForTesting } from 'old-enums/class-name-for-testing';
import { OfferLinkTypeEnum } from 'old-enums/offer-link-type';
import { showSnackbar } from 'old-reducer/snackbar';

import type { Maybe } from 'types/utils';
import { AlertSeverity } from 'old-enums/alert-severity';

type Props = {
  backlink: Maybe<string>;
  directLink: Maybe<string>;
};

export const LinksCell = memo(({ backlink, directLink }: Props) => {
  const dispatch = useAppDispatch();
  const handleLinkCopy = useCallback(
    (offerLinkType: OfferLinkTypeEnum) => () => {
      dispatch(
        showSnackbar({
          message: `${offerLinkType} is successfully copied to clipboard`,
          severity: AlertSeverity.SUCCESS,
        }),
      );
    },
    [dispatch],
  );

  return (
    <div>
      {directLink && (
        <CopyToClipboardButton
          className={ClassNameForTesting.COPY_DIRECT_LINK_BUTTON}
          label="Direct link"
          textToCopy={directLink}
          onCopy={handleLinkCopy(OfferLinkTypeEnum.DIRECT_LINK)}
        />
      )}
      {backlink && (
        <CopyToClipboardButton
          className={ClassNameForTesting.COPY_BACKLINK_BUTTON}
          label="Backlink"
          textToCopy={backlink}
          onCopy={handleLinkCopy(OfferLinkTypeEnum.BACKLINK)}
        />
      )}
    </div>
  );
});
