import { useAppDispatch, useAppSelector } from 'store/hooks';
import type { SubmitHandler } from 'react-hook-form';
import { FormProvider, useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useEffect } from 'react';
import { getPaysystemsList, submitForm } from 'old-reducer/signup/thunk';
import { SignUpFormField } from 'old-enums/sign-up-form-field';
import { checkAuth } from 'old-reducer/common/thunk';
import type { FormFieldsSignup } from './types';
import {
  emptySelectOption,
  initialFormFields,
  messengerList,
} from './constants';
import { FormPure } from './form-pure';
import { schema } from './schema';

export const FormSignup = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(checkAuth());
    dispatch(getPaysystemsList());
  }, [dispatch]);

  const {
    resources: { paySystemsList },
    form,
  } = useAppSelector((state) => state.signUp);

  const methods = useForm<FormFieldsSignup>({
    mode: 'onTouched',
    defaultValues: initialFormFields,
    resolver: yupResolver(schema),
  });

  const onSubmit: SubmitHandler<FormFieldsSignup> = (data) =>
    dispatch(submitForm(data));

  const isMessengerSelected =
    methods.watch(SignUpFormField.MESSENGER) !== emptySelectOption[0];

  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <FormProvider {...methods}>
      <FormPure
        onSubmit={methods.handleSubmit(onSubmit)}
        paySystemsList={paySystemsList}
        messengerList={messengerList()}
        form={form}
        toShowMessengerAccount={isMessengerSelected}
      />
    </FormProvider>
  );
};
