import { format } from 'date-fns';
import { enUS } from 'date-fns/locale';
import type { Props } from './types';
import * as Styled from './styles';

export const Post = (props: Props) => {
  const { pinned, post } = props;
  const date = new Date(post.publish_on * 1000);
  const year = date.getFullYear();
  const month = format(date, 'MMM', { locale: enUS });
  const day = `${date.getDate()}`.padStart(2, '0');

  return (
    <Styled.Post>
      <div>
        <Styled.Aside>
          <Styled.Pinned>{pinned && <Styled.BookmarkIcon />}</Styled.Pinned>
          <Styled.Date>
            <Styled.Day>{day}</Styled.Day>
            <Styled.Month>{month}</Styled.Month>
            <Styled.Year>{year}</Styled.Year>
          </Styled.Date>
        </Styled.Aside>
      </div>
      <Styled.Content>
        <Styled.Title>{post.title}</Styled.Title>
        <div
          dangerouslySetInnerHTML={{ __html: post.body }} // eslint-disable-line react/no-danger
        />
      </Styled.Content>
    </Styled.Post>
  );
};
