import type { WithTheme } from '@material-ui/core/styles';
import { withTheme } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import styled, { keyframes } from 'styled-components';
import type { BlockWithDelayProps } from './types';

const fadein = keyframes`
    0% { opacity:0; }
    100% { opacity:1; }
`;

export const Title = withTheme(styled(Typography)`
  border-left: 3px solid
    ${({ theme }: WithTheme) => theme.palette.secondary.main};
  padding-left: 5px;
  margin-bottom: ${({ theme }: WithTheme) => theme.spacing()}px !important;
`);

export const Description = withTheme(styled(Typography)`
  padding-left: ${({ theme }: WithTheme) => theme.spacing()}px;
  margin-bottom: ${({ theme }: WithTheme) => theme.spacing(3)}px !important;
  white-space: pre-line;
`);

export const Block = withTheme(styled.div`
  animation: 0.7s ease 0s normal forwards 1 ${fadein};
  opacity: 0;
  width: 64%;
  margin-right: 18%;
  animation-delay: ${(props: BlockWithDelayProps) => props.delay}s;

  &:last-of-type div {
    margin-bottom: 0 !important;
  }
`);
export const Header = withTheme(styled(Typography)`
  margin-bottom: ${({ theme }: WithTheme) => theme.spacing(4)}px !important;
`);

export const Ul = withTheme(styled.ul`
  list-style: none;

  & li::before {
    content: '';
    background-color: ${({ theme }: WithTheme) => theme.palette.secondary.main};
    border-radius: 50%;
    display: inline-block;
    width: 4px;
    height: 4px;
    margin-top: -2px;
    vertical-align: middle;
    margin-right: ${({ theme }: WithTheme) => theme.spacing(0.5)}px;
  }
`);
