import { getFormattedPayout } from 'utils/format';

type PayoutValue = number | '-';

const isNumber = (value: PayoutValue): value is number =>
  typeof value === 'number';

export const getFormattedPayoutRange = (
  minPayout: PayoutValue,
  maxPayout: PayoutValue,
) => {
  if (isNumber(minPayout) && isNumber(maxPayout)) {
    const formattedMinPayout = getFormattedPayout(minPayout);
    const formattedMaxPayout = getFormattedPayout(maxPayout);

    if (minPayout === maxPayout) {
      return `$${formattedMinPayout}`;
    }

    return `$${formattedMinPayout} - ${formattedMaxPayout}`;
  }

  return '-';
};
