import { useState } from 'react';
import { withRouter } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { toggleSidebar } from 'old-reducer/site-page';

import * as Styled from '../../styles';
import { MenuSubItems } from './menu-subitems';

import type { MenuGroupProps } from '../types';

export const MenuGroup = withRouter((props: MenuGroupProps) => {
  const { route, routesMap, currentPath } = props;
  const dispatch = useAppDispatch();
  const [isCollapsed, setIsCollapsed] = useState(false);
  const isSidebarCollapsed = useAppSelector(
    (state) => state.sitePage.isSidebarCollapsed,
  );

  const subItems = route
    ? [...routesMap.values()].filter(
        (item) => item.meta?.parentGroup === route.title,
      )
    : [];

  const isActive = subItems.reduce(
    (active, { path }) => active || path === currentPath,
    false,
  );

  const handleClick = () => {
    if (isSidebarCollapsed) {
      dispatch(toggleSidebar(!isSidebarCollapsed));
      setIsCollapsed(false);
    } else {
      setIsCollapsed((collapsed) => !collapsed);
    }
  };

  const icon =
    typeof route.meta?.icon === 'string' ? (
      <span className={`mdi ${route.meta?.icon}`} />
    ) : (
      route.meta?.icon
    );

  const showGroupContent = !isSidebarCollapsed && !isCollapsed;
  const isClickable = !isActive || !isSidebarCollapsed;

  return (
    <>
      <Styled.Item
        $isActive={isActive}
        $isClickable={isClickable}
        onClick={handleClick}
        title={route.title}
      >
        <Styled.ItemBody $isActive={isActive}>
          {icon}
          {!isSidebarCollapsed && (
            <>
              <Styled.ItemTitle>{route.title}</Styled.ItemTitle>
              <Styled.ExpandIcon $isExpanded={showGroupContent} />
            </>
          )}
        </Styled.ItemBody>
      </Styled.Item>
      <MenuSubItems
        isCollapsed={isCollapsed}
        items={subItems}
        routesMap={routesMap}
        currentPath={currentPath}
      />
    </>
  );
});
