import React, { memo, useState } from 'react';
import type { ChangeEvent, FC } from 'react';
import * as Styled from './styles';

type Props = {
  handleAddButtonClick(paramName: string, value: string): (_: unknown) => void;
  paramName: string;
};

const getHintText = (param: string) =>
  `insert your ${param === 's2s' ? 'click id' : param} value`;

export const LinkParamsForm: FC<Props> = memo(
  ({ handleAddButtonClick, paramName }) => {
    const [value, setValue] = useState('');

    const handleValueChange = (event: ChangeEvent<HTMLInputElement>) => {
      setValue(event.target.value.trim());
    };

    return (
      <Styled.Wrapper>
        <Styled.Field
          placeholder={getHintText(paramName)}
          name={paramName}
          dataTestId={paramName}
          type="text"
          value={value}
          onChange={handleValueChange}
        />

        <Styled.AddButton
          dataTestId={`link-params-form__${paramName}-add-button`}
          variant="contained"
          onClick={handleAddButtonClick(paramName, value)}
        >
          Add
        </Styled.AddButton>
      </Styled.Wrapper>
    );
  },
);
