import type { Document } from 'old-reducer/payments/types';
import { InsertDriveFileOutlined } from '@material-ui/icons';
import { Grid } from '@material-ui/core';
import { getDocumentTypeById } from '../utils';
import * as Styled from './styles';

export const DocumentTitle = ({ type, id }: Pick<Document, 'type' | 'id'>) => {
  const docTitle = `${getDocumentTypeById(type)}_${id}`;

  return (
    <Grid container alignItems="center">
      <Styled.IconGridContainer item>
        <InsertDriveFileOutlined />
      </Styled.IconGridContainer>
      <Grid item>{docTitle}</Grid>
    </Grid>
  );
};
