import { getRoutesByPermissions } from 'routes';
import { useMemo } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { withRouter } from 'react-router-dom';
import { toggleSidebar } from 'old-reducer/site-page';

import type { RouteComponentProps } from 'react-router-dom';
import type { PATHS } from 'routes';
import type { Route } from 'routes/types';

import { SidebarLogo } from './components/icons/sidebar-logo';
import { MENU_BLOCKS } from './constants';
import { CollapseIcon } from './components/icons/collapse-icon';
import { MenuBlock } from './components/menu/menu-block';
import { SidebarFooter } from './components/footer';
import * as Styled from './styles';

export const Sidebar = withRouter((props: RouteComponentProps) => {
  const { location } = props;
  const permissions = useAppSelector(
    (state) => state.user.data.ida_permissions,
  );
  const isSidebarCollapsed = useAppSelector(
    (state) => state.sitePage.isSidebarCollapsed,
  );
  const navRoutes = getRoutesByPermissions(permissions);
  const currentPath = location.pathname as PATHS;
  const routesMap = useMemo(() => {
    const res: Map<PATHS, Route> = new Map();
    navRoutes.forEach((navRoute) => {
      res.set(navRoute.path, navRoute);
    });

    return res;
  }, [navRoutes]);
  const dispatch = useAppDispatch();

  return (
    <Styled.Sidebar
      $isCollapsed={isSidebarCollapsed}
      data-testid="sidebar"
      data-collapsed={isSidebarCollapsed}
    >
      <SidebarLogo permissions={permissions} menuOpen={!isSidebarCollapsed} />
      <div>
        <Styled.CollapseButton
          onClick={() => dispatch(toggleSidebar(!isSidebarCollapsed))}
          data-testid="sidebar-title-hidemenu"
        >
          {!isSidebarCollapsed && (
            <Styled.MenuTitleText>HIDE MENU</Styled.MenuTitleText>
          )}
          <Styled.CollapseIcon $isCollapsed={isSidebarCollapsed}>
            <CollapseIcon />
          </Styled.CollapseIcon>
        </Styled.CollapseButton>
        {MENU_BLOCKS.map((menuBlock) => (
          <MenuBlock
            currentPath={currentPath}
            routesMap={routesMap}
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...menuBlock}
          />
        ))}
      </div>
      <SidebarFooter />
    </Styled.Sidebar>
  );
});
