import styled from 'styled-components';
import { withTheme } from '@material-ui/core/styles';

import type { WithTheme } from '@material-ui/core/styles';

export const Link = withTheme(styled.a`
  color: ${({ theme }: WithTheme) => theme.palette.success.main};
  display: inline-block;
  font-style: italic;
  font-weight: ${({ theme }: WithTheme) => theme.typography.fontWeightMedium};
  margin-bottom: ${({ theme }: WithTheme) => theme.spacing(1)}px;
`);

export const Paragraph = withTheme(styled.p`
  font-style: italic;
  margin-bottom: ${({ theme }: WithTheme) => theme.spacing(2)}px;
`);
