import { isAffiliate, isWebmaster } from 'old-enums/user-permission';
import { useAppSelector } from 'store/hooks';
import * as Styled from '../styles';

export const SidebarFooter = () => {
  const { isSidebarCollapsed, permissions } = useAppSelector((state) => ({
    permissions: state.user.data.ida_permissions,
    isSidebarCollapsed: state.sitePage.isSidebarCollapsed,
  }));

  if (isSidebarCollapsed) {
    return null;
  }

  return (
    <Styled.FooterWrapper>
      <Styled.Footer>
        <div>Adsterra Network</div>
        <Styled.FooterCopyright>
          All rights reserved  2013-2022
        </Styled.FooterCopyright>
        {isWebmaster(permissions) && (
          <div>
            <Styled.FooterTerms
              href="https://adsterra.com/publishers-terms-managed/"
              target="_blank"
              rel="noopener noreferrer"
            >
              Terms for Publishers
            </Styled.FooterTerms>
          </div>
        )}
        {isAffiliate(permissions) && (
          <div>
            <Styled.FooterTerms
              href="https://adsterra.com/publishers-terms-cpa/"
              target="_blank"
              rel="noopener noreferrer"
            >
              Terms for Publishers (CPA)
            </Styled.FooterTerms>
          </div>
        )}
      </Styled.Footer>
    </Styled.FooterWrapper>
  );
};
