/// <reference path="yup.d.ts" />

import { rangeRule, loginRule, passwordRule, urlRule } from 'utils/validation';
import * as yup from 'yup';

yup.addMethod(yup.string, 'login', loginRule);
yup.addMethod(yup.string, 'password', passwordRule);
yup.addMethod(yup.string, 'range', rangeRule);
yup.addMethod(yup.string, 'url', urlRule);

export { yup };
