import {
  formError,
  formLoading,
  generalErrorSignin,
  resendPassword,
  setAction,
  showRecaptchaV2,
} from 'old-reducer/sign-in';
import type { Thunk } from 'store/types';
import { getRecaptchaToken, getRecaptchaTokenV2 } from 'lib/google-recaptcha';
import { SignInActionsEnum } from 'old-enums/sign-in-actions-enum';
import { openDefaultPage } from 'utils/router';
import { string } from 'yup';
import { emailServices } from 'old-common/email-services';
import type { PlainObject } from 'types/utils';
import { getHeaders } from 'utils/http';
import { getCookie } from 'utils/cookie';
import { transformErrors } from 'utils/transform/transform-errors';
import { generalError, resend, setLoadingState, formErrors } from '../signup';
import { getUserPermissions } from './utils';

/**
 *   -    
 *  
 */
export const checkAuth = (): Thunk => async () => {
  const token = getCookie('tkn3');

  if (token && token.length) {
    const permissions = await getUserPermissions(token);
    openDefaultPage(permissions);
  }
};

export const resendEmail =
  (login = false): Thunk =>
  async (dispatch, getState) => {
    dispatch(setLoadingState(true));
    const state = getState();
    const { email } = state[login ? 'signIn' : 'signUp'].confirmStep;

    if (!email) {
      dispatch(generalError('An unknown error has occurred'));

      return;
    }

    const validationSchema = string().required().email();

    try {
      await validationSchema.validate(email);
    } catch (e) {
      dispatch(formErrors({ email: 'Enter a valid email address' }));
      dispatch(setLoadingState(false));

      return;
    }

    try {
      const response = await window.fetch('/api/user/send-confirm', {
        body: JSON.stringify({ email }),
        headers: getHeaders(),
        method: 'POST',
      });

      if (response.status === 200) {
        let link = '';
        let message = '';

        for (const service of emailServices) {
          if (service.pattern.test(email)) {
            ({ link, message } = service);
            break;
          }
        }

        dispatch(resend({ message, link }));
        setTimeout(() => dispatch(resend()), 30 * 1000);

        return;
      }

      if (response.status === 422) {
        const { data }: { data: PlainObject } = await response.json();
        dispatch(formErrors(transformErrors(data)));

        return;
      }

      if (response.status === 404) {
        const { message } = await response.json();
        dispatch(generalError(message));
      }
    } catch {
      // noop
    }
  };

export const getPassword = (): Thunk => async (dispatch, getState) => {
  dispatch(formLoading());
  setTimeout(async () => {
    let email = '';
    const {
      signIn: {
        forgotPassword,
        form: { recaptcha },
      },
    } = getState();

    if (forgotPassword.email !== undefined) {
      email = forgotPassword.email.trim();
    }

    if (!email) {
      dispatch(formError({ email: 'This field is required' }));

      return;
    }

    if (!/^([a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,})?$/i.test(email)) {
      dispatch(formError({ email: 'Enter a valid email address' }));

      return;
    }

    try {
      const token = recaptcha.wasRendered
        ? await getRecaptchaTokenV2()
        : await getRecaptchaToken('restore_pub');

      const response = await window.fetch('/api/restore', {
        body: JSON.stringify({
          email,
          token,
          isRecaptchaV2: recaptcha.wasRendered,
        }),
        headers: getHeaders(null, { hasBody: true }),
        method: 'POST',
      });

      if (response.status === 401) {
        const {
          message: error,
          data: { switchingRecaptcha: shouldSwitchRecaptcha },
        } = await response.json();

        if (error) {
          dispatch(generalErrorSignin(error));

          if (shouldSwitchRecaptcha === true && error.includes('bot')) {
            dispatch(showRecaptchaV2());
          }
        }

        return;
      }
      dispatch(setAction(SignInActionsEnum.CONFIRM));

      let emailLink = '';

      for (const { link, pattern } of emailServices) {
        if (pattern.test(email)) {
          emailLink = link;

          break;
        }
      }
      dispatch(resendPassword({ link: emailLink, email }));
    } catch {
      dispatch(generalErrorSignin('An unknown error has occurred'));
    }
  }, 200);
};
