import compose from 'compose-function';

import { IS_DEV, IS_PRODUCTION, IS_TEST } from 'constants/index';

import { withProfiler } from '@sentry/react';
import { withStore } from './with-store';
import { withMuiThemeV4, withMuiThemeV5 } from './with-theme';
import { withMuiStyles } from './with-mui-styles';
import { withRouter } from './with-router';
// import { withErrors } from './with-errors';

import { initWebVitals } from './setup-webvitals/setup-webvitals';
import { initReportError } from './setup-report-error/setup-report-error';
import { initGoogleTagManager } from './setup-google-tag-manager/setup-google-tag-manager';
import { initVersion } from './setup-version/setup-version';
import { initFacebookPixel } from './setup-fecebook-pixel';
import { withGlobalStyle } from './with-global-style';

export const beforePlugins = () => {
  if (IS_PRODUCTION) {
    initFacebookPixel();
    initGoogleTagManager();
  }
};

export const withProviders = compose(
  withProfiler,
  // withErrors, // TODO      
  withGlobalStyle,
  withMuiThemeV4,
  withMuiThemeV5,
  withMuiStyles,
  withStore,
  withRouter,
);

export const afterPlugins = () => {
  initVersion();

  if (IS_DEV) {
    initWebVitals();
  }

  if (IS_PRODUCTION || IS_TEST) {
    initReportError();
  }
};
