import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

import type { Props } from './types';

import * as Styled from './styles';

export const Title = ({
  onPlacementTitleChange,
  onZoneNameChange,
  props,
}: Props) => {
  const { form_error, data, action } = props;
  const isExistingSite = action !== WebsitesDialogTypesEnum.ADD_SITE;

  return action === WebsitesDialogTypesEnum.EDIT_PLACEMENT ? (
    <Styled.Input
      dataTestId="dialog__edit-placement-name"
      error={form_error.zone}
      helperText={form_error.zone}
      label="Placement"
      value={data.t_alias}
      onChange={onPlacementTitleChange}
      variant="standard"
    />
  ) : (
    <Styled.Input
      dataTestId="dialog__website-name"
      disabled={isExistingSite}
      error={form_error.zone}
      helperText={form_error.zone}
      label="Website"
      placeholder="Example: adsterra.com"
      value={data.zone}
      onChange={onZoneNameChange}
      variant="standard"
    />
  );
};
