import { memo } from 'react';
import { TableBody, TableHead, TableRow } from '@material-ui/core';

import { getFormattedPayout } from 'utils/format';
import { getConversionTypeTitleById } from 'old-enums/conversion-type';
import type { PlainObject } from 'types/utils';

import type { UserOfferData } from '../types';
import * as Styled from './styles';

type Props = {
  conversionType: UserOfferData['n_conversion_type'];
  payout: UserOfferData['payout'];
};

const groupPayoutData = (
  data: UserOfferData['payout'],
): PlainObject<string[]> => {
  const result = {};

  for (const { t_country_code, n_amount } of data) {
    if (n_amount in result) {
      result[n_amount] = [...result[n_amount], t_country_code];
    } else {
      result[n_amount] = [t_country_code];
    }
  }

  return result;
};

const prepareCountryListToOutput = (countryCodeList: string[]) =>
  countryCodeList.sort().join(', ');

const preparePayoutToOutput = (payout: string) =>
  payout === '-' ? payout : getFormattedPayout(Number(payout));

export const GeosAndGoals = memo(({ conversionType, payout }: Props) => {
  const payoutData = payout.length > 0 ? groupPayoutData(payout) : null;

  return (
    <Styled.PaperStyled>
      <Styled.Title>Geos and goals</Styled.Title>

      <Styled.ConversionType>
        <b>Conversion type:</b>{' '}
        {conversionType && getConversionTypeTitleById(conversionType)}
      </Styled.ConversionType>

      {payoutData && (
        <Styled.TableStyled>
          <TableHead>
            <TableRow>
              <Styled.TableCellHeadStyled>GEO</Styled.TableCellHeadStyled>
              <Styled.TableCellHeadStyled>Payout</Styled.TableCellHeadStyled>
            </TableRow>
          </TableHead>

          <TableBody>
            {Object.entries(payoutData).map(
              ([payoutValue, countryCodeList]) => (
                <TableRow key={payoutValue}>
                  <Styled.TableCellColumnStyled>
                    {prepareCountryListToOutput(countryCodeList)}
                  </Styled.TableCellColumnStyled>
                  <Styled.TableCellColumnStyled>
                    {preparePayoutToOutput(payoutValue)}
                  </Styled.TableCellColumnStyled>
                </TableRow>
              ),
            )}
          </TableBody>
        </Styled.TableStyled>
      )}
    </Styled.PaperStyled>
  );
});
