import { DataGridTable } from '@development/ui-kit';
import { Grid } from '@material-ui/core';
import { useAppDispatch } from 'store/hooks';
import { useCallback, useEffect } from 'react';
import { getSignature, removeDocument } from 'old-reducer/payments/thunk';
import type { Document } from 'old-reducer/payments/types';
import { documentColumns } from './columns';
import { ColumnNamesForDocumentsTable } from './column-names';

export const DocumentsTable = ({ documents }: { documents: Document[] }) => {
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(getSignature());
  }, [dispatch]);

  const onRemoveRowData = useCallback(
    (rowData: Document) => {
      dispatch(removeDocument(rowData.id));
    },
    [dispatch],
  );

  return (
    <Grid item sm={12}>
      <DataGridTable
        dataSource={documents}
        keyExpr={ColumnNamesForDocumentsTable.ID}
        columnsArray={documentColumns}
        allowDeleting
        removeRowData={onRemoveRowData}
      />
    </Grid>
  );
};
