import { useAppSelector } from 'store/hooks';

import { MenuItem } from './menu-item';
import * as Styled from '../../styles';

import type { MenuSubItemsProps } from '../types';

export const MenuSubItems = (props: MenuSubItemsProps) => {
  const { isCollapsed, items, routesMap, currentPath } = props;
  const isSidebarCollapsed = useAppSelector(
    (state) => state.sitePage.isSidebarCollapsed,
  );
  const showGroupContent = !isSidebarCollapsed && !isCollapsed;

  return (
    <Styled.MenuSubItems
      $isCollapsed={!showGroupContent}
      itemCount={items.length}
    >
      {items.map(({ path }) => {
        const nestedRoute = routesMap.get(path);

        if (!nestedRoute) return null;

        return (
          <MenuItem
            key={path}
            path={path}
            route={nestedRoute}
            nested
            currentPath={currentPath}
          />
        );
      })}
    </Styled.MenuSubItems>
  );
};
