import { useCallback } from 'react';
import { Grid } from '@material-ui/core';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { editPlacement, showDialog } from 'old-reducer/direct-links-page';

import { SiteType } from 'old-enums/site-type';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { WebsitesLoadingTypesEnum } from 'old-enums/websites-loading-types-enum';

import { Paper } from 'old-component';
import { BlockLoader } from 'old-component/block-loader';

import type { Placement } from '../types';
import { SitesTableContext } from '../sites-table-context';
import { AddDirectLinkButtonBlock } from '../add-direct-link-button-block';
import { TitleButtonsContainer } from '../title-buttons-container';
import { PlacementsList } from '../placements-list';
import { PlacementListTableTitle } from '../placements-list-table-title';
import { PlacementTableBody } from '../placement-table-body';
import * as Styled from './styles';

import type { SitesTableContextData } from '../sites-table-context';

export const DirectLinkPlacementsContent = () => {
  const dispatch = useAppDispatch();

  const {
    activate_placement_id_loading: activePlacementLoadingId,
    site,
    loading,
  } = useAppSelector((state) => state.directLinksPage);
  const { placements, pending_request: pendingRequest } = site;

  const isTherePendingRequest =
    !Array.isArray(site.pending_request) && site.pending_request.id !== 0;
  const isThereOnePlacement =
    site.placements.length > 0 || isTherePendingRequest;

  const showDialogHandler = useCallback(
    (dialogType: WebsitesDialogTypesEnum) =>
      dispatch(
        showDialog(
          dialogType === WebsitesDialogTypesEnum.ADD_SITE
            ? WebsitesDialogTypesEnum.ADD_AD_UNIT
            : dialogType,
          site,
        ),
      ),
    [site, dispatch],
  );

  const sitesTableContext: SitesTableContextData = {
    sites: [site],
    isHiddenFromStatistics: null,
    getSiteListHandler: () => () => {},
    showDialogHandler,
    siteType: SiteType.DIRECT_LINK,
  };

  const showDialogPlacementHandler = useCallback(
    (placement: Placement, type: WebsitesDialogTypesEnum) =>
      dispatch(showDialog(type, placement)),
    [dispatch],
  );

  const editPlacementHandler = useCallback(
    (type: string, id: number) => dispatch(editPlacement(type, id)),
    [dispatch],
  );

  const placementsRows = placements.map((placement) => (
    <PlacementTableBody
      activePlacementId={activePlacementLoadingId}
      editPlacement={editPlacementHandler}
      key={placement.id}
      placement={placement}
      showDialogPlacementHandler={(type: WebsitesDialogTypesEnum) =>
        showDialogPlacementHandler(placement, type)
      }
    />
  ));

  let directLinksTable = (
    <Styled.MainContent>
      {isThereOnePlacement && (
        <>
          <Paper>
            <TitleButtonsContainer />
            <PlacementListTableTitle />
            <PlacementsList
              placementsRows={placementsRows}
              pendingRequest={pendingRequest}
            />
          </Paper>
        </>
      )}
    </Styled.MainContent>
  );

  if (loading !== WebsitesLoadingTypesEnum.EMPTY) {
    directLinksTable = (
      <BlockLoader dataTestId="direct-link-placements__loader" />
    );
  }

  return (
    <SitesTableContext.Provider value={sitesTableContext}>
      <Grid container direction="column" spacing={2}>
        <Grid item>
          <AddDirectLinkButtonBlock isTherePlacements={isThereOnePlacement} />
        </Grid>
      </Grid>

      {directLinksTable}
    </SitesTableContext.Provider>
  );
};
