import { PATHS } from 'routes/paths';
import { isPureWebmaster } from 'old-enums/user-permission';
import type { UserPermissionEnum } from 'old-enums/user-permission';
import type { Maybe } from 'types/utils';

/**
 *   -     
 */
export const openDefaultPage = (permissions: Maybe<UserPermissionEnum[]>) => {
  if (!permissions) {
    return;
  }

  const redirectUrl = isPureWebmaster(permissions)
    ? PATHS.WEBSITES
    : PATHS.OFFERS;
  window.location.assign(redirectUrl);
};
