import { createAsyncThunk } from '@reduxjs/toolkit';
import type { AppState } from 'store/types';
import { fetchTicketTopics } from './api';

export const thunkTicketTopics = createAsyncThunk(
  'ticketTopics/fetchTicketTopics',
  async (_, { getState }) => {
    const {
      user: { token },
    } = getState() as AppState;

    const req = await fetchTicketTopics(token);
    const data = await req.json();

    return data.data;
  },
);
