import { showDialog } from 'old-reducer/site-page';
import type { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { ActionButton } from './action-button';
import { UserBalance } from './user-balance';
import { UserInfo } from './user-info';

export const User = () => {
  const user = useAppSelector((state) => state.user.data);
  const dispatch = useAppDispatch();

  if (!user.login) return null;

  return (
    <>
      <ActionButton
        user={user}
        showDialog={(action: WebsitesDialogTypesEnum) =>
          dispatch(showDialog(action))
        }
      />
      <UserBalance user={user} />
      <UserInfo user={user} />
    </>
  );
};
