import type { OffersTabEnum } from 'old-enums/offers-tab';

import React from 'react';
import { TableCell } from '@material-ui/core';

import { PreviewImage } from 'old-component/preview-image';
import { getFormattedPayoutRange } from 'utils/format';
import { getConversionTypeTitleById } from 'old-enums/conversion-type';
import { getVerticalTitleById } from 'old-enums/offer-vertical';

import type { UserOfferData } from 'old-view/offer/types';
import { GeoCell } from '../geo-cell';
import { LinksCell } from '../links-cell';
import { OfferCell } from '../offer-cell';
import { StatsCell } from '../stats-cell';
import * as Styled from '../styles';

type Props = {
  item: UserOfferData;
  tabId: OffersTabEnum;
};

export const UserOfferListItem = ({ item, tabId }: Props) => (
  <Styled.UserHeader key={item.id_offer}>
    <Styled.Id>{item.id_offer}</Styled.Id>
    <Styled.UserPreview>
      <PreviewImage height={75} url={item.t_preview_path} width={75} />
    </Styled.UserPreview>
    <TableCell>
      <OfferCell id={item.id_offer} name={item.t_name} tabId={tabId} />
    </TableCell>
    <Styled.Links>
      {item.offer_request && (
        <LinksCell
          directLink={item.offer_request.t_direct_link}
          backlink={item.offer_request.t_backlink}
        />
      )}
    </Styled.Links>
    <Styled.UserVertical>
      {getVerticalTitleById(item.id_advertiser_category)}
    </Styled.UserVertical>
    <Styled.UserPayout>
      {getFormattedPayoutRange(item.min_payout, item.max_payout)}
    </Styled.UserPayout>
    <Styled.UserConversionType>
      {getConversionTypeTitleById(item.n_conversion_type)}
    </Styled.UserConversionType>
    <TableCell>
      <GeoCell id={item.id_offer} geoData={item.ida_geo} tabId={tabId} />
    </TableCell>
    <Styled.Stats>
      <StatsCell offerId={item.id_offer} />
    </Styled.Stats>
  </Styled.UserHeader>
);
