export const getRoutesByPermissions = (permissions: UserPermissionEnum[]) => {
  if (cachedRoutes.length !== 0) {
    return cachedRoutes;
  }

  if (permissions.length === 0) {
    cachedRoutes = [];
  } else if (isPureAffiliate(permissions)) {
    cachedRoutes = routes.filter(
      (route) =>
        !route.meta?.permission ||
        route.meta?.permission === UserPermissionEnum.CPA_AFFILIATE_PROGRAM,
    );
  } else if (isPureWebmaster(permissions)) {
    cachedRoutes = routes.filter(
      (route) =>
        !route.meta?.permission ||
        route.meta?.permission === UserPermissionEnum.WEBMASTER_PROGRAM,
    );
  } else {
    cachedRoutes = routes;
  }

  return cachedRoutes;
};

export const isPathAvailableToUser = (
  pathName: string,
  permissions: UserPermissionEnum[],
) => {
  if (pathName === '/not-found') {
    return true;
  }

  return getRoutesByPermissions(permissions).some(
    ({ path }) => path === preparePathname(pathName),
  );
};

export const isLocatedInPaths = (title: string) =>
  routes
    .filter((item) => item.meta?.parentGroup === title)
    .some(({ path }) => window.location.pathname === path);
