import styled from 'styled-components';
import { themeColors } from 'styles/theme/constants';
import type { TitleStatus } from 'store/ticket-statuses/types';
import type { StyleProps } from './types';

const getStatusColor = (titleStatus: TitleStatus) => {
  let color = themeColors.tertiaryDark;

  if (titleStatus === 'Opened') {
    color = themeColors.success;
  }

  if (titleStatus === 'Closed') {
    color = themeColors.error;
  }

  return color;
};

export const StatusCell = styled.span<StyleProps>`
  font-weight: 700;
  color: ${(props) => getStatusColor(props.titleStatus)};
`;
