import { createSlice } from '@reduxjs/toolkit';
import { persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import stateReconciler from 'redux-persist/lib/stateReconciler/autoMergeLevel2';
import { createWhitelistFilter } from 'redux-persist-transform-filter';

import { getCookie, setCookie } from 'utils/cookie';
import { getHeaders } from 'utils/http';
import { AARequestStatusEnum } from 'old-enums/aa-request-status';
import { AdUnitEnum, getMultiformatAdUnits } from 'old-enums/ad-units';
import { AlertSeverity } from 'old-enums/alert-severity';
import { CodeTypeEnum } from 'old-enums/code-type';
import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';
import { EntityActiveEnum } from 'old-enums/entity-active';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import { VisibilityFilterEnum } from 'old-enums/visibility-filter';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { WebsitesLoadingTypesEnum } from 'old-enums/websites-loading-types-enum';
import { WebsitesTipTypesEnum } from 'old-enums/websites-tip-types-enum';
import { ZoneCategoryEnum } from 'old-enums/zone-category';
import { disableAaRequest } from 'store/user';

import type { PendingRequest } from 'types/pending-request';
import type { Site } from 'types/site';
import type { Maybe } from 'types/utils';
import type { AppState, Thunk } from 'store/types';
import {
  getMultitagGTMEventKey,
  WebsitesMultiformatType,
} from 'old-enums/websites-multiformat-type';
import type { Action, PayloadAction, ThunkDispatch } from '@reduxjs/toolkit';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import type {
  DialogData,
  DialogKeys,
  DialogValues,
} from 'old-view/direct-links/form-website/types';
import type { Placement } from 'old-view/direct-links/types';
import type { SitePageStoreList, SiteRequest, TipData } from './types';
import type { OffersPageState } from './offers-page';

import { IS_PRODUCTION } from '../constants';
import { showSnackbar } from './snackbar';
import { getDescriptionOpenState } from './common';
import { snackbarCommonError } from './snackbar/constants';

const SIDEBAR_COLLAPSED_COOKIE_KEY = 'sidebar-menu-collapsed';

const getSidebarCollapsed = (): boolean => {
  return getCookie(SIDEBAR_COLLAPSED_COOKIE_KEY) === 'true';
};

interface SitePageState {
  isAbTest: boolean;
  action: DialogData;
  descriptionOpen: boolean;
  list: SitePageStoreList;
  loading: WebsitesLoadingTypesEnum;
  message: string;
  offerOpen: boolean;
  php_code_pl_id: Maybe<number>;
  tip: TipData;
  tooltip: {
    anchorId: Maybe<string>;
    content: Maybe<string>;
  };
  php_code_link?: string;
  isSidebarCollapsed: boolean;
}

const initialState: SitePageState = {
  action: {
    action: WebsitesDialogTypesEnum.EMPTY,
    data: {
      ad_unit: 0,
      ad_units: [],
      b_hidden: VisibilityFilterEnum.FALSE,
      category_id: ZoneCategoryEnum.EMPTY,
      id: 0,
      na_feed_category_exclude: [],
      new_ad_units: [],
      status: 0,
      t_alias: '',
      type: 0,
      zone: '',
    },
    export_type: 'txt',
    form_error: {
      zone: '',
    },
    general_error: '',
    step: DialogStepsEnum.WAIT,
  },
  descriptionOpen: getDescriptionOpenState(),
  list: {
    page: 1,
    pages: 1,
    sites: [],
    b_hidden: VisibilityFilterEnum.ALL,
    domain_search: '',
    last_domain_search: '',
    total: 0,
  },
  loading: WebsitesLoadingTypesEnum.EMPTY,
  message: '',
  offerOpen: false,
  php_code_link: undefined,
  php_code_pl_id: null,
  tip: {
    adunit_id: null,
    anchorEl: null,
    type: WebsitesTipTypesEnum.EMPTY,
  },
  tooltip: {
    anchorId: null,
    content: null,
  },
  isAbTest: false,
  isSidebarCollapsed: getSidebarCollapsed(),
};

const persistConfig = {
  key: 'sitePage',
  storage,
  stateReconciler,
  whitelist: ['list'],
  transforms: [createWhitelistFilter('list', ['b_hidden'])],
};

const {
  reducer: sitePage,
  actions: {
    adUnitAddSuccess,
    closeSite,
    dialogLoading,
    expandDescription,
    expandOffer,
    generalError,
    getExpandedSite,
    getId,
    getPlacements,
    hideDialog,
    hideTip,
    hideTooltip,
    insertSiteProperty,
    loadingRequest,
    pageLoading,
    placementActivateLoading,
    saveTemplateParams,
    setDomainSearch,
    setAbTestStatus,
    setFilterHidden,
    setLastDomainSearch,
    setPhpCodeLink,
    setPlacement,
    setPlacementTitle,
    setTypeCodes,
    showCodes,
    showDialog,
    showDialogPlacement,
    showTip,
    showTooltip,
    siteAddData,
    siteAddGeneralError,
    siteAddSuccess,
    siteListError,
    siteListFormError,
    siteListSuccess,
    toggleSidebar,
  },
} = createSlice({
  name: 'websites',
  initialState,
  reducers: {
    adUnitAddSuccess: {
      prepare: (id: number, request: PendingRequest) => ({
        payload: { id, request },
      }),
      reducer: (
        state,
        {
          payload: { id, request },
        }: PayloadAction<{ id: number; request: PendingRequest }>,
      ) => {
        const site = state.list.sites.find((x) => x.id === id);

        if (site != null) {
          site.pending_request = request;
        }

        state.action = {
          ...initialState.action,
          action: state.action.action,
          step: DialogStepsEnum.FINISHED,
        };
      },
    },

    closeSite: (state) => {
      state.list.expand_site_id = 0;
    },

    dialogLoading: (state) => {
      state.action.step = DialogStepsEnum.LOADING;
    },

    expandDescription: (state, { payload: isOpen }: PayloadAction<boolean>) => {
      localStorage.setItem('websitesDescrOpen', JSON.stringify(isOpen));
      state.descriptionOpen = isOpen;
    },

    expandOffer: (state, { payload: isOpen }: PayloadAction<boolean>) => {
      state.offerOpen = isOpen;
    },

    generalError: (state, { payload: errorMessage }: PayloadAction<string>) => {
      state.action.general_error = errorMessage;
      state.action.step = DialogStepsEnum.START;
    },

    getExpandedSite: (state, { payload: siteId }: PayloadAction<number>) => {
      state.list.expand_site_id = siteId;
    },

    getId: (state, { payload: id }: PayloadAction<number>) => {
      state.action.data.id = id;
    },

    getPlacements: {
      prepare: (siteId: number, placements: Placement[]) => ({
        payload: { placements, siteId },
      }),
      reducer: (
        state,
        {
          payload: { placements, siteId },
        }: PayloadAction<{ placements: Placement[]; siteId: number }>,
      ) => {
        const site = state.list.sites.find((x) => x.id === siteId);

        if (site != null) {
          site.placements = placements;
        }

        state.loading = WebsitesLoadingTypesEnum.EMPTY;
      },
    },

    hideDialog: (state) => {
      state.action = { ...initialState.action };
    },

    hideTip: (state) => {
      state.tip = {
        adunit_id: null,
        anchorEl: null,
        type: WebsitesTipTypesEnum.EMPTY,
      };
    },

    insertSiteProperty: {
      prepare: (id: number, data: any, type: string) => ({
        payload: { id, data, type },
      }),
      reducer: (
        state,
        {
          payload: { id, data, type },
        }: PayloadAction<{ data: any; id: number; type: string }>,
      ) => {
        const site = state.list.sites.find((x) => x.id === id);

        if (site != null) {
          site[type] = data;
        }
      },
    },

    loadingRequest: (state) => {
      state.action.step = DialogStepsEnum.LOADING;
    },

    pageLoading: (
      state,
      { payload: loadingType }: PayloadAction<WebsitesLoadingTypesEnum>,
    ) => {
      state.loading = loadingType;
    },

    placementActivateLoading: (
      state,
      { payload: id }: PayloadAction<number>,
    ) => {
      state.list.activate_placement_id_loading = id;
    },

    saveTemplateParams: {
      prepare: (
        siteId: number,
        placementId: number,
        values: Placement['template_values'],
      ) => ({
        payload: {
          siteId,
          placementId,
          values,
        },
      }),
      reducer: (
        state,
        {
          payload: { siteId, placementId, values },
        }: PayloadAction<{
          placementId: number;
          siteId: number;
          values: Placement['template_values'];
        }>,
      ) => {
        const site = state.list.sites.find((x) => x.id === siteId);

        if (site != null) {
          const placement = site.placements.find((x) => x.id === placementId);

          if (placement != null) {
            placement.template_values = {
              ...placement.template_values,
              ...values,
            };
          }
        }
      },
    },

    setPhpCodeLink: {
      prepare: (placementId: number, phpCodeLink: string) => ({
        payload: { phpCodeLink, placementId },
      }),
      reducer: (
        state,
        {
          payload: { phpCodeLink, placementId },
        }: PayloadAction<{ phpCodeLink: string; placementId: number }>,
      ) => {
        state.php_code_link = phpCodeLink;
        state.php_code_pl_id = placementId;
      },
    },

    setPlacement: (state, { payload: placement }: PayloadAction<Placement>) => {
      const site = state.list.sites.find((x) => x.id === placement.zone_id);

      if (site != null) {
        const index = site.placements.findIndex((x) => x.id === placement.id);

        if (index >= 0) {
          //       
          //         
          // https://you.developzilla.com/issue/app_front-345
          if (site.placements[index].ad_unit === AdUnitEnum.NATIVE_BANNER) {
            placement.template_values = site.placements[index].template_values;
          }

          site.placements[index] = placement;
        }
      }

      state.list.activate_placement_id_loading = 0;
      state.loading = WebsitesLoadingTypesEnum.EMPTY;
    },

    setPlacementTitle: (state, { payload: title }: PayloadAction<string>) => {
      state.action.data.t_alias = title;
    },

    setTypeCodes: (state, { payload: exportType }: PayloadAction<string>) => {
      state.action.export_type = exportType;
    },

    setFilterHidden: (
      state,
      { payload }: PayloadAction<VisibilityFilterEnum>,
    ) => {
      state.list.b_hidden = payload;
    },

    showCodes: (state, { payload: placementId }: PayloadAction<number>) => {
      state.list.expand_placement_id =
        state.list.expand_placement_id === placementId ? 0 : placementId;
    },

    showDialog: {
      prepare: (
        action: WebsitesDialogTypesEnum,
        site?: Site | PendingRequest,
      ) => ({ payload: { action, site } }),
      reducer: (
        state,
        {
          payload: { action, site },
        }: PayloadAction<{
          action: WebsitesDialogTypesEnum;
          site?: Site | PendingRequest;
        }>,
      ) => {
        const isExistingSite = (x: unknown): x is Site =>
          action !== WebsitesDialogTypesEnum.ADD_SITE && !!x;

        state.action.action = action;
        state.action.step = DialogStepsEnum.START;

        if (isExistingSite(site)) {
          const { domain, ...props } = site;

          state.action.data = {
            ...state.action.data,
            ...props,
            zone: domain,
          };
        }

        if (state.isAbTest) {
          state.action.data.multiformat = WebsitesMultiformatType.MAX;
        }
      },
    },

    showDialogPlacement: {
      prepare: (
        action: WebsitesDialogTypesEnum,
        placement: Partial<Placement>,
      ) => ({ payload: { action, placement } }),
      reducer: (
        state,
        {
          payload: { action, placement },
        }: PayloadAction<{
          action: WebsitesDialogTypesEnum;
          placement: Partial<Placement>;
        }>,
      ) => {
        state.action.action = action;
        state.action.data = { ...state.action.data, ...placement };
        state.action.step = DialogStepsEnum.START;

        if (state.isAbTest) {
          state.action.data.multiformat = placement.money_scale_position
            ? placement.money_scale_position
            : WebsitesMultiformatType.MAX;
        }
      },
    },

    setAbTestStatus: (
      state,
      { payload: { data } }: PayloadAction<{ data: SitePageState['isAbTest'] }>,
    ) => {
      state.isAbTest = data;
    },

    showTip: {
      prepare: (anchorEl: any, adunit: number, type: WebsitesTipTypesEnum) => ({
        payload: {
          adunit_id: adunit,
          anchorEl,
          type,
        },
      }),
      reducer: (
        state,
        {
          payload,
        }: PayloadAction<{
          adunit_id: number;
          anchorEl: any;
          type: WebsitesTipTypesEnum;
        }>,
      ) => {
        state.tip = { ...payload };
      },
    },

    siteAddData: {
      prepare: (data: any, type: string) => ({ payload: { data, type } }),
      reducer: (
        state,
        { payload: { data, type } }: PayloadAction<{ data: any; type: string }>,
      ) => {
        state.action.data[type] = data;
        state.action.form_error = { ...initialState.action.form_error };
      },
    },

    siteAddGeneralError: (
      state,
      { payload: errorMessage }: PayloadAction<string>,
    ) => {
      state.action.general_error = errorMessage;
      state.action.step = DialogStepsEnum.START;
    },

    siteAddSuccess: (state) => {
      state.action = {
        ...initialState.action,
        action: state.action.action,
        step: DialogStepsEnum.FINISHED,
      };
    },

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    siteListError: (_, __: PayloadAction<string>) => {
      //         
    },

    siteListFormError: (
      state,
      { payload: errorMessage }: PayloadAction<string>,
    ) => {
      state.action.form_error.zone = errorMessage;
      state.action.step = DialogStepsEnum.START;
    },

    siteListSuccess: {
      prepare: (sites: Site[], page: number, pages: number, total: number) => ({
        payload: {
          page,
          pages,
          sites,
          total,
        },
      }),
      reducer: (
        state,
        {
          payload,
        }: PayloadAction<{
          page: number;
          pages: number;
          sites: Site[];
          total: number;
        }>,
      ) => {
        state.list = {
          ...state.list,
          ...payload,
        };
        state.loading = WebsitesLoadingTypesEnum.EMPTY;
      },
    },

    showTooltip: {
      prepare: (anchorId: string, content: string) => ({
        payload: { anchorId, content },
      }),
      reducer: (
        state,
        { payload }: PayloadAction<OffersPageState['tooltip']>,
      ) => {
        state.tooltip = payload;
      },
    },

    hideTooltip: (state) => {
      state.tooltip = {
        anchorId: null,
        content: null,
      };
    },

    toggleSidebar: (state, { payload: collapsed }: PayloadAction<boolean>) => {
      //  ,     v2  v3.
      const chunks = window.location.hostname.split('.');
      const rootDomain = chunks.slice(chunks.length - 2).join('.');
      const cookieValue = JSON.stringify(collapsed);
      setCookie(SIDEBAR_COLLAPSED_COOKIE_KEY, cookieValue, {
        domain: `.${rootDomain}`,
        maxAge: null,
      });
      state.isSidebarCollapsed = collapsed;
    },

    setDomainSearch: (state, { payload }: PayloadAction<string>) => {
      state.list = {
        ...state.list,
        domain_search: payload,
      };
    },

    setLastDomainSearch: (state) => {
      state.list = {
        ...state.list,
        last_domain_search: state.list.domain_search,
      };
    },
  },
});

const getAbTestStatus = (): Thunk => async (dispatch, getState) => {
  try {
    const { user } = getState();

    const response = await window.fetch(
      `/api/user/${user.data.id}/abtest-status`,
      {
        headers: getHeaders(user.token),
        method: 'GET',
      },
    );

    if (response.status !== 200) {
      return;
    }

    const { data } = await response.json();

    if (data !== null) {
      dispatch(setAbTestStatus(data));
    }
  } catch {
    // Not showing abtest request status to user
  }
};

const getSiteList = (page?: number, action?: string): Thunk => {
  return (dispatch, getState) => {
    dispatch(pageLoading(WebsitesLoadingTypesEnum.SITE_LIST));
    dispatch(getAbTestStatus());
    dispatch(setLastDomainSearch());

    setTimeout(async () => {
      try {
        const state = getState();
        const params = new URLSearchParams();

        params.append('page', (page || state.sitePage.list.page).toString());

        if (state.sitePage.list.b_hidden !== VisibilityFilterEnum.ALL) {
          params.append('filter[b_hidden]', state.sitePage.list.b_hidden);
        }

        if (state.sitePage.list.domain_search) {
          params.append(
            'filter[domain_search]',
            state.sitePage.list.domain_search,
          );
        }

        if (action === 'get-code') {
          params.append('filter[n_status]', '2');
          params.append('limit', '100000');
        } else {
          params.append('limit', '50');
        }

        const response = await window.fetch(`/api/sites?${params}`, {
          headers: getHeaders(state.user.token),
          method: 'GET',
        });

        if (response.status !== 200) {
          dispatch(showSnackbar(snackbarCommonError));

          return;
        }

        const { data } = await response.json();
        dispatch(
          siteListSuccess(
            data.items,
            data.page,
            Math.ceil(data.total / data.limit),
            data.total,
          ),
        );
      } catch (e: any) {
        dispatch(siteListError(e.message));
      }
    }, 200);
  };
};

const sendEventToGTM = (): Thunk => async (_, getState) => {
  const {
    sitePage: {
      action: {
        action,
        data: { multiformat },
      },
    },
  } = getState();

  if (multiformat === undefined) {
    return;
  }

  const multiformatValue =
    typeof multiformat === 'object' ? Object.keys(multiformat)[0] : multiformat;
  const gtmEvent = getMultitagGTMEventKey(Number(multiformatValue), action);

  if (IS_PRODUCTION && gtmEvent !== null) {
    pushToGoogleTagManager({ event: gtmEvent });
  }
};

// add site or placement
const addSite = (): Thunk => {
  return async (dispatch, getState) => {
    dispatch(loadingRequest());

    try {
      const state = getState();
      const site = state.sitePage.action.data;
      const isMultiformat =
        state.sitePage.isAbTest &&
        getMultiformatAdUnits().includes(site.new_ad_units[0]);
      const isNewSite = site.id === 0;
      const siteRequest: SiteRequest = {
        ad_units: site.new_ad_units,
        id_zone: isNewSite ? null : site.id,
        na_feed_category_exclude: site.na_feed_category_exclude,
        zone: site.zone,
      };

      if (isMultiformat) {
        siteRequest.multiformat = { [site.new_ad_units[0]]: site.multiformat };
      }

      if (!/\S{2,}\.\S{2,}/.test(siteRequest.zone)) {
        dispatch(siteListFormError('Enter your website URL'));

        return;
      }

      //        
      if (isNewSite) {
        if (site.category_id === ZoneCategoryEnum.EMPTY) {
          dispatch(
            siteAddGeneralError(
              'Please choose the site category to submit the request.',
            ),
          );

          return;
        }

        siteRequest.category_id = site.category_id || undefined;
      }

      if (!isNewSite && (siteRequest.ad_units ?? []).length === 0) {
        dispatch(
          siteAddGeneralError(
            'Please choose the ad units to submit the request.',
          ),
        );

        return;
      }

      const response = await window.fetch('/api/site', {
        body: JSON.stringify(siteRequest),
        headers: getHeaders(state.user.token, { hasBody: true }),
        method: 'POST',
      });

      if (isMultiformat) {
        dispatch(sendEventToGTM());
      }

      if (response.status === 200) {
        if (isNewSite) {
          dispatch(getSiteList(state.sitePage.list.page));
          dispatch(siteAddSuccess());

          if (IS_PRODUCTION) {
            pushToGoogleTagManager({ event: 'add_website_success' });
          }
        } else {
          dispatch(
            adUnitAddSuccess(site.id, {
              ad_units: siteRequest.ad_units,
              domain: siteRequest.zone,
              id: siteRequest.id_zone as any,
              na_feed_category_exclude: siteRequest.na_feed_category_exclude,
            }),
          );
        }

        return;
      }

      if (response.status === 422) {
        const { data } = await response.json();

        if (data.zone) {
          dispatch(siteListFormError(data.zone));
        } else {
          dispatch(siteListFormError(Object.values(data).join(', ')));
        }

        return;
      }

      const { message } = await response.json();

      if (message) {
        dispatch(siteAddGeneralError(message));
      }
    } catch {
      // noop
    }
  };
};

const editSite = (): Thunk => {
  return (dispatch, getState) => {
    dispatch(loadingRequest());
    setTimeout(async () => {
      try {
        const state = getState();
        const { data } = state.sitePage.action;
        const response = await window.fetch(
          `/api/site/${data.type}/${data.id}`,
          {
            body: JSON.stringify({
              na_feed_category_exclude: data.na_feed_category_exclude ?? [],
            }),
            headers: getHeaders(state.user.token, { hasBody: true }),
            method: 'PUT',
          },
        );

        if (response.status === 200) {
          dispatch(
            insertSiteProperty(
              data.id,
              data.na_feed_category_exclude ?? [],
              'na_feed_category_exclude',
            ),
          );
          dispatch(hideDialog());
        }
      } catch (e: any) {
        dispatch(siteListError(e.message));
      }
    }, 200);
  };
};

const manageSiteVisibility = (
  id: number,
  isHidden: boolean,
  dispatch: ThunkDispatch<AppState, unknown, Action<any>>,
  getState: () => AppState,
) => {
  setTimeout(async () => {
    try {
      const state = getState();
      const sitesList = state.sitePage.list;

      const response = await window.fetch(`/api/site/${id}`, {
        body: JSON.stringify({ b_hidden: isHidden }),
        headers: getHeaders(state.user.token, { hasBody: true }),
        method: 'PATCH',
      });

      if (response.status === 200) {
        dispatch(insertSiteProperty(id, isHidden, 'b_hidden'));
        dispatch(hideDialog());

        if (sitesList.b_hidden !== VisibilityFilterEnum.ALL) {
          dispatch(getSiteList(sitesList.page));
        }
      }
    } catch (e: any) {
      dispatch(siteListError(e.message));
    }
  }, 200);
};

const hideSite = (): Thunk => {
  return (dispatch, getState) => {
    const state = getState();

    dispatch(loadingRequest());
    manageSiteVisibility(
      state.sitePage.action.data.id,
      true,
      dispatch,
      getState,
    );
  };
};

const unhideSite = (id: number): Thunk => {
  return (dispatch, getState) => {
    dispatch(pageLoading(WebsitesLoadingTypesEnum.UNHIDE_FROM_STATS));
    dispatch(getId(id)); //  
    manageSiteVisibility(id, false, dispatch, getState);
  };
};

const resubmitSite = (id: number): Thunk => {
  return async (dispatch, getState) => {
    dispatch(pageLoading(WebsitesLoadingTypesEnum.RESUBMIT));
    dispatch(getId(id));
    setTimeout(async () => {
      try {
        const state = getState();
        const data = { status: 1 };
        const response = await window.fetch(`/api/site/1/${id}`, {
          body: JSON.stringify(data),
          headers: getHeaders(state.user.token, { hasBody: true }),
          method: 'PUT',
        });

        if (response.status === 200) {
          dispatch(insertSiteProperty(id, data.status, 'status'));
          dispatch(hideDialog());
        }
      } catch (e: any) {
        dispatch(siteListError(e.message));
      }
    }, 200);
  };
};

const getPlacementListExtended = (id: number): Thunk => {
  return async (dispatch, getState) => {
    const state = getState();

    if (id === state.sitePage.list.expand_site_id) {
      dispatch(closeSite());

      return;
    }

    dispatch(getExpandedSite(id));
    dispatch(pageLoading(WebsitesLoadingTypesEnum.GET_PLACEMENT_LIST));

    const site = state.sitePage.list.sites.find((x) => x.id === id);

    if (site != null && site.placements != null) {
      dispatch(getPlacements(id, site.placements));

      return;
    }

    try {
      const response = await window.fetch(`/api/placements/${id}/extended`, {
        headers: getHeaders(state.user.token),
        method: 'GET',
      });

      if (response.status !== 200) {
        dispatch(showSnackbar(snackbarCommonError));

        return;
      }

      const { data } = await response.json();
      dispatch(getPlacements(id, data));
    } catch {
      // noop
    }
  };
};

const editPlacement = (type: string, id: number): Thunk => {
  return async (dispatch, getState) => {
    if (type === 'dialog') {
      dispatch(loadingRequest());
    } else {
      dispatch(placementActivateLoading(id));
    }

    try {
      const state = getState();
      let placementData;

      if (type === 'dialog') {
        const { data } = state.sitePage.action;

        if (!data.t_alias) {
          dispatch(siteListFormError('Placement Alias is required'));

          return;
        }

        placementData = {
          na_feed_category_exclude: data.na_feed_category_exclude,
          t_alias: data.t_alias,
          multiformat: data.multiformat || data.money_scale_position,
        };
      } else {
        placementData = {
          active: EntityActiveEnum.ACTIVE,
        };
      }

      const response = await window.fetch(`/api/placement/${id}`, {
        body: JSON.stringify(placementData),
        headers: getHeaders(state.user.token, { hasBody: true }),
        method: 'PATCH',
      });

      if (response.status === 200) {
        dispatch(sendEventToGTM());

        const { data } = await response.json();
        dispatch(setPlacement(data));
        dispatch(hideDialog());

        return;
      }

      if (response.status === 422) {
        const { data } = await response.json();

        if (data.t_alias) {
          dispatch(
            siteListFormError('Placement Alias must not exceed 100 characters'),
          );
        }
      }
    } catch {
      // noop
    }
  };
};

const requestAntiAdblock = (): Thunk => {
  return async (dispatch, getState) => {
    try {
      const state = getState();
      const response = await window.fetch('/api/domain/request', {
        headers: getHeaders(state.user.token),
        method: 'POST',
      });

      if (response.status === 200) {
        dispatch(disableAaRequest(AARequestStatusEnum.Pending));
      }
    } catch {
      // noop
    }
  };
};

const getPhpCodeLink = (placementId: number): Thunk => {
  return async (dispatch, getState) => {
    try {
      const state = getState();
      const response = await window.fetch(`/api/code/${placementId}/server`, {
        headers: getHeaders(state.user.token),
        method: 'GET',
      });

      if (response.status === 200) {
        const { data } = await response.json();
        dispatch(setPhpCodeLink(placementId, data));
      }
    } catch {
      // noop
    }
  };
};

const getCodes = (): Thunk => (dispatch, getState) => {
  dispatch(dialogLoading());
  setTimeout(async () => {
    try {
      const state = getState();
      const response = await window.fetch('/api/codes', {
        headers: getHeaders(state.user.token),
        method: 'GET',
      });

      if (response.status !== 200) {
        return;
      }

      const { data } = await response.json();

      if (data != null) {
        dispatch(hideDialog());
        window.location.assign(data[state.sitePage.action.export_type]);
        dispatch(
          showSnackbar({
            message: SnackbarMessage.DOWNLOAD_FILE,
            severity: AlertSeverity.SUCCESS,
          }),
        );
      } else {
        dispatch(generalError("You haven't requested any codes."));
      }
    } catch {
      // noop
    }
  }, 200);
};

const hasPhpCode = (codes: Placement['codes']): boolean => {
  for (const code of codes) {
    if (code.type === CodeTypeEnum.PHP_CODE) {
      return true;
    }
  }

  return false;
};

