import { Dialog, DialogActions, DialogTitle } from '@material-ui/core';

import { Typography } from 'old-component';
import { Button } from '@development/ui-kit';

import { DialogStatus } from 'old-enums/dialog-status';
import type { Props } from './types';
import * as Styled from './styles';

export const DialogControl = ({
  title,
  children,
  width = 400,
  status,
  onCancel,
  onSubmit,
  hasError = false,
  dataTestId,
  cancelButtonText,
  submitButtonText,
  disablePortal,
  disableScrollLock,
  className,
}: Props) => {
  const isLoading = status === DialogStatus.LOADING || hasError;

  const actionButtons = (
    <>
      {cancelButtonText && (
        <Button
          dataTestId={`${dataTestId}__cancel`}
          isLoading={isLoading}
          variant="text"
          onClick={onCancel}
        >
          {cancelButtonText}
        </Button>
      )}
      <Button
        dataTestId={`${dataTestId}__submit`}
        isLoading={isLoading}
        variant="contained"
        onClick={onSubmit}
      >
        {submitButtonText}
      </Button>
    </>
  );

  return (
    <Dialog
      maxWidth="md"
      open={status !== DialogStatus.HIDE}
      scroll="body"
      onClose={onCancel}
      disablePortal={disablePortal}
      disableScrollLock={disableScrollLock}
      className={className}
      data-testid={dataTestId}
    >
      {title && (
        <DialogTitle disableTypography>
          <Typography variant="h4">{title}</Typography>
        </DialogTitle>
      )}
      <Styled.DialogContentWrapper width={width}>
        {children}
      </Styled.DialogContentWrapper>
      <DialogActions>{actionButtons}</DialogActions>
    </Dialog>
  );
};
