type AdUnitAdditionalInfo = {
  text: string;
  url: string;
};

export enum AdUnitEnum {
  BANNER_300_X_250 = 5,
  BANNER_728_X_90 = 23,
  BANNER_160_X_600 = 25,
  BANNER_160_X_300 = 26,
  BANNER_468_X_60 = 27,
  BANNER_320_X_50 = 32,
  BANNER_800_X_440 = 33,
  POPUNDER = 28,
  SOCIAL_BAR = 29,
  SLIDER = 30,
  PUSH_UP = 31,
  NEW_PUSH_UP = 34,
  PUSH_UP_NOT_ALERT = 35,
  INTERSTITIAL = 36,
  VAST = 42,
  WEB_PUSH = 44,
  WEB_PUSH_ADS = 45,
  VIDEO_300_X_250 = 46,
  VIDEO_728_X_90 = 47,
  DIRECT_LINK = 10001,
  NATIVE_BANNER = 49,
}

export const getMultiformatAdUnits = () => {
  const result: number[] = [];
  Object.keys(AdUnitEnum).forEach((key) => {
    if (key.includes('BANNER')) {
      result.push(AdUnitEnum[key]);
    }
  });

  return result;
};

export const getAdUnitTitle = (adUnit: AdUnitEnum): string | AdUnitEnum => {
  switch (adUnit) {
    case AdUnitEnum.BANNER_300_X_250:
      return '300  250 px';
    case AdUnitEnum.POPUNDER:
      return 'Popunder';
    case AdUnitEnum.BANNER_160_X_300:
      return '160  300 px';
    case AdUnitEnum.BANNER_728_X_90:
      return '728  90 px';
    case AdUnitEnum.BANNER_160_X_600:
      return '160  600 px';
    case AdUnitEnum.BANNER_468_X_60:
      return '468  60 px';
    case AdUnitEnum.BANNER_320_X_50:
      return '320  50 px';
    case AdUnitEnum.BANNER_800_X_440:
      return '800  440 px';
    case AdUnitEnum.DIRECT_LINK:
      return 'Direct link';
    case AdUnitEnum.INTERSTITIAL:
      return 'Interstitial';
    case AdUnitEnum.SLIDER:
      return 'Slider';
    case AdUnitEnum.SOCIAL_BAR:
      return 'Social Bar';
    case AdUnitEnum.NEW_PUSH_UP:
      return 'NewPushUp';
    case AdUnitEnum.PUSH_UP_NOT_ALERT:
      return 'PushUpNotAlert';
    case AdUnitEnum.PUSH_UP:
      return 'PushUp';
    case AdUnitEnum.VAST:
      return 'Vast';
    case AdUnitEnum.WEB_PUSH:
      return 'WebPush';
    case AdUnitEnum.WEB_PUSH_ADS:
      return 'WebPushAds';
    case AdUnitEnum.VIDEO_300_X_250:
      return 'Video 300  250 px';
    case AdUnitEnum.VIDEO_728_X_90:
      return 'Video 728  90 px';
    case AdUnitEnum.NATIVE_BANNER:
      return 'Native Banner';
    default:
      return adUnit;
  }
};

export const getAdUnitAdditionalInfo = (
  adUnit: AdUnitEnum,
): AdUnitAdditionalInfo | null => {
  switch (adUnit) {
    case AdUnitEnum.DIRECT_LINK:
      return {
        text: 'How it works',
        url: 'https://adsterra.com/blog/guide-for-working-with-direct-links/',
      };
    case AdUnitEnum.SOCIAL_BAR:
      return {
        text: 'What is Social Bar?',
        url: 'https://adsterra.com/blog/publishers-guide-to-social-bar/',
      };
    default:
      return null;
  }
};
