import type { yup } from 'lib/yup';
import { ErrorMessage, URL_PATTERN, URL_POSTBACK_PATTERN } from './constants';

/**
 * ,    
 */
export function urlRule(
  this: yup.StringSchema,
  { isPostbackURL } = { isPostbackURL: false },
) {
  return this.test('url', (value, ctx) => {
    const hasValidSymbols = this.matches(URL_PATTERN, {
      excludeEmptyString: true,
    }).isValidSync(value);

    if (!hasValidSymbols) {
      return ctx.createError({
        message: ErrorMessage.URL,
      });
    }

    const hasPostbackParams = this.matches(URL_POSTBACK_PATTERN, {
      excludeEmptyString: true,
    }).isValidSync(value);

    if (isPostbackURL && !hasPostbackParams) {
      return ctx.createError({
        message: ErrorMessage.POSTBACK,
      });
    }

    return true;
  });
}
