import { v2 } from 'constants/index';
import { setCookie } from 'utils/cookie/set-cookie';

/**
 *      (v2).
 * ID       
 *   .
 */
export const setCookieAndRedirectToStats = (
  zoneId: number,
  placementId: number,
) => {
  const maxAge = 15 * 60 * 1000; // 15 min

  Object.entries({ zone: zoneId, placement: placementId }).forEach(
    ([name, value]) => setCookie(name, value, { maxAge }),
  );

  window.location.assign(`${v2}/stats`);
};
