import { getHeaders } from 'utils/http';
import { showSnackbar } from 'old-reducer/snackbar';
import { snackbarCommonError } from 'old-reducer/snackbar/constants';
import type { Thunk } from 'store/types';

import {
  setCurrentToken,
  setInactiveTokens,
  setLoadingState,
} from '../api-token-page';

import type { ApiToken, ApiTokenResponse } from './types';

export const createToken = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token: userToken },
  } = getState();

  dispatch(setLoadingState(true));
  dispatch(setCurrentToken(null));

  try {
    const response = await window.fetch('/api/api-tokens', {
      headers: getHeaders(userToken, { hasBody: true }),
      method: 'POST',
      body: JSON.stringify({}),
    });

    if (response.status === 201) {
      const {
        id,
        token,
        created_on: createdOn,
      } = (await response.json()) as ApiTokenResponse;

      const apiToken: ApiToken = {
        id,
        token,
        createdOn,
      };

      dispatch(setCurrentToken(apiToken));
    } else {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(setLoadingState(false));
  }
};

export const getCurrentToken = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token: userToken },
  } = getState();

  dispatch(setLoadingState(true));

  try {
    const response = await window.fetch('/api/api-tokens/current', {
      headers: getHeaders(userToken),
    });

    if (response.status === 200) {
      const {
        id,
        token,
        created_on: createdOn,
      } = (await response.json()) as ApiTokenResponse;

      const apiToken: ApiToken = {
        id,
        token,
        createdOn,
      };

      dispatch(setCurrentToken(apiToken));
    } else if (response.status !== 404) {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(setLoadingState(false));
  }
};

export const getInactiveTokens = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token: userToken },
  } = getState();

  dispatch(setLoadingState(true));

  try {
    const response = await window.fetch('/api/api-tokens', {
      headers: getHeaders(userToken),
    });

    if (response.status === 200) {
      const inactiveTokens = (await response.json()) as ApiTokenResponse[];

      const tokens = inactiveTokens.map(
        ({ id, token, created_on: createdOn }) => ({
          id,
          token,
          createdOn,
        }),
      );

      dispatch(setInactiveTokens(tokens));
    } else {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(setLoadingState(false));
  }
};
