import { useController, useFormContext } from 'react-hook-form';
import { Password } from 'old-component';
import type { PasswordControllerProps } from './types';

export const PasswordController = ({
  required,
  fullWidth,
  dataTestId,
  name,
  error,
  helperText,
  toShowHelperOnFocus,
}: PasswordControllerProps) => {
  const { control } = useFormContext();

  const {
    field: { onChange, onBlur, value, ref },
    fieldState: { error: errorHook },
  } = useController({
    name,
    control,
    rules: { required: true },
    defaultValue: '',
  });

  return (
    <Password
      required={required}
      fullWidth={fullWidth}
      dataTestId={dataTestId}
      onChange={onChange}
      onBlur={onBlur}
      value={value}
      name={name}
      reference={ref}
      error={errorHook ? errorHook.message : error}
      helperText={helperText}
      toShowHelperOnFocus={toShowHelperOnFocus}
    />
  );
};
