import { FeedEnum } from 'old-enums/feed';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { ZoneCategoryEnum } from 'old-enums/zone-category';

import { DialogWrap } from 'old-component/dialog-common/dialog-wrap';
import { Feeds } from 'old-component/dialog-common/feeds';
import { SiteCategory } from 'old-component/dialog-common/site-category';
import { Title } from 'old-component/dialog-common/title';

import type { ChangeEvent } from 'react';
import type { siteAddDataType } from 'old-reducer/site-page';
import type { Maybe } from 'types/utils';
import type { Props } from './types';

import { DirectLinkCategorySelection } from './direct-link-category-selection';
import { DirectLinkNote } from './direct-link-note';

import * as Styled from './styles';

const onFeedCategoryChange =
  (excludeCategories: ZoneCategoryEnum[], siteAddData: siteAddDataType) =>
  (id: number, isNotAdult: boolean) => {
    const data = [...excludeCategories];
    const index = data.indexOf(id);
    const isInExclude = index !== -1;

    if (isNotAdult && !isInExclude) {
      data.push(id);
    } else if (!isNotAdult && isInExclude) {
      data.splice(index, 1);
    }

    siteAddData(data, 'na_feed_category_exclude');
  };

const onZoneCategoryChange =
  (excludeCategories: ZoneCategoryEnum[], siteAddData: siteAddDataType) =>
  (_: unknown, __: unknown, value: Maybe<number>) => {
    const isNotAdult =
      value != null &&
      value !== ZoneCategoryEnum.ADULT &&
      value !== ZoneCategoryEnum.ADULT_SOCIALBLACK;

    onFeedCategoryChange(excludeCategories, siteAddData)(
      FeedEnum.EROTIC_ADS,
      isNotAdult,
    );
    siteAddData(value, 'category_id');
  };

const hasCategory = (excludeCategories: FeedEnum[]) => (id: FeedEnum) =>
  excludeCategories && excludeCategories.includes(id);

export const EditAddAdUnitOrSiteDialog = ({
  dialogData,
  dialogWrapData,
  hideDialog,
  setPlacementTitle,
  siteAddData,
}: Props) => {
  const {
    action: dialogType,
    data: dialogDataData,
    step,
    general_error: generalError,
  } = dialogData;
  const {
    category_id: categoryId,
    na_feed_category_exclude: excludeCategories,
  } = dialogDataData;

  const isEditPlacement = dialogType === WebsitesDialogTypesEnum.EDIT_PLACEMENT;
  const isEditSiteOrPlacement = isEditPlacement;
  const isAddingDirectLink = !isEditSiteOrPlacement;
  const isExistingSite = dialogType !== WebsitesDialogTypesEnum.ADD_SITE;

  const onZoneCategoryChangeWithProps = onZoneCategoryChange(
    excludeCategories,
    siteAddData,
  );

  return (
    <DialogWrap data={dialogWrapData} hideDialog={hideDialog} step={step}>
      {generalError && <Styled.ErrorText>{generalError}</Styled.ErrorText>}
      {isAddingDirectLink && (
        <DirectLinkCategorySelection
          categoryId={categoryId}
          onZoneCategoryChange={(value) =>
            onZoneCategoryChangeWithProps(null, null, value)
          }
        />
      )}
      {!isAddingDirectLink && (
        <>
          <Title
            onPlacementTitleChange={(event: ChangeEvent<{ value: string }>) =>
              setPlacementTitle(event.target.value)
            }
            onZoneNameChange={(event: ChangeEvent<{ value: string }>) =>
              siteAddData(event.target.value, 'zone')
            }
            props={dialogData}
          />
          <div>
            <Styled.Caption>Placement category:</Styled.Caption>
            <SiteCategory
              categoryId={categoryId}
              isDirectLink
              isExistingSite={isExistingSite}
              onZoneCategoryChange={() =>
                onZoneCategoryChangeWithProps(null, null, null)
              }
            />
          </div>
        </>
      )}
      <Feeds
        findCategory={hasCategory(excludeCategories)}
        onChangeCategory={onFeedCategoryChange(excludeCategories, siteAddData)}
        shouldShowNote={isEditSiteOrPlacement}
      />
      {isAddingDirectLink && <DirectLinkNote />}
    </DialogWrap>
  );
};
