import { Select } from '@development/ui-kit';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { setDocumentType } from 'old-reducer/payments-page';
import type { SelectChangeEvent } from '@mui/material';
import * as Styled from './styles';
import { documentTypeOptions } from './constants';

export const TypeSelect = () => {
  const dispatch = useAppDispatch();
  const { documentType, typeErrorText, isUploadModalLoading } = useAppSelector(
    (state) => ({
      documentType: state.paymentsPage.documentUploadForm.documentType,
      typeErrorText: state.paymentsPage.documentUploadForm.errors.type,
      isUploadModalLoading: state.paymentsPage.isLoading.uploadModal,
    }),
  );

  const handleDocTypeChange = (event: SelectChangeEvent) => {
    const val = Number(event.target.value);

    dispatch(setDocumentType(val));
  };

  return (
    <Styled.SelectGridItem item error={typeErrorText}>
      <Select
        menuItems={documentTypeOptions}
        dataTestId="payments__document-type-select"
        label="Document type"
        fullWidth
        required
        value={documentType}
        onChange={handleDocTypeChange}
        error={typeErrorText}
        disabled={isUploadModalLoading}
      />
    </Styled.SelectGridItem>
  );
};
