import { connect } from 'react-redux';
import { Popover } from '@material-ui/core';

import { AARequestStatusEnum } from 'old-enums/aa-request-status';
import { WebsitesTipTypesEnum } from 'old-enums/websites-tip-types-enum';
import { hideTip } from 'old-reducer/site-page';

import type { AppState } from 'store/types';

import * as Styled from './styles';

type SiteAddDispatchers = {
  hideTip(): void;
};

const Tip = (props: AppState['sitePage'] & SiteAddDispatchers) => {
  const { tip } = props;

  // eslint-disable-next-line @typescript-eslint/no-shadow
  const { hideTip } = props;
  const text =
    tip.adunit_id === AARequestStatusEnum.Pending ? (
      <span>
        You have already requested anti-adblock solution.
        <br />
        Your personal manager will check it as soon as possible.
      </span>
    ) : (
      <span>
        Unfortunately your request was declined by your personal
        <br />
        manager for some reasons. you can retry after 30 days.
      </span>
    );

  return (
    <Popover
      anchorEl={tip.anchorEl}
      anchorOrigin={{
        horizontal: 'left',
        vertical: 'bottom',
      }}
      open={tip.type === WebsitesTipTypesEnum.REQUEST_ANTI_ADBLOCK}
      onClose={hideTip}
    >
      {tip.type !== WebsitesTipTypesEnum.EMPTY && (
        <Styled.ParagraphStyles>{text}</Styled.ParagraphStyles>
      )}
    </Popover>
  );
};

// eslint-disable-next-line
export default connect(
  (state: { sitePage: AppState['sitePage'] }) => state.sitePage,
  { hideTip },
)(Tip);
