export enum OSEnum {
  WINDOWS = 2,
  ANDROID = 3,
  MAC = 4,
  IOS = 5,
  LINUX = 6,
  WINDOWS_PHONE = 7,
  BLACKBERRY_OS = 8,
  OTHER = 9,
}

export const getOSTitleById = (id: OSEnum) => {
  switch (id) {
    case OSEnum.WINDOWS:
      return 'Windows';
    case OSEnum.ANDROID:
      return 'Android';
    case OSEnum.MAC:
      return 'Mac';
    case OSEnum.IOS:
      return 'IOS';
    case OSEnum.LINUX:
      return 'Linux';
    case OSEnum.WINDOWS_PHONE:
      return 'Windows phone';
    case OSEnum.BLACKBERRY_OS:
      return 'Blackberry OS';
    case OSEnum.OTHER:
      return 'Other';
    default:
      throw new Error(`Unknown operating system id: ${id}`);
  }
};
