import { Component } from 'react';
import { KeyboardArrowLeft, KeyboardArrowRight } from '@material-ui/icons';

import Item from './item';
import * as Styled from './styles';

interface Props {
  active: number;
  total: number;
  onTap(page: number): void;
}
export class Pagination extends Component<Props> {
  private readonly onNextButtonClick = () => {
    const { active, onTap, total } = this.props;

    if (active !== total) {
      onTap(active + 1);
    }
  };

  private readonly onPrevButtonClick = () => {
    const { active, onTap } = this.props;

    if (active !== 1) {
      onTap(active - 1);
    }
  };

  private readonly getButton = (index: number) => {
    const { active, onTap } = this.props;

    return <Item active={active} item={index} key={index} onTap={onTap} />;
  };

  private readonly getButtons = (): JSX.Element[] => {
    const output: JSX.Element[] = [];
    const { active, onTap, total } = this.props;

    const firstPage = <Item active={active} item={1} key={1} onTap={onTap} />;

    const lastPage = (
      <Item active={active} item={total} key={total} onTap={onTap} />
    );

    const spacer = (key: string) => (
      <Styled.Spacer disabled key={key} data-testid="pagination__spacer">
        ...
      </Styled.Spacer>
    );

    if (total <= 15) {
      for (let i = 1; i <= total; i += 1) {
        output.push(this.getButton(i));
      }
    } else if (active <= 6) {
      for (let index = 1; index <= 8; index += 1) {
        output.push(this.getButton(index));
      }
      output.push(spacer('spacer-last'), lastPage);
    } else if (active >= total - 5) {
      output.push(firstPage, spacer('spacer-first'));

      for (let index = total - 7; index <= total; index += 1) {
        output.push(this.getButton(index));
      }
    } else {
      output.push(firstPage, spacer('spacer-first'));

      for (let index = active - 3; index <= active + 3; index += 1) {
        output.push(this.getButton(index));
      }
      output.push(spacer('spacer-last'), lastPage);
    }

    return output;
  };

  render() {
    return (
      <Styled.Pagination>
        <Styled.PaginationArrow
          onClick={this.onPrevButtonClick}
          data-testid="pagination__previous-button"
        >
          <KeyboardArrowLeft />
        </Styled.PaginationArrow>
        {this.getButtons()}
        <Styled.PaginationArrow
          onClick={this.onNextButtonClick}
          data-testid="pagination__previous-button"
        >
          <KeyboardArrowRight />
        </Styled.PaginationArrow>
      </Styled.Pagination>
    );
  }
}
