const TIMEOUT = 30 * 1000;
const SHARP_SPRING_DOMAIN = 'marketingautomation.services';

/**
 *          
 */
const track = (path: string) =>
  new Promise<void>((resolve, reject) => {
    let timeout: ReturnType<typeof setTimeout>;

    const intl = setInterval(() => {
      const script = document.querySelector<HTMLScriptElement>(
        `script[src*="${SHARP_SPRING_DOMAIN}/${path}"]`,
      );

      if (script != null) {
        clearInterval(intl);
        clearTimeout(timeout);

        script.onerror = () => {
          clearTimeout(timeout);
          reject();
        };

        script.onload = () => {
          clearTimeout(timeout);
          resolve();
        };

        timeout = setTimeout(reject, TIMEOUT);
      }
    }, 16);

    timeout = setTimeout(() => {
      clearInterval(intl);
      reject();
    }, TIMEOUT);
  });

/**
 *    SharpSpring,      
 */
export const getSharpSpringTrackingCodePostBack = async () => {
  try {
    await track('client');
    await track('koi');
  } catch {
    // noop
  }
};
