export enum DeviceFormatEnum {
  DESKTOP = 1,
  TABLET = 2,
  MOBILE = 3,
  OTHER = 4,
  TV = 5,
  WATCH = 6,
  CONSOLE = 7,
}

export const getDeviceFormatTitleById = (id: DeviceFormatEnum) => {
  switch (id) {
    case DeviceFormatEnum.DESKTOP:
      return 'Desktop';
    case DeviceFormatEnum.TABLET:
      return 'Tablet';
    case DeviceFormatEnum.MOBILE:
      return 'Mobile';
    case DeviceFormatEnum.OTHER:
      return 'Other';
    case DeviceFormatEnum.TV:
      return 'TV';
    case DeviceFormatEnum.WATCH:
      return 'Watch';
    case DeviceFormatEnum.CONSOLE:
      return 'Console';
    default:
      throw new Error(`Unknown device format id: ${id}`);
  }
};
