import { useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { Grid } from '@material-ui/core';
import { GetApp } from '@mui/icons-material';

import { ColorPicker } from 'old-component/color-picker';
import { Button } from '@development/ui-kit';
import { TemplateParamsEnum } from 'old-enums/template-params';
import { saveTemplateParams } from 'old-reducer/site-page';

import type { ChangeEvent } from 'react';
import type { FontSizesEnum } from 'old-enums/font-sizes';
import type { LayoutsEnum } from 'old-enums/layouts';

import { saveTemplateParams as saveTemplateParamsApi } from './api';
import { LayoutSelect } from './layout-select';
import { Thumbs } from './thumbs';
import { FontSizeSelect } from './font-size-select';

import type { Props, TemplateValues } from './types';

import * as Styled from './styles';

const GUIDE_FILE_NAME = 'Adsterra_Native_Ads_Guide.pdf';

export const NativeBannerSettings = ({ siteId, placementId }: Props) => {
  const token = useAppSelector((state) => state.user.token);
  const values = useAppSelector((state) => {
    const site = state.sitePage.list.sites.find(({ id }) => id === siteId);

    if (!site) {
      throw new ReferenceError(`Unable to find site by id: ${siteId}`);
    }

    const placement = site.placements.find(({ id }) => id === placementId);

    if (!placement) {
      throw new ReferenceError(
        `Unable to find placement by id: ${placementId}`,
      );
    }

    return placement.template_values;
  });

  const dispatch = useAppDispatch();

  let currentLayout = `${values[TemplateParamsEnum.NATIVE_COLUMNS]}:${
    values[TemplateParamsEnum.NATIVE_ROWS]
  }` as LayoutsEnum;

  const [fontColor, setFontColor] = useState(
    values[TemplateParamsEnum.NATIVE_FONT_COLOR],
  );
  const [fontSize, setFontSize] = useState(
    values[TemplateParamsEnum.NATIVE_FONT_SIZE],
  );
  const [isLoading, setIsLoading] = useState(false);
  const [layout, setLayout] = useState(currentLayout);

  const onFontColorChange = (fontColorNext: string) => {
    setFontColor(fontColorNext);
  };

  const handleFontSizeChange = (event: ChangeEvent<{ value: unknown }>) => {
    setFontSize(event.target.value as FontSizesEnum);
  };

  const handleLayoutChange = (event: ChangeEvent<{ value: unknown }>) => {
    setLayout(event.target.value as LayoutsEnum);
  };

  const onSaveHandler = async () => {
    if (!isLoading) {
      const diff = {} as TemplateValues;
      const [columns, rows] = layout.split(':');

      if (fontColor !== values[TemplateParamsEnum.NATIVE_FONT_COLOR]) {
        diff[TemplateParamsEnum.NATIVE_FONT_COLOR] = fontColor;
      }

      if (fontSize !== values[TemplateParamsEnum.NATIVE_FONT_SIZE]) {
        diff[TemplateParamsEnum.NATIVE_FONT_SIZE] = fontSize;
      }

      if (
        columns !== values[TemplateParamsEnum.NATIVE_COLUMNS] ||
        rows !== values[TemplateParamsEnum.NATIVE_ROWS]
      ) {
        diff[TemplateParamsEnum.NATIVE_COLUMNS] = columns;
        diff[TemplateParamsEnum.NATIVE_ROWS] = rows;
      }

      if (Object.keys(diff).length) {
        setIsLoading(true);

        const done = await saveTemplateParamsApi(token, placementId, diff);

        setIsLoading(false);

        if (done) {
          // @ts-ignore
          dispatch(saveTemplateParams(siteId, placementId, diff));
          currentLayout = layout;
        }
      }
    }
  };

  return (
    <>
      <Grid container>
        <Styled.GridSelectListItem item xs={6}>
          <LayoutSelect
            isLoading={isLoading}
            onLayoutChange={handleLayoutChange}
            layout={layout}
          />
          <FontSizeSelect
            isLoading={isLoading}
            onFontSizeChange={handleFontSizeChange}
            fontSize={fontSize}
          />
          <ColorPicker
            disabled={isLoading}
            label="Font color"
            placeholder="Inherit"
            value={fontColor}
            onChange={onFontColorChange}
          />
        </Styled.GridSelectListItem>
        <Styled.GridThumbItem item xs={6}>
          <Thumbs layout={layout} />
        </Styled.GridThumbItem>
      </Grid>
      <Styled.ButtonsBlock>
        <Button
          variant="contained"
          onClick={onSaveHandler}
          isLoading={isLoading}
          dataTestId="native-banner-settings__save-button"
        >
          Save
        </Button>
        <Styled.GuideLinkButton
          download={GUIDE_FILE_NAME}
          href={`downloads/${GUIDE_FILE_NAME}`}
          startIcon={<GetApp />}
          variant="outlined"
          dataTestId="native-banner-settings__download-guide-button"
        >
          Download guide
        </Styled.GuideLinkButton>
      </Styled.ButtonsBlock>
    </>
  );
};
