import { Grid } from '@mui/material';
import { Button } from '@development/ui-kit';

import {
  VisibilityOutlined as VisibilityOutlinedIcon,
  VisibilityOffOutlined as VisibilityOffOutlinedIcon,
} from '@mui/icons-material';

import { Item } from './description-item';
import * as Styled from './styles';

import type { Props } from './types';

export const Description = ({
  dataTestID,
  toOpen,
  items,
  title,
  intro,
  expand,
}: Props) => (
  <Styled.AccordionControl
    summaryText={title}
    expanded={toOpen}
    expandOpenControl={
      <Button
        dataTestId="description__hide-tips-button"
        variant="text"
        startIcon={<VisibilityOffOutlinedIcon />}
      >
        Hide tips
      </Button>
    }
    expandClosedControl={
      <Button
        dataTestId="description__show-tips-button"
        variant="text"
        startIcon={<VisibilityOutlinedIcon />}
      >
        Show tips
      </Button>
    }
    onChange={(_, expanded: boolean) => expand(expanded)}
    content={
      <div>
        {intro}
        <Styled.GridContainer container direction="row">
          {items.map((item, index) => (
            <Grid item xs key={index.toString()}>
              {/* eslint-disable-next-line react/jsx-props-no-spreading */}
              <Item {...item} index={index} />
            </Grid>
          ))}
        </Styled.GridContainer>
      </div>
    }
    dataTestId={dataTestID}
    defaultExpanded
  />
);
