import { AdUnitEnum } from 'old-enums/ad-units';
import { CodeTypeEnum } from 'old-enums/code-type';
import { TagEnum } from 'old-enums/tags';

import { NativeBannerSettings } from './native-banner-settings';
import { PopunderCpaDesc } from './ad-codes-descriptions/popunder-cpa-description';
import { PopUnderJsSyncDesc } from './ad-codes-descriptions/popunder-jssync-description';
import { PopunderPhpDesc } from './ad-codes-descriptions/popunder-php-description';
import { WPDesc } from './ad-codes-descriptions/wp-description';
import { VastDesc } from './ad-codes-descriptions/vast-description';
import { VastSkipDesc } from './ad-codes-descriptions/vast-skip-description';
import { PopUnderInlineDesc } from './ad-codes-descriptions/popunder-inline-description';

import type { Props } from './types';

const puDirectCode = (
  <div>
    <p>
      Avoid using multiple popunder codes on the same page  they may conflict
      and show lower results
    </p>
  </div>
);

const vastSimpleCode = (
  <div>
    <p>Please, follow your player&apos;s VAST tag installation instructions.</p>
  </div>
);

export const CodesDescription = ({
  adunit,
  code_type,
  custom_domain,
  php_code_link,
  placement_id,
  site_id,
  tags,
  postback_url,
}: Props) => {
  const isPUCpaDirectLink =
    tags &&
    tags.includes(TagEnum.SRV_ADVERTISER_CPA) &&
    code_type === CodeTypeEnum.DIRECT_URL;

  const renderDescriptions = () => {
    switch (true) {
      case [AdUnitEnum.DIRECT_LINK, AdUnitEnum.POPUNDER].includes(adunit) &&
        isPUCpaDirectLink:
        return <PopunderCpaDesc postbackUrl={postback_url} />;

      case [AdUnitEnum.DIRECT_LINK, AdUnitEnum.POPUNDER].includes(adunit) &&
        code_type === CodeTypeEnum.DIRECT_URL:
        return puDirectCode;

      case [AdUnitEnum.DIRECT_LINK, AdUnitEnum.POPUNDER].includes(adunit) &&
        code_type === CodeTypeEnum.JS_SYNC_PU:
        return <PopUnderJsSyncDesc />;

      case adunit === AdUnitEnum.POPUNDER &&
        [CodeTypeEnum.INLINE_PU_NEW, CodeTypeEnum.INLINE_PU_LAYER].includes(
          code_type,
        ):
        return <PopUnderInlineDesc isAntiAdBlock={custom_domain} />;

      case [AdUnitEnum.DIRECT_LINK, AdUnitEnum.POPUNDER].includes(adunit) &&
        code_type === CodeTypeEnum.PHP_CODE:
        return (
          <PopunderPhpDesc
            phpCodeLink={php_code_link}
            placementId={placement_id}
          />
        );

      case adunit === AdUnitEnum.NATIVE_BANNER:
        return (
          <NativeBannerSettings placementId={placement_id} siteId={site_id} />
        );

      case adunit === AdUnitEnum.WEB_PUSH &&
        [CodeTypeEnum.WEBPUSH, CodeTypeEnum.WEBPUSH_ASYNC].includes(code_type):
        return <WPDesc />;

      case adunit === AdUnitEnum.VAST && code_type === CodeTypeEnum.VAST:
        return <VastDesc />;

      case adunit === AdUnitEnum.VAST && code_type === CodeTypeEnum.VAST_SIMPLE:
        return vastSimpleCode;

      case adunit === AdUnitEnum.VAST && code_type === CodeTypeEnum.VAST_SKIP:
        return <VastSkipDesc />;

      case adunit === AdUnitEnum.SOCIAL_BAR:
        return <p>Place this code just above the &lt;/body&gt; tag</p>;

      default:
        return (
          <p>
            Place this code on your website where you want it to be displayed.
          </p>
        );
    }
  };

  return renderDescriptions();
};
