export enum OfferLinkTypeEnum {
  DIRECT_LINK = 'Direct link',
  POSTBACK_URL = 'Postback url',
  BACKLINK = 'Backlink',
}

export const getOfferLinkTypeName = (type: OfferLinkTypeEnum) => {
  switch (type) {
    case OfferLinkTypeEnum.POSTBACK_URL:
      return 'postbackURL';
    case OfferLinkTypeEnum.BACKLINK:
      return 'backlink';
    default:
      throw new Error(`Unknown offer link type: ${type}`);
  }
};

export const getOfferLinkFieldName = (type: OfferLinkTypeEnum) => {
  switch (type) {
    case OfferLinkTypeEnum.POSTBACK_URL:
      return 'postback_urls';
    case OfferLinkTypeEnum.BACKLINK:
      return 't_backlink';
    default:
      throw new Error(`Unknown offer link type: ${type}`);
  }
};

export const getGlobalOfferLinkFieldName = (type: OfferLinkTypeEnum) => {
  switch (type) {
    case OfferLinkTypeEnum.BACKLINK:
      return 't_backlink';
    default:
      throw new Error(`Unknown offer link type: ${type}`);
  }
};

export const getOfferLinkAPIRequestURL = (
  type: OfferLinkTypeEnum,
  id?: number,
) => {
  switch (type) {
    case OfferLinkTypeEnum.POSTBACK_URL:
      return `/api/offer/${id}/postback-urls`;
    case OfferLinkTypeEnum.BACKLINK:
      return `/api/offer/${id}/backlink`;
    default:
      throw new Error(`Unknown offer link type: ${type}`);
  }
};

export const getGlobalOfferLinkAPIRequestURL = (type: OfferLinkTypeEnum) => {
  switch (type) {
    case OfferLinkTypeEnum.POSTBACK_URL:
      return '/api/offers/global-postback-url';
    case OfferLinkTypeEnum.BACKLINK:
      return '/api/offers/global-backlink';
    default:
      throw new Error(`Unknown global offer link type: ${type}`);
  }
};

export const isBacklinkByType = (type: OfferLinkTypeEnum) =>
  type === OfferLinkTypeEnum.BACKLINK;

export const isPostbackURLByType = (type: OfferLinkTypeEnum) =>
  type === OfferLinkTypeEnum.POSTBACK_URL;
