import styled from 'styled-components';
import { withTheme } from '@material-ui/core/styles';
import type { WithTheme } from '@material-ui/core/styles';
import { styleProps } from 'old-common/sign-style';

export const Wrapper = styled.div`
  min-height: 100%;
  height: fit-content;
  align-items: center;
`;

export const BlockWrap = withTheme(styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;

  & > *:first-child {
    width: calc(100vw - ${styleProps.sideBlockWidth});
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.paper};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      width: 100%;
    }
  }

  & > *:last-child {
    width: ${styleProps.sideBlockWidth};
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.default};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      display: none;
    }
  }
`);

export const MainWrap = withTheme(styled.div`
  display: flex;
  align-items: center;
  min-height: calc(100vh - 166px);
`);

export const Content = withTheme(styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: relative;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: calc(100vw - ${styleProps.sideBlockWidth});
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.paper};
    z-index: -1;

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      width: 100%;
    }
  }

  &::after {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: ${styleProps.sideBlockWidth};
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.default};
    z-index: -1;

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      display: none;
    }
  }

  & > *:first-child {
    width: calc(100vw - ${styleProps.sideBlockWidth});
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.paper};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      width: 100%;
    }
  }

  & > *:last-child {
    width: ${styleProps.sideBlockWidth};
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.default};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      display: none;
    }
  }
`);
