import type { ChangeEvent } from 'react';
import type { Banner } from 'old-reducer/referrals/types';

import { useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import {
  CircularProgress,
  FormControlLabel,
  MenuItem,
  Radio,
  Select,
} from '@material-ui/core';

import { SnackbarPure } from 'old-component/snackbar';
import { Button } from '@development/ui-kit';
import CopyToClipboard from 'react-copy-to-clipboard';

import { AlertSeverity } from 'old-enums/alert-severity';
import { SnackbarMessage } from 'old-enums/snackbar-message';

import { setSelectedBanner, showSnackbar } from 'old-reducer/referrals';
import { getBannerFile, getBannersList } from 'old-reducer/referrals/thunk';
import { hideSnackbar } from 'old-reducer/snackbar';

import * as Styled from './styles';
import { ReferralBannerExtension } from './referral-banner-extension';

export const BannerBlock = () => {
  const dispatch = useAppDispatch();
  const [bannerType, setBannerType] = useState(ReferralBannerExtension.PNG);

  const {
    snackbar: { message: snackbarMessage, severity: snackbarSeverity },
    bannerList,
    selectedBanner,
  } = useAppSelector((state) => state.referralsPage);

  const handleRadioCheck = (event: ChangeEvent<HTMLInputElement>) => {
    if (event.target.value === ReferralBannerExtension.PNG) {
      dispatch(getBannersList(ReferralBannerExtension.PNG));
      setBannerType(ReferralBannerExtension.PNG);
    } else {
      dispatch(getBannersList(ReferralBannerExtension.GIF));
      setBannerType(ReferralBannerExtension.GIF);
    }
  };

  const handleBannerSelect = (e: ChangeEvent<{ value: unknown }>) => {
    const banner = bannerList.find(
      (item: Banner) => item.id === e.target.value,
    );

    dispatch(setSelectedBanner(banner));
  };

  const handleShowSnackbar = () => {
    dispatch(
      showSnackbar({
        message: SnackbarMessage.COPY_TEXT,
        severity: AlertSeverity.SUCCESS,
      }),
    );
  };

  const onHideSnackbarHandler = () => {
    dispatch(hideSnackbar());
  };

  const handleDownloadBanner = async (banner: Banner) =>
    dispatch(getBannerFile(banner, bannerType));

  const getBannerCode = (banner: Banner): string => {
    return `<a href="${banner.href}"><img alt="banner" src="${banner.url}" /></a>`;
  };

  return (
    <Styled.PaperStyledBanner>
      <Styled.TitleBanner>Choose a Banner</Styled.TitleBanner>
      <Styled.TextBanner>
        Download the banners that suit you the most or copy them in code. Both
        static and animated versions are available.
      </Styled.TextBanner>

      {bannerList.length > 0 ? (
        <>
          <Styled.BannerPreviewBlock>
            <Styled.BannerPreviewBlock mixin={Styled.bannerSelectionBlockMixin}>
              <Styled.CheckboxBlockBanner>
                {Object.values(ReferralBannerExtension).map((type) => (
                  <FormControlLabel
                    key={type}
                    value={type}
                    control={
                      <Radio
                        checked={bannerType === type}
                        color="primary"
                        data-testid={`${type}-radiobutton`}
                        onChange={handleRadioCheck}
                      />
                    }
                    label={type.toUpperCase()}
                    labelPlacement="end"
                  />
                ))}
              </Styled.CheckboxBlockBanner>

              {bannerList.length > 0 && selectedBanner && (
                <>
                  <Styled.FormControlStyledBannerSelect>
                    <Select
                      value={
                        selectedBanner ? selectedBanner.id : bannerList[0].id
                      }
                      onChange={handleBannerSelect}
                      data-testid="banner-select"
                    >
                      {bannerList.map((banner: Banner) => (
                        <MenuItem key={banner.id} value={banner.id}>
                          {banner.name}
                        </MenuItem>
                      ))}
                    </Select>
                  </Styled.FormControlStyledBannerSelect>

                  <Button
                    variant="contained"
                    onClick={() => handleDownloadBanner(selectedBanner)}
                    dataTestId="download-banner-button"
                  >
                    Download banner
                  </Button>
                  <CopyToClipboard text={getBannerCode(selectedBanner)}>
                    <Styled.CodeTextarea
                      onFocus={handleShowSnackbar}
                      maxRows={5}
                      multiline
                      value={getBannerCode(selectedBanner)}
                      dataTestId="get-banner-code"
                    />
                  </CopyToClipboard>
                </>
              )}
            </Styled.BannerPreviewBlock>
            {selectedBanner && (
              <Styled.PreviewBlockBanner>
                <Styled.PreviewImg alt="banner" src={selectedBanner.url} />
              </Styled.PreviewBlockBanner>
            )}
          </Styled.BannerPreviewBlock>

          <Styled.TextBanner>
            Place the banners on your website with your referral link.&nbsp;
            <Styled.LinkBanner
              href="https://adsterra.com/blog/extra-income-with-adsterra-referral-program"
              target="_blank"
              title="Blog link"
              rel="noreferrer"
            >
              For more information see our blog.
            </Styled.LinkBanner>
          </Styled.TextBanner>
          <SnackbarPure
            dataTestId="banner-snack"
            message={snackbarMessage}
            severity={snackbarSeverity}
            onHide={onHideSnackbarHandler}
          />
        </>
      ) : (
        <CircularProgress />
      )}
    </Styled.PaperStyledBanner>
  );
};
