import { createSlice } from '@reduxjs/toolkit';

import { initialState } from './state';
import { thunkTicketStatuses } from './thunk';

const { reducer: ticketStatusesReducer } = createSlice({
  name: 'ticketStatuses',

  initialState,

  reducers: {},

  extraReducers: (builder) => {
    builder.addCase(thunkTicketStatuses.pending, (state) => {
      state.status = 'pending';
    });

    builder.addCase(thunkTicketStatuses.fulfilled, (state, { payload }) => {
      state.status = 'fulfilled';
      state.data = payload.items ?? [];
    });

    builder.addCase(thunkTicketStatuses.rejected, (state) => {
      state.status = 'rejected';
    });
  },
});

export {
  // actions
  // reducer
  ticketStatusesReducer,
};
