import { PendingRequestRow } from './pending-request-row';

import type { Props } from './types';

export const PlacementsList = ({ pendingRequest, placementsRows }: Props) => {
  const isPendingRequestExist =
    !Array.isArray(pendingRequest) && pendingRequest.id !== 0;

  return (
    <>
      {placementsRows}

      {isPendingRequestExist && (
        <PendingRequestRow adUnits={pendingRequest.ad_units} />
      )}
    </>
  );
};
