import { ZoneCategoryEnum } from 'old-enums/zone-category';
import { RadioButton, RadioButtonGroup } from '@development/ui-kit';

import type { ChangeEvent } from 'react';
import type { Props } from './types';

import * as Styled from './styles';

export const DirectLinkCategorySelection = ({
  categoryId,
  onZoneCategoryChange,
}: Props) => (
  <>
    <Styled.Title>Direct link category</Styled.Title>
    <RadioButtonGroup
      dataTestId="dialog__export-code-type"
      value={Number(categoryId)}
      name="category"
      onChange={({ target: { value } }: ChangeEvent<HTMLInputElement>) =>
        onZoneCategoryChange(Number(value) as ZoneCategoryEnum)
      }
    >
      <RadioButton
        label="Adult"
        dataTestId="radiobutton-adult"
        value={ZoneCategoryEnum.ADULT_SOCIALBLACK}
      />
      <RadioButton
        label="Non-adult"
        dataTestId="radiobutton-non-adult"
        value={ZoneCategoryEnum.SOCIAL_BLACK}
      />
    </RadioButtonGroup>
  </>
);
