import type { Props } from './types';
import * as Styled from './styles';

export const PaysystemAccount = ({ accountInfo, paysystemId }: Props) => {
  const hasPaysystemId = paysystemId && paysystemId > 0;
  const replacedValue =
    hasPaysystemId && accountInfo ? accountInfo.replace(/(\n)+/, '<br />') : '';
  const splitValues = replacedValue.split('\n');

  const preparedValue = splitValues
    .map((splitValue: string, index: number) =>
      index ? `<span>${splitValue}</span>` : splitValue,
    )
    .join('');

  return (
    <Styled.PaysystemAccountInfo
      dangerouslySetInnerHTML={{ __html: preparedValue }} // eslint-disable-line react/no-danger
    />
  );
};
