import { useRef, useState } from 'react';

import { ArrowDropDown } from '@mui/icons-material';
import { Popover, styled } from '@material-ui/core';
import { IconButton } from '@development/ui-kit';
import type { PopoverProps } from '@material-ui/core';
import { Grid } from '@mui/material';
import * as Styled from './styles';
import type { UserBalanceProps } from './types';
import { formatAmount, sign } from './utils';

const dtiPrefix = 'header__user-balance';

const StyledPopover = styled((props: PopoverProps) => (
  <Popover
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    // eslint-disable-next-line react/jsx-props-no-spreading
    {...props}
  />
))(() => ({
  '& .MuiPaper-root': {
    borderRadius: 5,
  },
}));

export function UserBalance({ user }: UserBalanceProps) {
  const { balance, details } = user.balance;
  const adjustmentsColorStyle = details.balance_altered < 0;
  const ref = useRef<HTMLDivElement | null>(null);
  const [anchorEl, setAnchorEl] = useState<null | HTMLDivElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = () => {
    setAnchorEl(ref.current);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Styled.Balance container alignItems="center">
      <Styled.BalanceLabel>Balance: </Styled.BalanceLabel>
      <Styled.BalanceCurrency data-testid={`${dtiPrefix}--currency`}>
        $
      </Styled.BalanceCurrency>
      <Styled.BalanceAmount data-testid={`${dtiPrefix}--amount`}>
        {formatAmount(balance, true)}
      </Styled.BalanceAmount>
      <div ref={ref}>
        <IconButton
          onClick={handleClick}
          size="small"
          dataTestId={`${dtiPrefix}--show`}
        >
          <ArrowDropDown />
        </IconButton>
      </div>
      <StyledPopover anchorEl={anchorEl} open={open} onClose={handleClose}>
        <Styled.BalanceCalculation container flexDirection="row">
          <Grid item display="flex" flexDirection="column" alignItems="center">
            <div data-testid={`${dtiPrefix}--earned`}>
              {formatAmount(Math.abs(details.earned))}
            </div>
            <Styled.BalanceInfo>Earned</Styled.BalanceInfo>
          </Grid>
          <Styled.BalanceSignPaid>-</Styled.BalanceSignPaid>
          <Grid item display="flex" flexDirection="column" alignItems="center">
            <Styled.BalancePaid data-testid={`${dtiPrefix}--paid`}>
              {formatAmount(Math.abs(details.paid))}
            </Styled.BalancePaid>
            <Styled.BalanceInfo>Paid</Styled.BalanceInfo>
          </Grid>
          <Styled.BalanceAltered balanceAltered={adjustmentsColorStyle}>
            {sign(details.balance_altered)}
          </Styled.BalanceAltered>
          <Grid item display="flex" flexDirection="column" alignItems="center">
            <Styled.BalancePaidCondition
              balanceAltered={adjustmentsColorStyle}
              data-testid={`${dtiPrefix}--adjustments`}
            >
              {formatAmount(Math.abs(details.balance_altered))}
            </Styled.BalancePaidCondition>
            <Styled.BalanceInfo>Adjustments</Styled.BalanceInfo>
          </Grid>
          <Styled.BalanceCurrency>
            {sign(details.referrals)}
          </Styled.BalanceCurrency>
          <Grid item display="flex" flexDirection="column" alignItems="center">
            <div data-testid={`${dtiPrefix}--referrals`}>
              {formatAmount(Math.abs(details.referrals))}
            </div>
            <Styled.BalanceInfo>Referrals</Styled.BalanceInfo>
          </Grid>
          <Styled.BalanceSignTotal>=</Styled.BalanceSignTotal>
          <Grid item display="flex" flexDirection="column" alignItems="center">
            <Styled.BalanceTotal data-testid={`${dtiPrefix}--total`}>
              {formatAmount(balance)}
            </Styled.BalanceTotal>
            <Styled.BalanceInfo>Balance</Styled.BalanceInfo>
          </Grid>
        </Styled.BalanceCalculation>
      </StyledPopover>
    </Styled.Balance>
  );
}
