import type { DataGridTableProps, CellRenderProps } from '@development/ui-kit';
import type { Maybe } from 'types/utils';
import { PayoutType } from 'old-enums/payout-type';
import { ColumnNamesForPaymentHistory } from './column-names';
import { PDFLink } from './pdf-link';
import { PaysystemAccount } from './paysystem-account';

const getAmountOutput = (currencyPrefix = '$', value = 0) => {
  const fixedValue = Math.abs(Number(value)).toFixed(2);
  const isNegative = value < 0 && Number(fixedValue) !== 0;
  const minusPrefix = isNegative ? '-' : '';

  return minusPrefix + currencyPrefix + fixedValue;
};

const getCommentOutput = (comment: Maybe<string>) => {
  const pattern = /(\d{4})-(\d{2})-(\d{2}) - (\d{4})-(\d{2})-(\d{2})/g;
  const replaceValue = '$1/$2/$3  $4/$5/$6';

  return (comment || '').replace(pattern, replaceValue);
};

export const paymentColumns: DataGridTableProps['columnsArray'] = [
  {
    dataField: ColumnNamesForPaymentHistory.DATE,
    caption: 'Date',
    width: 130,
    selectedFilterOperation: 'between',
    dataType: 'date',
    format: 'yyyy/MM/dd',
    customizeText: ({ valueText }: { valueText: string }) => {
      return valueText.replace(',', ',<br />');
    },
    encodeHtml: false,
    sortOrder: 'desc',
    calculateFilterExpression(
      filterValue: string | string[],
      selectedFilterOperation: string,
    ) {
      const additionalFilter = [
        'or',
        ['type', '=', PayoutType.NEXT_PAYOUT],
        'or',
        ['type', '=', PayoutType.PENDING_PAYOUT],
      ];
      let filterExpression = [];

      if (selectedFilterOperation === 'between' && Array.isArray(filterValue)) {
        filterExpression = [
          [
            [this.dataField, '>=', filterValue[0]],
            'and',
            [this.dataField, '<=', filterValue[1]],
          ],
        ];
      } else {
        filterExpression = [
          this.dataField,
          selectedFilterOperation,
          filterValue,
        ];
      }

      return [filterExpression, ...additionalFilter];
    },
  },
  {
    dataField: ColumnNamesForPaymentHistory.PAYMENT_AMOUNT,
    caption: 'Sum',
    width: 150,
    allowFiltering: false,
    cellRender: (cellData: CellRenderProps['cellData']) =>
      getAmountOutput(cellData.data.pre, cellData.value),
  },
  {
    dataField: ColumnNamesForPaymentHistory.FEE_AMOUNT,
    caption: 'Fee',
    width: 100,
    allowFiltering: false,
    cellRender: (cellData: CellRenderProps['cellData']) =>
      getAmountOutput(cellData.data.pre, cellData.value),
  },
  {
    dataField: ColumnNamesForPaymentHistory.TYPE,
    caption: 'Type',
    allowFiltering: false,
  },
  {
    dataField: ColumnNamesForPaymentHistory.PAYSYSTEM_ACCOUNT,
    caption: 'Account',
    width: 220,
    allowFiltering: false,
    cellRender: ({
      value,
      data: { paysystem_id: paysystemId },
    }: {
      value: Maybe<string>;
      data: { paysystem_id: Maybe<number> };
    }) => <PaysystemAccount paysystemId={paysystemId} accountInfo={value} />,
  },
  {
    dataField: ColumnNamesForPaymentHistory.COMMENT,
    caption: 'Comment',
    width: 200,
    allowFiltering: false,
    cellRender: ({ value }: { value: Maybe<string> }) =>
      getCommentOutput(value),
  },
  {
    dataField: ColumnNamesForPaymentHistory.INVOICE_ID,
    caption: 'Documents',
    width: 110,
    allowFiltering: false,
    cellRender: ({
      value,
      data: { parent_invoice_id: parentInvoiceId },
    }: {
      value: Maybe<number>;
      data: { parent_invoice_id: Maybe<number> };
    }) => <PDFLink invoiceId={value} parentInvoiceId={parentInvoiceId} />,
  },
];
