import type { SelectProps } from '@development/ui-kit';
import { Select } from '@development/ui-kit';
import type { SelectChangeEvent } from '@mui/material';
import { isFieldType, isFilteredField } from './helpers';
import type { TableFields } from './types';
import { TableSelect } from './styles';

export const getSelects = <T,>(tableFields: TableFields<T>) =>
  Object.keys(tableFields).map((fieldKey) => {
    const field = tableFields[fieldKey];

    if (!isFilteredField(field)) {
      return null;
    }

    const selectProps: SelectProps = {
      menuItems: field.availableValues,
      label: field.caption,
      dataTestId: `filtered-list__${fieldKey}-select`,
      value: field.currentValue,
      minWidth: field.filterWidth,
      disabled: field.availableValues.length === 0,
      onChange: (event: SelectChangeEvent) => {
        const selectValue = event.target.value;

        if (!isFieldType(selectValue)) {
          return;
        }
        field.onChange(selectValue);
      },
    };

    return (
      <TableSelect key={`select-${fieldKey}`}>
        <Select
          {...selectProps} // eslint-disable-line react/jsx-props-no-spreading
        />
      </TableSelect>
    );
  });
