import type { AppState } from 'store/types';
import type { IdTopic } from './types';

export const selectTicketTopics = (state: AppState) => {
  return state.ticketTopics.data;
};

export const selectStatusTicketTopics = (state: AppState) => {
  return state.ticketTopics.status;
};

export const selectAllTicketTopics = (state: AppState) =>
  selectTicketTopics(state).reduce<[number, string][]>(
    (acc, v) => [...acc, [v.id, v.name]],
    [],
  );

export const selectTicketHint = (state: AppState) =>
  selectTicketTopics(state).find((v) => v.id === state.newTicket.type)?.hint;

export const selectTopicTitleById = (id: IdTopic) => (state: AppState) =>
  selectTicketTopics(state).find((v) => v.id === id)?.name;
