export enum CodeTypeEnum {
  IFRAME_SYNC = 1,
  JS_SYNC = 2,
  JS_SYNC_PU = 3,
  DIRECT_URL = 4,
  SIMPLE_IFRAME = 5,
  JS_ASYNC = 6,
  WEBPUSH = 8,
  WEBPUSH_ASYNC = 9,
  OVERLAY = 10,
  PHP_CODE = 11,
  NATIVE_ASYNC = 12,
  VAST = 7,
  VAST_SIMPLE = 44,
  VAST_SKIP = 45,
  JS_ASYNC_SB = 77,
  INLINE_PU_NEW = 78,
  INLINE_PU_LAYER = 79,
}

const titles = {
  [CodeTypeEnum.IFRAME_SYNC]: 'IFRAME SYNC',
  [CodeTypeEnum.JS_SYNC]: 'JS SYNC',
  [CodeTypeEnum.JS_SYNC_PU]: 'JS SYNC',
  [CodeTypeEnum.DIRECT_URL]: 'DIRECT URL',
  [CodeTypeEnum.SIMPLE_IFRAME]: 'SIMPLE IFRAME',
  [CodeTypeEnum.JS_ASYNC]: 'JS ASYNC',
  [CodeTypeEnum.WEBPUSH]: 'WEBPUSH',
  [CodeTypeEnum.WEBPUSH_ASYNC]: 'WEBPUSH ASYNC',
  [CodeTypeEnum.OVERLAY]: 'OVERLAY',
  [CodeTypeEnum.PHP_CODE]: 'ANTI_ADBLOCK PHP CODE',
  [CodeTypeEnum.NATIVE_ASYNC]: 'NATIVE ASYNC',
  [CodeTypeEnum.VAST]: 'VAST',
  [CodeTypeEnum.VAST_SIMPLE]: 'SIMPLE VAST',
  [CodeTypeEnum.VAST_SKIP]: 'VAST WITH ADDITIONAL SKIP SETTINGS',
  [CodeTypeEnum.JS_ASYNC_SB]: 'JS ASYNC',
  [CodeTypeEnum.INLINE_PU_NEW]: 'INLINE CODE (ADBLOCK BYPASS)',
  [CodeTypeEnum.INLINE_PU_LAYER]: 'INLINE CODE (ADBLOCK BYPASS)',
};

const popunderTitles = {
  [CodeTypeEnum.JS_SYNC]: 'JS SYNC (NO ADBLOCK BYPASS)',
  [CodeTypeEnum.JS_SYNC_PU]: 'JS SYNC (NO ADBLOCK BYPASS)',
  [CodeTypeEnum.INLINE_PU_NEW]: 'INLINE CODE (ADBLOCK BYPASS)',
  [CodeTypeEnum.INLINE_PU_LAYER]: 'INLINE CODE (ADBLOCK BYPASS)',
};

const popunderAntiAdBlockTitles = {
  [CodeTypeEnum.JS_SYNC]: 'ANTI-ADBLOCK JS SYNC',
  [CodeTypeEnum.JS_SYNC_PU]: 'ANTI-ADBLOCK JS SYNC',
  [CodeTypeEnum.INLINE_PU_NEW]: 'INLINE CODE + ANTI-ADBLOCK',
  [CodeTypeEnum.INLINE_PU_LAYER]: 'INLINE CODE + ANTI-ADBLOCK',
};

export const getCodeTypeTitle = (codeType: CodeTypeEnum): string => {
  if (codeType in titles) {
    return titles[codeType];
  }

  return codeType.toString();
};

export const getPopunderAntiAdBlockTitle = (codeType: CodeTypeEnum): string => {
  if (codeType in popunderAntiAdBlockTitles) {
    return popunderAntiAdBlockTitles[codeType];
  }

  return getCodeTypeTitle(codeType);
};

export const getPopunderCodeTitle = (codeType: CodeTypeEnum): string => {
  if (codeType in popunderTitles) {
    return popunderTitles[codeType];
  }

  return getCodeTypeTitle(codeType);
};
