export enum OfferRequestStatusEnum {
  CREATED = 1,
  APPROVED = 2,
  DECLINED = 3,
  DEPRIVED = 4,
}

const getAllOfferRequestStatusItems = () => [
  { id: OfferRequestStatusEnum.CREATED, name: 'Created' },
  { id: OfferRequestStatusEnum.APPROVED, name: 'Approved' },
  { id: OfferRequestStatusEnum.DECLINED, name: 'Declined' },
  { id: OfferRequestStatusEnum.DEPRIVED, name: 'Deprived' },
];

export const getOfferRequestStatusNameById = (
  statusId: OfferRequestStatusEnum,
) => {
  const requestStatus = getAllOfferRequestStatusItems().find(
    (status) => status.id === statusId,
  );

  if (requestStatus == null) {
    throw new Error(`Unknown offer request status id: ${statusId}`);
  }

  return requestStatus.name;
};
