import type { RouteComponentProps } from 'react-router-dom';

import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { withRouter } from 'react-router-dom';

import { PageLoader } from 'old-component/page-loader';
import { OfferRequestStatusEnum } from 'old-enums/offer-request-status';
import { getOfferData } from 'old-reducer/offer-page';
import { NotFound } from 'pages/not-found';

import { AdditionalInfo } from './additional-info';
import { GeosAndGoals } from './geos-and-goals';
import { OfferInfo } from './offer-info';
import { OfferLinks } from './offer-links';
import { OfferRequest } from './offer-request';
import * as Styled from './styles';

type Props = {
  id: string;
};

const OfferComponent = ({
  match: {
    params: { id },
  },
}: RouteComponentProps<Props>) => {
  const { data } = useAppSelector((state) => state.offerPage);
  const { page: isPageLoading } = useAppSelector(
    (state) => state.offerPage.isLoading,
  );

  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(getOfferData(id));
  }, [id, dispatch]);

  if (isPageLoading) {
    return <PageLoader />;
  }

  if (data === null) {
    return <NotFound />;
  }

  const isRequestStatusApproved =
    data.offer_request?.n_status === OfferRequestStatusEnum.APPROVED;

  return (
    <div className="content">
      <Styled.Name>{data.t_name}</Styled.Name>

      <Styled.Main>
        <Styled.MainContent>
          <Styled.PaperStyled>
            <OfferInfo
              previewPath={data.t_preview_path}
              publicInfo={data.t_public_information}
            />

            {!isRequestStatusApproved && (
              <OfferRequest
                hasPrivateOfferPermission={data.b_has_private_offer_permission}
                id={id}
                privacyType={data.n_privacy_type}
                status={data.offer_request?.n_status}
              />
            )}
          </Styled.PaperStyled>

          {isRequestStatusApproved && (
            <OfferLinks
              backlink={data.offer_request?.t_backlink}
              directLink={data.offer_request?.t_direct_link}
              offerId={data.id_offer}
              postbackURLs={data.offer_request?.postback_urls}
            />
          )}
        </Styled.MainContent>

        <Styled.AdditionalInfoContainer>
          <GeosAndGoals
            conversionType={data.n_conversion_type}
            payout={data.payout}
          />
          <AdditionalInfo
            deviceFormat={data.na_device_format}
            osType={data.ida_os_vendor}
            vertical={data.id_advertiser_category}
          />
        </Styled.AdditionalInfoContainer>
      </Styled.Main>
    </div>
  );
};

export const Offer = withRouter(OfferComponent);
