import { FileUploader } from '@development/ui-kit';
import { Grid } from '@material-ui/core';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { setUploadErrors, setFilesErrorText } from 'old-reducer/payments-page';
import {
  acceptedExtensions,
  acceptedFileTypes,
  hintText,
  MAX_FILE_SIZE,
  MAX_FILES_COUNT,
} from './constants';
import type { Props } from './types';

export const DocumentsUploader = ({ onUploadFiles }: Props) => {
  const dispatch = useAppDispatch();
  const { isUploadModalLoading } = useAppSelector((state) => ({
    isUploadModalLoading: state.paymentsPage.isLoading.uploadModal,
  }));

  const onUpdateFiles = (files: Array<File>, hasErrors: boolean) => {
    onUploadFiles(files);
    dispatch(setFilesErrorText(''));
    dispatch(setUploadErrors(hasErrors));
  };

  return (
    <Grid item>
      <FileUploader
        dataTestId="payments__file-uploader"
        fileExtensions={acceptedExtensions}
        acceptedFileTypes={acceptedFileTypes}
        isMultipleUpload
        updateFiles={onUpdateFiles}
        maxFileSize={MAX_FILE_SIZE}
        maxFilesCount={MAX_FILES_COUNT}
        hintText={hintText}
        readOnly={isUploadModalLoading}
      />
    </Grid>
  );
};
