import { useAppSelector } from 'store/hooks';
import { Grid } from '@material-ui/core';
import { isPureAffiliate } from 'old-enums/user-permission';
import { PaymentAlerts } from './payment-alerts';
import { PaymentDateInfo } from './payment-date-info';
import { RulesTable } from './rules-table';

export const PaymentRules = () => {
  const { permissions, isPayoutsLoading } = useAppSelector((state) => ({
    permissions: state.user.data.ida_permissions,
    isPayoutsLoading: state.paymentsPage.isLoading.payouts,
  }));

  return (
    <Grid container spacing={2} direction="column" wrap="nowrap">
      {!isPayoutsLoading && (
        <Grid item>
          <PaymentAlerts />
        </Grid>
      )}
      {!isPureAffiliate(permissions) && (
        <Grid item>
          <PaymentDateInfo />
        </Grid>
      )}
      <Grid item>
        <RulesTable />
      </Grid>
    </Grid>
  );
};
