import { useEffect } from 'react';
import { Grid } from '@material-ui/core';

import type { RouteComponentProps } from 'react-router-dom';

import description1 from 'assets/images/websites/websites-description-1.svg';
import description2 from 'assets/images/websites/websites-description-2.svg';
import description3 from 'assets/images/websites/websites-description-3.svg';
import antiAdBlock1 from 'assets/images/websites/websites-anti-adblock-1.svg';
import antiAdBlock2 from 'assets/images/websites/websites-anti-adblock-2.svg';
import antiAdBlock3 from 'assets/images/websites/websites-anti-adblock-3.svg';
import { Description } from 'components';
import { withRouter } from 'react-router-dom';
import { FormWebsite } from 'old-component/websites/form-website';
import RequestAntiAdblock from 'old-component/request-anti-adblock';
import Tip from 'old-component/websites/tip';
import {
  showDialog,
  expandDescription,
  expandOffer,
} from 'old-reducer/site-page';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { GoogleTagManagerEventsTypesEnum } from 'old-enums/google-tag-manager-events-type';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import type { OnboardingItem } from '../types';
import { GoToDirectLinkPageBlock } from './go-to-direct-link-page-block';
import { SiteList } from './sites-list';
import * as Styled from './styles';

const ITEMS: OnboardingItem[] = [
  {
    image: description1,
    title: 'Add your Website',
    description: (
      <>
        Click the <b>ADD WEBSITE</b> button to start.
        <br />
        Put your domain, set website category
        <br />
        and ad campaigns
      </>
    ),
  },
  {
    image: description2,
    title: 'Create Code',
    description: (
      <>
        Click <b>+ADD CODE</b> next to your website.
        <br />
        Choose ad format and, if needed, remove campaigns with unwanted ads
      </>
    ),
  },
  {
    image: description3,
    title: 'Copy Code',
    description: (
      <>
        Click your website to reveal its ad units and then click <b>GET CODE</b>
        . Copy and put the code to your website
      </>
    ),
  },
];

const ANTI_ADBLOCK_ITEMS: OnboardingItem[] = [
  {
    image: antiAdBlock1,
    title: 'Turn Anti-Adblock on',
    description: (
      <>
        Click the <b>REQUEST ANTI-ADBLOCK</b> button anywhere in your account,
        we&apos;ll generate new codes with custom domains
      </>
    ),
  },
  {
    image: antiAdBlock2,
    title: 'Replace codes on your website',
    description: (
      <>
        We&apos;ll email you the new codes and update them in your Adsterra
        account. New formats will have a domain from the start
      </>
    ),
  },
  {
    image: antiAdBlock3,
    title: 'Keep up to date',
    description: (
      <>
        We&apos;ll notify you if a custom domain expires and send you new codes.
        Use them in place of the old ones to keep your traffic monetized
      </>
    ),
  },
];

export const WebsitesComponent = ({
  history,
  location,
}: RouteComponentProps) => {
  const dispatch = useAppDispatch();
  const { descriptionOpen, offerOpen } = useAppSelector(
    (state) => state.sitePage,
  );
  const { has_popunder: hasPopunder, custom_domain: customDomain } =
    useAppSelector((state) => state.user.account_info);

  useEffect(() => {
    if (location.hash === '#add-new-website') {
      //         (    ,  v2  v3).
      pushToGoogleTagManager({
        event: GoogleTagManagerEventsTypesEnum.HEADER_ADD_WEBSITE,
      });
      history.replace(location.pathname, null);
      dispatch(showDialog(WebsitesDialogTypesEnum.ADD_SITE));
    }
  }, [history, location.hash, location.pathname, dispatch]);

  return (
    <div className="content">
      <Grid container spacing={2} direction="column">
        <Grid item>
          <Description
            dataTestID="description"
            expand={(expanded: boolean) =>
              dispatch(expandDescription(expanded))
            }
            items={ITEMS}
            toOpen={descriptionOpen}
            title="3 steps to monetize your website"
          />
        </Grid>

        {hasPopunder && !customDomain && (
          <Grid item>
            <Description
              dataTestID="description__anti-adblock"
              expand={(expanded: boolean) => dispatch(expandOffer(expanded))}
              items={ANTI_ADBLOCK_ITEMS}
              toOpen={offerOpen}
              title="Increase your revenue with Anti-Adblock"
              intro={<RequestAntiAdblock />}
            />
          </Grid>
        )}

        <Grid item>
          <GoToDirectLinkPageBlock />
        </Grid>
      </Grid>

      <Styled.MainContent>
        <SiteList />
      </Styled.MainContent>

      <FormWebsite />
      <Tip />
    </div>
  );
};

export const Websites = withRouter(WebsitesComponent);
