import { useEffect } from 'react';
import { useAppSelector } from 'store/hooks';
import { v2 } from 'constants/index';

import { selectNewTicket } from 'old-view/new-ticket/store-new-ticket/selectors';
import { useGetTicketTopicsQuery } from 'store/ticket-topics/hooks';

import { FormNewTicket } from './form-new-ticket';
import { TicketAlert } from './ticket-alert';

import * as Styled from './style';

export const NewTicket = () => {
  const { createdTicketId } = useAppSelector(selectNewTicket);
  useGetTicketTopicsQuery();

  useEffect(() => {
    if (typeof createdTicketId === 'number') {
      window.location.href = `${v2}/ticket/${createdTicketId}`;
    }
  }, [createdTicketId]);

  return (
    <Styled.ContentWrapper variant="outlined">
      <TicketAlert />
      <Styled.Header>Submit new ticket</Styled.Header>
      <FormNewTicket />
    </Styled.ContentWrapper>
  );
};
