import { useCallback } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { TableBody, TableHead } from '@material-ui/core';

import { Pagination } from 'components/pagination';
import { getTabClassNameById, OffersTabEnum } from 'old-enums/offers-tab';
import { getOffersListData } from 'old-reducer/offers-page';

import type { UserOfferData, OfferItem } from 'old-view/offer/types';
import { Header } from './list/header';
import { OfferListItem } from './list/offer-list-item';
import { UserOfferListItem } from './list/user-offer-list-item';
import * as Styled from './styles';

const isUserOffer = (
  _data: OfferItem,
  tabId: OffersTabEnum,
): _data is UserOfferData => tabId === OffersTabEnum.MY_OFFERS;

export const OffersList = ({ tabId }: { tabId: OffersTabEnum }) => {
  const { items, currentPage, pagesCount } = useAppSelector(
    (state) => state.offersPage.list,
  );
  const dispatch = useAppDispatch();

  const handlePaginationTap = useCallback(
    (pageNumber: number) => {
      dispatch(getOffersListData(pageNumber));
    },
    [dispatch],
  );

  const rows = items.map((item) =>
    isUserOffer(item, tabId) ? (
      <UserOfferListItem item={item} key={item.id_offer} tabId={tabId} />
    ) : (
      <OfferListItem item={item} key={item.id_offer} tabId={tabId} />
    ),
  );

  if (items.length > 0) {
    return (
      <>
        <Styled.TableStyled
          className={`offer-list-${getTabClassNameById(tabId)}`}
          data-testid={`offer-list-${getTabClassNameById(tabId)}`}
        >
          <TableHead>
            <Header tabId={tabId} />
          </TableHead>
          <TableBody>{rows}</TableBody>
        </Styled.TableStyled>

        {pagesCount > 1 && (
          <Styled.PaginationContainer>
            <Pagination
              active={currentPage}
              total={pagesCount}
              onTap={handlePaginationTap}
            />
          </Styled.PaginationContainer>
        )}
      </>
    );
  }

  return <Styled.NoDataContainer>No data</Styled.NoDataContainer>;
};
