import { memo } from 'react';
import { Grid } from '@material-ui/core';

import type { Layout } from '../types';

import * as Styled from './styles';

const getIndexedArray = (length: number) => Array.from({ length }, (_, i) => i);

export const Thumbs = memo(({ layout }: Layout) => {
  const [columnsNumber, rowsNumber] = layout.split(':').map(parseFloat);
  const columns = getIndexedArray(columnsNumber);

  const thumbItems = getIndexedArray(rowsNumber).map((row) => (
    <Grid container key={row}>
      {columns.map((column) => (
        <Styled.GridItem key={column} item xs={2}>
          <div>
            <Styled.ThumbDisplay />
            <Styled.ThumbText />
            <Styled.ThumbTextLast />
          </div>
        </Styled.GridItem>
      ))}
    </Grid>
  ));

  return <>{thumbItems}</>;
});
