import React, { memo, useCallback } from 'react';
import { useAppDispatch } from 'store/hooks';

import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
} from '@material-ui/core';
import { Button } from '@development/ui-kit';

import type { Maybe } from 'types/utils';

import { hideAlert } from 'old-reducer/offer-page';

type Props = {
  alertMessage: Maybe<string>;
  isOpen: boolean;
};

export const Alert = memo(({ alertMessage, isOpen }: Props) => {
  const dispatch = useAppDispatch();

  const handleButtonClick = useCallback(() => {
    dispatch(hideAlert());
  }, [dispatch]);

  if (!alertMessage) {
    return null;
  }

  return (
    <Dialog maxWidth="xs" open={isOpen} onClose={handleButtonClick}>
      <DialogContent>
        <DialogContentText>
          <p>{alertMessage}</p>
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          dataTestId="alert-wrap__got-it-button"
          onClick={handleButtonClick}
        >
          Got it
        </Button>
      </DialogActions>
    </Dialog>
  );
});
