import { DocumentType } from 'old-enums/document-type';
import { getDocumentTypeById } from 'old-view/payments/documents/documents-table/utils';

export const getDocumentTypesForSelect = () =>
  [
    DocumentType.INSERTION_ORDER,
    DocumentType.AGREEMENT,
    DocumentType.ID_PHOTO_SCAN,
    DocumentType.OTHER_PHOTO_SCAN,
  ].map((id) => [id, getDocumentTypeById(id)]);
