import { PASSWORD_MAX_LENGTH, PASSWORD_MIN_LENGTH } from 'constants/validation';
import { yup } from 'lib/yup';
import { SignUpFormField } from 'old-enums/sign-up-form-field';
import { emptySelectOption } from './constants';

const validator = {
  required: {
    message: 'This field is required',
  },
  password: {
    message: `Password should contain from ${PASSWORD_MIN_LENGTH} to ${PASSWORD_MAX_LENGTH} characters`,
  },
  email: {
    message: 'Enter a valid email address',
  },
  trim: {
    message: 'Value should be a trimmed string',
  },
  emailLatin: {
    // extra email check for latin symbols https://you.developzilla.com/issue/BestLK-390
    message: 'Email should contain only latin symbols',
    pattern: /^[a-z0-9._%+\-@]+$/i,
  },
};

export const schema = yup
  .object({
    [SignUpFormField.EMAIL]: yup
      .string()
      .required(validator.required.message)
      .email(validator.email.message)
      .matches(validator.emailLatin.pattern, validator.emailLatin.message),
    [SignUpFormField.LOGIN]: yup
      .string()
      .required(validator.required.message)
      .login(),
    [SignUpFormField.MESSENGER_ACCOUNT]: yup
      .string()
      .required(validator.required.message)
      .trim(validator.trim.message),
    [SignUpFormField.NAME]: yup
      .string()
      .required(validator.required.message)
      .trim(validator.trim.message),
    [SignUpFormField.PASSWORD]: yup.string().password({
      rangeErrorMessage: validator.password.message,
      trimErrorMessage: validator.trim.message,
    }),
    [SignUpFormField.ARE_TERMS_ACCEPTED]: yup
      .bool()
      .oneOf([true], validator.required.message),
    [SignUpFormField.MESSENGER]: yup
      .mixed()
      .notOneOf([emptySelectOption[0]], validator.required.message),
    [SignUpFormField.PAYSYSTEM_ID]: yup
      .mixed()
      .notOneOf([emptySelectOption[0]], validator.required.message),
  })
  .required(validator.required.message)
  .strict();
