import type { AdUnitEnum } from 'old-enums/ad-units';

import { getAdUnitTitle, getAdUnitAdditionalInfo } from 'old-enums/ad-units';
import * as Styled from './styles';

export const AdUnitLabel = ({ item }: { item: AdUnitEnum }) => {
  const title = getAdUnitTitle(item);
  const additionalInfo = getAdUnitAdditionalInfo(item);

  return (
    <>
      <span>{title}</span>
      {additionalInfo !== null && (
        <Styled.Link
          className="link"
          href={additionalInfo.url}
          rel="noopener noreferrer"
          target="_blank"
        >
          {additionalInfo.text}
        </Styled.Link>
      )}
    </>
  );
};
