export enum SiteStatusEnum {
  PENDING = 1,
  APPROVED = 2,
  DECLINED = 3,
  QUEUED_FOR_APPROVAL = 4,
}

export function getStatusTitles(id: SiteStatusEnum): string {
  switch (id) {
    //     ,        
    case SiteStatusEnum.PENDING:
    case SiteStatusEnum.QUEUED_FOR_APPROVAL:
      return 'Pending';
    case SiteStatusEnum.APPROVED:
      return 'Approved';
    case SiteStatusEnum.DECLINED:
      return 'Declined';
    default:
      throw new ReferenceError(`Unknown status: ${id}`);
  }
}
