import { memo } from 'react';

import { Tag } from 'old-component/tag';
import type { OfferTagType } from 'old-enums/offer-tag-type';
import { offerTags } from './offer-tags';

type Props = {
  type: OfferTagType;
};

export const OfferTag = memo(({ type }: Props) => (
  <Tag
    icon={offerTags[type].icon()}
    label={offerTags[type].label}
    className={offerTags[type].className}
    dataTestId={offerTags[type].className}
  />
));
