import { ThemeProvider as ThemeV4Provider } from '@material-ui/core/styles';
import { ThemeProvider as ThemeV5Provider } from '@mui/material/styles';

import { themeV4, themeV5 } from 'styles/theme';
import type { ProviderProps } from 'providers/types';

/**
 * HOC    MUI v4
 */
export const withMuiThemeV4 = (WrappedComponent: ProviderProps) => () => {
  return (
    <ThemeV4Provider theme={themeV4}>{WrappedComponent()}</ThemeV4Provider>
  );
};

/**
 * HOC    MUI v5
 */
export const withMuiThemeV5 = (WrappedComponent: ProviderProps) => () => {
  return (
    <ThemeV5Provider theme={themeV5}>{WrappedComponent()}</ThemeV5Provider>
  );
};
