import { Alert } from '@development/ui-kit';
import type { AlertProps } from '@development/ui-kit';
import * as Styled from './styles';

export const GeneralErrorGridItem = ({
  message,
  dataTestId,
  severity,
}: AlertProps) => {
  if (message.length === 0) {
    return null;
  }

  return (
    <Styled.GrigItem item xs={12}>
      <Alert dataTestId={dataTestId} message={message} severity={severity} />
    </Styled.GrigItem>
  );
};
