export enum FeedEnum {
  EROTIC_ADS = 1,
  SOFTWARE_ADS = 2,
  ALERT_ADS = 3,
  ADS_WITH_SOUND = 4,
  GAMBLING_ADS = 5,
}

export const getFeedTitle = (feed: FeedEnum) => {
  switch (feed) {
    case FeedEnum.EROTIC_ADS:
      return 'Erotic ads';
    case FeedEnum.SOFTWARE_ADS:
      return 'Software ads';
    case FeedEnum.ALERT_ADS:
      return 'Alert ads';
    case FeedEnum.ADS_WITH_SOUND:
      return 'Ads with sound';
    case FeedEnum.GAMBLING_ADS:
      return 'Gambling ads';
    default:
      return feed;
  }
};
