import { Alert } from '@development/ui-kit';
import React from 'react';
import { Snackbar } from '@material-ui/core';

import type { SnackbarProps } from './types';
import { snackbarPosition } from './constants';

export const SnackbarPure = ({
  message,
  onHide,
  severity = 'info',
  canClose,
  dataTestId,
}: SnackbarProps) => {
  const toShow = message !== '';
  const onClose = canClose ? onHide : undefined;
  //    Alert,    .
  const content = message ? (
    <Alert
      dataTestId={dataTestId}
      severity={severity}
      message={message}
      variant="filled"
      onClose={onClose}
      elevation={6}
    />
  ) : undefined;

  return (
    <Snackbar
      autoHideDuration={3000}
      onClose={onHide}
      open={toShow}
      anchorOrigin={snackbarPosition}
    >
      {content}
    </Snackbar>
  );
};
