import type { Thunk } from 'store/types';
import { postCreateTicket } from './api';

import {
  setType,
  setSubject,
  setQuestion,
  setIsLoading,
  setErrorMessage,
  setTicketId,
} from './slice';
import type { TicketCreationData, ResponseData, ResponseError } from './types';

const DELAY = 3000;

export const setTicketType =
  (type: number): Thunk =>
  async (dispatch) => {
    dispatch(setType(type));
  };

export const setTicketSubject =
  (subject: string): Thunk =>
  async (dispatch) => {
    dispatch(setSubject(subject));
  };

export const setTicketQuestion =
  (question: string): Thunk =>
  async (dispatch) => {
    dispatch(setQuestion(question));
  };

export const createTicket = (): Thunk => async (dispatch, getState) => {
  const {
    newTicket,
    user: { token },
  } = getState();
  const data: TicketCreationData = {
    body: newTicket.question,
    department: newTicket.type,
    title: newTicket.subject,
  };

  dispatch(setIsLoading(true));

  const response = await postCreateTicket(data, token);

  dispatch(setIsLoading(false));

  if ([200, 201].includes(response.status)) {
    const responseData: ResponseData = await response.json();

    dispatch(setTicketId(responseData.data.item.id));
  } else {
    const ResponseError: ResponseError = await response.json();

    dispatch(setErrorMessage(ResponseError.message));
    setTimeout(() => {
      setErrorMessage('');
    }, DELAY);
  }
};
