import type { AppState } from 'store/types';
import type { IdStatus } from './types';

export const selectTicketStatuses = (state: AppState) => {
  return state.ticketStatuses.data;
};

export const selectStatusTicketStatuses = (state: AppState) => {
  return state.ticketStatuses.status;
};

export const selectAllTicketStatuses = (state: AppState) =>
  selectTicketStatuses(state).reduce<[number, string][]>(
    (acc, v) => [...acc, [v.id, v.name]],
    [],
  );

export const selectTicketStatusesTitleById =
  (id: IdStatus) => (state: AppState) => {
    return selectTicketStatuses(state).find((v) => v.id === id)?.name;
  };
