import type { Document } from 'old-reducer/payments/types';
import { getDocumentStatusById, getStatusTextColor } from '../utils';
import * as Styled from './styles';

export const DocumentStatusBlock = ({ status }: Pick<Document, 'status'>) => {
  const textColor = getStatusTextColor(status);

  return (
    <Styled.StatusText textColor={textColor} vairant="body2">
      {getDocumentStatusById(status)}
    </Styled.StatusText>
  );
};
