export enum MessengerEnum {
  ICQ = 1,
  SKYPE = 2,
  JABBER = 3,
  TELEGRAM = 4,
  FACEBOOK = 5,
  WHATSAPP = 6,
  WECHAT = 7,
  INSTAGRAM = 8,
  TWITTER = 9,
}

export const getMessengerTitle = (item: MessengerEnum) => {
  switch (item) {
    case MessengerEnum.ICQ:
      return 'ICQ';
    case MessengerEnum.SKYPE:
      return 'Skype';
    case MessengerEnum.JABBER:
      return 'Jabber';
    case MessengerEnum.TELEGRAM:
      return 'Telegram';
    case MessengerEnum.FACEBOOK:
      return 'Facebook';
    case MessengerEnum.WHATSAPP:
      return 'WhatsApp';
    case MessengerEnum.WECHAT:
      return 'WeChat';
    case MessengerEnum.INSTAGRAM:
      return 'Instagram';
    case MessengerEnum.TWITTER:
      return 'Twitter';
    default:
      return item;
  }
};

const publisherMessengers = [
  MessengerEnum.ICQ,
  MessengerEnum.SKYPE,
  MessengerEnum.JABBER,
  MessengerEnum.TELEGRAM,
  MessengerEnum.FACEBOOK,
  MessengerEnum.WHATSAPP,
  MessengerEnum.WECHAT,
  MessengerEnum.INSTAGRAM,
  MessengerEnum.TWITTER,
];

export const getPublisherMessengers = () =>
  publisherMessengers.map((id) => [id, getMessengerTitle(id)]);

export const getAffiliateMessengers = () =>
  [MessengerEnum.SKYPE, MessengerEnum.TELEGRAM, MessengerEnum.WHATSAPP].map(
    (id) => [id, getMessengerTitle(id)],
  );
