import { v2 } from 'constants/index';
import { existingRoutes, externalRoutes } from 'routes';
import { withRouter } from 'react-router-dom';
import { useAppSelector } from 'store/hooks';

import { SidebarNestedIcon } from '../icons/sidebar-nested-icon';
import * as Styled from '../../styles';

import type { MenuItemProps } from '../types';

export const MenuItem = withRouter((props: MenuItemProps) => {
  const { path, route, history, nested, currentPath } = props;
  const isSidebarCollapsed = useAppSelector(
    (state) => state.sitePage.isSidebarCollapsed,
  );

  const isActive = path === currentPath;

  const handleClick = () => {
    history.push(route.path);
  };

  let icon = route.meta?.icon;

  if (nested) {
    icon = !isSidebarCollapsed && <SidebarNestedIcon />;
  } else if (typeof route.meta?.icon === 'string') {
    icon = <span className={`mdi ${route.meta?.icon}`} />;
  }

  const isExternalRoute = Object.values(externalRoutes).includes(route.path);
  const isLinkToV2 = !existingRoutes.includes(route.path);
  const btnProps = isLinkToV2
    ? {
        href: isExternalRoute ? route.path : v2 + route.path,
        target: isExternalRoute ? '_blank' : undefined,
        rel: 'noreferrer',
      }
    : {
        onClick: handleClick,
      };
  const btnDataTestid = `menu-item-${route.title
    .toLowerCase()
    .replace(/\s/, '')}`;

  return (
    <>
      <Styled.Item
        $isActive={isActive}
        $isClickable={!isActive}
        title={route.title}
        // eslint-disable-next-line react/jsx-props-no-spreading
        {...btnProps}
        data-testid={btnDataTestid}
      >
        <Styled.ItemBody $isActive={isActive} $isNested={nested}>
          {icon}
          {!isSidebarCollapsed && (
            <Styled.ItemTitle>{route.title}</Styled.ItemTitle>
          )}
        </Styled.ItemBody>
      </Styled.Item>
    </>
  );
});
