import type { PayloadAction } from '@reduxjs/toolkit';
import type { Maybe } from 'types/utils';
import type { Thunk } from 'store/types';

import { createSlice } from '@reduxjs/toolkit';
import { getHeaders } from 'utils/http';
import { AlertSeverity } from 'old-enums/alert-severity';
import { GlobalPostbackFields } from 'old-enums/global-postback-fields';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import {
  getGlobalOfferLinkAPIRequestURL,
  getOfferLinkFieldName,
  getOfferLinkTypeName,
  OfferLinkTypeEnum,
} from 'old-enums/offer-link-type';
import type { PostbackData } from 'old-component/offer/user-offer-link-form/types';
import { showSnackbar } from './snackbar';
import { snackbarCommonError } from './snackbar/constants';

type GlobalLinksResponse = {
  data: {
    t_backlink: Maybe<string>;
    t_postback_url: Maybe<string>;
  };
};

type GlobalPostbackPageState = {
  backlink: Maybe<string>;
  countEqualGlobalPostback: number;
  isLoading: {
    backlink: boolean;
    postbackURL: boolean;
    page: boolean;
  };
  postbackURL: Maybe<string>;
};

type OfferLinkTypeName = 'backlink' | 'page' | 'postbackURL';

type CountEqualGlobalPostbackResponse = {
  data: number;
};

const initialState: GlobalPostbackPageState = {
  backlink: null,
  countEqualGlobalPostback: 0,
  isLoading: {
    backlink: false,
    postbackURL: false,
    page: false,
  },
  postbackURL: null,
};

const {
  actions: { setCountEqualGlobalPostback, setGlobalURL, setLoadingState },
  reducer: globalOfferLinksPageReducer,
} = createSlice({
  name: 'globalOfferLinksPage',
  initialState,
  reducers: {
    setCountEqualGlobalPostback: (
      state,
      { payload: count }: PayloadAction<number>,
    ) => {
      state.countEqualGlobalPostback = count;
    },
    setGlobalURL: {
      prepare: (value: Maybe<string>, offerLinkType: OfferLinkTypeEnum) => ({
        payload: { value, offerLinkType },
      }),
      reducer: (
        state,
        {
          payload,
        }: PayloadAction<{
          value: Maybe<string>;
          offerLinkType: OfferLinkTypeEnum;
        }>,
      ) => {
        const typeName = getOfferLinkTypeName(payload.offerLinkType);
        state[typeName] = payload.value;
      },
    },
    setLoadingState: {
      prepare: (isLoading: boolean, type: OfferLinkTypeName) => ({
        payload: { isLoading, type },
      }),
      reducer: (
        state,
        { payload }: PayloadAction<{ isLoading: boolean; type: string }>,
      ) => {
        state.isLoading[payload.type] = payload.isLoading;
      },
    },
  },
});

const getGlobalPostbackPageData = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  dispatch(setLoadingState(true, 'page'));

  try {
    const request = await window.fetch('/api/offers/global-links', {
      headers: getHeaders(token),
    });
    const {
      data: { t_backlink, t_postback_url },
    }: GlobalLinksResponse = await request.json();

    dispatch(setGlobalURL(t_backlink, OfferLinkTypeEnum.BACKLINK));
    dispatch(setGlobalURL(t_postback_url, OfferLinkTypeEnum.POSTBACK_URL));
    dispatch(setLoadingState(false, 'page'));
  } catch {
    // noop
  }
};

const getCountEqualGlobalPostback = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  dispatch(setLoadingState(true, 'page'));

  try {
    const request = await window.fetch(
      '/api/postbacks/count-equal-global-postback',
      {
        headers: getHeaders(token),
      },
    );
    const { data }: CountEqualGlobalPostbackResponse = await request.json();
    dispatch(setCountEqualGlobalPostback(data));
  } finally {
    dispatch(setLoadingState(false, 'page'));
  }
};

const setGlobalOfferURL =
  (postbackData: PostbackData): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();
    const { uniqueURLs, offerLinkType, isUpdatePostbackURLs } = postbackData;
    const typeName = getOfferLinkTypeName(offerLinkType);
    const requestUrl = getGlobalOfferLinkAPIRequestURL(offerLinkType);
    const url = uniqueURLs[0];
    dispatch(setLoadingState(true, typeName));

    let fetchBody = {};

    if (offerLinkType === OfferLinkTypeEnum.POSTBACK_URL) {
      fetchBody = {
        [GlobalPostbackFields.T_POSTBACK_URL]: url,
        [GlobalPostbackFields.B_UPDATE_OFFERS]: isUpdatePostbackURLs,
      };
    }

    if (offerLinkType === OfferLinkTypeEnum.BACKLINK) {
      fetchBody = {
        [getOfferLinkFieldName(OfferLinkTypeEnum.BACKLINK)]: url,
      };
    }

    try {
      const response = await window.fetch(requestUrl, {
        headers: getHeaders(token, { hasBody: true }),
        method: 'POST',
        body: JSON.stringify(fetchBody),
      });

      if (response.status !== 200) {
        dispatch(setLoadingState(false, typeName));
        dispatch(showSnackbar(snackbarCommonError));

        return;
      }

      dispatch(setGlobalURL(url, offerLinkType));
      dispatch(
        showSnackbar({
          message: SnackbarMessage.SUCCESS_URL_SAVE,
          severity: AlertSeverity.SUCCESS,
        }),
      );
      dispatch(getCountEqualGlobalPostback());
      dispatch(setLoadingState(false, typeName));
    } catch {
      dispatch(setLoadingState(false, typeName));
      dispatch(showSnackbar(snackbarCommonError));
    }
  };

export {
  getCountEqualGlobalPostback,
  getGlobalPostbackPageData,
  setGlobalOfferURL,
  globalOfferLinksPageReducer,
};
