export enum SiteDeclineReasonEnum {
  SITE_NOT_WORKING = 1,
  SITE_LOW_TRAFFIC = 2,
  TOO_MUCH_ADS = 3,
  NO_CONTENT = 4,
  OTHER = 5,
}

export const getSiteDeclineReasonTitle = (status: SiteDeclineReasonEnum) => {
  switch (status) {
    case SiteDeclineReasonEnum.SITE_NOT_WORKING:
      return 'Site is not working';
    case SiteDeclineReasonEnum.SITE_LOW_TRAFFIC:
      return 'Site has low traffic for banner ads';
    case SiteDeclineReasonEnum.TOO_MUCH_ADS:
      return 'Too much ads on site';
    case SiteDeclineReasonEnum.NO_CONTENT:
      return 'No content on site';
    default:
      return '';
  }
};
