import { Button } from '@development/ui-kit';
import { withRouter } from 'react-router-dom';
import { isPureAffiliate, isPureWebmaster } from 'old-enums/user-permission';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { GoogleTagManagerEventsTypesEnum } from 'old-enums/google-tag-manager-events-type';

import type { ActionButtonProps } from './types';

const dtiPrefix = 'header';

export const ActionButtonComponent = (props: ActionButtonProps) => {
  const { user, history, location, showDialog } = props;

  if (isPureWebmaster(user.ida_permissions)) {
    return (
      <Button
        dataTestId={`${dtiPrefix}__add-website`}
        variant="outlined"
        onClick={() => {
          if (location.pathname === '/websites') {
            //    ,   .
            showDialog(WebsitesDialogTypesEnum.ADD_SITE);
            //     .
            pushToGoogleTagManager({
              event: GoogleTagManagerEventsTypesEnum.HEADER_ADD_WEBSITE,
            });
          } else {
            history.push('/websites#add-new-website');
          }
        }}
      >
        Add website
      </Button>
    );
  }

  if (
    isPureAffiliate(user.ida_permissions) &&
    location.pathname !== '/offers'
  ) {
    return (
      <Button
        dataTestId={`${dtiPrefix}__show-offers`}
        variant="outlined"
        onClick={() => {
          history.push('/offers');
        }}
      >
        Show offers
      </Button>
    );
  }

  return null;
};

export const ActionButton = withRouter(ActionButtonComponent);
