import { SignUpPageContent } from 'features/signup';
import { useAppSelector } from 'store/hooks';
import { Redirect } from 'react-router-dom';
import { useEffect } from 'react';
import { createFacebookPixel } from 'lib/facebook-pixel';

export const SignUpTY = () => {
  const {
    confirmStep: { email },
  } = useAppSelector((state) => state.signUp);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    if (email.length > 0) {
      createFacebookPixel();
    }
  }, [email.length]);

  if (email.length === 0) {
    return <Redirect to="/login" />;
  }

  return <SignUpPageContent />;
};
