import { connect } from 'react-redux';

import { getManager } from 'store/user/thunk';
import type { MapStateToProps } from 'react-redux';
import type { AppState } from 'store/types';
import { PureManager } from './personal-manager';

const mapStateToProps: MapStateToProps<AppState['user'], {}, AppState> = (
  state,
) => state.user;

const mapDispatchToProps = {
  getManager,
};

export const Manager = connect(
  mapStateToProps,
  mapDispatchToProps,
)(PureManager);
