import { Link } from 'old-component';
import { useCallback } from 'react';
import { useAppDispatch } from 'store/hooks';
import { generateDocument } from 'old-reducer/payments/thunk';
import type { Props } from './types';

export const PDFLink = ({ invoiceId, parentInvoiceId }: Props) => {
  const dispatch = useAppDispatch();

  const handleLinkClick = useCallback(
    (link: string, documentName: string) => {
      dispatch(generateDocument(link, documentName));
    },
    [dispatch],
  );
  const documentId = invoiceId || parentInvoiceId;

  if (documentId) {
    const dataTestId = invoiceId
      ? 'payments-invoice-pdf-link'
      : 'payments-credit-note-pdf-link';

    const documentLink = invoiceId
      ? `/api/invoice/${invoiceId}/generate/`
      : `/api/invoice/${parentInvoiceId}/credit-note/`;

    const title = invoiceId
      ? 'Download invoice as a PDF file'
      : 'Download credit-note as a PDF file';

    const documentName = invoiceId
      ? `invoice-${invoiceId}`
      : `credit-note-${parentInvoiceId}`;

    return (
      <Link
        dataTestId={dataTestId}
        text="PDF"
        component="button"
        title={title}
        color="inherit"
        variant="inherit"
        onClick={() => handleLinkClick(documentLink, documentName)}
      />
    );
  }

  return <></>;
};
