import { useRef, useState } from 'react';
import { Menu, MenuItem } from '@material-ui/core';
import { IconButton } from '@development/ui-kit';
import type { MenuProps } from '@material-ui/core/Menu';
import { styled } from '@material-ui/core/styles';
import { getUserTypeTitle } from 'old-enums/user-permission';
import { ArrowDropDown } from '@mui/icons-material';
import { logout, v2 } from 'constants/index';
import { Grid } from '@mui/material';
import * as Styled from './styles';
import type { UserInfoProps } from './types';

const dtiPrefix = 'header__user-info';

const StyledMenu = styled((props: MenuProps) => (
  <Menu
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    // eslint-disable-next-line react/jsx-props-no-spreading
    {...props}
  />
))(() => ({
  '& .MuiPaper-root': {
    borderRadius: 5,
    minWidth: 140,
  },
}));

export function UserInfo(props: UserInfoProps) {
  const { user } = props;
  const ref = useRef<HTMLDivElement | null>(null);
  const [anchorEl, setAnchorEl] = useState<null | HTMLDivElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = () => {
    setAnchorEl(ref.current);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Grid item display="flex" alignSelf="stretch">
      <Grid item display="flex" alignItems="center">
        <Styled.UserAvatar />
      </Grid>
      <Styled.UserInfoBlock
        display="flex"
        flexDirection="column"
        justifyContent="center"
      >
        <Styled.UserName data-testid={`${dtiPrefix}--name`}>
          {user.login}
        </Styled.UserName>
        <Styled.UserType data-testid={`${dtiPrefix}--type`}>
          {getUserTypeTitle(user.ida_permissions)}
        </Styled.UserType>
      </Styled.UserInfoBlock>
      <Grid item display="flex" flexDirection="column" justifyContent="center">
        <div ref={ref}>
          <IconButton
            onClick={handleClick}
            size="small"
            dataTestId={`${dtiPrefix}--show-menu`}
          >
            <ArrowDropDown />
          </IconButton>
        </div>
        <StyledMenu anchorEl={anchorEl} open={open} onClose={handleClose}>
          <a
            href={`${v2}/profile`}
            data-testid={`${dtiPrefix}--profile-action`}
          >
            <MenuItem>Profile</MenuItem>
          </a>
          <MenuItem
            onClick={logout}
            data-testid={`${dtiPrefix}--logout-action`}
          >
            Log out
          </MenuItem>
        </StyledMenu>
      </Grid>
    </Grid>
  );
}
