import {
  LOGIN_MAX_LENGTH,
  LOGIN_MIN_LENGTH,
  LOGIN_PATTERN,
} from 'constants/validation';
import type { yup } from 'lib/yup';
import { ErrorMessage } from './constants';

/**
 * ,      
 */
export function loginRule(this: yup.StringSchema) {
  return this.test('double error', (value, ctx) => {
    const isDefined = this.defined().isValidSync(value);
    const isWithinRange = this.range(
      LOGIN_MIN_LENGTH,
      LOGIN_MAX_LENGTH,
    ).isValidSync(value);
    const hasValidSymbols = this.matches(LOGIN_PATTERN).isValidSync(value);

    if (isDefined && !isWithinRange && !hasValidSymbols) {
      return ctx.createError({
        message: ErrorMessage.RANGE_AND_PATTERN,
      });
    }

    return true;
  })
    .range(LOGIN_MIN_LENGTH, LOGIN_MAX_LENGTH, ErrorMessage.RANGE)
    .matches(LOGIN_PATTERN, ErrorMessage.PATTERN);
}
