import { createSlice } from '@reduxjs/toolkit';

import { yup } from 'lib/yup';
import { getHeaders } from 'utils/http';

import type { PayloadAction } from '@reduxjs/toolkit';
import type { Thunk } from 'store/types';

interface State {
  error: string;
  form: {
    hash: {
      value: string;
    };
    password: {
      error: string;
      value: string;
    };
  };
  isLoading: boolean;
  isSuccess: boolean;
}

const initialState: State = {
  error: '',
  form: {
    hash: {
      value: '',
    },
    password: {
      error: '',
      value: '',
    },
  },
  isLoading: false,
  isSuccess: false,
};

const {
  reducer: resetPasswordReducer,
  actions: {
    setFormError,
    setHash,
    setLoading,
    setPassword,
    setPasswordError,
    setSuccess,
  },
} = createSlice({
  name: 'reset-password',

  initialState,
  reducers: {
    setFormError: (
      state,
      {
        payload: errorMessage = 'An unknown error has occurred',
      }: PayloadAction<string | undefined>,
    ) => {
      state.error = errorMessage;
    },

    setHash: (state, { payload: hash }: PayloadAction<string>) => {
      state.form.hash.value = hash;
    },

    setLoading: (state, { payload: isLoading }: PayloadAction<boolean>) => {
      state.isLoading = isLoading;
    },

    setPassword: (state, { payload: password }: PayloadAction<string>) => {
      state.form.password.error = '';
      state.form.password.value = password;
    },

    setPasswordError: (
      state,
      { payload: errorMessage }: PayloadAction<string>,
    ) => {
      state.form.password.error = errorMessage;
    },

    setSuccess: (state) => {
      state.isSuccess = true;
      setTimeout(() => window.location.assign('/login'), 10000);
    },
  },
});

const sendRestoreData = ({
  hash,
  password,
}: {
  hash: string;
  password: string;
}): Thunk => {
  return async (dispatch) => {
    dispatch(setLoading(true));

    try {
      const response = await fetch(`/api/restore`, {
        body: JSON.stringify({ hash, password }),
        method: 'PUT',
      });

      if (response.status === 200) {
        dispatch(setSuccess());

        return;
      }

      const { data, message } = await response.json();

      if (data?.password != null) {
        dispatch(setPasswordError(data.password));

        return;
      }

      dispatch(setFormError(message));
    } catch {
      dispatch(setFormError());
    } finally {
      dispatch(setLoading(false));
    }
  };
};

const submitForm = (): Thunk => {
  return async (dispatch, getState) => {
    const {
      resetPassword: {
        form: {
          hash: { value: hash },
          password: { value: password },
        },
      },
    } = getState();

    if (hash === '') {
      return;
    }

    try {
      const passwordSchema = yup.string().password();
      await passwordSchema.validate(password);
      dispatch(sendRestoreData({ hash, password }));
    } catch (error: any) {
      if (error instanceof yup.ValidationError) {
        dispatch(setPasswordError(error.message));
      } else {
        // eslint-disable-next-line no-console
        console.warn(error.toString());
      }
    }
  };
};

const validateHash = (hash: string): Thunk => {
  return async (dispatch) => {
    const error = 'Invalid restore password link';

    if (hash === '') {
      dispatch(setFormError(error));

      return;
    }

    try {
      const { status } = await window.fetch(`/api/restore/${hash}/validate`, {
        headers: getHeaders(),
        method: 'GET',
      });

      if (status === 200) {
        dispatch(setHash(hash));
      } else {
        dispatch(setFormError(error));
      }
    } catch {
      dispatch(setFormError(error));
    }
  };
};

export { setPassword, submitForm, validateHash, resetPasswordReducer };
