import { useEffect } from 'react';
import { useAppDispatch } from 'store/hooks';
import { ContentWrapper } from 'old-component';
import {
  getPayments,
  getPayouts,
  getDocuments,
} from 'old-reducer/payments/thunk';
import { PaymentRules } from './payment-rules';
import { PaymentsHistory } from './payments-history';
import * as Styled from './styles';
import { Documents } from './documents';

export const Payments = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(getPayments());
    dispatch(getPayouts());
    dispatch(getDocuments());
  }, [dispatch]);

  return (
    <ContentWrapper>
      <Styled.HeaderText variant="h3">Payment rules</Styled.HeaderText>
      <PaymentRules />
      <Documents />
      <PaymentsHistory />
    </ContentWrapper>
  );
};
