import { memo } from 'react';
import { Skeleton } from '@material-ui/lab';

import { useAppSelector } from 'store/hooks';
import { selectTicketStatusesTitleById } from 'store/ticket-statuses/selectors';
import type { Props } from './types';

import * as Styled from './style';

export const StatusCell = memo(({ idStatus }: Props) => {
  const name = useAppSelector(selectTicketStatusesTitleById(idStatus));

  if (name === undefined) {
    return <Skeleton variant="text" />;
  }

  return <Styled.StatusCell titleStatus={name}>{name}</Styled.StatusCell>;
});
