import * as Styled from './styles';
import type { DescriptionProps, SideTextsProps } from './types';
import { texts } from './constants';

const Description = ({ text }: DescriptionProps) => {
  if (typeof text === 'string') {
    return <>{text}</>;
  }

  return (
    <Styled.Ul>
      {text.map(([key, data]) => (
        <li key={key}>{data}</li>
      ))}
    </Styled.Ul>
  );
};

export const SideTexts = ({ entity }: SideTextsProps) => {
  return (
    <>
      {texts[entity].map(({ title, description }, index) => (
        <Styled.Block key={title.toLowerCase().replace(' ', '_')} delay={index}>
          {index === 0 && (
            <Styled.Header variant="h3">What you get:</Styled.Header>
          )}
          <Styled.Title variant="h4">{title}</Styled.Title>
          <Styled.Description variant="body2" component="div">
            <Description text={description} />
          </Styled.Description>
        </Styled.Block>
      ))}
    </>
  );
};
