import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { AlertSeverity } from 'old-enums/alert-severity';
import { initialState } from './snackbar/state';
import type { SetSnackbarDataPayload } from './snackbar/types';

const {
  actions: { showSnackbar, hideSnackbar },
  reducer: snackbarReducer,
} = createSlice({
  name: 'snackbar',
  initialState,
  reducers: {
    showSnackbar: (
      state,
      { payload: { message, severity } }: PayloadAction<SetSnackbarDataPayload>,
    ) => {
      state.message = message;
      state.severity = severity || AlertSeverity.INFO;
    },
    hideSnackbar: (state) => {
      state.message = initialState.message;
      state.severity = initialState.severity;
    },
  },
});

export { showSnackbar, hideSnackbar, snackbarReducer };
