import { BlockLoader } from 'old-component/block-loader';
import { useCallback, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { loadPage } from 'old-reducer/dashboard/thunk';
import { Pagination } from 'components/pagination';
import { Post } from 'features/post';
import { ContentWrapper } from 'old-component';
import * as Styled from './styles';

export const Dashboard = () => {
  const dispatch = useAppDispatch();
  const { isLoading } = useAppSelector((state) => state.dashboardPage);
  const { pinnedPosts, posts } = useAppSelector((state) => state.dashboardPage);

  const handlePaginationTap = useCallback(
    (pageNumber: number) => {
      dispatch(loadPage(pageNumber));
    },
    [dispatch],
  );

  useEffect(() => {
    dispatch(loadPage(1));
  }, [dispatch]);

  if (isLoading) {
    return (
      <ContentWrapper>
        <Styled.Block>
          <BlockLoader dataTestId="dashboard__loader" />
        </Styled.Block>
      </ContentWrapper>
    );
  }

  if (pinnedPosts.items.length === 0 && posts.items.length === 0) {
    return (
      <ContentWrapper>
        <Styled.Block>
          <Styled.Sorry>
            Sorry, weve got no convenient news for you right now
            <br />
            Stay tuned!
          </Styled.Sorry>
        </Styled.Block>
      </ContentWrapper>
    );
  }

  return (
    <ContentWrapper>
      <Styled.Block>
        {pinnedPosts.items.map((post) => (
          <Post pinned post={post} key={post.title} />
        ))}
        {posts.items.map((post) => (
          <Post post={post} key={post.title} />
        ))}
      </Styled.Block>
      {posts.total > posts.limit && (
        <Styled.PaginationContainer>
          <Pagination
            active={posts.page}
            total={Math.ceil(posts.total / posts.limit)}
            onTap={handlePaginationTap}
          />
        </Styled.PaginationContainer>
      )}
    </ContentWrapper>
  );
};
