import { Typography } from '@material-ui/core';
import { useCallback, useEffect, useRef } from 'react';
import { RESEND_COUNT_INITIAL_VALUE, SECOND } from './constants';
import * as Styled from './styles';
import { formatCountdown } from './utils';
import type { ResendProps } from './types';
import { useLocalStorageState } from './hooks';

export const Resend = ({ onResend, email }: ResendProps) => {
  const key = `resend-email-${email}`;
  const [count, setCount] = useLocalStorageState(
    key,
    RESEND_COUNT_INITIAL_VALUE,
  );

  const intervalRef = useRef<number>();

  const startCounting = useCallback(() => {
    intervalRef.current = window.setInterval(() => {
      setCount((c) => c - SECOND);
    }, SECOND);
  }, [setCount]);

  const stopCounting = () => window.clearInterval(intervalRef.current);

  useEffect(() => {
    startCounting();

    return stopCounting;
  }, [startCounting]);

  useEffect(() => {
    if (!count) {
      stopCounting();
    }
  }, [count]);

  const handleResendClick = () => {
    onResend();
    setCount(RESEND_COUNT_INITIAL_VALUE);
    startCounting();
  };

  if (count <= 0) {
    return (
      <span>
        <Styled.ResendLink
          variant="caption"
          display="inline"
          onClick={handleResendClick}
          data-testid="signup__link-confirm-resend"
        >
          RESEND
        </Styled.ResendLink>
      </span>
    );
  }

  return (
    <span>
      <Styled.ResendLabel variant="caption" display="inline">
        RESEND
      </Styled.ResendLabel>{' '}
      <Typography variant="caption" display="inline">
        IN {formatCountdown(count)}
      </Typography>
    </span>
  );
};
