import type { OSEnum } from 'old-enums/os';
import type { DeviceFormatEnum } from 'old-enums/device-format';

import { memo } from 'react';

import { getDeviceFormatTitleById } from 'old-enums/device-format';
import { getVerticalTitleById } from 'old-enums/offer-vertical';
import { getOSTitleById } from 'old-enums/os';

import type { UserOfferData } from '../types';
import * as Styled from './styles';

type Props = {
  deviceFormat: UserOfferData['na_device_format'];
  osType: UserOfferData['ida_os_vendor'];
  vertical: UserOfferData['id_advertiser_category'];
};

const prepareDeviceFormatsToOutput = (deviceFormat: DeviceFormatEnum[]) =>
  deviceFormat.map(getDeviceFormatTitleById).join(', ');

const prepareOSToOutput = (osType: OSEnum[]) =>
  osType.map(getOSTitleById).join(', ');

export const AdditionalInfo = memo(
  ({ deviceFormat, osType, vertical }: Props) => (
    <Styled.PaperStyled>
      <Styled.Title>Additional information</Styled.Title>

      <div>
        <p>
          <b>Vertical:</b> {getVerticalTitleById(vertical)}
        </p>
        <p>
          <b>Device format:</b>{' '}
          {deviceFormat.length > 0
            ? prepareDeviceFormatsToOutput(deviceFormat)
            : '-'}
        </p>
        <p>
          <b>OS type:</b> {osType.length > 0 ? prepareOSToOutput(osType) : '-'}
        </p>
      </div>
    </Styled.PaperStyled>
  ),
);
