export enum OffersTabEnum {
  ALL = 0,
  MY_OFFERS = 1,
}

export const getOffersTabs = () => [OffersTabEnum.ALL, OffersTabEnum.MY_OFFERS];

export const getTabTitleById = (id: OffersTabEnum) => {
  switch (id) {
    case OffersTabEnum.ALL:
      return 'All';
    case OffersTabEnum.MY_OFFERS:
      return 'My offers';
    default:
      throw new Error(`Unknown offers tab id: ${id}`);
  }
};

export const getTabClassNameById = (id: OffersTabEnum) => {
  switch (id) {
    case OffersTabEnum.ALL:
      return 'all';
    case OffersTabEnum.MY_OFFERS:
      return 'my-offers';
    default:
      throw new Error(`Unknown offers tab id: ${id}`);
  }
};
