import styled from 'styled-components';
import type { WithTheme } from '@material-ui/core/styles';
import { withTheme } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import { styleProps } from 'old-common/sign-style';

export const Footer = withTheme(styled.div`
  padding: 0 ${({ theme }: WithTheme) => theme.spacing(4)}px;
  width: 100%;
  height: 78px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;

  ${({ theme }: WithTheme) =>
    theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
    flex-direction: column-reverse;
    width: 100%;
    height: auto;
    padding: ${({ theme }: WithTheme) => theme.spacing(1)}px 0 0 0;
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.default};
  }
`);

export const Text = withTheme(styled(Typography)`
  display: none;

  ${({ theme }: WithTheme) =>
    theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
    display: block;
    line-height: 48px !important;
  }
`);

export const Buttons = withTheme(styled.div`
  width: 330px;
  max-width: 70%;
  display: flex;
  justify-content: space-between;

  ${({ theme }: WithTheme) =>
    theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
    width: 88%;
    max-width: 88%;
  }
`);

export const FirstBlock = withTheme(styled.div`
  ${({ theme }: WithTheme) =>
    theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
    display: flex;
    justify-content: space-between;
    width: 88%;
  }
`);
