import { memo } from 'react';
import { InputLabel, MenuItem, Select } from '@material-ui/core';

import { FontSizesEnum, getAllFontSizes } from 'old-enums/font-sizes';

import type { FontSizeProps } from '../types';

import * as Styled from '../styles';

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: 200,
    },
  },
};

export const FontSizeSelect = memo(
  ({ isLoading, onFontSizeChange, fontSize }: FontSizeProps) => {
    const fontSizes = getAllFontSizes();

    const fontSizeOptions = fontSizes.map((item) => (
      <MenuItem key={item} value={item}>
        {item === FontSizesEnum.EMPTY ? 'Inherit' : item}
      </MenuItem>
    ));

    return (
      <Styled.FormControlBlock fullWidth>
        <InputLabel shrink id="font-size-label-filter">
          Font size
        </InputLabel>
        <Select
          data-testid="font-size-label-filter"
          disabled={isLoading}
          labelId="font-size-label-filter"
          value={fontSize}
          onChange={onFontSizeChange}
          MenuProps={MenuProps}
        >
          {fontSizeOptions}
        </Select>
      </Styled.FormControlBlock>
    );
  },
);
