import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { initialState } from './state';
import type { TicketPageState } from './types';

const {
  actions: {
    setType,
    setSubject,
    setQuestion,
    setIsLoading,
    setErrorMessage,
    setTicketId,
  },
  reducer: newTicket,
} = createSlice({
  name: 'newTicket',
  initialState,
  reducers: {
    setType: (
      state,
      { payload: type }: PayloadAction<TicketPageState['type']>,
    ) => {
      state.type = type;
    },
    setSubject: (
      state,
      { payload: subject }: PayloadAction<TicketPageState['subject']>,
    ) => {
      state.subject = subject;
    },
    setQuestion: (
      state,
      { payload: question }: PayloadAction<TicketPageState['question']>,
    ) => {
      state.question = question;
    },
    setIsLoading: (
      state,
      { payload: isLoading }: PayloadAction<TicketPageState['isLoading']>,
    ) => {
      state.isLoading = isLoading;
    },
    setErrorMessage: (
      state,
      { payload: errorMessage }: PayloadAction<TicketPageState['errorMessage']>,
    ) => {
      state.errorMessage = errorMessage;
    },
    setTicketId: (
      state,
      {
        payload: createdTicketId,
      }: PayloadAction<TicketPageState['createdTicketId']>,
    ) => {
      state.createdTicketId = createdTicketId;
    },
  },
});

export {
  setType,
  setSubject,
  setQuestion,
  setIsLoading,
  setErrorMessage,
  setTicketId,
  newTicket,
};
