import { Button } from '@development/ui-kit';
import { Grid } from '@material-ui/core';
import { useAppDispatch } from 'store/hooks';
import { showUploadDialog } from 'old-reducer/payments-page';
import { UploadDialog } from '../upload-dialog';

export const UploadButton = () => {
  const dispatch = useAppDispatch();

  return (
    <>
      <UploadDialog />
      <Grid item>
        <Button
          dataTestId="payments__upload-documents"
          variant="contained"
          onClick={() => dispatch(showUploadDialog())}
        >
          Add new document
        </Button>
      </Grid>
    </>
  );
};
