import type { Thunk } from 'store/types';
import { getHeaders } from 'utils/http';
import { renderRecaptchaV2 } from 'lib/google-recaptcha';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { GoogleTagManagerEventsTypesEnum } from 'old-enums/google-tag-manager-events-type';
import { emailServices } from 'old-common/email-services';
import { IS_PRODUCTION } from 'old-common/is-production';
import { setAmplitudeInstanceData } from 'lib/amplitude';
import { getSharpSpringTrackingCodePostBack } from 'lib/sharp-spring';
import {
  setLoadingState,
  resend,
  confirmStep,
  generalError,
  setPaysystemsList,
  setRecaptchaV2RenderState,
  confirm,
  setEmail,
  formErrors,
} from 'old-reducer/signup';
import type { FormFieldsSignup } from 'features/signup/components/form/types';
import { transformErrors } from 'utils/transform/transform-errors';
import type { User } from './types';
import {
  isSignUpAfterShowingAffiliatePopup,
  prepareFormDataForBack,
  preparePaysystemsForSelect,
} from './utils';
import { CONFIRM_REDIRECT_TIMEOUT } from './constants';

export const getPaysystemsList = (): Thunk => {
  return async (dispatch) => {
    dispatch(setLoadingState(true));
    setTimeout(async () => {
      try {
        const response = await window.fetch('/api/pay-systems', {
          headers: getHeaders(),
          method: 'GET',
        });

        if (response.status === 200) {
          const { data } = await response.json();
          const dataForSelectOptions = preparePaysystemsForSelect(data);

          dispatch(setPaysystemsList(dataForSelectOptions));
          dispatch(setLoadingState(false));
        }
      } catch {
        // noop
      }
    }, 200);
  };
};

const showRecaptchaV2 = (): Thunk => async (dispatch, getState) => {
  const {
    signUp: {
      form: { recaptcha },
    },
  } = getState();

  if (!recaptcha.wasRendered) {
    await renderRecaptchaV2();
    dispatch(setRecaptchaV2RenderState(true));
  }
};

export const submitForm =
  (fields: FormFieldsSignup): Thunk =>
  async (dispatch, getState) => {
    dispatch(setLoadingState(true));

    const {
      signUp: {
        form: { recaptcha },
      },
    } = getState();

    try {
      const body = await prepareFormDataForBack({ fields, recaptcha });

      const response = await window.fetch('/api/user', {
        body: JSON.stringify(body),
        credentials: 'include',
        headers: getHeaders(),
        method: 'POST',
      });

      if (response.status === 200) {
        pushToGoogleTagManager({
          event: GoogleTagManagerEventsTypesEnum.SIGNUP_PUB_SUCCESS,
        });

        if (isSignUpAfterShowingAffiliatePopup) {
          pushToGoogleTagManager({
            event:
              GoogleTagManagerEventsTypesEnum.SIGNUP_PUB_SUCCESS_AFTER_SHOWING_AFFILIATE_POPUP,
          });
        }

        dispatch(setEmail(body.email));

        for (const { link, message, pattern } of emailServices) {
          if (pattern.test(body.email)) {
            dispatch(resend({ message, link }));
            setTimeout(() => dispatch(resend()), 30000);

            break;
          }
        }

        dispatch(confirmStep());

        return;
      }

      if (response.status === 422) {
        const { data: errors } = await response.json();
        const preparedErrors = transformErrors(errors);

        dispatch(formErrors(preparedErrors));

        return;
      }

      const {
        message: error,
        data: { switchingRecaptcha: shouldSwitchRecaptcha },
      } = await response.json();

      dispatch(generalError(error));

      if (shouldSwitchRecaptcha === true && error.includes('bot')) {
        dispatch(showRecaptchaV2());
      }
    } catch (e) {
      // noop
    } finally {
      dispatch(setLoadingState(false));
    }
  };

export const confirmEmail = (confirmString: string): Thunk => {
  return async (dispatch) => {
    dispatch(setLoadingState(true));
    setTimeout(async () => {
      if (!confirmString) {
        dispatch(generalError('An unknown error has occurred'));

        return;
      }

      try {
        const response = await window.fetch('/api/user/confirm', {
          body: JSON.stringify({ confirmString }),
          headers: getHeaders(),
          method: 'POST',
        });

        if (response.status === 200) {
          const { data }: { data: User } = await response.json();

          if (IS_PRODUCTION) {
            pushToGoogleTagManager({ userId: data.analytics_user_id });
            pushToGoogleTagManager({ event: 'EmailConfirmed' });

            setAmplitudeInstanceData(data.amplitude_user_id, {
              Id: data.id,
              Email: data.email,
              Login: data.login,
              Type: 'Publisher',
            });

            await getSharpSpringTrackingCodePostBack();
          }

          dispatch(
            confirm(
              false,
              false,
              `<p>The verification code is successfully confirmed.</p>
                 <p>Wait until you are redirected to the Login page or follow <a href="/login">the link</a></p>`,
            ),
          );
          setTimeout(
            () => dispatch(confirm(true, false, '')),
            CONFIRM_REDIRECT_TIMEOUT,
          );

          return;
        }

        if (response.status === 409) {
          dispatch(
            confirm(
              false,
              false,
              '<h3>This email has been already confirmed.</h3>',
            ),
          );
          setTimeout(
            () => dispatch(confirm(true, false, '')),
            CONFIRM_REDIRECT_TIMEOUT,
          );

          return;
        }

        if (response.status === 422) {
          dispatch(
            confirm(
              false,
              true,
              '<h3>Expired verification code.</h3><p>Please enter your email to get a new code.</p>',
            ),
          );

          return;
        }

        if (response.status === 500) {
          dispatch(
            confirm(
              false,
              true,
              '<h3>Incorrect verification code.</h3><p>Please enter your email to get a new code.</p>',
            ),
          );
        }
      } catch {
        // noop
      }
    }, 200);
  };
};
