import type { ChangeEvent } from 'react';
import type { Thunk } from 'store/types';

import { memo, useCallback, useMemo, useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import {
  Checkbox,
  CircularProgress,
  FormControlLabel,
} from '@material-ui/core';

import { AddButton } from 'old-component/add-button';
import {
  getOfferLinkTypeName,
  isBacklinkByType,
  isPostbackURLByType,
  OfferLinkTypeEnum,
} from 'old-enums/offer-link-type';
import { nanoid } from '@reduxjs/toolkit';

import { yup } from 'lib/yup';
import * as Styled from './styles';
import type { PostbackData } from './types';

type Props = {
  offerLinkType: OfferLinkTypeEnum;
  setURL: (postbackData: PostbackData) => Thunk;
  urls?: string[];
  countEqualGlobalPostback?: number;
  isAddButtonNeeded?: boolean;
  isGlobal?: boolean;
  offerId?: number;
};

const getButtonLabel = (isLoading: boolean) =>
  isLoading ? <CircularProgress size={25} /> : 'Save';

const getHintText = (offerLinkType: OfferLinkTypeEnum) => {
  switch (offerLinkType) {
    case OfferLinkTypeEnum.BACKLINK:
      return 'Example: https://ads.com/in.php';
    case OfferLinkTypeEnum.POSTBACK_URL:
      return 'Example: https://ads.com/in.php?click_id=##S2S##&payout=##PAYOUT##';
    default:
      throw new Error(`Unknown offer link type: ${offerLinkType}`);
  }
};

const getNote = (offerLinkType: OfferLinkTypeEnum) => {
  switch (offerLinkType) {
    case OfferLinkTypeEnum.BACKLINK:
      return (
        <>
          <p>Backlink is your custom fallback URL.</p>
          <p>
            It will be activated in case your traffic does not match the targets
            of the offer or your offer is stopped.
          </p>
          <p>
            If backlink is not specified by you, we will show a white screen
            when the conditions above are triggered.
          </p>
        </>
      );
    case OfferLinkTypeEnum.POSTBACK_URL:
      return (
        <>
          <p>Required:</p>
          <p>click_id=##S2S##</p>
          <p>Optional:</p>
          <p>payout=##PAYOUT##</p>
          <br />
          <p>You can set additional postback urls if required.</p>
        </>
      );
    default:
      return null;
  }
};

const getReducerName = (isGlobal?: boolean) =>
  isGlobal ? 'globalOfferLinksPage' : 'offerPage';

const getError = (
  url: string,
  { isPostbackURL } = { isPostbackURL: false },
) => {
  try {
    yup.string().url({ isPostbackURL }).validateSync(url);

    return '';
  } catch (e: any) {
    if (e instanceof yup.ValidationError) {
      return e.message;
    }
    // eslint-disable-next-line no-console
    console.warn(e.toString());
  }

  return '';
};

const createUrlContainer = (url: string) => ({
  url,
  id: nanoid(6),
});

export const UserOfferLinkForm = memo(
  ({
    offerId,
    setURL,
    offerLinkType,
    urls,
    isAddButtonNeeded,
    isGlobal,
    countEqualGlobalPostback,
  }: Props) => {
    const reducerName = getReducerName(isGlobal);
    const {
      backlink: isBacklinkFormLoading,
      postbackURL: isPostbackURLFormLoading,
    } = useAppSelector((state) => state[reducerName].isLoading);

    const isPostbackURL = isPostbackURLByType(offerLinkType);
    const isBacklink = isBacklinkByType(offerLinkType);
    const isLoading =
      (isPostbackURL && isPostbackURLFormLoading) ||
      (isBacklink && isBacklinkFormLoading);

    const defaultValue = [createUrlContainer('')];
    const [values, setValues] = useState(
      urls && urls.length !== 0 ? urls.map(createUrlContainer) : defaultValue,
    );
    const [errors, setErrors] = useState<string[]>([]);

    const shouldCheckboxBeShown = useMemo(
      () =>
        isGlobal &&
        !!countEqualGlobalPostback &&
        offerLinkType === OfferLinkTypeEnum.POSTBACK_URL,
      [countEqualGlobalPostback, isGlobal, offerLinkType],
    );

    const [isUpdatePostbackURLs, setIsUpdatePostbackURLs] = useState(
      shouldCheckboxBeShown,
    );

    const dispatch = useAppDispatch();

    const handleAddButtonClick = () => {
      setValues((state) => {
        const valueList = [...state];

        if (!valueList.find(({ url }) => url === '')) {
          valueList.push(createUrlContainer(''));
        }

        return valueList;
      });
    };

    const handleInputValueChange =
      (index: number) => (event: ChangeEvent<HTMLInputElement>) => {
        const state = [...values];
        state[index].url = event.target.value.trim();
        setValues(state);
      };

    const handleSaveButtonClick = useCallback(() => {
      let updatedErrors = values.map(({ url }) =>
        getError(url, { isPostbackURL }),
      );

      const hasNoErrors = updatedErrors.filter(Boolean).length === 0;

      if (hasNoErrors) {
        let uniqueURLs = values.filter(
          (url, index) => values.indexOf(url) === index,
        );
        const urlData: PostbackData = {
          offerId,
          offerLinkType,
          uniqueURLs: uniqueURLs.map(({ url }) => url),
        };

        if (isGlobal) {
          urlData.isUpdatePostbackURLs = isUpdatePostbackURLs;
        }

        dispatch(setURL(urlData));

        if (uniqueURLs.length > 1) {
          uniqueURLs = uniqueURLs.filter(({ url }) => Boolean(url));
          //   ,      .
          updatedErrors = updatedErrors.slice(0, uniqueURLs.length);
        }

        setValues(uniqueURLs);
      }

      setErrors(updatedErrors);
    }, [
      dispatch,
      isGlobal,
      isUpdatePostbackURLs,
      isPostbackURL,
      offerId,
      offerLinkType,
      setURL,
      values,
    ]);

    const handleUpdatePostbacksCheckBoxChange = () => {
      setIsUpdatePostbackURLs((value) => !value);
    };

    return (
      <Styled.Wrapper>
        <Styled.TitleContainer>
          <Styled.Title>{offerLinkType}</Styled.Title>
          {isAddButtonNeeded && (
            <AddButton
              dataTestId={`user-offer-link-form__${getOfferLinkTypeName(
                offerLinkType,
              )}-add-button`}
              onClick={handleAddButtonClick}
            />
          )}
        </Styled.TitleContainer>

        <Styled.Container>
          {values.map(({ url, id }, index) => (
            <Styled.Field
              key={id}
              dataTestId={`input-${offerLinkType}`}
              error={errors[index]}
              helperText={errors[index]}
              placeholder={getHintText(offerLinkType)}
              name={offerLinkType}
              type="text"
              value={url}
              onChange={handleInputValueChange(index)}
            />
          ))}

          <Styled.SaveButton
            dataTestId={`user-offer-link-form__${getOfferLinkTypeName(
              offerLinkType,
            )}-save-button`}
            variant="contained"
            disabled={isLoading}
            onClick={handleSaveButtonClick}
          >
            {getButtonLabel(isLoading)}
          </Styled.SaveButton>

          {shouldCheckboxBeShown && (
            <FormControlLabel
              control={
                <Checkbox
                  checked={isUpdatePostbackURLs}
                  onChange={handleUpdatePostbacksCheckBoxChange}
                />
              }
              label={`Change this postback for ${countEqualGlobalPostback} offer(s)`}
            />
          )}
        </Styled.Container>

        <Styled.Note>{getNote(offerLinkType)}</Styled.Note>
      </Styled.Wrapper>
    );
  },
);
