import { memo, useState } from 'react';
import { useAppDispatch } from 'store/hooks';

import { CopyToClipboardButton } from 'old-component/offers/copy-to-clipboard-button';
import { getAllDirectLinkParams } from 'old-enums/direct-link-param';
import { LinkParamsForm } from 'old-view/offer/link-params-form';
import { showSnackbar } from 'old-reducer/snackbar';

import type { ChangeEvent } from 'react';
import type { Maybe } from 'types/utils';
import { Notice } from 'components';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import { AlertSeverity } from 'old-enums/alert-severity';

import * as Styled from './styles';

type Props = {
  value: Maybe<string>;
};

type Params = {
  s2s: string;
  sub1: string;
  sub2: string;
  sub3: string;
};

const getLinkWithParams = (params: Params, url: string) => {
  const paramsList: string[] = [];

  for (const [key, value] of Object.entries(params)) {
    if (value) {
      paramsList.push(`&${key}=${value}`);
    }
  }

  return `${url}${paramsList.join('')}`;
};

export const DirectLinkConstructor = memo(({ value }: Props) => {
  const dispatch = useAppDispatch();

  const handleCopyButtonClick = () => {
    dispatch(
      showSnackbar({
        message: SnackbarMessage.COPY_LINK,
        severity: AlertSeverity.SUCCESS,
      }),
    );
  };

  const [link, setLink] = useState(value ?? '');
  const [params, setParams] = useState({
    s2s: '',
    sub1: '',
    sub2: '',
    sub3: '',
  });

  const directLinkParams = getAllDirectLinkParams();

  const handleAddButtonClick =
    (paramType: string, paramValue: string) => () => {
      setParams((prevState) => ({
        ...prevState,
        [paramType]: paramValue.trim(),
      }));
    };

  const handleValueChange = (event: ChangeEvent<HTMLInputElement>) => {
    setLink(event.target.value);
  };

  return (
    <Styled.Main>
      <Styled.Title>Direct link</Styled.Title>

      <Notice>
        Make sure your targeting matches advertisers, otherwise your audience
        will see a blank page or an error when you click the direct link
      </Notice>

      <Styled.FieldWrapper>
        <Styled.Field
          disabled
          name="direct-link"
          dataTestId="direct-link"
          type="text"
          value={getLinkWithParams(params, link)}
          onChange={handleValueChange}
        />

        <CopyToClipboardButton
          textToCopy={getLinkWithParams(params, link)}
          onCopy={handleCopyButtonClick}
        />
      </Styled.FieldWrapper>

      <Styled.Params>
        {directLinkParams.map((param) => (
          <LinkParamsForm
            key={param}
            paramName={param}
            handleAddButtonClick={handleAddButtonClick}
          />
        ))}
      </Styled.Params>
    </Styled.Main>
  );
});
