import 'old-common/node-contains';
import 'whatwg-fetch';

import { createRoot } from 'react-dom/client';
import { beforePlugins, afterPlugins } from 'providers';
import { App } from 'app';

beforePlugins();

const container = document.getElementById('entry');

if (container !== null) {
  const root = createRoot(container);
  root.render(<App />);
}

afterPlugins();
