import { isAffiliateDomain } from 'constants/index';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { setDialogChooseRoleState } from 'old-reducer/signup';
import { useCallback } from 'react';
import { PureDialogChooseRole } from './pure-dialog';
import type { DialogChooseRoleProps } from './types';

const blocks = ['advertiser', isAffiliateDomain ? 'publisher' : 'affiliate'];

export const DialogChooseRole = ({ login = false }: DialogChooseRoleProps) => {
  const { isDialogChooseRoleOpen } = useAppSelector((state) => state.signUp);
  const dispatch = useAppDispatch();

  const handleDialogClose = useCallback(() => {
    dispatch(setDialogChooseRoleState(false));
  }, [dispatch]);

  return (
    <PureDialogChooseRole
      isDialogChooseRoleOpen={isDialogChooseRoleOpen}
      handleDialogClose={handleDialogClose}
      blocks={blocks}
      login={login}
    />
  );
};
