export enum StatsItemFilterParam {
  DAY = 1,
  MONTH = 2,
  REFERRAL_USER = 3,
}

export const getStatsParamTitle = (statsParam: StatsItemFilterParam) => {
  if (StatsItemFilterParam[statsParam]) {
    return StatsItemFilterParam[statsParam];
  }

  throw new Error(`Unknown statistics param: ${statsParam}`);
};
