import styled from 'styled-components';
import { TableCell } from '@material-ui/core';
import { withTheme } from '@material-ui/core/styles';

import type { WithTheme } from '@material-ui/core/styles';

export const AdUnitCell = withTheme(styled(TableCell)`
  color: ${({ theme }: WithTheme) => theme.palette.text.disabled};
  padding-left: ${({ theme }: WithTheme) => theme.spacing(6)}px;
  width: 420px;
`);

export const StatusCell = withTheme(styled(TableCell)`
  color: ${({ theme }: WithTheme) => theme.palette.warning.main};
  padding-left: ${({ theme }: WithTheme) => theme.spacing(3.5)}px;
`);
