import type { FC } from 'react';

import { useCallback, memo } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { CircularProgress } from '@mui/material';
import { Button } from '@development/ui-kit';

import { OfferPrivacyType } from 'old-enums/offer-privacy-type';
import { OfferRequestStatusEnum } from 'old-enums/offer-request-status';
import { getOfferRequestData } from 'old-reducer/offer-page';
import { Alert } from '../alert';
import type { OfferRequestData, UserOfferData } from '../types';
import * as Styled from './styles';

type Props = {
  hasPrivateOfferPermission: UserOfferData['b_has_private_offer_permission'];
  id: string;
  privacyType: UserOfferData['n_privacy_type'];
  status: OfferRequestData['n_status'];
};

const getButtonLabel = (
  privacyType: Props['privacyType'],
  hasSpecialPermission: Props['hasPrivateOfferPermission'],
) => {
  const isPublicOffer = privacyType === OfferPrivacyType.PUBLIC;
  const isPrivateOffer = privacyType === OfferPrivacyType.PRIVATE;

  if (isPublicOffer || hasSpecialPermission) {
    return 'Get link';
  }

  if (isPrivateOffer) {
    return 'Request for a link';
  }

  return null;
};

const getButtonClassName = (privacyType: Props['privacyType']) => {
  switch (privacyType) {
    case OfferPrivacyType.PUBLIC:
      return 'public-offer-request';
    case OfferPrivacyType.PRIVATE:
      return 'private-offer-request';
    default:
      return '';
  }
};

const getRequestStatusDescription = (status: Props['status']) => {
  switch (status) {
    case OfferRequestStatusEnum.CREATED:
      return <p>Waiting for approval</p>;
    case OfferRequestStatusEnum.DECLINED:
      return (
        <>
          <p>Your request was declined.</p>
          <p>
            Please follow the instructions sent to your email or contact our
            support.
          </p>
          <p>After that try again.</p>
        </>
      );
    default:
      return null;
  }
};

export const OfferRequest: FC<Props> = memo(
  ({ hasPrivateOfferPermission, id, privacyType, status }) => {
    const { alertMessage } = useAppSelector((state) => state.offerPage);
    const { offerRequest: isOfferRequestLoading } = useAppSelector(
      (state) => state.offerPage.isLoading,
    );

    const dispatch = useAppDispatch();
    const handleButtonClick = useCallback(() => {
      dispatch(getOfferRequestData(id));
    }, [id, dispatch]);

    const isRequestStatusCreated = status === OfferRequestStatusEnum.CREATED;
    const isRequestStatusDeclined = status === OfferRequestStatusEnum.DECLINED;

    return (
      <Styled.Wrapper>
        {(isRequestStatusCreated || isRequestStatusDeclined) && (
          <Styled.RequestStatusDescription>
            {getRequestStatusDescription(status)}
          </Styled.RequestStatusDescription>
        )}

        <Button
          dataTestId="offer-request__submit-button"
          variant="contained"
          className={getButtonClassName(privacyType)}
          disabled={isOfferRequestLoading || isRequestStatusCreated}
          onClick={handleButtonClick}
        >
          {isOfferRequestLoading ? (
            <CircularProgress size={25} />
          ) : (
            getButtonLabel(privacyType, hasPrivateOfferPermission)
          )}
        </Button>

        <Alert alertMessage={alertMessage} isOpen={isRequestStatusCreated} />
      </Styled.Wrapper>
    );
  },
);
