import { useCallback, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { Grid } from '@material-ui/core';

import { Description } from 'components';
import { expandOffer } from 'old-reducer/site-page';
import RequestAntiAdblock from 'old-component/request-anti-adblock';
import Tip from 'old-component/websites/tip';

import {
  addPlacement,
  editPlacement,
  getCodes,
  getPlacementList,
  hideDialog,
  setPlacementTitle,
  setTypeCodes,
  toggleDescriptionState,
} from 'old-reducer/direct-links-page';

import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

import { getAlertsData, getDialogsData } from './form-website/dialogs-data';

import { DirectLinkPlacementsContent } from './direct-link-placements-content';
import { FormWebsite } from './form-website';

import description1 from './assets/direct-links-description-1.svg';
import description2 from './assets/direct-links-description-2.svg';
import description3 from './assets/direct-links-description-3.svg';
import antiAdBlock1 from './assets/direct-links-anti-adblock-1.svg';
import antiAdBlock2 from './assets/direct-links-anti-adblock-2.svg';
import antiAdBlock3 from './assets/direct-links-anti-adblock-3.svg';
import type { DialogKeys, DialogValues } from './form-website/types';
import type { OnboardingItem } from '../types';

const ITEMS: OnboardingItem[] = [
  {
    image: description1,
    title: 'Create Direct Link',
    description: (
      <>
        Click green <b>CREATE DIRECT LINK</b> button below to start. Set your
        link category and, if needed, remove campaigns with unwanted ads
      </>
    ),
  },
  {
    image: description2,
    title: 'Put the link anywhere',
    description: (
      <>
        A post on Facebook, buttons, images <br />
        everywhere where your visitors will be able to click the link
      </>
    ),
  },
  {
    image: description3,
    title: 'Track and make changes',
    description: (
      <>
        Track <b>statistics</b> and manage your links settings. Click{' '}
        <b>Edit</b> to change your link name or remove ads that you don&apos;t
        want to display
      </>
    ),
  },
];

const ANTI_ADBLOCK_ITEMS: OnboardingItem[] = [
  {
    image: antiAdBlock1,
    title: 'Turn Anti-Adblock on',
    description: (
      <>
        Click the <b>REQUEST ANTI-ADBLOCK</b> button anywhere in your account,
        and we&apos;ll generate new codes with custom domains
      </>
    ),
  },
  {
    image: antiAdBlock2,
    title: 'Replace codes on your website',
    description: (
      <>
        We&apos;ll email you the new codes and update them in your Adsterra
        account. New formats will have a domain from the start
      </>
    ),
  },
  {
    image: antiAdBlock3,
    title: 'Keep up to date',
    description: (
      <>
        We&apos;ll notify you if a custom domain expires and send you new codes.
        Use them in place of the old ones to keep your traffic monetized
      </>
    ),
  },
];

export const DirectLinks = () => {
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(getPlacementList());
  }, [dispatch]);

  const {
    dialogData,
    descriptionOpen,
    site: { placements },
  } = useAppSelector((state) => state.directLinksPage);
  const { data: dialogDataData } = dialogData;

  const { offerOpen } = useAppSelector((state) => state.sitePage);
  const { custom_domain: customDomain } = useAppSelector(
    (state) => state.user.account_info,
  );

  const getCodesHandler = useCallback(() => dispatch(getCodes()), [dispatch]);

  const hideDialogHandler = useCallback(
    () => dispatch(hideDialog()),
    [dispatch],
  );

  const setPlacementTitleHandler = useCallback(
    (title: string) => dispatch(setPlacementTitle(title)),
    [dispatch],
  );

  const setTypeCodesHandler = useCallback(
    (exportType: string) => dispatch(setTypeCodes(exportType)),
    [dispatch],
  );

  const addPlacementHandler = useCallback(
    (dialogDataProp?: DialogValues, keyProp?: DialogKeys) =>
      dispatch(addPlacement(dialogDataProp, keyProp)),
    [dispatch],
  );

  const editPlacementHandler = useCallback(
    (_type: unknown, id: number) => dispatch(editPlacement('dialog', id)),
    [dispatch],
  );

  const toggleDescriptionStateHandler = useCallback(
    (isOpen: boolean) => dispatch(toggleDescriptionState(isOpen)),
    [dispatch],
  );

  const dialogsData = getDialogsData(
    addPlacementHandler,
    editPlacementHandler,
    dialogDataData.id,
    'direct link',
  );

  dialogsData[WebsitesDialogTypesEnum.ADD_AD_UNIT].width =
    dialogsData[WebsitesDialogTypesEnum.EDIT_PLACEMENT].width;

  const alertsData = getAlertsData(dialogsData);

  return (
    <div className="content">
      <Grid container direction="column" spacing={2}>
        <Grid item>
          <Description
            dataTestID="description"
            items={ITEMS}
            title="Monetize any traffic with a link"
            toOpen={descriptionOpen}
            expand={toggleDescriptionStateHandler}
          />
        </Grid>
        {placements.length > 0 && !customDomain && (
          <Grid item>
            <Description
              dataTestID="description__anti-adblock"
              expand={(expanded: boolean) => dispatch(expandOffer(expanded))}
              items={ANTI_ADBLOCK_ITEMS}
              toOpen={offerOpen}
              title="Increase your revenue with Anti-Adblock"
              intro={<RequestAntiAdblock />}
            />
          </Grid>
        )}
        <Grid item>
          <DirectLinkPlacementsContent />
        </Grid>
      </Grid>
      <FormWebsite
        alertsData={alertsData[dialogData.action]}
        dialogData={dialogData}
        dialogsData={dialogsData[dialogData.action]}
        getCodes={getCodesHandler}
        hideDialog={hideDialogHandler}
        setPlacementTitle={setPlacementTitleHandler}
        setTypeCodes={setTypeCodesHandler}
        siteAddData={addPlacementHandler}
      />
      <Tip />
    </div>
  );
};
