import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';

import { AlertSeverity } from 'old-enums/alert-severity';
import type {
  Banner,
  Invite,
  ReferralsInfo,
  ReferralStatsItem,
  StatsFilter,
  ReferralsPagination,
  StatsPagination,
  Referral,
} from './referrals/types';

import { initialState } from './referrals/state';
import type { SetSnackbarDataPayload } from './snackbar/types';

const {
  actions: {
    addInviteLink,
    setBannerList,
    setLoadingState,
    setReferralsInfo,
    setReferralPagination,
    setStatsItems,
    setStatsPagination,
    setFilterParams,
    setSelectedBanner,
    showSnackbar,
    setFilterPublishersList,
  },
  reducer: referralsPageReducer,
} = createSlice({
  name: 'referralsPage',
  initialState,
  reducers: {
    addInviteLink: (state, { payload: invite }: PayloadAction<Invite>) => {
      state.referralsInfo.invites.push(invite);
    },
    setLoadingState: (
      state,
      { payload: isLoading }: PayloadAction<boolean>,
    ) => {
      state.isLoading = isLoading;
    },
    setReferralsInfo: (
      state,
      { payload: referralsInfo }: PayloadAction<ReferralsInfo>,
    ) => {
      state.referralsInfo = referralsInfo;
    },
    setReferralPagination: (
      state,
      { payload: pagination }: PayloadAction<ReferralsPagination>,
    ) => {
      state.refListPagination = pagination;
    },
    setStatsItems: (
      state,
      { payload: statsItems }: PayloadAction<ReferralStatsItem[]>,
    ) => {
      state.stats = statsItems;
    },
    setStatsPagination: (
      state,
      { payload: pagination }: PayloadAction<StatsPagination>,
    ) => {
      state.statsPagination = pagination;
    },
    showSnackbar: (
      state,
      {
        payload: { message, severity = AlertSeverity.INFO },
      }: PayloadAction<SetSnackbarDataPayload>,
    ) => {
      state.snackbar.message = message;
      state.snackbar.severity = severity;
    },
    setFilterParams: (
      state,
      { payload: filter }: PayloadAction<StatsFilter>,
    ) => {
      state.filter = filter;
    },
    setBannerList: (
      state,
      { payload: bannerList }: PayloadAction<Banner[]>,
    ) => {
      state.bannerList = bannerList;
    },
    setFilterPublishersList: (
      state,
      { payload }: PayloadAction<Referral[]>,
    ) => {
      state.filterPublishersList = payload;
    },
    setSelectedBanner: (
      state,
      { payload: banner }: PayloadAction<Banner | undefined>,
    ) => {
      const {
        referralsInfo: { invites },
      } = state;

      if (banner === undefined) {
        return;
      }

      const href = invites.length > 0 ? invites[0].url : '';
      state.selectedBanner = { ...banner, href };
    },
  },
});

export {
  addInviteLink,
  setBannerList,
  setFilterParams,
  setLoadingState,
  setReferralsInfo,
  setReferralPagination,
  setStatsPagination,
  setStatsItems,
  setSelectedBanner,
  showSnackbar,
  setFilterPublishersList,
  referralsPageReducer,
};
