import { createSlice } from '@reduxjs/toolkit';

import { initialState } from './state';
import { thunkTicketTopics } from './thunk';

const { reducer: ticketTopicsReducer } = createSlice({
  name: 'ticketTopics',

  initialState,

  reducers: {},

  extraReducers: (builder) => {
    builder.addCase(thunkTicketTopics.pending, (state) => {
      state.status = 'pending';
    });

    builder.addCase(thunkTicketTopics.fulfilled, (state, { payload }) => {
      state.status = 'fulfilled';
      state.data = payload.items ?? [];
    });

    builder.addCase(thunkTicketTopics.rejected, (state) => {
      state.status = 'rejected';
    });
  },
});

export {
  // actions
  // reducer
  ticketTopicsReducer,
};
