import type { PayloadAction } from '@reduxjs/toolkit';
import type { Maybe } from 'types/utils';
import type { Thunk } from 'store/types';

import { createSlice } from '@reduxjs/toolkit';

import { getHeaders } from 'utils/http';
import { AlertSeverity } from 'old-enums/alert-severity';
import {
  getOfferLinkAPIRequestURL,
  getOfferLinkFieldName,
  getOfferLinkTypeName,
  isPostbackURLByType,
} from 'old-enums/offer-link-type';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import type { UserOfferData } from 'old-view/offer/types';
import type { PostbackData } from 'old-component/offer/user-offer-link-form/types';
import { showSnackbar } from './snackbar';
import { snackbarCommonError } from './snackbar/constants';

type OfferPageState = {
  alertMessage: Maybe<string>;
  data: null | UserOfferData;
  isLoading: {
    backlink: boolean;
    offerRequest: boolean;
    page: boolean;
    postbackURL: boolean;
  };
};

type UrlsField = 'postback_urls' | 't_backlink';

const messages = {
  requestSending:
    'Your request has been sent. Our manager will contact you as soon as possible.',
};

const initialState: OfferPageState = {
  alertMessage: null,
  data: null,
  isLoading: {
    backlink: false,
    offerRequest: false,
    page: false,
    postbackURL: false,
  },
};

const {
  actions: {
    hideAlert,
    setLoadingState,
    setOfferData,
    setOfferRequestData,
    setOfferURL,
    showAlert,
  },
  reducer: offerPageReducer,
} = createSlice({
  name: 'offerPage',
  initialState,
  reducers: {
    hideAlert: (state) => {
      state.alertMessage = null;
    },
    setLoadingState: {
      prepare: (isLoading: boolean, type: string) => ({
        payload: { isLoading, type },
      }),
      reducer: (
        state,
        { payload }: PayloadAction<{ isLoading: boolean; type: string }>,
      ) => {
        state.isLoading[payload.type] = payload.isLoading;
      },
    },
    setOfferData: (
      state,
      { payload: { data } }: PayloadAction<{ data: UserOfferData }>,
    ) => {
      state.isLoading.page = false;
      state.data = data;
    },
    setOfferRequestData: (
      state,
      {
        payload: { data },
      }: PayloadAction<{ data: UserOfferData['offer_request'] }>,
    ) => {
      state.isLoading.offerRequest = false;

      if (state.data != null) {
        state.data.offer_request = data;
      }
    },
    setOfferURL: {
      prepare: (field: UrlsField, value: string | string[], type: string) => ({
        payload: { field, value, type },
      }),
      reducer: (
        state,
        {
          payload,
        }: PayloadAction<{
          field: string;
          value: string | string[];
          type: string;
        }>,
      ) => {
        state.isLoading[payload.type] = false;

        if (state?.data?.offer_request != null) {
          state.data.offer_request[payload.field] = payload.value;
        }
      },
    },
    showAlert: (state, { payload }: PayloadAction<string>) => {
      state.alertMessage = payload;
    },
  },
});

const getOfferData =
  (id: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    dispatch(setLoadingState(true, 'page'));

    try {
      const response = await window.fetch(`/api/offer/${id}`, {
        headers: getHeaders(token),
        method: 'GET',
      });

      if (response.status === 200) {
        const { data } = await response.json();
        dispatch(setOfferData({ data }));
      } else {
        dispatch(setLoadingState(false, 'page'));
      }
    } catch {
      dispatch(setLoadingState(false, 'page'));
    }
  };

const getOfferRequestData =
  (id: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    dispatch(setLoadingState(true, 'offerRequest'));

    try {
      const response = await window.fetch(`/api/offer/${id}/request`, {
        headers: getHeaders(token),
        method: 'POST',
      });

      if (response.status === 200) {
        const { data } = await response.json();
        dispatch(setOfferRequestData({ data }));
        dispatch(showAlert(messages.requestSending));
      }
    } catch {
      dispatch(setLoadingState(false, 'page'));
    }
  };

const setUserOfferURL =
  (postbackData: PostbackData): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();
    const { uniqueURLs, offerLinkType, offerId } = postbackData;

    const value = isPostbackURLByType(offerLinkType)
      ? uniqueURLs.filter(Boolean)
      : uniqueURLs[0];

    const typeForLoadingState = getOfferLinkTypeName(offerLinkType);
    dispatch(setLoadingState(true, typeForLoadingState));

    try {
      const url = getOfferLinkAPIRequestURL(offerLinkType, offerId);
      const field = getOfferLinkFieldName(offerLinkType);

      const response = await window.fetch(url, {
        body: JSON.stringify({ [field]: value }),
        headers: getHeaders(token, { hasBody: true }),
        method: 'POST',
      });

      if (response.status === 200) {
        dispatch(setOfferURL(field, value, typeForLoadingState));
        dispatch(
          showSnackbar({
            message: SnackbarMessage.SUCCESS_URL_SAVE,
            severity: AlertSeverity.SUCCESS,
          }),
        );
      } else {
        dispatch(setLoadingState(false, typeForLoadingState));
        dispatch(showSnackbar(snackbarCommonError));
      }
    } catch {
      dispatch(setLoadingState(false, typeForLoadingState));
      dispatch(showSnackbar(snackbarCommonError));
    }
  };

export {
  hideAlert,
  getOfferData,
  getOfferRequestData,
  setOfferData,
  setOfferRequestData,
  setUserOfferURL,
  setOfferURL,
  showAlert,
  offerPageReducer,
};
