import { getHeaders } from 'utils/http';
import type { TicketCreationData } from './types';

export const postCreateTicket = async (
  data: TicketCreationData,
  token: string,
): Promise<Response> =>
  window.fetch(`/api/support/tickets`, {
    method: 'POST',
    headers: getHeaders(token),
    body: JSON.stringify(data),
  });
