import { createSlice } from '@reduxjs/toolkit';

import type { PayloadAction } from '@reduxjs/toolkit';
import type { UserInfo } from './types';

import { initialState } from './state';

const {
  reducer: auth,
  actions: { disableAaRequest, setToken, setUserInfo, setAutoLogin },
} = createSlice({
  name: 'auth',

  initialState,
  reducers: {
    disableAaRequest: (state, { payload: status }: PayloadAction<number>) => {
      state.account_info.aa_request_status = status;
    },

    setToken: (state, { payload: token }: PayloadAction<string>) => {
      state.token = token;
    },

    setAutoLogin: (state, { payload: isAutoLogin }: PayloadAction<boolean>) => {
      state.isAutoLogin = isAutoLogin;
    },

    setUserInfo: (state, { payload }: PayloadAction<UserInfo>) => {
      for (const [key, value] of Object.entries(payload)) {
        state[key] = value;
      }
    },
  },
});

export {
  // actions
  disableAaRequest,
  setToken,
  setUserInfo,
  setAutoLogin,
  // reducer
  auth,
};
