import { getPaysystemTitle } from 'old-enums/paysystem';
import {
  getPaysystemCurrency,
  getPaysystemDeliveryTime,
  getPaysystemFee,
  getPaysystemLogo,
  getPaySystemMinAmount,
} from './utils';
import {
  payPalHWConfig,
  payPalConfig,
  webMoneyConfig,
  paxumConfig,
  wireTransferConfig,
  bitcoinConfig,
  tetherConfig,
  localBankTransferConfig,
} from './configs';

import type { PaySystem } from './types';

export const getPaySystems = (
  hasHyperwalletPermission: boolean,
): PaySystem[] => {
  const basePaySystems: PaySystem[] = [
    webMoneyConfig,
    paxumConfig,
    wireTransferConfig,
    bitcoinConfig,
    tetherConfig,
  ];

  let paySystemList: PaySystem[];

  if (hasHyperwalletPermission) {
    paySystemList = [payPalHWConfig, ...basePaySystems];
    paySystemList.splice(4, 0, localBankTransferConfig);
  } else {
    paySystemList = [payPalConfig, ...basePaySystems];
  }

  return paySystemList.map(
    ({
      id,
      deliveryTimeTooltip,
      currency,
      minAmountTooltip,
      currencyTooltip,
      fee,
      feeTooltip,
      info,
    }) => {
      return {
        id,
        title: getPaysystemTitle(id),
        logoName: getPaysystemLogo(id),
        deliveryTime: getPaysystemDeliveryTime(id),
        deliveryTimeTooltip,
        minAmount: getPaySystemMinAmount(id),
        minAmountTooltip,
        currency: currency || getPaysystemCurrency(id),
        currencyTooltip,
        fee: fee || getPaysystemFee(id),
        feeTooltip,
        info,
      };
    },
  );
};
