import { memo } from 'react';
import { useAppDispatch } from 'store/hooks';
import { green, yellow } from '@material-ui/core/colors';

import {
  EntityActiveEnum,
  getPlacementActiveTitle,
} from 'old-enums/entity-active';
import { showCodes } from 'old-reducer/site-page';

import * as Styled from './styles';

import type { Props } from './types';

const getActivityColor = (activity: number): string => {
  const statusColor: { [key: number]: string } = {
    [EntityActiveEnum.NON_ACTIVE]: yellow[800],
    [EntityActiveEnum.NON_ACTIVE_BY_LIMITS]: green[500],
    [EntityActiveEnum.ACTIVE]: green[500],
  };

  return statusColor[activity];
};

export const CommonStatus = memo(({ activity, id }: Props) => {
  const activityColor = getActivityColor(activity);

  const dispatch = useAppDispatch();

  const handleShowCodes = () => dispatch(showCodes(id));

  return (
    <Styled.Item color={activityColor} onClick={handleShowCodes}>
      <Styled.PlayCircleOutlineStyled
        overrideColor={activityColor}
        fontSize="small"
      />
      <span>{getPlacementActiveTitle(activity)}</span>
    </Styled.Item>
  );
});
