import { Grid } from '@material-ui/core';
import { Paper } from 'old-component';
import { DataGridTable } from '@development/ui-kit';
import { useAppSelector } from 'store/hooks';
import { isHyperwalletUser } from 'old-enums/user-permission';
import { getPaySystems } from './paysystems';
import { paySystemColumns } from './columns';
import { TermsLink } from '../terms-link';

export const RulesTable = () => {
  const { permissions } = useAppSelector((state) => ({
    permissions: state.user.data.ida_permissions,
  }));

  const hasHyperwalletPermission = isHyperwalletUser(permissions);

  return (
    <Paper>
      <Grid container spacing={2} direction="column" wrap="nowrap">
        <Grid item>
          <DataGridTable
            dataSource={getPaySystems(hasHyperwalletPermission)}
            keyExpr="id"
            columnsArray={paySystemColumns}
            hasMasterDetail
            repaintChangesOnly
            columnAutoWidth
          />
        </Grid>
        <Grid item>
          <TermsLink />
        </Grid>
      </Grid>
    </Paper>
  );
};
