import { memo } from 'react';

import { TooltipPosition as Position } from 'old-enums/tooltip-position';
import * as Styled from './styles';

import type { Props } from './types';

const { TOOLTIP_WIDTH, TOP_PADDING, PADDING } = Styled;

export const Tooltip = memo(({ anchorId, content, position }: Props) => {
  const anchor = document.querySelector(`#${anchorId}`);

  if (!anchor) {
    return null;
  }

  let leftPosition;
  let topPosition;
  const { left, top } = anchor.getBoundingClientRect();

  switch (position) {
    case Position.TOP:
      leftPosition = left - TOOLTIP_WIDTH / 2;
      topPosition = top - TOP_PADDING;
      break;
    default:
      leftPosition = left - (TOOLTIP_WIDTH + PADDING);
      topPosition = top;
      break;
  }

  return (
    <Styled.Tooltip left={leftPosition} top={topPosition}>
      {content}
    </Styled.Tooltip>
  );
});
