import { useAppDispatch } from 'store/hooks';
import React, { useCallback } from 'react';
import { Link } from '@development/ui-kit';
import { setDialogChooseRoleState } from 'old-reducer/signup';
import { entity, generalText } from 'constants/sign';
import type { DialogLinkProps } from './types';

export const DialogLink = ({
  isMobile,
  variant = 'body2',
}: DialogLinkProps) => {
  const dispatch = useAppDispatch();

  const handleDialogOpen = useCallback(() => {
    dispatch(setDialogChooseRoleState(true));
  }, [dispatch]);

  const testId = `signup__link-open-dialog${isMobile ? '-mobile' : ''}`;

  return (
    <Link
      variant={variant}
      onClick={handleDialogOpen}
      dataTestId={testId}
      color="textSecondary"
      text={`Im not ${generalText.article[entity]} ${generalText.name[entity]}`}
    />
  );
};
