import type { DataGridTableProps, CellRenderProps } from '@development/ui-kit';
import { ColumnNamesForDocumentsTable } from './column-names';
import { DocumentControlButtons } from './document-control-buttons';
import { DocumentStatusBlock } from './document-status';
import { DocumentTitle } from './document-title';

export const documentColumns: DataGridTableProps['columnsArray'] = [
  {
    dataField: ColumnNamesForDocumentsTable.TYPE,
    caption: 'Document title',
    alignment: 'left',
    cellRender: (cellData: CellRenderProps['cellData']) => (
      <DocumentTitle id={cellData.data.id} type={cellData.data.type} />
    ),
  },
  {
    dataField: ColumnNamesForDocumentsTable.STATUS,
    caption: 'Status',
    alignment: 'left',
    cellRender: (cellData: CellRenderProps['cellData']) => (
      <DocumentStatusBlock status={cellData.data.status} />
    ),
  },
  {
    dataField: ColumnNamesForDocumentsTable.PATH,
    caption: '',
    width: 150,
    alignment: 'right',
    cellRender: (cellData: CellRenderProps['cellData']) => (
      <DocumentControlButtons
        id={cellData.data.id}
        path={cellData.data.path}
        status={cellData.data.status}
        type={cellData.data.type}
      />
    ),
  },
];
