import { useEffect } from 'react';
import { useAppDispatch } from 'store/hooks';
import {
  getCurrentToken,
  getInactiveTokens,
} from 'old-reducer/api-token/thunk';
import { CurrentTokenBlock } from './current-token-block';
import { InactiveTokensBlock } from './inactive-tokens-block';

export const ApiToken = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(getCurrentToken());
    dispatch(getInactiveTokens());
  }, [dispatch]);

  return (
    <div className="content">
      <CurrentTokenBlock />
      <InactiveTokensBlock />
    </div>
  );
};
