import { useAppSelector } from 'store/hooks';
import { Grid } from '@material-ui/core';
import { isAffiliate, isSuperUser } from 'old-enums/user-permission';
import { PayoutType } from 'old-enums/payout-type';
import { NextPayoutAlert } from './next-payout-alert';
import { NewAffiliatesAlert } from './new-affiliates-alert';
import { CpaClientsAlert } from './cpa-clients-alert';

export const PaymentAlerts = () => {
  const { permissions, payments, payouts } = useAppSelector((state) => ({
    permissions: state.user.data.ida_permissions,
    payments: state.paymentsPage.payments,
    payouts: state.paymentsPage.payouts,
  }));

  const hasPayments = payments.length !== 0;
  const hasPayouts = payouts.length !== 0;
  const toShowNewAffiliateAlert = !hasPayments && isAffiliate(permissions);

  return (
    <Grid container spacing={1} direction="column">
      {hasPayouts &&
        payouts.map(({ id, type, date, payment_amount, pre }) => {
          if (type === PayoutType.NEXT_PAYOUT) {
            return (
              <Grid item key={id}>
                <NextPayoutAlert
                  payoutDate={date}
                  payoutAmount={payment_amount}
                  prefix={pre}
                />
              </Grid>
            );
          }

          return null;
        })}
      {toShowNewAffiliateAlert && (
        <Grid item>
          <NewAffiliatesAlert />
        </Grid>
      )}
      {isSuperUser(permissions) && (
        <Grid item>
          <CpaClientsAlert />
        </Grid>
      )}
    </Grid>
  );
};
