import { DataGridTable } from '@development/ui-kit';
import type { Props } from './types';
import { ColumnNamesForTokenList } from './column-names';
import { tokenListColumns } from './columns';

export const TokenList = ({ tokens }: Props) => {
  const pagerProps = {
    toShowPageSizeSelector: true,
  };

  return (
    <DataGridTable
      dataSource={tokens}
      keyExpr={ColumnNamesForTokenList.ID}
      columnsArray={tokenListColumns}
      toShowPaging
      pagerProps={pagerProps}
      hoverStateEnabled
    />
  );
};
