import { memo } from 'react';

import { UserOfferLinkForm } from 'old-component/offer/user-offer-link-form';
import { OfferLinkTypeEnum } from 'old-enums/offer-link-type';
import { setUserOfferURL } from 'old-reducer/offer-page';
import { DirectLinkConstructor } from '../direct-link-constructor';
import type { OfferRequestData, UserOfferData } from '../types';
import * as Styled from './styles';

type Props = {
  backlink: OfferRequestData['t_backlink'];
  directLink: OfferRequestData['t_direct_link'];
  postbackURLs: OfferRequestData['postback_urls'];
  offerId: UserOfferData['id_offer'];
};

export const OfferLinks = memo(
  ({ backlink, directLink, offerId, postbackURLs }: Props) => {
    const backlinkURLs = backlink ? [backlink] : [];

    return (
      <Styled.PaperStyled>
        <DirectLinkConstructor value={directLink} />
        <UserOfferLinkForm
          offerId={offerId}
          setURL={setUserOfferURL}
          offerLinkType={OfferLinkTypeEnum.POSTBACK_URL}
          urls={postbackURLs}
          isAddButtonNeeded
        />
        <UserOfferLinkForm
          offerId={offerId}
          setURL={setUserOfferURL}
          offerLinkType={OfferLinkTypeEnum.BACKLINK}
          urls={backlinkURLs}
        />
      </Styled.PaperStyled>
    );
  },
);
