import type { WithTheme } from '@material-ui/core/styles';
import { withTheme } from '@material-ui/core/styles';
import styled from 'styled-components';
import { IconButton, Paper, Typography } from '@material-ui/core';

export const Link = withTheme(styled.a`
  color: ${({ theme }: WithTheme) => theme.palette.secondary.main};
  text-decoration: underline;

  &:hover {
    color: ${({ theme }: WithTheme) => theme.palette.secondary.dark};
  }
`);

export const Content = withTheme(styled.div`
  width: 592px;
  text-align: center;
  position: relative;
  padding-top: ${({ theme }: WithTheme) => theme.spacing(5)}px;
  padding-bottom: ${({ theme }: WithTheme) => theme.spacing(3)}px;
  padding-left: ${({ theme }: WithTheme) => theme.spacing(8)}px;
  padding-right: ${({ theme }: WithTheme) => theme.spacing(8)}px;

  ${({ theme }: WithTheme) => theme.breakpoints.down('xs')} {
    width: auto;
    padding-top: ${({ theme }: WithTheme) => theme.spacing(4)}px;
    padding-bottom: ${({ theme }: WithTheme) => theme.spacing(3)}px;
    padding-left: ${({ theme }: WithTheme) => theme.spacing(2)}px;
    padding-right: ${({ theme }: WithTheme) => theme.spacing(2)}px;
  }
`);

export const Close = withTheme(styled(IconButton)`
  position: absolute !important;
  padding: 0 !important;
  top: ${({ theme }: WithTheme) => theme.spacing(3)}px;
  right: ${({ theme }: WithTheme) => theme.spacing(3)}px;

  ${({ theme }: WithTheme) => theme.breakpoints.down('xs')} {
    top: ${({ theme }: WithTheme) => theme.spacing(2)}px;
    right: ${({ theme }: WithTheme) => theme.spacing(2)}px;
  }
`);

export const Image = withTheme(styled.img`
  ${({ theme }: WithTheme) => theme.breakpoints.down('xs')} {
    display: none;
  }
`);

export const PaperWrap = withTheme(styled(Paper)`
  padding-top: ${({ theme }: WithTheme) => theme.spacing(2)}px;
  padding-bottom: ${({ theme }: WithTheme) => theme.spacing(2)}px;

  &:hover {
    box-shadow: 0 2px 10px rgb(0 0 0 / 24%);
  }
`);

export const CardTitle = withTheme(styled(Typography)`
  padding-top: ${({ theme }: WithTheme) => theme.spacing(2)}px;

  ${({ theme }: WithTheme) => theme.breakpoints.down('xs')} {
    padding-top: ${({ theme }: WithTheme) => theme.spacing(1)}px;
  }
`);

export const CardDescription = withTheme(styled(Typography)`
  padding-top: ${({ theme }: WithTheme) => theme.spacing(1)}px;
  padding-bottom: ${({ theme }: WithTheme) => theme.spacing(2)}px;
`);

export const CardNote = withTheme(styled(Typography)`
  padding-top: ${({ theme }: WithTheme) => theme.spacing(2)}px;
  display: block;
`);

export const Header = withTheme(styled(Typography)`
  padding-bottom: ${({ theme }: WithTheme) => theme.spacing(4)}px;
`);

export const Footer = withTheme(styled(Typography)`
  margin-top: ${({ theme }: WithTheme) => theme.spacing(3)}px !important;
`);
