import { useState } from 'react';
import { entity } from 'constants/sign';
import * as Styled from '../../styles';
import type { ErrorMessageProps } from './types';

export const ErrorMessage = ({ errorText }: ErrorMessageProps) => {
  const [showMore, setShowMore] = useState(false);

  if (!errorText.includes('account is no longer active')) {
    return <Styled.ErrorText>{errorText}</Styled.ErrorText>;
  }

  const termsLink =
    entity === 'affiliate'
      ? 'https://adsterra.com/publishers-terms-cpa/'
      : 'https://adsterra.com/publishers-terms-managed/';

  return (
    <Styled.ErrorMessageWrapper>
      <Styled.ErrorText>{errorText}</Styled.ErrorText>
      <Styled.Link
        color="textSecondary"
        dataTestId="sign-in__learn-more-link"
        text="&nbsp;Learn more"
        onClick={() => setShowMore(!showMore)}
      />
      {showMore && (
        <div>
          <div>
            We noticed some activity in this account that is inconsistent with
            our Terms and Conditions, and we no longer offer you Adsterra
            services.
          </div>
          <div>
            Any funds you had in your account balance have been returned to our
            Advertisers and are not available for payout.
          </div>
          <br />
          <div>
            For any additional information, please refer to our
            <div>
              <Styled.Link
                href={termsLink}
                color="textSecondary"
                dataTestId="sign-in__terms-and-conditions-link"
                text="Terms and Conditions"
              />
              .
            </div>
          </div>
        </div>
      )}
    </Styled.ErrorMessageWrapper>
  );
};
