import React, { useState, useRef, useCallback, useEffect } from 'react';
import SignatureCanvas from 'react-signature-canvas';
import { DialogStatus } from 'old-enums/dialog-status';
import { Grid } from '@material-ui/core';
import type ReactSignatureCanvas from 'react-signature-canvas';
import { Add, DriveFileRenameOutline, Refresh } from '@mui/icons-material';
import { Alert, Button } from '@development/ui-kit';
import { Dialog, IconButton } from '..';
import type { Props } from './types';
import * as Styled from './styles';

const signaturePadDimensions = {
  width: 552,
  height: 260,
};

export const SignaturePad = ({
  dataTestId,
  onSubmitSignatureDialog,
  userSignature,
  isDisabled,
}: Props) => {
  const [dialogStatus, setDialogStatus] = useState(DialogStatus.HIDE);
  const [hasSignature, setHasSignature] = useState(false);
  const [emptyError, setEmptyError] = useState(false);
  const canvas = useRef<ReactSignatureCanvas>(null);

  useEffect(() => {
    setHasSignature(Boolean(userSignature));
  }, [userSignature]);

  const clearSignature = useCallback(() => {
    canvas.current?.clear();
  }, []);

  const createNewSignature = useCallback(() => {
    clearSignature();
    setHasSignature(false);
    canvas.current?.on();
  }, [clearSignature]);

  const openSignatureDialog = useCallback(() => {
    setTimeout(() => {
      if (userSignature) {
        canvas.current?.fromDataURL(userSignature, signaturePadDimensions);
        canvas.current?.off();
      }
    });
    setDialogStatus(DialogStatus.SHOW);
  }, [userSignature]);

  const closeSignatureDialog = useCallback(() => {
    setDialogStatus(DialogStatus.HIDE);
    setHasSignature(Boolean(userSignature));
    setEmptyError(false);
  }, [userSignature]);

  const submitSignature = useCallback(() => {
    if (canvas.current?.isEmpty()) {
      setEmptyError(true);

      return;
    }
    const signatureUrl = canvas.current?.toDataURL();

    if (signatureUrl) {
      onSubmitSignatureDialog(signatureUrl);
      setDialogStatus(DialogStatus.HIDE);
    }
  }, [onSubmitSignatureDialog]);

  const dialogTitle = hasSignature
    ? 'Choose signature'
    : 'Create new signature';

  const submitButtonText = hasSignature ? 'Use existing' : 'Submit';
  const signatureButton = hasSignature ? (
    <Button
      dataTestId="signature__create-new-signature"
      variant="outlined"
      startIcon={<Add />}
      onClick={createNewSignature}
    >
      Create new
    </Button>
  ) : (
    <Button
      dataTestId="signature__clear-signature"
      variant="outlined"
      startIcon={<Refresh />}
      onClick={clearSignature}
    >
      Clear
    </Button>
  );

  return (
    <>
      <IconButton
        title="Sign document"
        dataTestId={dataTestId}
        onClick={openSignatureDialog}
        edge="end"
        disabled={isDisabled}
      >
        <DriveFileRenameOutline />
      </IconButton>
      <Dialog
        onSubmit={submitSignature}
        submitButtonText={submitButtonText}
        dataTestId="signature__sign-document"
        status={dialogStatus}
        onCancel={closeSignatureDialog}
        cancelButtonText="Cancel"
        width={600}
        title={dialogTitle}
      >
        <Grid container spacing={2} direction="column" wrap="nowrap">
          {emptyError && (
            <Grid item>
              <Alert
                dataTestId="signature__empty-signature-alert"
                message="Please provide signature first."
                severity="error"
              />
            </Grid>
          )}
          <Grid item>
            <Styled.SignatureGridContainer>
              <SignatureCanvas
                penColor="black"
                ref={canvas}
                onBegin={() => setEmptyError(false)}
                canvasProps={{
                  ...signaturePadDimensions,
                  className: 'sigCanvas',
                }}
              />
            </Styled.SignatureGridContainer>
          </Grid>
          <Grid item>{signatureButton}</Grid>
        </Grid>
      </Dialog>
    </>
  );
};
