import { RadioButton } from '@development/ui-kit';

import { v2 } from 'constants/index';
import { AdUnitEnum, getAdUnitTitle } from 'old-enums/ad-units';

import type { MouseEvent } from 'react';

import { AdUnitLabel } from './ad-unit-label';
import * as Styled from './styles';

import type { Props } from './types';

const columns = [
  [
    AdUnitEnum.POPUNDER,
    AdUnitEnum.BANNER_468_X_60,
    AdUnitEnum.BANNER_300_X_250,
    AdUnitEnum.BANNER_320_X_50,
    AdUnitEnum.NATIVE_BANNER,
  ],
  [
    AdUnitEnum.DIRECT_LINK,
    AdUnitEnum.BANNER_160_X_300,
    AdUnitEnum.BANNER_160_X_600,
    AdUnitEnum.BANNER_728_X_90,
    AdUnitEnum.SOCIAL_BAR,
  ],
];

export const AdUnits = ({
  findAdUnit,
  findNewAdUnit,
  onChangeAdUnit,
}: Props) => {
  const handleWebPushClick = (e: MouseEvent) => {
    if (window.Intercom === undefined) {
      return;
    }

    e.preventDefault();
    window.Intercom(
      'showNewMessage',
      "Hi there, I'd like to add web push to my website",
    );
  };

  return (
    <div>
      <Styled.Caption>Available Ad Units</Styled.Caption>
      <Styled.Columns>
        {columns.map((column) => (
          <Styled.Column key={column.join('-')}>
            {column.map((item) => (
              <RadioButton
                label={<AdUnitLabel item={item} />}
                dataTestId={`dialog__${getAdUnitTitle(item)
                  .toString()
                  .replaceAll(' ', '_')}`}
                checked={findNewAdUnit(item) || findAdUnit(item)}
                disabled={findAdUnit(item)}
                key={item}
                value={item}
                onChange={(event) => onChangeAdUnit(event, item)}
              />
            ))}
          </Styled.Column>
        ))}
      </Styled.Columns>

      <Styled.Note>
        For better results, please use only one banner of each size per page.
        <br />
        Please get in touch with us if you need multiple banners <br />
        of the same size {'  '}
        <a
          className="link"
          href={`${v2}/ticket`}
          rel="noopener noreferrer"
          target="_blank"
        >
          contact support
        </a>
        <Styled.WebPushLink
          className="link"
          href="mailto:publishers@adsterra.com"
          onClick={handleWebPushClick}
        >
          Looking for WebPush? Chat with us
        </Styled.WebPushLink>
      </Styled.Note>
    </div>
  );
};
