import { Route, Switch } from 'react-router-dom';

import { Auth } from 'old-component/auth';
import { Page } from 'old-component/page';

import { routes } from './routes';
import { anonymousRoutes, existingRoutes } from './paths';

export const SwitchRouter = () => {
  return (
    <Switch>
      {routes.map((route) => {
        const { component: Component, path, title } = route;

        if (!existingRoutes.includes(path)) {
          return null;
        }

        const exact = !route.routes || !route.routes.length;
        const component =
          exact || !Component
            ? Component
            : () => <Component routes={route.routes} />;

        return (
          <Route
            exact={exact}
            key={path}
            path={path}
            render={() =>
              anonymousRoutes.includes(path) ? (
                <Page title={title} component={component} />
              ) : (
                <Auth>
                  <Page title={title} component={component} />
                </Auth>
              )
            }
          />
        );
      })}
    </Switch>
  );
};
