type LinksData = {
  FAQLink: string;
  blogLink: string;
  onBoardingLink: string | null;
  gettingStartedLink: string;
  technicalPartText: string;
  technicalPartDataTestId: string;
  technicalPartRatesAndFormats: string;
  paymentsLink: string;
};

export const linksGenerator = (isUserPureAffiliate: boolean): LinksData => {
  const blogLink = 'https://adsterra.com/blog';
  const FAQLink = isUserPureAffiliate
    ? 'https://help-affiliates.adsterra.com'
    : 'https://help-publishers.adsterra.com';

  return {
    FAQLink,
    blogLink,
    onBoardingLink: isUserPureAffiliate
      ? null
      : `${blogLink}/set-up-publishers-dashboard/`,
    gettingStartedLink: isUserPureAffiliate
      ? `${FAQLink}/en/collections/2953441-getting-started`
      : `${FAQLink}/en/collections/2270069-getting-started`,
    technicalPartText: isUserPureAffiliate
      ? 'Technical part'
      : 'Rates and ad formats',
    technicalPartDataTestId: isUserPureAffiliate
      ? 'support__technical-part-link'
      : 'support__rates-formats-link',
    technicalPartRatesAndFormats: isUserPureAffiliate
      ? `${FAQLink}/en/collections/2953444-technical-part`
      : `${FAQLink}/en/collections/2274770-rates-and-ad-formats`,
    paymentsLink: isUserPureAffiliate
      ? `${FAQLink}/en/collections/2953443-payments`
      : `${FAQLink}/en/collections/3030699-statistics-and-payouts`,
  };
};
