import { useContext } from 'react';

import { ActionBlock } from 'old-component/action-block';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

import { Button } from '@development/ui-kit';
import { SitesTableContext } from '../sites-table-context';

export const AddDirectLinkButtonBlock = ({
  isTherePlacements,
}: {
  isTherePlacements: boolean;
}) => {
  const { showDialogHandler, sites } = useContext(SitesTableContext);

  const pendingRequest = sites.length === 1 ? sites[0].pending_request : [];
  const isTherePendingRequest =
    !Array.isArray(pendingRequest) && pendingRequest.id !== 0;
  const blockText = isTherePlacements
    ? 'Click CREATE DIRECT LINK button to request your first link'
    : 'There are no added direct links. Click CREATE DIRECT LINK button to request your first link';

  return (
    <ActionBlock
      actionButton={
        <Button
          className="add_new"
          dataTestId="direct-links__add-new-direct-link-button"
          disabled={isTherePendingRequest}
          variant="contained"
          onClick={() => showDialogHandler(WebsitesDialogTypesEnum.ADD_SITE)}
        >
          Create direct link
        </Button>
      }
      text={blockText}
    />
  );
};
