import { memo } from 'react';
import { Skeleton } from '@material-ui/lab';

import { useAppSelector } from 'store/hooks';
import { selectTopicTitleById } from 'store/ticket-topics/selectors';
import type { Props } from './types';

import * as Styled from './style';

export const DepartmentCell = memo(({ id }: Props) => {
  const name = useAppSelector(selectTopicTitleById(id));

  if (name === undefined) {
    return <Skeleton variant="text" />;
  }

  return <Styled.TypeCell>{name}</Styled.TypeCell>;
});
