import { CircularProgress } from '@material-ui/core';
import type { PropsWithChildren } from 'react';
import { Button, DataGridTable } from '@development/ui-kit';
import { Pagination } from 'components/pagination';
import { FontSizesEnum } from 'old-enums/font-sizes';
import {
  ApplyButtonWrapper,
  FilterRow,
  TablePaginationWrapper,
  ButtonsRow,
} from './styles';

import type { FilteredTableComponentProps } from './types';
import { prepareColumnsData } from './helpers';
import { getSelects } from './filter-selects';

export const FilteredList = <T,>({
  tableFields,
  tableData,
  totalItems,
  updateData,
  updatePage,
  currentPage,
  itemsPerPage,
  isLoading,
  toShowFilters,
  children,
}: PropsWithChildren<FilteredTableComponentProps<T>>) => {
  const columnsArray = prepareColumnsData<T>(tableFields);

  const selects = getSelects(tableFields);

  return (
    <>
      {toShowFilters && (
        <ButtonsRow>
          <FilterRow>
            {selects}
            <ApplyButtonWrapper>
              <Button
                dataTestId="filtered-list__apply-filter-button"
                variant="contained"
                color="tertiary"
                onClick={updateData}
                size="large"
              >
                Apply
              </Button>
            </ApplyButtonWrapper>
          </FilterRow>
          {children}
        </ButtonsRow>
      )}

      {!isLoading && (
        <>
          <DataGridTable
            dataSource={tableData}
            keyExpr="ID"
            columnsArray={columnsArray}
            toShowPaging={totalItems > itemsPerPage}
            pageSize={itemsPerPage}
            noDataText="Nothing found"
            fontSize={FontSizesEnum.SIZE_16_PX}
          />

          {totalItems > itemsPerPage && (
            <TablePaginationWrapper>
              <Pagination
                active={currentPage}
                total={
                  totalItems % itemsPerPage === 0
                    ? totalItems / itemsPerPage
                    : totalItems / itemsPerPage + 1
                }
                onTap={updatePage}
              />
            </TablePaginationWrapper>
          )}
        </>
      )}
      {isLoading && <CircularProgress />}
    </>
  );
};
