import type { OffersTabEnum } from 'old-enums/offers-tab';

import { TableCell, TableRow } from '@material-ui/core';
import { getFormattedPayoutRange } from 'utils/format';
import { PreviewImage } from 'old-component/preview-image';
import { getConversionTypeTitleById } from 'old-enums/conversion-type';
import { OfferExclusivityTypeEnum } from 'old-enums/offer-exclusivity-type';
import { OfferPrivacyType } from 'old-enums/offer-privacy-type';
import { OfferTagType } from 'old-enums/offer-tag-type';
import { getVerticalTitleById } from 'old-enums/offer-vertical';

import type { OfferData } from 'old-view/offer/types';
import { GeoCell } from '../geo-cell';
import { OfferCell } from '../offer-cell';
import { OfferTag } from '../offer-tag';
import * as Styled from '../styles';

type Props = {
  item: OfferData;
  tabId: OffersTabEnum;
};

export const OfferListItem = ({
  item: {
    b_top: isTop,
    id_advertiser_category: advertiserCategoryID,
    id_offer: offerID,
    ida_geo: geoList,
    is_new: isNew,
    max_payout: maxPayout,
    min_payout: minPayout,
    n_conversion_type: conversionType,
    n_exclusivity_type: exclusivityType,
    n_privacy_type: privacyType,
    t_name: offerName,
    t_preview_path: previewPath,
  },
  tabId,
}: Props) => {
  const isPrivate = privacyType === OfferPrivacyType.PRIVATE;
  const isExclusive = exclusivityType === OfferExclusivityTypeEnum.EXCLUSIVE;

  return (
    <TableRow key={offerID}>
      <Styled.Id>{offerID}</Styled.Id>
      <Styled.OfferPreview>
        <PreviewImage height={100} url={previewPath} width={100} />
      </Styled.OfferPreview>
      <TableCell>
        <OfferCell id={offerID} name={offerName} />
      </TableCell>
      <Styled.OfferColumn>
        {isExclusive && <OfferTag type={OfferTagType.EXCLUSIVITY} />}
        {isNew && <OfferTag type={OfferTagType.NEW} />}
        {isTop && <OfferTag type={OfferTagType.TOP} />}
        {isPrivate && <OfferTag type={OfferTagType.PRIVACY} />}
      </Styled.OfferColumn>
      <Styled.OfferVertical>
        {getVerticalTitleById(advertiserCategoryID)}
      </Styled.OfferVertical>
      <Styled.OfferPayout>
        {getFormattedPayoutRange(minPayout, maxPayout)}
      </Styled.OfferPayout>
      <Styled.OfferConversionType>
        {getConversionTypeTitleById(conversionType)}
      </Styled.OfferConversionType>
      <TableCell>
        <GeoCell id={offerID} geoData={geoList} tabId={tabId} />
      </TableCell>
    </TableRow>
  );
};
