import { memo } from 'react';
import { InputLabel, MenuItem, Select } from '@material-ui/core';

import { getAllLayouts } from 'old-enums/layouts';

import type { LayoutProps } from '../types';

import * as Styled from '../styles';

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: 200,
    },
  },
};

export const LayoutSelect = memo(
  ({ isLoading, onLayoutChange, layout }: LayoutProps) => {
    const layouts = getAllLayouts();
    const layoutsToRender = layouts.includes(layout)
      ? layouts
      : [...layouts, layout];

    const layoutOptions = layoutsToRender.map((item) => (
      <MenuItem key={item} value={item}>
        {item}
      </MenuItem>
    ));

    return (
      <Styled.FormControlBlock fullWidth>
        <InputLabel shrink id="widget-layout-label-filter">
          Widget layout
        </InputLabel>
        <Select
          data-testid="widget-layout-label-filter"
          disabled={isLoading}
          labelId="widget-layout-label-filter"
          value={layout}
          onChange={onLayoutChange}
          MenuProps={MenuProps}
        >
          {layoutOptions}
        </Select>
      </Styled.FormControlBlock>
    );
  },
);
