import { Component } from 'react';
import { connect } from 'react-redux';

import { resendEmail } from 'old-reducer/common/thunk';
import { setEmail } from 'old-reducer/signup';

import type { MapDispatchToProps, MapStateToProps } from 'react-redux';
import type { ChangeEvent } from 'react';
import type { AppState } from 'store/types';
import type { Props, Dispatchers } from './types';

import * as Styled from './styles';

export class PureResend extends Component<Props> {
  private onChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { setEmail: setEmailProp } = this.props;
    setEmailProp(event.target.value);
  };

  private onSubmit = () => {
    // eslint-disable-next-line react/destructuring-assignment
    this.props.resendEmail();
  };

  render() {
    /* eslint-disable */
    const {
      confirmStep: { emailServices: email_services, resend },
    } = this.props;

    const {
      form: {
        formErrors: form_error,
        generalError: general_error,
        isLoading: loading,
      },
    } = this.props;
    /* eslint-enable */

    return (
      <div>
        <Styled.Error>{general_error}</Styled.Error>
        {resend ? (
          <div>
            <Styled.EmailField
              dataTestId="resend__email"
              error={form_error.email}
              helperText={form_error.email}
              label="Email"
              onChange={this.onChange}
              variant="standard"
            />
            <br />
            <Styled.ButtonControl
              dataTestId="resend__submit-button"
              disabled={loading}
              variant="contained"
              color="tertiary"
              onClick={this.onSubmit}
              isLoading={loading}
            >
              Resend
            </Styled.ButtonControl>
          </div>
        ) : (
          <Styled.SuccessContainer>
            <Styled.EmailSent>Email sent!</Styled.EmailSent>
            {email_services.link.length > 0 && (
              <Styled.Link
                rel="noreferrer"
                href={email_services.link}
                target="_blank"
              >
                {email_services.message}
              </Styled.Link>
            )}
          </Styled.SuccessContainer>
        )}
      </div>
    );
  }
}

const mapStateToProps: MapStateToProps<AppState['signUp'], {}, AppState> = (
  state,
) => state.signUp;

const mapDispatchToProps: MapDispatchToProps<Dispatchers, {}> = {
  resendEmail,
  setEmail,
};

export const Resend = connect(mapStateToProps, mapDispatchToProps)(PureResend);
