import { Alert } from '@development/ui-kit';
import { useAppSelector } from 'store/hooks';

import { selectNewTicket } from 'old-view/new-ticket/store-new-ticket/selectors';

import * as Styled from './style';

export const TicketAlert = () => {
  const { errorMessage, createdTicketId } = useAppSelector(selectNewTicket);
  const alertMessage = errorMessage || 'Ticket successfully created';

  if (!errorMessage && !createdTicketId) {
    return null;
  }

  return (
    <Styled.AlertWrapper>
      <Alert
        message={alertMessage}
        severity={errorMessage ? 'error' : 'success'}
        variant="filled"
        dataTestId="error-alert"
      />
    </Styled.AlertWrapper>
  );
};
