export enum PaysystemEnum {
  NO_PAYSYSTEM = 0,
  TWO_CHECKOUT = 12,
  CHECK = 13,
  ECOIN = 9,
  INTERNAL_TRANSFER = 11,
  OTHER = 5,
  PAXUM = 4,
  PAYPAL = 1,
  PAYONEER = 6,
  PAYZA = 7,
  WEBMONEY = 2,
  WIRETRANSFER = 3,
  YANDEX_MONEY = 15,
  BITCOIN = 14,
  EPAYMENTS = 8,
  BANK_TRANSFER = -1,
  TETHER = 86,
  PAYPAL_HW = 87,
  LBT_HW = 88,
}

//  - id  ,     
export const PAYSYSTEM_MAIN_FIELD = {
  [PaysystemEnum.EPAYMENTS]: 17,
  [PaysystemEnum.PAXUM]: 5,
  [PaysystemEnum.PAYPAL]: 1,
  [PaysystemEnum.WEBMONEY]: 2,
};

export const getPaysystemTitle = (paysystemId: PaysystemEnum) => {
  switch (paysystemId) {
    case PaysystemEnum.TWO_CHECKOUT:
      return '2heckout';
    case PaysystemEnum.CHECK:
      return 'Check';
    case PaysystemEnum.ECOIN:
      return 'Ecoin';
    case PaysystemEnum.INTERNAL_TRANSFER:
      return 'Internal Transfer';
    case PaysystemEnum.OTHER:
      return 'Other';
    case PaysystemEnum.PAXUM:
      return 'Paxum';
    case PaysystemEnum.PAYPAL:
    case PaysystemEnum.PAYPAL_HW:
      return 'PayPal';
    case PaysystemEnum.PAYONEER:
      return 'Payoneer';
    case PaysystemEnum.PAYZA:
      return 'Payza';
    case PaysystemEnum.WEBMONEY:
      return 'Webmoney';
    case PaysystemEnum.WIRETRANSFER:
      return 'WireTransfer';
    case PaysystemEnum.YANDEX_MONEY:
      return 'YandexMoney';
    case PaysystemEnum.BITCOIN:
      return 'Bitcoin';
    case PaysystemEnum.EPAYMENTS:
      return 'ePayments';
    case PaysystemEnum.LBT_HW:
      return 'Local Bank Transfer (Hyperwallet)';
    case PaysystemEnum.TETHER:
      return 'USDT (Tether)';
    default:
      return paysystemId;
  }
};

export const getSignUpPaysystems = () => [
  PaysystemEnum.NO_PAYSYSTEM,
  PaysystemEnum.PAYPAL,
  PaysystemEnum.WIRETRANSFER,
  PaysystemEnum.PAXUM,
  PaysystemEnum.WEBMONEY,
  PaysystemEnum.BITCOIN,
];
