import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { BlockLoader } from 'old-component/block-loader';
import { TypographyControl } from 'old-component/typography';
import { isPureAffiliate } from 'old-enums/user-permission';
import { getAccessState } from 'old-reducer/tickets/thunk';

import { useGetTicketTopicsQuery } from 'store/ticket-topics/hooks';
import { useGetTicketStatusesQuery } from 'store/ticket-statuses/hooks';

import { ContentWrapper, SupportDescription } from './styles';

import { TicketsTable } from './tickets-table';
import { LinksBlock } from './links-block';

export const TicketsPage = () => {
  const dispatch = useAppDispatch();
  const { ida_permissions } = useAppSelector((state) => state.user.data);
  const { isAccessLoading, toEnableTickets } = useAppSelector(
    (state) => state.ticketsReducer,
  );

  useGetTicketTopicsQuery();
  useGetTicketStatusesQuery();

  useEffect(() => {
    if (toEnableTickets === null) {
      dispatch(getAccessState());
    }
  }, [dispatch, toEnableTickets]);

  const MainContent = (
    <>
      {!toEnableTickets && (
        <SupportDescription>
          <TypographyControl variant="body1">
            Any questions? You can get in touch via the live chat that big red
            button over there in the lower right corner.
          </TypographyControl>
          <TypographyControl variant="body1">
            However, if you need anything from your old tickets  they will
            still be here.
          </TypographyControl>
        </SupportDescription>
      )}
      <TicketsTable toShowFilters={toEnableTickets as boolean} />
    </>
  );

  return (
    <>
      <ContentWrapper>
        <TypographyControl variant="h3">Support</TypographyControl>
        {isAccessLoading ? (
          <BlockLoader dataTestId="tickets__loader" />
        ) : (
          MainContent
        )}
      </ContentWrapper>
      <ContentWrapper>
        <LinksBlock isPureAffiliate={isPureAffiliate(ida_permissions)} />
      </ContentWrapper>
      <button
        type="button"
        onClick={() => {
          unknownFunc();
        }}
      >
        Error Button
      </button>
    </>
  );
};
