import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { SignUpProgressEnum } from 'old-enums/sign-up-progress';
import type { Resent, SelectOptions } from './signup/types';
import { initialState } from './signup/state';
import type { PlainObject } from '../types/utils';

const {
  reducer: signUp,
  actions: {
    confirm,
    setLoadingState,
    generalError,
    formErrors,
    resend,
    setRecaptchaV2RenderState,
    setPaysystemsList,
    confirmStep,
    setDialogChooseRoleState,
    setEmail,
  },
} = createSlice({
  name: 'signUp',
  initialState,
  reducers: {
    confirm: {
      prepare: (redirect: boolean, button: boolean, text: string) => ({
        payload: { button, redirect, text },
      }),
      reducer: (
        state,
        {
          payload,
        }: PayloadAction<{ button: boolean; redirect: boolean; text: string }>,
      ) => {
        state.confirm = { ...payload };
        state.form.isLoading = false;
      },
    },

    setLoadingState: (
      state,
      { payload: isLoading }: PayloadAction<boolean>,
    ) => {
      state.form.isLoading = isLoading;
    },

    generalError: (state, { payload }: PayloadAction<string>) => {
      if (payload?.length >= 0) {
        state.form.generalError = payload;
      }

      state.form.formErrors = initialState.form.formErrors;
      state.form.isLoading = false;
    },

    formErrors: (
      state,
      { payload }: PayloadAction<PlainObject<string | PlainObject<string>>>,
    ) => {
      state.form.formErrors = payload;
    },

    resend: (state, { payload: data }: PayloadAction<Resent | undefined>) => {
      state.confirmStep.resend = !state.confirmStep.resend;
      state.form.isLoading = false;

      if (data !== undefined) {
        const { link, message } = data;
        state.confirmStep.emailServices = { link, message };
      }
    },

    setPaysystemsList: (state, { payload }: PayloadAction<SelectOptions>) => {
      state.resources.paySystemsList = [...payload];
    },

    setEmail: (state, { payload: email }: PayloadAction<string>) => {
      state.confirmStep.email = email;
    },

    confirmStep: (state) => {
      state.form = { ...initialState.form };
      state.step = SignUpProgressEnum.CONFIRM_STEP;
    },

    setRecaptchaV2RenderState: (
      state,
      { payload: wasRendered }: PayloadAction<boolean>,
    ) => {
      state.form.recaptcha.wasRendered = wasRendered;
      state.form.isLoading = false;
    },

    setDialogChooseRoleState: (
      state,
      { payload: isDialogOpen }: PayloadAction<boolean>,
    ) => {
      state.isDialogChooseRoleOpen = isDialogOpen;
    },
  },
});

export {
  // reducer
  signUp,
  // actions
  confirm,
  resend,
  setRecaptchaV2RenderState,
  setLoadingState,
  setPaysystemsList,
  confirmStep,
  generalError,
  formErrors,
  setDialogChooseRoleState,
  setEmail,
};
