import { SignaturePad } from 'old-component';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { saveSignature, signDocument } from 'old-reducer/payments/thunk';
import type { Props } from './types';

export const SignDocument = ({ id }: Props) => {
  const dispatch = useAppDispatch();
  const { userSignature, isSignatureLoading } = useAppSelector((state) => ({
    userSignature: state.paymentsPage.userSignature,
    isSignatureLoading: state.paymentsPage.isLoading.signature,
  }));

  const onDocumentSign = (signature: string) => {
    dispatch(saveSignature(signature));
    dispatch(signDocument(id));
  };

  return (
    <SignaturePad
      dataTestId="payments__sign-document"
      onSubmitSignatureDialog={onDocumentSign}
      userSignature={userSignature}
      isDisabled={isSignatureLoading}
    />
  );
};
