import { Component } from 'react';
import { connect } from 'react-redux';

import {
  CircularProgress,
  Table,
  TableBody,
  TableRow,
  TableCell,
} from '@material-ui/core';
import { Button } from '@development/ui-kit';
import {
  ArrowDownward,
  ArrowUpward,
  Settings,
  TrendingUp,
} from '@mui/icons-material';

import { setCookieAndRedirectToStats } from 'utils/cookie';
import { Codes } from 'old-component/ad-codes';
import { PlacementStatus } from 'old-component/websites/placement-status';
import { PendingStatus } from 'old-component/websites/placement-status/pending-status';
import { IdCellContent } from 'features/websites';
import { AdUnitEnum, getAdUnitTitle } from 'old-enums/ad-units';
import { PublisherStatusEnum } from 'old-enums/publisher-status';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import {
  getPhpCodeLink,
  hasPhpCode,
  showCodes,
  showDialog,
  showDialogPlacement,
} from 'old-reducer/site-page';
import { showSnackbar } from 'old-reducer/snackbar';

import type { PendingRequest } from 'types/pending-request';
import type { AppState } from 'store/types';

import * as Styled from './styles';

import type {
  ComponentProps,
  Place,
  SiteDispatchers,
  SiteProps,
  State,
} from './types';

class PlacementList extends Component<ComponentProps, State> {
  constructor(props: ComponentProps) {
    super(props);

    this.state = {
      isLoading: false,
    };
  }

  private readonly getPlace = (): Place => {
    const {
      sitePage: {
        list: { expand_site_id, sites },
      },
    } = this.props;

    for (const current of sites) {
      if (current.id === expand_site_id) {
        return {
          pendingRequest: current.pending_request as PendingRequest,
          placements: current.placements,
          site: current,
        };
      }
    }

    return {} as Place;
  };

  render() {
    const {
      sitePage: {
        list: { expand_placement_id, activate_placement_id_loading },
        php_code_link,
        php_code_pl_id,
      },
      user: {
        account_info: { custom_domain },
        data: { status: userStatus },
      },
    } = this.props;

    /* eslint-disable @typescript-eslint/no-shadow */
    const {
      showDialog,
      showCodes,
      showDialogPlacement,
      showSnackbar,
      getPhpCodeLink,
    } = this.props;
    /* eslint-enable @typescript-eslint/no-shadow */

    const { isLoading } = this.state;

    const { pendingRequest, placements = [], site } = this.getPlace();

    const showCodesHandler = (adUnit: number, id: number) => {
      if (adUnit === AdUnitEnum.WEB_PUSH) {
        return;
      }

      showCodes(id);
    };

    const adCodesCellStyles = {
      padding:
        userStatus === PublisherStatusEnum.NOT_CONFIRMED ? '0 24px' : '0px',
    };

    if (
      (placements && placements.length) ||
      (pendingRequest && pendingRequest.id)
    ) {
      return (
        <div>
          {placements.length > 0 &&
            placements.map((placement) => {
              if (
                hasPhpCode(placement.codes) &&
                placement.id !== php_code_pl_id &&
                placement.id === expand_placement_id
              ) {
                getPhpCodeLink(placement.id);
              }

              return (
                <Table
                  key={placement.id}
                  data-testid={`placement-list__item-${placement.id}`}
                >
                  <TableBody>
                    <Styled.PlacementRow hover>
                      <Styled.TableCellPlacementId>
                        <IdCellContent
                          id={placement.id}
                          dataTestId="placement-list"
                        />
                      </Styled.TableCellPlacementId>
                      <Styled.TableCellAdUnit
                        onClick={() =>
                          showCodesHandler(placement.ad_unit, placement.id)
                        }
                      >
                        {getAdUnitTitle(placement.ad_unit)}
                      </Styled.TableCellAdUnit>
                      <Styled.TableCellPlacementName
                        onClick={() =>
                          showCodesHandler(placement.ad_unit, placement.id)
                        }
                      >
                        {placement.t_alias}
                      </Styled.TableCellPlacementName>
                      <Styled.TableCellStatus>
                        <PlacementStatus
                          activatePlacementId={activate_placement_id_loading}
                          activity={placement.active}
                          adUnit={placement.ad_unit}
                          id={placement.id}
                        />
                      </Styled.TableCellStatus>
                      <Styled.TableCellGetCode>
                        <Button
                          dataTestId="placement-list__get-code-button"
                          disabled={placement.ad_unit === AdUnitEnum.WEB_PUSH}
                          startIcon={
                            expand_placement_id === placement.id ? (
                              <ArrowUpward />
                            ) : (
                              <ArrowDownward />
                            )
                          }
                          onClick={() =>
                            showCodesHandler(placement.ad_unit, placement.id)
                          }
                        >
                          Get code
                        </Button>
                      </Styled.TableCellGetCode>
                      <Styled.TableCellStatistics>
                        <Button
                          dataTestId="placement-list__statistics-button"
                          disabled={site?.b_hidden === 'true'}
                          startIcon={<TrendingUp />}
                          onClick={() =>
                            setCookieAndRedirectToStats(
                              placement.zone_id,
                              placement.id,
                            )
                          }
                        >
                          Statistics
                        </Button>
                      </Styled.TableCellStatistics>
                      <Styled.TableCellEdit>
                        <Button
                          dataTestId="placement-list__edit-button"
                          disabled={placement.ad_unit === AdUnitEnum.WEB_PUSH}
                          startIcon={<Settings />}
                          onClick={() =>
                            showDialogPlacement(
                              WebsitesDialogTypesEnum.EDIT_PLACEMENT,
                              placement,
                            )
                          }
                        >
                          About
                        </Button>
                      </Styled.TableCellEdit>
                      <Styled.TableCellSpacer />
                    </Styled.PlacementRow>
                    {expand_placement_id === placement.id && (
                      <Styled.PlacementRow>
                        <TableCell colSpan={8} style={adCodesCellStyles}>
                          {userStatus === PublisherStatusEnum.NOT_CONFIRMED ? (
                            'odes will be available only after account approval.'
                          ) : (
                            <Codes
                              ad_unit={placement.ad_unit}
                              codes={placement.codes}
                              custom_domain={custom_domain}
                              php_code_link={php_code_link}
                              placement_id={placement.id}
                              showSnackbar={showSnackbar}
                              site_id={site.id}
                              tags={placement.tags}
                              postback_url={placement.t_postback_url}
                            />
                          )}
                        </TableCell>
                      </Styled.PlacementRow>
                    )}
                  </TableBody>
                </Table>
              );
            })}

          {pendingRequest && pendingRequest.id && (
            <Table>
              <TableBody>
                {pendingRequest.ad_units.map((adunit) => (
                  <Styled.PlacementRow
                    hover
                    key={`${pendingRequest.id}_${adunit}`}
                  >
                    <Styled.TableCellPendingAdUnit>
                      {getAdUnitTitle(adunit)}
                    </Styled.TableCellPendingAdUnit>
                    <Styled.TableCellPendingStatus>
                      <PendingStatus />
                    </Styled.TableCellPendingStatus>
                  </Styled.PlacementRow>
                ))}
              </TableBody>
            </Table>
          )}

          {isLoading && (
            <Styled.LoaderWrapper>
              <CircularProgress />
            </Styled.LoaderWrapper>
          )}
        </div>
      );
    }

    return (
      <Styled.TableEmpty>
        <TableBody>
          <TableRow hover>
            <TableCell>
              You haven&apos;t requested any code for this website yet.&nbsp;
              <a
                className="link"
                onClick={() =>
                  showDialog(WebsitesDialogTypesEnum.ADD_AD_UNIT, site)
                }
              >
                Add code
              </a>
            </TableCell>
          </TableRow>
        </TableBody>
      </Styled.TableEmpty>
    );
  }
}

/* eslint-disable import/no-default-export */
export default connect<SiteProps, SiteDispatchers, {}, AppState>(
  (state) => state,
  {
    getPhpCodeLink,
    showCodes,
    showDialog,
    showDialogPlacement,
    showSnackbar,
  },
)(PlacementList);
