import type { ChangeEvent } from 'react';
import { useCallback, useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { Button, Select, TextField } from '@development/ui-kit';
import { Box } from '@material-ui/core';

import { selectAllTicketTopics } from 'store/ticket-topics/selectors';
import type { SelectChangeEvent } from '@mui/material';
import {
  ERROR_TEXT,
  MAX_QUESTION_LENGTH,
  MAX_SUBJECT_LENGTH,
  MENU_PROPS,
  TOO_LONG_QUESTION_TEXT,
  TOO_LONG_SUBJECT_TEXT,
} from './constants';

import {
  createTicket,
  setTicketQuestion,
  setTicketSubject,
  setTicketType,
} from '../store-new-ticket/thunk';

import * as Styled from './style';

import { TicketTypeTooltip } from '../ticket-type-tooltip';

import type { ErrorsFields } from './types';

export const FormNewTicket = () => {
  const dispatch = useAppDispatch();

  const { isLoading, type, subject, question } = useAppSelector(
    (state) => state.newTicket,
  );
  const ticketTopics = useAppSelector(selectAllTicketTopics);

  const [errorsFields, setErrorsFields] = useState<ErrorsFields>({
    hasType: false,
    hasSubject: false,
    hasQuestion: false,
    isTooLongSubject: false,
    isTooLongQuestion: false,
  });

  const setTypeHandler = useCallback(
    (updatedType: number) => dispatch(setTicketType(updatedType)),
    [dispatch],
  );

  const setSubjectHandler = useCallback(
    (updatedSubject: string) => dispatch(setTicketSubject(updatedSubject)),
    [dispatch],
  );

  const setQuestionHandler = useCallback(
    (updatedQuestion: string) => dispatch(setTicketQuestion(updatedQuestion)),
    [dispatch],
  );

  const createTicketHandler = useCallback(
    () => dispatch(createTicket()),
    [dispatch],
  );

  const typeChangeHandler = (evt: SelectChangeEvent) => {
    const { value } = evt.target;

    setErrorsFields((oldErrors) => {
      return { ...oldErrors, hasType: false };
    });

    setTypeHandler(Number(value));
  };

  const subjectChangeHandler = (evt: ChangeEvent<{ value: string }>) => {
    setErrorsFields((oldErrors) => {
      return { ...oldErrors, hasSubject: false };
    });

    if (
      errorsFields.isTooLongSubject &&
      evt.target.value.length <= MAX_SUBJECT_LENGTH
    ) {
      setErrorsFields((oldErrors) => {
        return { ...oldErrors, isTooLongSubject: false };
      });
    }

    setSubjectHandler(evt.target.value);
  };

  const questionChangeHandler = (evt: ChangeEvent<{ value: string }>) => {
    setErrorsFields((oldErrors) => {
      return { ...oldErrors, hasQuestion: false };
    });

    if (
      errorsFields.isTooLongQuestion &&
      evt.target.value.length <= MAX_QUESTION_LENGTH
    ) {
      setErrorsFields((oldErrors) => {
        return { ...oldErrors, isTooLongQuestion: false };
      });
    }

    setQuestionHandler(evt.target.value);
  };

  const validationForm = () => {
    const isTypeEmpty = type === 0;
    const isSubjectEmpty = subject.trim().length === 0;
    const isQuestionEmpty = question.trim().length === 0;
    const isTooLongSubject = subject.trim().length > MAX_SUBJECT_LENGTH;
    const isTooLongQuestion = question.trim().length > MAX_QUESTION_LENGTH;

    setErrorsFields({
      hasType: isTypeEmpty,
      hasSubject: isSubjectEmpty,
      hasQuestion: isQuestionEmpty,
      isTooLongSubject,
      isTooLongQuestion,
    });

    return (
      isTypeEmpty ||
      isSubjectEmpty ||
      isQuestionEmpty ||
      isTooLongSubject ||
      isTooLongQuestion
    );
  };

  const onCreateHandler = (): void => {
    if (!validationForm()) {
      createTicketHandler();
    }
  };

  return (
    <Styled.Container>
      <Select
        menuItems={ticketTopics}
        label="Type"
        dataTestId="submit-new-ticket__type-select"
        required
        disabled={ticketTopics.length === 0}
        onChange={typeChangeHandler}
        error={errorsFields.hasType ? ERROR_TEXT : ''}
        value={ticketTopics.length > 0 ? type : ''}
        minWidth={350}
        menuProps={MENU_PROPS}
      />

      <TicketTypeTooltip />

      <Box my={3}>
        <TextField
          required
          dataTestId="submit-new-ticket__subject-field"
          disabled={isLoading}
          error={
            (errorsFields.hasSubject ? ERROR_TEXT : '') ||
            (errorsFields.isTooLongSubject ? TOO_LONG_SUBJECT_TEXT : '')
          }
          label="Subject"
          value={subject}
          variant="outlined"
          onChange={subjectChangeHandler}
          fullWidth
        />
      </Box>

      <Box my={3}>
        <TextField
          id="standard-multiline-flexible"
          dataTestId="submit-new-ticket__question-field"
          disabled={isLoading}
          error={
            (errorsFields.hasQuestion ? ERROR_TEXT : '') ||
            (errorsFields.isTooLongQuestion ? TOO_LONG_QUESTION_TEXT : '')
          }
          label="Your question"
          variant="outlined"
          value={question}
          minRows={5}
          fullWidth
          multiline
          required
          onChange={questionChangeHandler}
        />
      </Box>

      <Button
        dataTestId="submit-new-ticket__submit-button"
        color="tertiary"
        isLoading={isLoading}
        variant="contained"
        onClick={onCreateHandler}
      >
        Submit ticket
      </Button>
    </Styled.Container>
  );
};
