import { Grid } from '@mui/material';
import { Button, Typography } from '@development/ui-kit';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { MailOutline } from '@mui/icons-material';
import { resendEmail, getPassword } from 'old-reducer/common/thunk';
import { Resend } from './resend';
import type { ConfirmStepProps } from './types';
import * as Styled from './styles';
import confirmEmail from './assets/confirm-email.svg';

export const ConfirmStep = ({ login = false }: ConfirmStepProps) => {
  const { email, emailServices } = useAppSelector(
    (state) => state[login ? 'signIn' : 'signUp'].confirmStep,
  );

  const textContent = login
    ? {
        sendText: "We've just sent a letter to",
        infoText:
          'Please check your inbox and click the confirmation link to&nbsp;change password.',
      }
    : {
        sendText: "We've just sent a verification link to",
        infoText:
          'Please check your inbox and click on the link to get started.',
      };

  const headerText = `${login ? 'Check' : 'Confirm'} your email`;

  const dispatch = useAppDispatch();

  const onResend = () => {
    if (login) {
      dispatch(getPassword());

      return;
    }

    dispatch(resendEmail());
  };

  return (
    <>
      <Styled.LogoContainer>
        <img src={confirmEmail} alt="Confirm email image" />
      </Styled.LogoContainer>
      <Styled.Head variant="h3">{headerText}</Styled.Head>
      <Grid container justifyContent="flex-start">
        <Grid item xs={12} sm={login ? 12 : 10}>
          <Typography variant="body2">
            {textContent.sendText}&nbsp;
            <Styled.Email>{email}</Styled.Email>
          </Typography>
          <Typography variant="body2">
            {/* eslint-disable-next-line react/no-danger */}
            <span dangerouslySetInnerHTML={{ __html: textContent.infoText }} />
          </Typography>
        </Grid>
        {!!emailServices.link.length && (
          <Styled.OpenEmailItem item xs={12}>
            <Grid item xs={12} sm={login ? 12 : 6}>
              <Button
                color="tertiary"
                target="_blank"
                variant="contained"
                dataTestId="signup__submit"
                startIcon={<MailOutline />}
                href={emailServices.link}
                fullWidth
              >
                Open your inbox
              </Button>
            </Grid>
          </Styled.OpenEmailItem>
        )}
        <Styled.ResendGridItem item xs={12} sm={login ? 12 : 6}>
          <Styled.ResendNote variant="caption" display="inline">
            Still no email in inbox and spam?
          </Styled.ResendNote>
          <Resend onResend={onResend} email={email} />
        </Styled.ResendGridItem>
      </Grid>
    </>
  );
};
