import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { thunkTicketTopics } from './thunk';
import { selectTicketTopics, selectStatusTicketTopics } from './selectors';

export function useGetTicketTopicsQuery() {
  const dispatch = useAppDispatch();

  const status = useAppSelector(selectStatusTicketTopics);
  const data = useAppSelector(selectTicketTopics);

  useEffect(() => {
    dispatch(thunkTicketTopics());
  }, [dispatch]);

  const isUninitialized = status === undefined;
  const isLoading = status === 'pending' || status === undefined;
  const isError = status === 'rejected';
  const isSuccess = status === 'fulfilled';

  return { data, isUninitialized, isLoading, isError, isSuccess };
}
