import { useState } from 'react';
import type { ReactNode, MouseEvent } from 'react';
import {
  FormControl,
  FormHelperText,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
} from '@mui/material';
import {
  VisibilityOutlined as Visibility,
  VisibilityOffOutlined as VisibilityOff,
} from '@material-ui/icons';
import type { PasswordProps } from './types';

export const Password = ({
  required,
  fullWidth = false,
  dataTestId,
  value,
  name,
  reference,
  onChange,
  onFocus,
  onBlur,
  onKeyDown,
  error,
  toShowHelperOnFocus,
  helperText,
}: PasswordProps) => {
  const [toShowPassword, setToShowPassword] = useState(false);
  const [internalFocused, setInternalFocused] = useState(false);

  let helper: ReactNode = '';

  if (!toShowHelperOnFocus || internalFocused) {
    helper = helperText;
  }

  const handleClickShowPassword = () => {
    setToShowPassword(!toShowPassword);
  };

  const handleMouseDownPassword = (event: MouseEvent) => {
    event.preventDefault();
  };

  const handleFocus = () => {
    if (typeof onFocus === 'function') {
      onFocus();
    }
    setInternalFocused(true);
  };

  const handleBlur = () => {
    if (typeof onBlur === 'function') {
      onBlur();
    }
    setInternalFocused(false);
  };

  return (
    <FormControl variant="outlined" required={required} fullWidth={fullWidth}>
      <InputLabel htmlFor={`password-${name}`} error={Boolean(error)}>
        Password
      </InputLabel>
      <OutlinedInput
        id={`password-${name}`}
        type={toShowPassword ? 'text' : 'password'}
        data-testid={dataTestId}
        value={value}
        name={name}
        inputRef={reference}
        onChange={onChange}
        onFocus={handleFocus}
        onBlur={handleBlur}
        onKeyDown={onKeyDown}
        error={Boolean(error)}
        label="Password"
        endAdornment={
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
              edge="end"
            >
              {toShowPassword ? <Visibility /> : <VisibilityOff />}
            </IconButton>
          </InputAdornment>
        }
      />
      {error ? (
        <FormHelperText error>{error}</FormHelperText>
      ) : (
        helper && <FormHelperText>{helper}</FormHelperText>
      )}
    </FormControl>
  );
};
