import React, { useCallback } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { hideSnackbar } from 'old-reducer/snackbar';

import { SnackbarPure } from '..';

export const SnackbarWrap = () => {
  const { message, severity } = useAppSelector((state) => state.snackbar);
  const dispatch = useAppDispatch();

  const handleSnackbarClose = useCallback(() => {
    dispatch(hideSnackbar());
  }, [dispatch]);

  return (
    <SnackbarPure
      dataTestId="snackbar-wrap"
      message={message}
      onHide={handleSnackbarClose}
      severity={severity}
    />
  );
};
