import {
  startTicketsLoading,
  endTicketsLoading,
  setTicketsData,
  setAccessStatus,
  startAccessLoading,
  endAccessLoading,
} from 'old-reducer/tickets';
import { showSnackbar } from 'old-reducer/snackbar';
import { getHeaders } from 'utils/http';

import type { IdTopic } from 'store/ticket-topics/types';
import type { IdStatus } from 'store/ticket-statuses/types';
import type { Thunk } from 'store/types';
import { snackbarCommonError } from 'old-reducer/snackbar/constants';
import type { GetAccessResponse, GetTicketsResponse } from './types';

export const ITEMS_PER_PAGE = 25;

export const updateTicketsList =
  (newStatus: IdStatus, newType: IdTopic, newPage = 1): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    dispatch(startTicketsLoading());

    try {
      const queryString = new URLSearchParams();
      queryString.append('department', newType.toString());
      queryString.append('status', newStatus.toString());
      queryString.append('page', newPage.toString());
      queryString.append('limit', ITEMS_PER_PAGE.toString());

      const response = await window.fetch(
        `/api/support/tickets?${queryString.toString()}`,
        {
          headers: getHeaders(token),
        },
      );

      if (response.status !== 200) {
        dispatch(endTicketsLoading());
        dispatch(showSnackbar(snackbarCommonError));

        return;
      }

      const {
        data: { items, page, total },
      } = (await response.json()) as GetTicketsResponse;

      dispatch(
        setTicketsData({
          items,
          currentPage: page,
          totalItems: total,
          currentType: newType,
          currentStatus: newStatus,
        }),
      );

      dispatch(endTicketsLoading());
    } catch (error) {
      dispatch(showSnackbar(snackbarCommonError));
      dispatch(endTicketsLoading());
    }
  };

export const initTicketsList = (): Thunk => async (dispatch, getState) => {
  const {
    ticketsReducer: { currentStatus, currentType },
  } = getState();

  dispatch(updateTicketsList(currentStatus, currentType));
};

export const getAccessState = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  try {
    dispatch(startAccessLoading());

    const response = await window.fetch('/api/support/ticket/access', {
      headers: getHeaders(token),
    });

    if (response.status !== 200) {
      dispatch(showSnackbar(snackbarCommonError));

      return;
    }

    const {
      data: { enabled },
    } = (await response.json()) as GetAccessResponse;

    dispatch(setAccessStatus(enabled));
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(endAccessLoading());
  }
};

export const updateTicketsPage =
  (pageNumber: number): Thunk =>
  async (dispatch, getState) => {
    const {
      ticketsReducer: { currentStatus, currentType },
    } = getState();

    dispatch(updateTicketsList(currentStatus, currentType, pageNumber));
  };
