import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';
import { initialState } from './tickets/state';
import type { SetTicketDataPayload } from './tickets/types';

const {
  actions: {
    startAccessLoading,
    startTicketsLoading,
    setTicketsData,
    endAccessLoading,
    endTicketsLoading,
    setAccessStatus,
  },
  reducer: ticketsReducer,
} = createSlice({
  name: 'support',
  initialState,
  reducers: {
    startAccessLoading: (state) => {
      state.isAccessLoading = true;
    },
    startTicketsLoading: (state) => {
      state.isLoading = true;
    },
    setTicketsData: (
      state,
      {
        payload: { items, totalItems, currentPage, currentType, currentStatus },
      }: PayloadAction<SetTicketDataPayload>,
    ) => {
      state.isLoading = false;
      state.items = [...items];
      state.totalItems = totalItems;
      state.currentPage = currentPage;
      state.currentType = currentType;
      state.currentStatus = currentStatus;
    },
    endAccessLoading: (state) => {
      state.isAccessLoading = false;
    },
    endTicketsLoading: (state) => {
      state.isLoading = false;
    },
    setAccessStatus: (state, { payload: enabled }: PayloadAction<boolean>) => {
      state.toEnableTickets = enabled;
    },
  },
});

export {
  ticketsReducer,
  startAccessLoading,
  startTicketsLoading,
  setTicketsData,
  endAccessLoading,
  endTicketsLoading,
  setAccessStatus,
};
