import { useController, useFormContext } from 'react-hook-form';
import { Select } from '@development/ui-kit';
import type { Props } from './types';

export const SelectController = (props: Props) => {
  const { control } = useFormContext();
  const { name, error: selectError, ...selectProps } = props;

  const {
    field: { onChange, onBlur, value, ref },
    fieldState: { error: errorHook },
  } = useController({
    name,
    control,
  });

  return (
    <Select
      onChange={onChange}
      onBlur={onBlur}
      value={value}
      name={name}
      reference={ref}
      error={errorHook ? errorHook.message : selectError}
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...selectProps}
    />
  );
};
