import { useController, useFormContext } from 'react-hook-form';
import { TextField } from '@development/ui-kit';
import type { Props } from './types';

export const TextFieldController = (props: Props) => {
  const { control } = useFormContext();
  const { name, error: textFieldError, ...textFieldProps } = props;

  const {
    field: { onChange, onBlur, value, ref },
    fieldState: { error: hookError },
  } = useController({
    name,
    control,
  });

  return (
    <TextField
      error={hookError ? hookError.message : textFieldError}
      onChange={onChange}
      onBlur={onBlur}
      value={value}
      reference={ref}
      name={name}
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...textFieldProps}
    />
  );
};
