import { createAsyncThunk } from '@reduxjs/toolkit';

import type { AppState } from 'store/types';
import { fetchTicketStatuses } from './api';

export const thunkTicketStatuses = createAsyncThunk(
  'ticketStatuses/thunkTicketStatuses',
  async (_, { getState }) => {
    const {
      user: { token },
    } = getState() as AppState;

    const req = await fetchTicketStatuses(token);
    const data = await req.json();

    return data.data;
  },
);
