import { memo, useCallback, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import type { ChangeEvent } from 'react';

import { Button } from '@development/ui-kit';
import { OffersFilter } from 'old-component/offers/filter';
import { getAllConversionTypeItems } from 'old-enums/conversion-type';
import { getAllVerticalItems } from 'old-enums/offer-vertical';
import { OffersFilterName } from 'old-enums/offers-filter-name';
import { getAllOfferTags } from 'old-enums/offer-tag';
import { OffersTabEnum } from 'old-enums/offers-tab';
import {
  searchFieldDefaultValue,
  resetFilters,
  setFilterData,
  setSearchInputText,
  getCountries,
} from 'old-reducer/offers-page';
import { Search } from './filters/search';
import type { OffersFilterValues } from './types';
import * as Styled from './styles';

type Props = {
  tabId: OffersTabEnum;
};

export const Filters = memo(({ tabId }: Props) => {
  const { ida_advertiser_category, ida_country, na_conversion_type, tags } =
    useAppSelector((state) => state.offersPage.filters);

  const countries = useAppSelector((state) => state.offersPage.countries);
  const dispatch = useAppDispatch();

  const verticals = getAllVerticalItems();
  const conversionTypes = getAllConversionTypeItems();
  const tagList = getAllOfferTags();

  const isAllOffersTab = tabId === OffersTabEnum.ALL;

  useEffect(() => {
    dispatch(getCountries());
  }, [dispatch]);

  const handleVerticalChange = useCallback(
    ({ target: { value } }: ChangeEvent<{ value: OffersFilterValues }>) => {
      dispatch(setFilterData(OffersFilterName.VERTICALS, value));
    },
    [dispatch],
  );

  const handleCountryChange = useCallback(
    ({ target: { value } }: ChangeEvent<{ value: OffersFilterValues }>) => {
      dispatch(setFilterData(OffersFilterName.COUNTRIES, value));
    },
    [dispatch],
  );

  const handleConversionChange = useCallback(
    ({ target: { value } }: ChangeEvent<{ value: OffersFilterValues }>) => {
      dispatch(setFilterData(OffersFilterName.CONVERSION_TYPES, value));
    },
    [dispatch],
  );

  const handleTagChange = useCallback(
    ({ target: { value } }: ChangeEvent<{ value: OffersFilterValues }>) => {
      dispatch(setFilterData(OffersFilterName.TAGS, value));
    },
    [dispatch],
  );

  const handleResetClick = useCallback(() => {
    dispatch(setSearchInputText(searchFieldDefaultValue.search));
    dispatch(resetFilters());
  }, [dispatch]);

  return (
    <Styled.Wrapper>
      <Styled.Section>
        <OffersFilter
          dataTestID="offers-vertical"
          items={verticals}
          label="Vertical"
          value={ida_advertiser_category}
          onFilterChange={handleVerticalChange}
        />
        <OffersFilter
          dataTestID="offers-geo"
          items={countries}
          label="Geo"
          minWidthFormControl={365}
          value={ida_country}
          onFilterChange={handleCountryChange}
        />
        <OffersFilter
          dataTestID="offers-conversion-type"
          items={conversionTypes}
          label="Conversion type"
          value={na_conversion_type}
          onFilterChange={handleConversionChange}
        />
      </Styled.Section>

      {isAllOffersTab && (
        <Styled.Section>
          <OffersFilter
            dataTestID="offers-tags"
            items={tagList}
            label="Offer tags"
            value={tags}
            onFilterChange={handleTagChange}
          />

          <Search dataTestID="offers-search" />
        </Styled.Section>
      )}

      <Styled.ButtonWrapper>
        <Button
          dataTestId="offers-filters__reset-button"
          startIcon={<Styled.HistoryIconStyled />}
          onClick={handleResetClick}
        >
          Reset filters
        </Button>
      </Styled.ButtonWrapper>
    </Styled.Wrapper>
  );
});
