import type { MapStateToProps } from 'react-redux';
import { connect } from 'react-redux';
import type { AppState } from 'store/types';

import * as Styled from './styles';
import type { Props } from './types';

export const PureAccountWarning = ({ account_active }: Props) =>
  account_active ? null : (
    <Styled.Wrapper className="account_error">
      <div className="content">
        Your account status is INACTIVE due to the traffic activity absence on
        your codes. To start sending traffic you need to re-activate your codes
        or request new ones
      </div>
    </Styled.Wrapper>
  );

const mapStateToProps: MapStateToProps<Props, {}, AppState> = (state) => ({
  account_active: state.user.account_info.account_active,
});

export const AccountWarning = connect(mapStateToProps)(PureAccountWarning);
