import { Accordion, Link } from 'old-component';
import { Typography } from '@material-ui/core';
import { useAppSelector, useAppDispatch } from 'store/hooks';
import { useCallback } from 'react';
import { togglePaymentDateInfoState } from 'old-reducer/payments/thunk';
import * as Styled from './styles';

const contentText = (
  <div>
    <Typography variant="body2" color="textPrimary">
      Payment dates are <b>1-2 and 16-17</b> of each month,{' '}
      <Link
        dataTestId="payments-gmt-info-link"
        text="from 9.00 am till 6.00 pm GMT"
        color="secondary"
        variant="inherit"
        title="Greenwich Mean Time"
        href="https://time.is/GMT"
        target="_blank"
        rel="noopener noreferrer"
      />
      .
      <br /> If these dates fall on <b>weekends or holidays</b> the payments
      will be processed <b>on nearest business day</b>.
      <br />
      Once you reach the minimum payout amount, the money is put on a two-week
      hold.
      <br />
      You will get the payout during the first payment period available after
      your hold period ends.
    </Typography>
    <Styled.ExamplesBlock variant="body2" color="textSecondary">
      For example:
      <br /> You reach the minimum payout amount on June 8. The two-week hold
      period ends on June 22. The next available payment period is July 1-2.
      <br />
      You reach the minimum payout amount on June 17. The two-week hold period
      ends on July 1. The next available payment period is July 16-17.
    </Styled.ExamplesBlock>
  </div>
);

export const PaymentDateInfo = () => {
  const dispatch = useAppDispatch();

  const { isPaymentDateInfoExpanded } = useAppSelector(
    (state) => state.paymentsPage,
  );

  const togglePaymentDateInfoHandler = useCallback(
    (isOpen: boolean) => dispatch(togglePaymentDateInfoState(isOpen)),
    [dispatch],
  );

  return (
    <Accordion
      dataTestId="payments__description"
      defaultExpanded
      summaryText="How to get the date of your payment"
      content={contentText}
      expanded={isPaymentDateInfoExpanded}
      onChange={(_, expanded: boolean) =>
        togglePaymentDateInfoHandler(expanded)
      }
    />
  );
};
