import { IS_PRODUCTION } from 'old-common/is-production';
import { CodeIntercom } from 'lib/intercom/code-intercom';
import { anonymousRoutes } from 'routes';
import type { PATHS } from 'routes';

export const createIntercom = (
  appID: string,
  userID: number,
  userName: string,
) => {
  const path = window.location.pathname as PATHS;

  if (!IS_PRODUCTION || anonymousRoutes.includes(path)) {
    return;
  }

  const intercom = document.createElement('script');
  intercom.textContent = CodeIntercom(appID);
  document.head.appendChild(intercom);

  if (!window.Intercom) {
    return;
  }

  window.Intercom('boot', {
    app_id: appID,
    user_id: String(userID),
    name: userName,
    api_base: `https://${appID}.intercom-messenger.com`,
  });
};
