import { Component } from 'react';

import { Redirect } from 'react-router-dom';

import { Header } from 'old-component/header';
import { Sidebar } from 'old-component/sidebar';

import { isPathAvailableToUser } from 'routes';

import type { PropsWithChildren } from 'react';
import type { State, StateProps, Dispatchers } from './types';
import * as Styled from './styles';

export class AuthComponent extends Component<
  PropsWithChildren<StateProps & Dispatchers>,
  State
> {
  state: State = {
    done: false,
  };

  async componentDidMount() {
    const {
      data: { login },
      manager: { name },
      token,
      getToken,
      getUser,
      getAccountInfo,
    } = this.props;

    if (!token) {
      await getToken();
    }

    if (!login) {
      // ,   ,      
      if (await getUser()) {
        this.setState({ done: true });
      }
    } else {
      this.setState({ done: true });
    }

    if (!name) {
      await getAccountInfo();
    }
  }

  render() {
    const {
      data: { ida_permissions: permissions },
      isSidebarCollapsed,
    } = this.props;
    const currentPath = window.location.pathname;

    if (
      permissions.length !== 0 &&
      !isPathAvailableToUser(currentPath, permissions)
    ) {
      return <Redirect to="/not-found" />;
    }

    const { done } = this.state;
    const { children } = this.props;

    if (done) {
      return <div>{children}</div>;
    }

    return (
      <div>
        <Sidebar />
        <Styled.ContentContainer isSidebarCollapsed={isSidebarCollapsed}>
          <Header />
          <div className="content">
            <Styled.Container>
              <Styled.CircularProgressStyled />
              <Styled.LoaderLabel>Loading. Please, wait...</Styled.LoaderLabel>
            </Styled.Container>
          </div>
        </Styled.ContentContainer>
      </div>
    );
  }
}
