export const { APP_ENV } = process.env;
export const { APP_VERSION } = process.env;

export const IS_DEV = APP_ENV === 'development';
export const IS_TEST = APP_ENV === 'test';
export const IS_PRODUCTION = APP_ENV === 'production';
const AUTH_COOKIE = 'tkn3';

const BASE_DOMAIN = 'adsterra.com';
const DEFAULT_DOMAIN = `publishers.${BASE_DOMAIN}`;
export const COOKIE_DOMAIN = window.location.hostname.replace(
  /(beta|local)[.]/,
  '',
);
export const MOCK_API_DOMAIN = 'mock.api';

export const protocol = `${window.location.protocol}//`;

export const DOMAIN =
  window.location.hostname.match(/^(beta|local)[.](.*)$/)?.[2] ??
  DEFAULT_DOMAIN;

export const v2 = protocol + DOMAIN;

export const baseDomain =
  window.location.host.match(/^beta.(?:publishers|affiliates).(.*)$/)?.[1] ??
  BASE_DOMAIN;

export const topLevelDomain = baseDomain.split('.').pop();

export const CONFIG_SENTRY = {
  DSN: 'https://6094ad8bcb8b41728a78620c7ab2e34a@sentry.developzilla.com/2',
  TRACES_RATE: IS_TEST ? 0 : 0.3,
  ENVIRONMENT: APP_ENV,
};

export const logout = () => {
  window.location.assign(`//${DOMAIN}/logout`);
};

export const setAuthCookie = (token: string): void => {
  const expires = new Date(Date.now() + 365 * 86400 * 1000).toUTCString();
  document.cookie = `${AUTH_COOKIE}=${token}; expires=${expires}; domain=.${DOMAIN}; path=/`;
};

export const isAffiliateDomain = window.location.href.includes('affiliates');
