import React, { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { getReferralsInfo } from 'old-reducer/referrals/thunk';

import { InviteLink } from './invite-link';
import { ReferralStats } from './referral-stats';
import * as Styled from './styles';

export const ReferralsBlock = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(getReferralsInfo());
  }, [dispatch]);

  const { referrals } = useAppSelector(
    (state) => state.referralsPage.referralsInfo,
  );

  return (
    <>
      <Styled.PaperStyledBanner>
        <InviteLink />
      </Styled.PaperStyledBanner>
      {referrals.length > 0 && (
        <Styled.PaperStyledBanner>
          <ReferralStats />
        </Styled.PaperStyledBanner>
      )}
    </>
  );
};
