import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from '@mui/material';

import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import type { Props } from './types';

const defaultExpandOpenControl = (
  <ExpandMoreIcon data-testid="accordion__expand-button" />
);

export const AccordionControl = ({
  summaryText,
  content,
  dataTestId,
  defaultExpanded,
  expanded,
  onChange,
  className,
  expandOpenControl = defaultExpandOpenControl,
  expandClosedControl = defaultExpandOpenControl,
}: Props) => {
  return (
    <Accordion
      data-testid={dataTestId}
      defaultExpanded={defaultExpanded}
      expanded={expanded}
      onChange={onChange}
      className={className}
    >
      <AccordionSummary
        expandIcon={expanded ? expandOpenControl : expandClosedControl}
      >
        <Typography variant="subtitle1">{summaryText}</Typography>
      </AccordionSummary>
      <AccordionDetails>{content}</AccordionDetails>
    </Accordion>
  );
};
