import type { OffersTabEnum } from 'old-enums/offers-tab';

import { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { hideTooltip, showTooltip } from 'old-reducer/offers-page';
import type { OfferItem } from 'old-view/offer/types';
import { getCountryNameById } from './utils';
import * as Styled from './styles';

type Props = {
  geoData: OfferItem['ida_geo'];
  id: OfferItem['id_offer'];
  tabId: OffersTabEnum;
};

export const GeoCell = ({ geoData, id, tabId }: Props) => {
  const [toShowMore, setShowMore] = useState(false);
  const dispatch = useAppDispatch();
  const countriesBlock = useRef<HTMLDivElement>(null);
  const countries = useAppSelector((state) => state.offersPage.countries);
  const countryNames = geoData
    .map((countryId) => getCountryNameById(countries, countryId))
    .sort()
    .join(', ');
  const cellId = `offer_${id}_geo_${tabId}`;

  useLayoutEffect(() => {
    if (
      Number(countriesBlock.current?.clientHeight) -
        Number(countriesBlock.current?.scrollHeight) <
      0
    ) {
      setShowMore(true);
    }
  }, []);

  const handleMouseOver = useCallback(() => {
    dispatch(showTooltip(cellId, countryNames));
  }, [cellId, countryNames, dispatch]);

  const handleMouseOut = useCallback(() => {
    dispatch(hideTooltip());
  }, [dispatch]);

  return (
    <>
      <Styled.GeoData id={cellId} ref={countriesBlock}>
        {countryNames}
      </Styled.GeoData>
      {toShowMore && (
        <Styled.More onMouseOut={handleMouseOut} onMouseOver={handleMouseOver}>
          More
        </Styled.More>
      )}
    </>
  );
};
