import React, { useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { TableBody, TableCell, TableHead, TableRow } from '@material-ui/core';
import { FormatListBulleted } from '@mui/icons-material';

import type { Invite, Referral } from 'old-reducer/referrals/types';

import { Pagination } from 'components/pagination';

import {
  getBannerLink,
  setRefListPagination,
} from 'old-reducer/referrals/thunk';

import { InformationTooltip } from '@development/ui-kit';
import * as Styled from './styles';

export const InviteLink = () => {
  const dispatch = useAppDispatch();
  const {
    referralsInfo: { invites, userInviteLimit, referrals },
    refListPagination: pagination,
  } = useAppSelector((state) => state.referralsPage);

  const [isReferralsVisible, setIsReferralsVisible] = useState(false);

  const handleGenerateInvite = () => {
    dispatch(getBannerLink());
  };

  const handleHideReferrals = () => {
    setIsReferralsVisible(!isReferralsVisible);
  };

  const handlePagination = (page: number) => {
    dispatch(setRefListPagination(page));
  };

  return (
    <>
      <Styled.TitleReferrals>Invite link</Styled.TitleReferrals>
      <Styled.TextReferrals>
        Refer new publishers to Adsterra Network and earn 5% of their revenue
        lifetime! The more people you refer, the more income you get.
      </Styled.TextReferrals>

      {invites.length > 0 && (
        <Styled.TableStyledReferralsList>
          <TableBody>
            {invites.map((invite: Invite) => (
              <TableRow key={invite.id}>
                <Styled.TableCellInviteLink>
                  {invite.url}
                </Styled.TableCellInviteLink>
              </TableRow>
            ))}
          </TableBody>
        </Styled.TableStyledReferralsList>
      )}

      {invites.length < userInviteLimit && (
        <Styled.ButtonControl
          variant="contained"
          onClick={handleGenerateInvite}
          dataTestId="generate-invite-button"
        >
          Generate Invite
        </Styled.ButtonControl>
      )}

      {referrals.length > 0 && (
        <>
          <Styled.ButtonControl
            startIcon={<FormatListBulleted />}
            onClick={handleHideReferrals}
            dataTestId="show-referrals-button"
          >
            {isReferralsVisible ? 'Hide' : 'Show'} referrals list
          </Styled.ButtonControl>

          {isReferralsVisible && (
            <div>
              <Styled.TableStyledReferralsList>
                <TableHead>
                  <TableRow>
                    <TableCell>LOGIN</TableCell>
                    <TableCell>PERCENT</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {pagination.paginatedReferrals.map((referral: Referral) => (
                    <TableRow key={referral.id}>
                      <TableCell>{referral.login}</TableCell>
                      <Styled.Cell>
                        {referral.is_referral_active
                          ? `${referral.percent}%`
                          : 'Inactive'}
                        {!referral.is_referral_active && (
                          <Styled.TooltipWrapper>
                            <InformationTooltip text="This referral stopped sending traffic, and we set the percent you earn to 0. If you bring the referral back to our platform, we will change it to 5%" />
                          </Styled.TooltipWrapper>
                        )}
                      </Styled.Cell>
                    </TableRow>
                  ))}
                </TableBody>
              </Styled.TableStyledReferralsList>

              {pagination.total > 1 && (
                <Pagination
                  active={pagination.active}
                  total={pagination.total}
                  onTap={handlePagination}
                />
              )}
            </div>
          )}
        </>
      )}
    </>
  );
};
