import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

import { initialState } from './api-token/state';
import type { ApiToken } from './api-token/types';

const {
  actions: {
    deactivateToken,
    setCurrentToken,
    setInactiveTokens,
    setLoadingState,
  },
  reducer: apiTokenPageReducer,
} = createSlice({
  name: 'apiTokenPage',
  initialState,
  reducers: {
    deactivateToken: (state, { payload: token }: PayloadAction<ApiToken>) => {
      state.inactiveTokens.push(token);
    },

    setCurrentToken: (
      state,
      { payload: currentToken }: PayloadAction<ApiToken | null>,
    ) => {
      state.currentToken = currentToken;
    },

    setInactiveTokens: (
      state,
      { payload: inactiveTokens }: PayloadAction<ApiToken[]>,
    ) => {
      state.inactiveTokens = inactiveTokens;
    },

    setLoadingState: (
      state,
      { payload: isLoading }: PayloadAction<boolean>,
    ) => {
      state.isLoading = isLoading;
    },
  },
});

export {
  apiTokenPageReducer,
  deactivateToken,
  setCurrentToken,
  setInactiveTokens,
  setLoadingState,
};
