import { Paper, Typography } from 'old-component';
import { Grid } from '@material-ui/core';
import { useAppSelector } from 'store/hooks';
import { BlockLoader } from 'old-component/block-loader';
import { UploadButton } from 'features/documents-upload';
import * as Styled from './styles';
import { DocumentsTable } from './documents-table';

export const Documents = () => {
  const { documents, isDocumentsLoading } = useAppSelector((state) => ({
    documents: state.paymentsPage.documents,
    isDocumentsLoading: state.paymentsPage.isLoading.documents,
  }));

  const hasDocuments = documents.length !== 0;

  const paperContent = isDocumentsLoading ? (
    <BlockLoader dataTestId="payments__documents-loader" />
  ) : (
    <Grid
      container
      alignItems="center"
      justifyContent="space-between"
      spacing={3}
    >
      <Grid item>
        <Typography variant="h3">Documents</Typography>
        {!hasDocuments && (
          <Styled.HeaderText variant="body1">
            You dont have any documents
          </Styled.HeaderText>
        )}
      </Grid>
      <UploadButton />
      {hasDocuments && <DocumentsTable documents={documents} />}
    </Grid>
  );

  return <Paper>{paperContent}</Paper>;
};
