import { memo } from 'react';
import { Button } from '@development/ui-kit';
import { GetApp } from '@mui/icons-material';
import * as Styled from './styles';

type PopunderPhpCodeProps = {
  placementId: number;
  phpCodeLink?: string;
};

export const PopunderPhpDesc = memo(
  ({ placementId, phpCodeLink }: PopunderPhpCodeProps) => (
    <div>
      <p>
        This PHP code updates automatically. There is no need to replace it
        manually, it will always be relevant.
      </p>
      <Styled.ParagraphStyle>
        First, download this file and upload it to the root directory of your
        website:
      </Styled.ParagraphStyle>
      {phpCodeLink && (
        <Styled.HyperlinkStyle href={phpCodeLink} download="AntiAdblock.php">
          <Button
            dataTestId="popunder-php-description__download-button"
            startIcon={<GetApp />}
            variant="contained"
          >
            Download
          </Button>
        </Styled.HyperlinkStyle>
      )}
      <Styled.ParagraphStyle>
        Second, paste this line somewhere inside the &lt;head&gt; tag. We
        suggest to paste it directly above &lt;/head&gt;:
      </Styled.ParagraphStyle>
      <Styled.ParagraphStyle>
        &lt;?php echo include_once (dirname(__FILE__) . &apos;/__AntiBlock_
        {placementId}.php&apos;); ?&gt;
      </Styled.ParagraphStyle>
    </div>
  ),
);
