import { memo } from 'react';
import { TrendingUp } from '@material-ui/icons';

import { v2 } from 'constants/index';
import { ClassNameForTesting } from 'old-enums/class-name-for-testing';
import type { UserOfferData } from 'old-view/offer/types';
import * as Styled from './styles';

export const StatsCell = memo(
  ({ offerId }: { offerId: UserOfferData['id_offer'] }) => {
    const statsUrl = `${v2}/stats?offerId=${offerId}`;

    return (
      <Styled.IconButtonStyled
        data-testid="my-offers__stats-icon-button"
        className={ClassNameForTesting.STATS_BUTTON}
        href={statsUrl}
      >
        <TrendingUp />
      </Styled.IconButtonStyled>
    );
  },
);
