import CopyToClipboard from 'react-copy-to-clipboard';
import { CopyAll } from '@mui/icons-material';
import { showSnackbar } from 'old-reducer/snackbar';
import { useAppDispatch } from 'store/hooks';
import { copyIdSnackbarData } from '../../constants';
import * as Styled from './styles';
import type { Props } from './types';

export const IdCellContent = ({ id, dataTestId }: Props) => {
  const dispatch = useAppDispatch();

  const handleCopy = () => {
    dispatch(showSnackbar(copyIdSnackbarData));
  };

  return (
    <CopyToClipboard text={String(id)} onCopy={handleCopy}>
      <Styled.Container data-testid={`${dataTestId}__copy-id-container-${id}`}>
        <Styled.Id>{id}</Styled.Id>
        <Styled.CopyButton
          onClick={handleCopy}
          size="small"
          dataTestId={`${dataTestId}__copy-id-button-${id}`}
        >
          <CopyAll fontSize="small" />
        </Styled.CopyButton>
      </Styled.Container>
    </CopyToClipboard>
  );
};
