import { FileCopyOutlined, Settings, TrendingUp } from '@mui/icons-material';

import CopyToClipboard from 'react-copy-to-clipboard';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

import { setCookieAndRedirectToStats } from 'utils/cookie';

import type { Props } from './types';

import * as Styled from './styles';

export const PlacementTableRowButtons = ({
  placement,
  showDialogPlacementHandler,
}: Props) => {
  return (
    <>
      <Styled.RowButton
        dataTestId="direct-links__statistics-button"
        startIcon={<TrendingUp />}
        onClick={() =>
          setCookieAndRedirectToStats(placement.zone_id, placement.id)
        }
      >
        Statistics
      </Styled.RowButton>
      <CopyToClipboard text={placement.codes[0].code}>
        <Styled.RowButton
          dataTestId="direct-links__copy-button"
          startIcon={<FileCopyOutlined />}
        >
          Copy
        </Styled.RowButton>
      </CopyToClipboard>
      <Styled.RowButton
        dataTestId="direct-links__settings-button"
        startIcon={<Settings />}
        onClick={() =>
          showDialogPlacementHandler(
            WebsitesDialogTypesEnum.EDIT_PLACEMENT,
            placement,
          )
        }
      >
        Edit
      </Styled.RowButton>
    </>
  );
};
