import { createContext } from 'react';

import { SiteType } from 'old-enums/site-type';

import type { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import type { PendingRequest } from 'types/pending-request';
import type { Site } from 'types/site';
import type { Maybe } from 'types/utils';

export type SitesTableContextData = {
  getSiteListHandler: (
    page: number,
    hidden: 'all' | boolean,
    action?: string,
  ) => void;
  isHiddenFromStatistics: Maybe<boolean>;
  showDialogHandler: (
    action: WebsitesDialogTypesEnum,
    site?: Site | PendingRequest,
  ) => void;
  sites: Site[];
  siteType: SiteType;
};

const SitesTableContextDefaultData: SitesTableContextData = {
  getSiteListHandler: () => () => {},
  isHiddenFromStatistics: null,
  showDialogHandler: () => () => {},
  sites: [],
  siteType: SiteType.ZONE,
};

export const SitesTableContext = createContext<SitesTableContextData>(
  SitesTableContextDefaultData,
);
