import { Component } from 'react';

import { Tab, Tabs } from '@material-ui/core';
import CopyToClipboard from 'react-copy-to-clipboard';

import { AdUnitEnum } from 'old-enums/ad-units';
import { AlertSeverity } from 'old-enums/alert-severity';
import {
  CodeTypeEnum,
  getCodeTypeTitle,
  getPopunderAntiAdBlockTitle,
  getPopunderCodeTitle,
} from 'old-enums/code-type';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import type { TagEnum } from 'old-enums/tags';
import type { SetSnackbarDataPayload } from 'old-reducer/snackbar/types';

/* HD-27348     https    ( IBT) */
// @ts-ignore
import customCode from './ad-code-hd-27348.txt';
// @ts-ignore
import customCode2 from './ad-code-hd-28270.txt';

import { TabTitle } from '../tab-title';
import { CodesDescription } from './description';
import * as Styled from './styles';

interface Code {
  code: string;
  type: CodeTypeEnum;
}

interface Props {
  ad_unit: AdUnitEnum;
  codes: Code[];
  custom_domain: boolean;
  placement_id: number;
  postback_url: string;
  php_code_link?: string;
  site_id?: number;
  tags?: TagEnum[];
  showSnackbar(payload: SetSnackbarDataPayload): void;
}

interface State {
  activeTab: number;
  antiAdblock: boolean;
}

