function prepare(code: string, useBlock: boolean): string {
  if (!useBlock) {
    return code;
  }

  const index = code.search('src=');
  const onError = `onerror='absda()' `;

  return (
    ANTI_ADBLOCK_SCRIPT + code.substr(0, index) + onError + code.substr(index)
  );
}

export class Codes extends Component<Props, State> {
  state: State = {
    activeTab: 0,
    antiAdblock: false,
  };

  private readonly onChangeAntiAdblock = () => {
    this.setState((prevState) => ({ antiAdblock: !prevState.antiAdblock }));
  };

  private readonly onFocus = () => {
    const { showSnackbar } = this.props;
    showSnackbar({
      message: SnackbarMessage.COPY_TEXT,
      severity: AlertSeverity.SUCCESS,
    });
  };

  private readonly setActiveTab = (_: unknown, value: number) => {
    this.setState({ activeTab: value, antiAdblock: false });
  };

  render() {
    const {
      codes,
      ad_unit,
      custom_domain,
      php_code_link,
      placement_id,
      site_id,
      tags,
      postback_url,
    } = this.props;
    const { antiAdblock, activeTab } = this.state;

    if (codes.length > 0) {
      const sortedCodes = [...codes];

      if (ad_unit === AdUnitEnum.POPUNDER) {
        sortedCodes.sort((a, b) => {
          return b.type - a.type;
        });
      } else if (ad_unit === AdUnitEnum.VAST) {
        const elem = sortedCodes.shift();

        if (elem !== undefined) {
          sortedCodes.push(elem);
        }
      }

      return (
        <>
          <Tabs value={activeTab} onChange={this.setActiveTab}>
            {codes.map((item, key) => {
              const isSuitableForAntiAdblockTitle =
                [
                  CodeTypeEnum.JS_SYNC_PU,
                  CodeTypeEnum.INLINE_PU_LAYER,
                  CodeTypeEnum.INLINE_PU_NEW,
                ].includes(item.type) && custom_domain;

              let tabTitle = getCodeTypeTitle(item.type);

              if (ad_unit === AdUnitEnum.POPUNDER) {
                tabTitle = isSuitableForAntiAdblockTitle
                  ? getPopunderAntiAdBlockTitle(item.type)
                  : getPopunderCodeTitle(item.type);
              }

              const isRecommended = [
                CodeTypeEnum.INLINE_PU_LAYER,
                CodeTypeEnum.INLINE_PU_NEW,
              ].includes(item.type);

              return (
                <Tab
                  label={
                    isRecommended ? <TabTitle text={tabTitle} /> : tabTitle
                  }
                  key={tabTitle}
                  value={key}
                />
              );
            })}
          </Tabs>
          {codes.map((item, key) => {
            /* HD-27348     https    ( IBT) */
            let code = '';

            if (placement_id === 16448832 && activeTab === 0) {
              code = customCode;
            } else if (placement_id === 17465618 && activeTab === 0) {
              code = customCode2;
            } else {
              code = prepare(item.code, antiAdblock);
            }

            return (
              key === activeTab && (
                <Styled.GridContainer container key={item.type}>
                  <Styled.GridItem item xs={6}>
                    <CodesDescription
                      adunit={ad_unit}
                      code_type={item.type}
                      php_code_link={php_code_link}
                      placement_id={placement_id}
                      site_id={site_id}
                      antiAdblock={antiAdblock}
                      custom_domain={custom_domain}
                      tags={tags}
                      postback_url={postback_url}
                      changeUseAa={this.onChangeAntiAdblock}
                    />
                  </Styled.GridItem>
                  <Styled.GridItem item xs={6}>
                    <CopyToClipboard text={code}>
                      <Styled.Textarea
                        className="get-code-textarea"
                        id={key.toString()}
                        maxRows={14}
                        minRows={14}
                        value={code}
                        multiline
                        onFocus={this.onFocus}
                        dataTestId="get-code-input"
                        variant="filled"
                      />
                    </CopyToClipboard>
                  </Styled.GridItem>
                </Styled.GridContainer>
              )
            );
          })}
        </>
      );
    }

    const noCodesText =
      ad_unit === AdUnitEnum.WEB_PUSH_ADS
        ? 'Placement with this ad unit does not require any invocation code.'
        : 'Your placement code is not available at the moment, please, contact your personal manager to enable the code.';

    return <Styled.Paragraph>{noCodesText}</Styled.Paragraph>;
  }
}
