import { withRouter } from 'react-router-dom';
import type { RouteComponentProps } from 'react-router-dom';
import { PATHS } from 'routes';
import image404 from 'assets/images/404.png';
import { ContentWrapper } from 'old-component';
import { Link } from '@development/ui-kit';
import * as Styled from './styles';

export const NotFoundComponent = (props: RouteComponentProps) => {
  const { history } = props;

  return (
    <ContentWrapper>
      <Styled.Container data-testid="not-found">
        <img src={image404} alt="" />
        <p>We are sorry but the page you are looking for cannot be found.</p>
        <p>
          Try again, go to our{' '}
          <Link
            dataTestId="not-found__goto-dashboard"
            text="Dashboard"
            color="primary"
            onClick={() => history.push(PATHS.DASHBOARD)}
          />{' '}
          or{' '}
          <Link
            dataTestId="not-found__goto-tickets"
            text="Support"
            color="primary"
            onClick={() => history.push(PATHS.TICKETS)}
          />
        </p>
      </Styled.Container>
    </ContentWrapper>
  );
};

export const NotFound = withRouter(NotFoundComponent);
