import { ExpandMore } from '@mui/icons-material';
import { ButtonBase } from '@mui/material';
import styled, { css } from 'styled-components';
import { themeColors } from 'styles/theme/constants';
import { COLLAPSED_WIDTH, ITEM_HEIGHT, WIDTH } from './constants';
import type {
  CollapseIconProps,
  ExpandIconProps,
  ItemBodyProps,
  ItemProps,
  MenuSubItemsProps,
  SidebarProps,
} from './types';

const itemStyles = css`
  opacity: 0.5;
  font-size: ${({ theme }) => theme.typography.body2.fontSize};
  line-height: ${({ theme }) => theme.typography.body2.lineHeight};
  font-weight: ${({ theme }) => theme.typography.fontWeightMedium};
  padding: 1px 10px !important;
  height: ${ITEM_HEIGHT}px;
  transition: transform 0.5s ease;
  width: 100%;
  display: flex !important;
  flex-direction: row;
  align-items: center !important;
  justify-content: flex-start !important;
`;

const menuTitleStyles = css`
  font-size: ${({ theme }) => theme.typography.caption.fontSize};
  text-transform: uppercase;
  padding: 7px 16px 7px 20px !important;
  justify-content: space-between !important;
  opacity: 0.3;
  height: 40px;
`;

const clickableStyles = css`
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
`;

const activeStyles = css`
  opacity: 1;
  fill: ${({ theme }) => theme.palette.secondary.main};
`;

export const Sidebar = styled.nav<SidebarProps>`
  background: #303030;
  bottom: 0;
  color: ${themeColors.textContrast};
  fill: ${themeColors.textContrast};
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  top: 0;
  width: ${({ $isCollapsed }) => ($isCollapsed ? COLLAPSED_WIDTH : WIDTH)}px;
  z-index: 10;
  transition: width 0.5s ease;
  white-space: nowrap;
  display: flex;
  flex-direction: column;
`;

export const MenuTitle = styled.div`
  ${itemStyles}
  ${menuTitleStyles}
`;

export const MenuTitleText = styled.span`
  padding-left: 4px;
`;

export const Item = styled(ButtonBase)<ItemProps>`
  ${itemStyles}
  ${({ $isActive }) => $isActive && activeStyles}
  ${({ $isClickable }) => $isClickable && clickableStyles}
`;

export const ItemTitle = styled.div`
  flex-grow: 1;
  margin-left: 15px;
  text-align: left;
`;

export const ItemTopBorder = styled.div`
  border-top: solid 1px #484747;
`;

export const ItemPadding = styled.div`
  height: 20px;
`;

export const ItemBody = styled.div<ItemBodyProps>`
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  height: 100%;
  padding: 14px 6px 14px 13px;

  > svg:first-child {
    fill: ${themeColors.textContrast};
    color: ${themeColors.textContrast};
  }

  ${({ $isActive, $isNested }) =>
    $isActive &&
    !$isNested &&
    css`
      background: rgba(255, 255, 255, 0.1);
      border-radius: 4px;
    `}

  ${({ $isActive }) =>
    $isActive &&
    css`
      > span:first-child {
        color: ${({ theme }) => theme.palette.secondary.main};
      }

      > svg:first-child {
        fill: ${({ theme }) => theme.palette.secondary.main};
        color: ${({ theme }) => theme.palette.secondary.main};
      }
    `}
`;

export const NestedIcon = styled.svg`
  color: ${themeColors.textContrast};
  fill: ${themeColors.textContrast};
  margin: ${({ theme }) => theme.spacing(1)};
`;

export const ExpandIcon = styled(ExpandMore)<ExpandIconProps>`
  ${({ $isExpanded }) =>
    $isExpanded &&
    css`
      transform: rotate(180deg);
    `}
`;

export const MenuSubItems = styled.div<MenuSubItemsProps>`
  transition: height 0.5s ease, opacity 0.5s ease;
  overflow: hidden;
  height: ${({ $isCollapsed, itemCount }) =>
    $isCollapsed ? 0 : ITEM_HEIGHT * itemCount}px;
  opacity: ${({ $isCollapsed }) => ($isCollapsed ? 0 : 1)};
`;

export const CollapseButton = styled(ButtonBase)`
  ${itemStyles}
  ${menuTitleStyles}
  ${clickableStyles};
  margin-top: ${({ theme }) => theme.spacing(1)} !important;
`;

export const CollapseIcon = styled.span<CollapseIconProps>`
  width: 24px;
  padding: 0 2px;
  margin-left: 2px;
  transition: transform 0.5s ease;

  ${({ $isCollapsed }) =>
    $isCollapsed &&
    css`
      transform: scale(-1, 1);
    `}
`;

export const Logo = styled.a`
  border-bottom: 1px solid #484747;
  color: ${themeColors.textContrast};
  width: 100%;
  height: 70px;
  min-height: 70px;
  display: flex;
  justify-content: center;
`;

export const LogoImage = styled.img`
  width: 34px;
`;

export const LogoText = styled.img`
  width: 108px;
  margin-left: 12px;
`;

export const FooterWrapper = styled.div`
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
`;

export const Footer = styled.div`
  border-top: solid 1px #484747;
  font-size: ${({ theme }) => theme.typography.body2.fontSize};
  line-height: ${({ theme }) => theme.typography.body2.lineHeight};
  font-weight: ${({ theme }) => theme.typography.fontWeightMedium};
  padding: 14px 0px 16px 22px;
  opacity: 0.5;
`;

export const FooterCopyright = styled.div`
  font-size: ${({ theme }) => theme.typography.caption.fontSize};
  line-height: 22px;
  font-weight: ${({ theme }) => theme.typography.caption.fontWeight};
`;

export const FooterTerms = styled.a`
  font-size: ${({ theme }) => theme.typography.caption.fontSize};
  line-height: 22px;
  font-weight: ${({ theme }) => theme.typography.caption.fontWeight};
  text-decoration: dotted underline;
  color: inherit;
`;
