export enum ConversionTypeEnum {
  CPI = 1,
  CPA = 2,
  CPL = 3,
  REVSHARE = 4,
}

export const getAllConversionTypeItems = () => [
  { id: ConversionTypeEnum.CPI, name: 'CPI' },
  { id: ConversionTypeEnum.CPA, name: 'CPA' },
  { id: ConversionTypeEnum.CPL, name: 'CPL' },
  { id: ConversionTypeEnum.REVSHARE, name: 'Revshare' },
];

export const getConversionTypeTitleById = (id: ConversionTypeEnum) => {
  const conversionType = getAllConversionTypeItems().find(
    (type) => type.id === id,
  );

  if (conversionType == null) {
    throw new Error(`Unknown conversion type id: ${id}`);
  }

  return conversionType.name;
};
