import type { DataGridTableProps } from '@development/ui-kit';
import type { ArrElement } from 'types/utils';
import { ColumnNamesForPaymentHistory } from './column-names';

const columnsForSummary = [
  ColumnNamesForPaymentHistory.DATE,
  ColumnNamesForPaymentHistory.PAYMENT_AMOUNT,
  ColumnNamesForPaymentHistory.FEE_AMOUNT,
];

export const getSummaryColumns = () =>
  columnsForSummary.map((columnForSummary) => {
    const columnProps: ArrElement<DataGridTableProps['summaryColumns']> = {
      showInColumn: columnForSummary,
    };

    if (
      [
        ColumnNamesForPaymentHistory.PAYMENT_AMOUNT,
        ColumnNamesForPaymentHistory.FEE_AMOUNT,
      ].includes(columnForSummary)
    ) {
      columnProps.name = columnForSummary;
      columnProps.displayFormat = '{0}';
      columnProps.summaryType = 'custom';
      columnProps.valueFormat = '$,##0.##';
    }

    if (columnForSummary === ColumnNamesForPaymentHistory.DATE) {
      columnProps.displayFormat = 'Total paid';
    }

    return columnProps;
  });
