import { Grid } from '@mui/material';
import { Button } from '@development/ui-kit';
import * as Styled from './styles';
import type { CardsProps } from './types';
import { cards } from './constants';

export const Cards = ({ blocks, login }: CardsProps) => {
  return (
    <Grid container spacing={3}>
      {blocks.map((entity) => {
        const { image, header, description, signupLink, loginLink } =
          cards[entity];

        const signUpData = {
          href: signupLink,
          text: 'Sign up',
          dataTestIdButton: `signup__link-signup-${entity}`,
          dataTestIdLink: `signup__link-login-${entity}`,
        };

        const logInData = {
          href: loginLink,
          text: 'Log in',
          dataTestIdButton: `signup__link-login-${entity}`,
          dataTestIdLink: `signup__link-signup-${entity}`,
        };

        const controlsData = login
          ? {
              button: { ...logInData },
              link: { ...signUpData },
            }
          : {
              button: { ...signUpData },
              link: { ...logInData },
            };
        const cardNote = `${login ? "Don't" : 'Already'} have an account?`;

        return (
          <Grid item xs={12} sm={6} key={entity}>
            <Styled.PaperWrap>
              <Styled.Image src={image} alt={entity} />
              <Styled.CardTitle variant="subtitle2">{header}</Styled.CardTitle>
              <Styled.CardDescription variant="body2">
                {description}
              </Styled.CardDescription>
              <Button
                dataTestId={controlsData.button.dataTestIdButton}
                href={controlsData.button.href}
                variant="contained"
                color="tertiary"
              >
                {controlsData.button.text}
              </Button>
              <Styled.CardNote variant="caption">{cardNote}</Styled.CardNote>
              <Styled.Link
                href={controlsData.link.href}
                data-testid={controlsData.button.dataTestIdLink}
              >
                {controlsData.link.text}
              </Styled.Link>
            </Styled.PaperWrap>
          </Grid>
        );
      })}
    </Grid>
  );
};
