import {
  Dialog,
  DialogActions,
  DialogTitle,
  LinearProgress,
} from '@material-ui/core';
import { Button } from '@development/ui-kit';

import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';

import type { DialogWrapProps, DialogProps } from './types';

import * as Styled from './styles';

export { DialogProps };

export const DialogWrap = ({
  children,
  data,
  step,
  hideDialog,
  error,
}: DialogWrapProps) => {
  const isLoading = step === DialogStepsEnum.LOADING;
  const actions = (() => {
    if (step === DialogStepsEnum.FINISHED) {
      return (
        <Button
          dataTestId="dialog-wrap__got-it-button"
          variant="outlined"
          onClick={() => hideDialog()}
        >
          Got it
        </Button>
      );
    }

    return (
      <>
        <Button
          dataTestId="dialog-wrap__cancel-button"
          disabled={isLoading}
          variant="outlined"
          onClick={() => hideDialog()}
        >
          Cancel
        </Button>
        <Button
          dataTestId="dialog-wrap__submit-button"
          disabled={isLoading || error}
          variant="contained"
          onClick={data.onClick}
        >
          {data.submit || 'Add'}
        </Button>
      </>
    );
  })();

  return (
    <Dialog
      maxWidth="md"
      open={step !== DialogStepsEnum.WAIT}
      scroll="body"
      onClose={() => hideDialog()}
    >
      <DialogTitle>{data.title}</DialogTitle>
      <Styled.DialogContentStyled
        width={
          step === DialogStepsEnum.FINISHED
            ? 392
            : data.width?.toString().replace('px', '')
        }
      >
        {isLoading ? <LinearProgress /> : <Styled.DialogStylesEmpty />}
        {children}
      </Styled.DialogContentStyled>
      <DialogActions>{actions}</DialogActions>
    </Dialog>
  );
};
