import { Component, createRef } from 'react';
import { findDOMNode } from 'react-dom';

import { Popover } from '@material-ui/core';

import type { ColorResult, CustomPickerProps } from 'react-color';
import { CustomPicker } from 'react-color';
import { Hue, Saturation } from 'react-color/lib/components/common';

import type { ChangeEvent } from 'react';

import * as Styled from './styles';

import type { Props, State } from './types';

const isHex = (maybeHex: string): boolean =>
  !Number.isNaN(parseInt(maybeHex, 16));

const isColor = (maybeColor: string): boolean => {
  const maybeColorDigits = maybeColor.substring(1);

  if (maybeColorDigits.length === 3) {
    return maybeColorDigits
      .split('')
      .map((item) => item.repeat(2))
      .every(isHex);
  }

  if (maybeColorDigits.length === 6) {
    const chunks = maybeColorDigits.match(/(.{2})/g);

    return !!chunks && chunks.every(isHex);
  }

  return false;
};

const Picker = CustomPicker(function CustomColorPicker(
  props: CustomPickerProps<any>,
) {
  /* eslint-disable react/jsx-props-no-spreading */
  return (
    <Styled.Picker>
      <Styled.PickerSaturation>
        <Saturation {...props} />
      </Styled.PickerSaturation>
      <Styled.PickerHue>
        <Hue {...props} />
      </Styled.PickerHue>
    </Styled.Picker>
  );
  /* eslint-enable react/jsx-props-no-spreading */
});

export class ColorPicker extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      open: false,
      visible: false,
    };
  }

  private readonly onBlur = () => {
    const { value, onChange } = this.props;

    if (value !== '' && !isColor(value)) {
      onChange('');
    }
  };

  private readonly onChange = (event: ChangeEvent<{ value: unknown }>) => {
    const value = event.target.value as string;
    const { onChange } = this.props;

    if (
      value === '' ||
      (value.startsWith('#') &&
        value.length <= 7 &&
        value.replace('#', '').split('').every(isHex))
    ) {
      onChange(value);
    }
  };

  private readonly onFocus = () => {
    const { value, onChange } = this.props;

    if (!value) {
      onChange('#');
    }
  };

  private readonly onOpen = () => {
    const { disabled } = this.props;
    const { open, visible } = this.state;

    if (!disabled && !open && !visible) {
      this.setState({ open: true }, () => {
        setTimeout(() => {
          this.setState({ visible: true }, () => {
            document.addEventListener('click', this.onOuterClick);
          });
        }, 20);
      });
    }
  };

  private readonly onOuterClick = ({ target }: any) => {
    const { open, visible } = this.state;

    if (open && visible && this.picker.current) {
      // eslint-disable-next-line react/no-find-dom-node
      const picker = findDOMNode(this.picker.current);

      if (picker && !picker.contains(target)) {
        document.removeEventListener('click', this.onOuterClick);
        this.setState({ open: false, visible: false });
      }
    }
  };

  private readonly onPickColor = (color?: ColorResult) => {
    const { onChange } = this.props;

    if (color) {
      onChange(color.hex);
    }
  };

  private readonly container: any = createRef();

  private readonly picker: any = createRef();

  render() {
    const { disabled, label, placeholder, value } = this.props;
    const { open: toOpenPopover } = this.state;

    return (
      <Styled.Container onClick={this.onOpen} ref={this.container}>
        <Styled.Input
          dataTestId="picker-input"
          disabled={disabled}
          label={label}
          value={value || placeholder}
          onBlur={this.onBlur}
          onChange={this.onChange}
          onFocus={this.onFocus}
        />

        {value && value !== '#' && <Styled.Swatch backgroundColor={value} />}

        <Popover anchorEl={this.container.current} open={toOpenPopover}>
          <Picker color={value} ref={this.picker} onChange={this.onPickColor} />
        </Popover>
      </Styled.Container>
    );
  }
}
