import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

export const getDialogsData = (
  addSite: Function,
  editPlacement: Function,
  dialogId: number,
  title = 'website',
) => ({
  [WebsitesDialogTypesEnum.EMPTY]: {
    onClick: () => {},
    title: '',
    width: 'auto',
  },
  [WebsitesDialogTypesEnum.ADD_AD_UNIT]: {
    onClick: addSite,
    title: `Add new ${title}`,
    width: 'auto',
  },
  [WebsitesDialogTypesEnum.EDIT_PLACEMENT]: {
    onClick: () => editPlacement('dialog', dialogId),
    title: 'Edit placement',
    width: '512px',
  },
});

export const getAlertsData = (dialogs: DialogsData) => ({
  [WebsitesDialogTypesEnum.EMPTY]: {
    title: '',
  },
  [WebsitesDialogTypesEnum.ADD_AD_UNIT]: {
    title: dialogs[WebsitesDialogTypesEnum.ADD_AD_UNIT]?.title,
  },
});

export type DialogsData = Partial<ReturnType<typeof getDialogsData>>;
