import { Component } from 'react';
import { connect } from 'react-redux';
import {
  Grid,
  InputLabel,
  MenuItem,
  Paper,
  Switch,
  Table,
  TableBody,
  InputAdornment,
} from '@material-ui/core';

import { Button, IconButton, TextField } from '@development/ui-kit';
import {
  AddToQueue,
  Clear,
  GetApp,
  Refresh,
  Search,
  Settings,
  TrendingUp,
} from '@mui/icons-material';

import { Pagination } from 'components/pagination';
import { IdCellContent, WebsiteMenuButton } from 'features/websites';
import { v2 } from 'constants/index';
import { AdUnitEnum } from 'old-enums/ad-units';
import {
  getSiteDeclineReasonTitle,
  SiteDeclineReasonEnum,
} from 'old-enums/site-decline-reason';
import { getStatusTitles, SiteStatusEnum } from 'old-enums/site-status';
import { VisibilityFilterEnum } from 'old-enums/visibility-filter';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';
import { WebsitesLoadingTypesEnum } from 'old-enums/websites-loading-types-enum';
import {
  getPlacementListExtended,
  getSiteList,
  resubmitSite,
  setFilterHidden,
  showDialog,
  unhideSite,
  setDomainSearch,
} from 'old-reducer/site-page';

import type { ChangeEvent } from 'react';
import type { PendingRequest } from 'types/pending-request';
import type { AppState } from 'store/types';
import type { Site } from 'types/site';

import { setCookie } from 'utils/cookie';
import { BlockLoader } from 'old-component/block-loader';
import { ActionBlock } from 'old-component';
import PlacementList from '../placements-list';
import * as Styled from './styles';

interface SiteDispatchers {
  getPlacementListExtended(id: number): void;
  getSiteList(page?: number): void;
  reSubmitSite(id: number): void;
  setFilterHidden(value: VisibilityFilterEnum): void;
  showDialog(
    action: WebsitesDialogTypesEnum,
    site?: Site | PendingRequest,
  ): void;
  unHideSite(id: number): void;
  setDomainSearch(domain_search: string): void;
}

interface WidgetCounter {
  sitePage: AppState['sitePage'];
}

class SiteListComponent extends Component<
  AppState['sitePage'] & SiteDispatchers
> {
  private static checkActiveSitesAndPlacements(sitesInfo: Site[]) {
    const requiredAdUnits = [
      AdUnitEnum.POPUNDER,
      AdUnitEnum.DIRECT_LINK,
      AdUnitEnum.SOCIAL_BAR,
    ];
    const approvedSites = sitesInfo.filter(
      ({ status }) => status === SiteStatusEnum.APPROVED,
    );
    const isThereSomeRequiredAdUnit = (adUnits: Site['ad_units']) =>
      requiredAdUnits.some((requiredAdUnit) =>
        adUnits.includes(requiredAdUnit),
      );

    return approvedSites.some(({ ad_units }) =>
      isThereSomeRequiredAdUnit(ad_units),
    );
  }

  private static goToStats(zoneId: number) {
    const maxAge = 15 * 60 * 1000; // 15 min

    setCookie('zone', zoneId, { maxAge });

    window.location.assign(`${v2}/stats`);
  }

  componentDidMount() {
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { getSiteList } = this.props;
    /* eslint-enable no-shadow */

    getSiteList();
  }

  private onSiteFilterHidden(value: VisibilityFilterEnum) {
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { getSiteList, setFilterHidden } = this.props;
    /* eslint-enable no-shadow */

    setFilterHidden(value);
    getSiteList(1);
  }

  private onDomainChange(search: string) {
    const { setDomainSearch: doSetDomainSearch } = this.props;
    doSetDomainSearch(search);
  }

  private onDomainSearch() {
    const { getSiteList: doGetSiteList } = this.props;
    doGetSiteList(1);
  }

  private onDomainReset() {
    const { setDomainSearch: doSetDomainSearch, getSiteList: doGetSiteList } =
      this.props;
    doSetDomainSearch('');
    doGetSiteList(1);
  }

  private onSiteHideStats(site: Site, checked: boolean) {
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { showDialog, unHideSite } = this.props;
    /* eslint-enable no-shadow */

    if (checked) {
      showDialog(WebsitesDialogTypesEnum.HIDE_CONFIRM, site);
    } else {
      unHideSite(site.id);
    }
  }

  private renderFilter = () => {
    const {
      list: { b_hidden, domain_search, last_domain_search },
    } = this.props;
    const canReset = last_domain_search !== '';
    const showClear =
      domain_search === last_domain_search && domain_search !== '';

    const handleChange = (event: ChangeEvent<{ value: unknown }>) => {
      this.onSiteFilterHidden(event.target.value as VisibilityFilterEnum);
    };

    const handleDomainChange = (event: ChangeEvent<HTMLInputElement>) => {
      this.onDomainChange(event.target.value);
    };

    const inputIcon = showClear ? (
      <Styled.DomainSearchIconButton
        onClick={() => this.onDomainChange('')}
        dataTestId="sites-list__domain-clear-button"
      >
        <Clear />
      </Styled.DomainSearchIconButton>
    ) : (
      <Styled.DomainSearchIconButton
        onClick={() => this.onDomainSearch()}
        dataTestId="domain-search-button"
      >
        <Search />
      </Styled.DomainSearchIconButton>
    );

    const resetBtn = canReset && (
      <Styled.FilterFormControl>
        <Styled.ResetButtonContainer>
          <Button
            onClick={() => this.onDomainReset()}
            startIcon={<Refresh />}
            dataTestId="domain-reset-button"
          >
            Reset
          </Button>
        </Styled.ResetButtonContainer>
      </Styled.FilterFormControl>
    );

    return (
      <Grid item xs>
        <Styled.FilterFormControl>
          <TextField
            id="search-input"
            value={domain_search}
            dataTestId="domain-search-input-wrapper"
            variant="standard"
            placeholder="Find your websites"
            onInput={handleDomainChange}
            label="Search"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">{inputIcon}</InputAdornment>
              ),
            }}
            // eslint-disable-next-line react/jsx-no-duplicate-props
            inputProps={{
              'data-testid': 'domain-search-input',
            }}
            InputLabelProps={{
              shrink: true,
            }}
          />
        </Styled.FilterFormControl>
        {resetBtn}
        <Styled.FilterFormControl>
          <InputLabel shrink id="visibility-label-filter">
            Visibility
          </InputLabel>
          <Styled.VisibilityFilter
            autoWidth={false}
            data-testid="websites-visibility"
            labelId="visibility-label-filter"
            value={b_hidden ?? VisibilityFilterEnum.ALL}
            onChange={handleChange}
          >
            <MenuItem value="all">All</MenuItem>
            <MenuItem value="true">Hidden</MenuItem>
            <MenuItem value="false">Visible</MenuItem>
          </Styled.VisibilityFilter>
        </Styled.FilterFormControl>
      </Grid>
    );
  };

  private renderExportCodesButton = () => {
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { showDialog } = this.props;
    /* eslint-enable no-shadow */

    return (
      <Styled.ExportCodesButton
        dataTestId="websites-export-codes"
        startIcon={<GetApp />}
        variant="outlined"
        onClick={() => showDialog(WebsitesDialogTypesEnum.EXPORT_CODES)}
      >
        Export codes
      </Styled.ExportCodesButton>
    );
  };

  private renderAddNewWebsiteButton = () => {
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { showDialog } = this.props;
    /* eslint-enable no-shadow */

    return (
      <Button
        className="add_new"
        dataTestId="websites-add-new-website"
        variant="contained"
        onClick={() => showDialog(WebsitesDialogTypesEnum.ADD_SITE)}
      >
        Add website
      </Button>
    );
  };

  private renderFilterButtonsContainer = () => {
    const {
      list: { sites },
    } = this.props;

    return (
      <Grid item xs>
        <Styled.FilterButtonsGrid container>
          {SiteListComponent.checkActiveSitesAndPlacements(sites) &&
            this.renderExportCodesButton()}
          {this.renderAddNewWebsiteButton()}
        </Styled.FilterButtonsGrid>
      </Grid>
    );
  };

  private renderResultsCount = () => {
    const {
      list: { total, last_domain_search },
    } = this.props;

    //    .
    if (!last_domain_search) {
      return null;
    }

    return (
      <Grid item xs>
        <Styled.ResultsCount>
          <Styled.ResultsCountLabel>Total number:</Styled.ResultsCountLabel>{' '}
          {total}
        </Styled.ResultsCount>
      </Grid>
    );
  };

  private renderPendingSiteRow = (site: Site) => {
    const {
      action: {
        data: { id: currentSiteId },
      },
    } = this.props;
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { showDialog } = this.props;
    /* eslint-enable no-shadow */

    return (
      <Styled.Container key={site.id}>
        <Table data-testid={`sites-list__item-${site.id}`}>
          <TableBody>
            <Styled.WebsiteTableRow>
              <Styled.CollapserCell />
              <Styled.IdCell />
              <Styled.DomainCell>{site.domain}</Styled.DomainCell>
              <Styled.StatusCell $status={site.status}>
                {getStatusTitles(site.status)}
              </Styled.StatusCell>
              <Styled.AddCodeCell>
                <Button
                  dataTestId="sites-list__add-code-button"
                  startIcon={<AddToQueue />}
                  disabled
                >
                  Add code
                </Button>
              </Styled.AddCodeCell>
              <Styled.StatisticsCell>
                <Button
                  dataTestId="sites-list__statistics-button"
                  startIcon={<TrendingUp />}
                  disabled
                >
                  Statistics
                </Button>
              </Styled.StatisticsCell>
              <Styled.EditCell>
                <Button
                  dataTestId="sites-list__view-button"
                  onClick={() =>
                    showDialog(WebsitesDialogTypesEnum.VIEW_SITE, site)
                  }
                  startIcon={<Settings />}
                  disabled={currentSiteId === site.id}
                >
                  About
                </Button>
              </Styled.EditCell>
              <Styled.HideStatisticsCell />
              <Styled.MenuCell />
            </Styled.WebsiteTableRow>
          </TableBody>
        </Table>
      </Styled.Container>
    );
  };

  private renderDeclinedSiteRow = (site: Site) => {
    const {
      action: {
        data: { id: currentSiteId },
      },
    } = this.props;
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { reSubmitSite } = this.props;
    /* eslint-enable no-shadow */

    const declineReason =
      site.n_decline_reason_type !== SiteDeclineReasonEnum.OTHER
        ? getSiteDeclineReasonTitle(site.n_decline_reason_type)
        : site.t_decline_reason_description;

    return (
      <Styled.Container key={site.id}>
        <Table data-testid={`sites-list__item-${site.id}`}>
          <TableBody>
            <Styled.WebsiteTableRow>
              <Styled.CollapserCell />
              <Styled.IdCell />
              <Styled.DomainCell>{site.domain}</Styled.DomainCell>
              <Styled.StatusCell $status={site.status}>
                {getStatusTitles(site.status)}
              </Styled.StatusCell>
              <Styled.ReSubmitCell>
                <Button
                  dataTestId="sites-list__resubmit-button"
                  onClick={() => reSubmitSite(site.id)}
                  startIcon={
                    <Styled.DeclinedStatusIcon
                      $isLoading={currentSiteId === site.id}
                    />
                  }
                  disabled={currentSiteId === site.id}
                >
                  Re-submit
                </Button>
              </Styled.ReSubmitCell>
              <Styled.ReasonCell colSpan={3}>{declineReason}</Styled.ReasonCell>
            </Styled.WebsiteTableRow>
          </TableBody>
        </Table>
      </Styled.Container>
    );
  };

  private renderApprovedSiteRow = (site: Site) => {
    const arePendingAdunits = (site.pending_request as PendingRequest).id > 0;
    const {
      list: { expand_site_id },
      action: {
        data: { id: currentSiteId },
        action,
      },
      loading,
    } = this.props;
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { getPlacementListExtended, showDialog } = this.props;
    /* eslint-enable no-shadow */

    const isExpanded = expand_site_id === site.id;
    const isLoadingPlacements =
      isExpanded && loading === WebsitesLoadingTypesEnum.GET_PLACEMENT_LIST;

    const handleHideStatsSwitch = (event: ChangeEvent<HTMLInputElement>) => {
      this.onSiteHideStats(site, event.target.checked);
    };

    const ArrowIcon = () => {
      if (isExpanded) {
        return <Styled.ExpandedIcon $isLoading={isLoadingPlacements} />;
      }

      return <Styled.CollapsedIcon $isLoading={isLoadingPlacements} />;
    };

    const isSiteHidden = String(site.b_hidden) === VisibilityFilterEnum.TRUE;
    const switchLabel = isSiteHidden
      ? 'Hidden from list & stats'
      : 'Hide from list & stats';

    return (
      <Styled.Container key={site.id}>
        <Table data-testid={`sites-list__item-${site.id}`}>
          <TableBody>
            <Styled.WebsiteTableRow
              $isActive={site.id === currentSiteId}
              key={site.id}
            >
              <Styled.CollapserCell>
                <IconButton
                  dataTestId={`sites-list__show-placements-button-${site.id}`}
                  disabled={isLoadingPlacements}
                  onClick={() => getPlacementListExtended(site.id)}
                >
                  <ArrowIcon />
                </IconButton>
              </Styled.CollapserCell>
              <Styled.IdCell>
                <IdCellContent id={site.id} dataTestId="sites-list" />
              </Styled.IdCell>
              <Styled.DomainCell
                onClick={() => getPlacementListExtended(site.id)}
              >
                {site.domain}
              </Styled.DomainCell>
              <Styled.StatusCell
                $status={site.status}
                onClick={() => getPlacementListExtended(site.id)}
              >
                {getStatusTitles(site.status)}
              </Styled.StatusCell>
              <Styled.AddCodeCell>
                <Button
                  dataTestId="sites-list__add-code-button"
                  startIcon={<AddToQueue />}
                  onClick={() =>
                    showDialog(WebsitesDialogTypesEnum.ADD_AD_UNIT, site)
                  }
                  disabled={arePendingAdunits}
                >
                  Add code
                </Button>
              </Styled.AddCodeCell>
              <Styled.StatisticsCell>
                <Button
                  dataTestId="sites-list__statistics-button"
                  disabled={site.b_hidden === VisibilityFilterEnum.TRUE}
                  startIcon={<TrendingUp />}
                  onClick={() => SiteListComponent.goToStats(site.id)}
                >
                  Statistics
                </Button>
              </Styled.StatisticsCell>
              <Styled.EditCell>
                <Button
                  dataTestId="sites-list__edit-button"
                  startIcon={<Settings />}
                  onClick={() =>
                    showDialog(WebsitesDialogTypesEnum.EDIT_SITE, site)
                  }
                >
                  About
                </Button>
              </Styled.EditCell>
              <Styled.HideStatisticsCell>
                <Styled.HideStatisticsLabel
                  control={
                    <Switch
                      checked={isSiteHidden}
                      color="secondary"
                      disabled={
                        action === WebsitesDialogTypesEnum.HIDE_CONFIRM &&
                        currentSiteId === site.id
                      }
                      onChange={handleHideStatsSwitch}
                    />
                  }
                  label={switchLabel}
                />
              </Styled.HideStatisticsCell>
              <Styled.MenuCell>
                <WebsiteMenuButton id={site.id} />
              </Styled.MenuCell>
            </Styled.WebsiteTableRow>
          </TableBody>
        </Table>
        {expand_site_id === site.id &&
          loading !== WebsitesLoadingTypesEnum.GET_PLACEMENT_LIST && (
            <PlacementList />
          )}
      </Styled.Container>
    );
  };

  render() {
    const {
      list: { sites, page, pages, b_hidden, last_domain_search },
      loading,
    } = this.props;
    // TODO Eslint
    /* eslint-disable no-shadow */
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { getSiteList } = this.props;
    /* eslint-enable no-shadow */

    const listSites = sites.map((site) => {
      if (site.status === SiteStatusEnum.APPROVED) {
        return this.renderApprovedSiteRow(site);
      }

      if (site.status === SiteStatusEnum.DECLINED) {
        return this.renderDeclinedSiteRow(site);
      }

      return this.renderPendingSiteRow(site);
    });

    const renderTable = () => {
      let NoWebsitesFoundComponent = (
        <Paper>
          <Grid container>
            <Grid container item>
              {this.renderFilter()}
              {this.renderFilterButtonsContainer()}
            </Grid>
            <Grid container item xs>
              <Styled.NoWebsitesFoundText>
                No results
              </Styled.NoWebsitesFoundText>
            </Grid>
          </Grid>
        </Paper>
      );

      if (b_hidden === VisibilityFilterEnum.ALL && !last_domain_search) {
        NoWebsitesFoundComponent = (
          <ActionBlock
            actionButton={this.renderAddNewWebsiteButton()}
            text="Click the ADD WEBSITE button to submit your first website for approval"
          />
        );
      }

      return sites.length > 0 ? (
        <div>
          <Styled.Filter>
            <Grid container>
              {this.renderFilter()}
              {this.renderFilterButtonsContainer()}
            </Grid>
            {this.renderResultsCount()}
          </Styled.Filter>
          {listSites}
          {pages > 1 && (
            <Styled.PaginationContainer>
              <Pagination onTap={getSiteList} active={page} total={pages} />
            </Styled.PaginationContainer>
          )}
        </div>
      ) : (
        NoWebsitesFoundComponent
      );
    };

    return (
      <div>
        {loading === WebsitesLoadingTypesEnum.SITE_LIST ? (
          <BlockLoader dataTestId="sites-list__loader" />
        ) : (
          renderTable()
        )}
      </div>
    );
  }
}

export const SiteList = connect((state: WidgetCounter) => state.sitePage, {
  getSiteList,
  showDialog,
  setDomainSearch,
  reSubmitSite: resubmitSite,
  unHideSite: unhideSite,
  getPlacementListExtended,
  setFilterHidden,
})(SiteListComponent as any);
