const MIN_FOUR_DIGIT_VALUE = 0.0001;
const TWO_SIGNS = 2;
const FOUR_SIGNS = 4;

/**
 *    
 */
export const getFormattedPayout = (value: number) => {
  if (value < MIN_FOUR_DIGIT_VALUE) {
    return '0.00';
  }

  const stringValue = String(value);
  const [integerPart, fractionalPart = ''] = stringValue.split('.');

  if (fractionalPart.length < TWO_SIGNS) {
    return value.toFixed(TWO_SIGNS);
  }

  if (fractionalPart.length > FOUR_SIGNS) {
    const slicedFractionalPart = fractionalPart.slice(0, FOUR_SIGNS);

    return `${integerPart}.${slicedFractionalPart}`;
  }

  return stringValue;
};
