import { RadioButton, RadioButtonGroup } from '@development/ui-kit';

import { DialogWrap } from 'old-component/dialog-common/dialog-wrap';
import * as Styled from './styles';

import type { Props } from './types';

export const ExportCodesDialog = ({
  generalError,
  getCodes,
  hideDialog,
  setTypeCodes,
  step,
}: Props) => {
  const dialogWrapData = {
    onClick: getCodes,
    submit: 'Export',
    title: 'Export codes',
    width: 512,
  };

  return (
    <DialogWrap data={dialogWrapData} hideDialog={hideDialog} step={step}>
      {generalError && <Styled.ErrorText>{generalError}</Styled.ErrorText>}
      <p>Here you can download your codes</p>
      <RadioButtonGroup
        dataTestId="dialog__export-code-type"
        defaultValue="txt"
        name="exportType"
        row
        onChange={(_: unknown, value: string) => setTypeCodes(value)}
      >
        <RadioButton value="txt" label="TXT" dataTestId="radiobutton-txt" />
        <RadioButton value="csv" label="CSV" dataTestId="radiobutton-csv" />
      </RadioButtonGroup>
    </DialogWrap>
  );
};
