import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';
import { WebsitesDialogTypesEnum } from 'old-enums/websites-dialog-types-enum';

import { ApprovalDialog } from './approval-dialog';
import { EditAddAdUnitOrSiteDialog } from './edit-add-adunit-or-site-dialog';
import { ExportCodesDialog } from './export-codes-dialog';

import type { Props } from './types';

export const FormWebsite = ({
  alertsData,
  dialogData,
  dialogsData,
  getCodes,
  hideDialog,
  setPlacementTitle,
  setTypeCodes,
  siteAddData,
}: Props) => {
  const { action, step, general_error: generalError } = dialogData;

  if (action === WebsitesDialogTypesEnum.EXPORT_CODES) {
    return (
      <ExportCodesDialog
        getCodes={getCodes}
        generalError={generalError}
        hideDialog={hideDialog}
        setTypeCodes={setTypeCodes}
        step={step}
      />
    );
  }

  if (step === DialogStepsEnum.FINISHED) {
    return (
      <ApprovalDialog
        alertTitle={alertsData}
        hideDialog={hideDialog}
        step={step}
      />
    );
  }

  return (
    <EditAddAdUnitOrSiteDialog
      dialogData={dialogData}
      dialogWrapData={dialogsData}
      hideDialog={hideDialog}
      siteAddData={siteAddData}
      setPlacementTitle={setPlacementTitle}
    />
  );
};
