import { useAppSelector } from 'store/hooks';
import { Paper } from 'old-component';
import { DataGridTable } from '@development/ui-kit';
import { BlockLoader } from 'old-component/block-loader';
import { paymentColumns } from './columns';
import * as Styled from './styles';
import { getSummaryColumns } from './get-summary-columns';
import { calculatePaymentRows } from './calculate-payment-rows';

export const PaymentsHistory = () => {
  const { payments, payouts, isPaymentsLoading } = useAppSelector((state) => ({
    payments: state.paymentsPage.payments,
    payouts: state.paymentsPage.payouts,
    isPaymentsLoading: state.paymentsPage.isLoading.payments,
  }));

  const tableData = [...payouts, ...payments];

  const filterRowPlaceholderTexts = {
    betweenStartText: 'Start date',
    betweenEndText: 'Finish date',
  };

  return (
    <Paper>
      <Styled.HeaderText variant="h3">Payments history</Styled.HeaderText>
      {!isPaymentsLoading && (
        <DataGridTable
          dataSource={tableData}
          keyExpr="id"
          columnsArray={paymentColumns}
          toShowPaging
          hasMasterDetail
          repaintChangesOnly
          columnAutoWidth
          summaryColumns={getSummaryColumns()}
          showFilterRow
          calculateCustomSummary={calculatePaymentRows}
          filterRow={filterRowPlaceholderTexts}
        />
      )}
      {isPaymentsLoading && (
        <BlockLoader dataTestId="payments-history__loader" />
      )}
    </Paper>
  );
};
