import { RECAPTCHA_SITE_KEY } from './constants';

const getToken = async (action: string) => {
  if (window.grecaptcha != null) {
    try {
      return await window.grecaptcha.execute(RECAPTCHA_SITE_KEY, { action });
    } catch {
      return '';
    }
  }

  return '';
};

export const getRecaptchaToken = (action: string) => {
  if (window.grecaptcha != null) {
    return getToken(action);
  }

  return new Promise((resolve) => {
    const script = document.createElement('script');
    script.src = `//www.google.com/recaptcha/api.js?render=${RECAPTCHA_SITE_KEY}`;

    script.onload = () => {
      if (window.grecaptcha != null) {
        window.grecaptcha.ready(async () => {
          const token = await getToken(action);
          resolve(token);
        });
      }
    };
    document.head.appendChild(script);
  });
};

export const getRecaptchaTokenV2 = async () => window.grecaptcha?.getResponse();
