import { useAppSelector } from 'store/hooks';

import { selectTicketHint } from 'store/ticket-topics/selectors';
import { TooltipCard, TooltipContent } from './style';

export const TicketTypeTooltip = () => {
  const ticketHint = useAppSelector(selectTicketHint);

  if (!ticketHint) {
    return null;
  }

  return (
    <TooltipCard>
      <TooltipContent dangerouslySetInnerHTML={{ __html: ticketHint }} />
    </TooltipCard>
  );
};
