import styled from 'styled-components';
import { withTheme } from '@material-ui/core/styles';
import type { WithTheme } from '@material-ui/core/styles';
import { styleProps } from 'old-common/sign-style';

export const Wrapper = withTheme(styled.footer`
  width: 100%;
  display: flex;
  justify-content: space-between;

  & > *:first-child {
    width: calc(100vw - ${styleProps.sideBlockWidth});
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.paper};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      width: 100%;
    }
  }

  & > *:last-child {
    width: ${styleProps.sideBlockWidth};
    background-color: ${({ theme }: WithTheme) =>
      theme.palette.background.default};

    ${({ theme }: WithTheme) =>
      theme.breakpoints.down(styleProps.hideSideBlockBreakpoint)} {
      display: none;
    }
  }
`);
