import styled from 'styled-components';
import { Grid } from '@material-ui/core';
import { withTheme } from '@material-ui/core/styles';
import type { WithTheme } from '@material-ui/core/styles';
import type { GrigItemMessengerAccountProps } from './types';

export const GrigItem = withTheme(styled(Grid)`
  padding-top: ${({ theme }: WithTheme) => theme.spacing(2)}px !important;
  padding-bottom: ${({ theme }: WithTheme) => theme.spacing(2)}px !important;
`);

export const GrigItemMessengerAccount = withTheme(
  styled(Grid)<GrigItemMessengerAccountProps>(
    ({ theme, $toShow }) => `
      padding-top: ${theme.spacing(2)}px !important;
      padding-bottom: ${theme.spacing(2)}px !important;
      ${theme.breakpoints.down('xs')} {
        display: ${$toShow ? 'block' : 'none'};
      }`,
  ),
);

export const Hr = withTheme(styled.hr`
  display: none;
  width: 100vw;
  height: 1px;
  margin: ${({ theme }: WithTheme) => theme.spacing(1)}px 0;
  background-color: ${({ theme }: WithTheme) =>
    theme.palette.background.default};

  ${({ theme }) => theme.breakpoints.down('xs')} {
    display: block;
  }
`);
