import { Grid } from '@material-ui/core';
import { Button } from '@development/ui-kit';
import { TextField, Select, Password } from 'hook-form-components';
import { SignUpFormField } from 'old-enums/sign-up-form-field';
import {
  LOGIN_MAX_LENGTH,
  LOGIN_MIN_LENGTH,
  PASSWORD_MIN_LENGTH,
} from 'constants/validation';
import * as Styled from './styles';
import { TermsCheckbox } from '../terms';
import type { FormPureProps } from './types';
import { GeneralErrorGridItem } from './error';
import { Recaptcha } from './recaptcha';

export const FormPure = ({
  onSubmit,
  paySystemsList,
  messengerList,
  form,
  toShowMessengerAccount,
}: FormPureProps) => {
  const { generalError, isLoading, recaptcha, formErrors } = form;
  const loginHelper = `${LOGIN_MIN_LENGTH}-${LOGIN_MAX_LENGTH} characters, case sensitive: A-z, 0-9, _ and -`;
  const passwordHelper = `At least ${PASSWORD_MIN_LENGTH} characters, case sensitive`;

  return (
    <form>
      <Grid container spacing={5}>
        {generalError.length > 0 && (
          <GeneralErrorGridItem
            dataTestId="signup__general-error"
            message={generalError}
            severity="error"
          />
        )}
        <Styled.GrigItem item xs={12} sm={6}>
          <TextField
            name={SignUpFormField.EMAIL}
            label="E-mail"
            required
            dataTestId="signup__email"
            error={formErrors[SignUpFormField.EMAIL]}
            fullWidth
          />
        </Styled.GrigItem>
        <Styled.GrigItem item xs={12} sm={6}>
          <TextField
            name={SignUpFormField.NAME}
            label="Full Name"
            required
            dataTestId="signup__name"
            error={formErrors[SignUpFormField.NAME]}
            fullWidth
          />
        </Styled.GrigItem>
        <Styled.GrigItem item xs={12} sm={6}>
          <TextField
            name={SignUpFormField.LOGIN}
            label="Login"
            required
            helperText={loginHelper}
            dataTestId="signup__login"
            error={formErrors[SignUpFormField.LOGIN]}
            fullWidth
            toShowHelperOnFocus
          />
        </Styled.GrigItem>
        <Styled.GrigItem item xs={12} sm={6}>
          <Password
            name={SignUpFormField.PASSWORD}
            required
            helperText={passwordHelper}
            dataTestId="signup__password"
            error={formErrors[SignUpFormField.PASSWORD]}
            fullWidth
            toShowHelperOnFocus
          />
        </Styled.GrigItem>
        <Styled.Hr />
        <Styled.GrigItem item xs={12} sm={6}>
          <Select
            name={SignUpFormField.MESSENGER}
            label="Messenger"
            menuItems={messengerList}
            required
            dataTestId="signup__messenger"
            error={formErrors[SignUpFormField.MESSENGER]}
            fullWidth
            minWidth={0}
          />
        </Styled.GrigItem>
        <Styled.GrigItemMessengerAccount
          item
          xs={12}
          sm={6}
          $toShow={toShowMessengerAccount}
        >
          {toShowMessengerAccount && (
            <TextField
              name={SignUpFormField.MESSENGER_ACCOUNT}
              label="Messenger account"
              required
              dataTestId="signup__messenger-account"
              error={formErrors[SignUpFormField.MESSENGER_ACCOUNT]}
              fullWidth
            />
          )}
        </Styled.GrigItemMessengerAccount>
        <Styled.GrigItem item xs={12} sm={6}>
          <Select
            name={SignUpFormField.PAYSYSTEM_ID}
            label="Payment method"
            menuItems={paySystemsList}
            required
            dataTestId="signup__payment-method"
            error={formErrors[SignUpFormField.PAYSYSTEM_ID]}
            fullWidth
            minWidth={0}
          />
        </Styled.GrigItem>
        <Styled.GrigItem item xs={12}>
          <TermsCheckbox
            dataTestId="signup__terms"
            name={SignUpFormField.ARE_TERMS_ACCEPTED}
          />
        </Styled.GrigItem>
        {recaptcha.wasRendered && <Recaptcha />}
        <Styled.GrigItem item xs={12} sm={6}>
          <Button
            onClick={onSubmit}
            isLoading={isLoading}
            variant="contained"
            dataTestId="signup__submit"
            color="tertiary"
            fullWidth
          >
            Sign up
          </Button>
        </Styled.GrigItem>
      </Grid>
    </form>
  );
};
