import React, { useCallback, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import { useHistory } from 'react-router-dom';
import { Grid, Tabs } from '@material-ui/core';
import type { ChangeEvent } from 'react';

import {
  getOffersTabs,
  getTabTitleById,
  OffersTabEnum,
} from 'old-enums/offers-tab';
import {
  expandDescription,
  getOffersListData,
  setActiveTab,
} from 'old-reducer/offers-page';

import { Alert } from '@development/ui-kit';
import { Counter } from 'old-component/counter';
import { Tooltip } from 'old-component/tooltip';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { GoogleTagManagerEventsTypesEnum } from 'old-enums/google-tag-manager-events-type';
import { BlockLoader } from 'old-component/block-loader';
import { Description } from 'components';
import { AlertText } from 'old-enums/alert-text';
import description1 from './assets/offers-description-1.svg';
import description2 from './assets/offers-description-2.svg';
import description3 from './assets/offers-description-3.svg';
import type { OnboardingItem } from '../types';
import { Filters } from './filters';
import { OffersList } from './list';
import * as Styled from './styles';

const items: OnboardingItem[] = [
  {
    image: description1,
    title: 'Choose offer & get link',
    description:
      'Find an offer that suits your traffic and copy its direct link. You can also set up postback and backlink',
  },
  {
    image: description2,
    title: 'Send traffic',
    description: (
      <>
        Send the exact traffic as described in your offer. Track numbers in{' '}
        <b>Statistics</b> and adjust your traffic if needed
      </>
    ),
  },
  {
    image: description3,
    title: 'Get paid',
    description: (
      <>
        Check your payment method in <b>Profile</b>, and contact your manager to
        request a payout. Learn more details in <b>Payments</b>
      </>
    ),
  },
];

export const Offers = () => {
  const { activeTab, isDescriptionExpanded, isLoading } = useAppSelector(
    (state) => state.offersPage,
  );
  const { anchorId, content } = useAppSelector(
    (state) => state.offersPage.tooltip,
  );
  const { allOffersCount, myOffersCount } = useAppSelector(
    (state) => state.offersPage.list,
  );
  const hasPayments = useAppSelector((state) => state.user.data.has_payments);
  const history = useHistory();

  const dispatch = useAppDispatch();
  const tabs = getOffersTabs();

  useEffect(() => {
    dispatch(getOffersListData());
  }, [activeTab, dispatch]);

  useEffect(() => {
    if (window.location.hash === '#show-offers') {
      //         (    ,  v2  v3).
      pushToGoogleTagManager({
        event: GoogleTagManagerEventsTypesEnum.HEADER_SHOW_OFFERS,
      });
      history.replace(window.location.pathname, null);
    }
  }, [history]);

  const handleExpand = useCallback(
    (isOpen: boolean) => {
      dispatch(expandDescription(isOpen));
    },
    [dispatch],
  );

  const handleTabChange = useCallback(
    (_event: ChangeEvent<{}>, newActiveTab: OffersTabEnum) => {
      dispatch(setActiveTab(newActiveTab));
    },
    [dispatch],
  );

  const getOffersCountByTabId = (tabId: OffersTabEnum) => {
    switch (tabId) {
      case OffersTabEnum.ALL:
        return allOffersCount;
      case OffersTabEnum.MY_OFFERS:
        return myOffersCount;
      default:
        throw new Error(`Unknown offers tab id: ${tabId}`);
    }
  };

  return (
    <div className="content">
      <Grid container spacing={2} direction="column">
        {!hasPayments && (
          <Grid item>
            <Alert
              severity="warning"
              title="NOTE:"
              message={AlertText.OFFERS}
              dataTestId="offers--alert"
            />
          </Grid>
        )}

        <Grid item>
          <Description
            dataTestID="description"
            expand={handleExpand}
            items={items}
            toOpen={isDescriptionExpanded}
            title="How it works"
          />
        </Grid>
      </Grid>

      <Styled.MainContentPaper>
        <Styled.Heading>Offers</Styled.Heading>

        <Tabs
          data-testid={activeTab}
          value={activeTab}
          onChange={handleTabChange}
        >
          {tabs.map((tabId) => (
            <Styled.TabStyled
              key={tabId}
              label={
                <>
                  {getTabTitleById(tabId)}
                  <Counter count={getOffersCountByTabId(tabId)} />
                </>
              }
            />
          ))}
        </Tabs>

        {tabs.map(
          (index) =>
            activeTab === index && (
              <div key={index}>
                <Filters tabId={activeTab} />
                {isLoading ? (
                  <BlockLoader dataTestId="offers-loader" />
                ) : (
                  <OffersList tabId={activeTab} />
                )}
              </div>
            ),
        )}
      </Styled.MainContentPaper>

      <Tooltip anchorId={anchorId} content={content} />
    </div>
  );
};
