import { WebsitesDialogTypesEnum } from './websites-dialog-types-enum';

export enum WebsitesMultiformatType {
  MIN = 1,
  MIDDLE,
  MAX,
}

const newSiteGTMEventKey = {
  1: 'multitag_add_new_ws_left',
  2: 'multitag_add_new_ws_middle',
  3: 'multitag_add_new_ws_right',
};

const editPlacementGTMEventKey = {
  1: 'multitag_edit_placement_left',
  2: 'multitag_edit_placement_middle',
  3: 'multitag_edit_placement_right',
};

export const getMultitagGTMEventKey = (
  multitag: WebsitesMultiformatType,
  type: WebsitesDialogTypesEnum,
) => {
  if (
    type === WebsitesDialogTypesEnum.ADD_SITE ||
    type === WebsitesDialogTypesEnum.ADD_AD_UNIT
  ) {
    return newSiteGTMEventKey[multitag];
  }

  if (type === WebsitesDialogTypesEnum.EDIT_PLACEMENT) {
    return editPlacementGTMEventKey[multitag];
  }

  return null;
};
