export enum UserPermissionEnum {
  AD_BLOCK_STATISTICS = 1,
  WIN_NOTICE_STATISTICS = 2,
  SHOW_MANAGER_CONTACTS = 3,
  AUTO_APPROVE = 4,
  CPA_AFFILIATE_PROGRAM = 5,
  WEBMASTER_PROGRAM = 6,
  HYPERWALLET_ALLOWED = 7,
}

export const isPureAffiliate = (permissionIds: number[]) =>
  permissionIds.includes(UserPermissionEnum.CPA_AFFILIATE_PROGRAM) &&
  !permissionIds.includes(UserPermissionEnum.WEBMASTER_PROGRAM);

export const isPureWebmaster = (permissionIds: number[]) =>
  permissionIds.includes(UserPermissionEnum.WEBMASTER_PROGRAM) &&
  !permissionIds.includes(UserPermissionEnum.CPA_AFFILIATE_PROGRAM);

export const isAffiliate = (permissionIds: number[]) =>
  permissionIds.includes(UserPermissionEnum.CPA_AFFILIATE_PROGRAM);

export const isWebmaster = (permissionIds: number[]) =>
  permissionIds.includes(UserPermissionEnum.WEBMASTER_PROGRAM);

export const isSuperUser = (permissionIds: number[]) =>
  isWebmaster(permissionIds) && isAffiliate(permissionIds);

export const isHyperwalletUser = (permissionIds: number[]) =>
  permissionIds.includes(UserPermissionEnum.HYPERWALLET_ALLOWED);

export const getUserTypeTitle = (permissionIds: number[]) => {
  if (isPureAffiliate(permissionIds)) {
    return 'Affiliate';
  }

  if (isPureWebmaster(permissionIds)) {
    return 'Publisher';
  }

  return 'Publisher/Affiliate';
};
