import { Link } from 'old-component';
import { useAppSelector } from 'store/hooks';
import { isAffiliate } from 'old-enums/user-permission';

const linkText = 'Terms for Publishers';

export const TermsLink = () => {
  const { permissions } = useAppSelector((state) => ({
    permissions: state.user.data.ida_permissions,
  }));

  const termsLink = isAffiliate(permissions)
    ? 'https://adsterra.com/publishers-terms-cpa'
    : 'https://adsterra.com/publishers-terms-managed/';

  return (
    <Link
      dataTestId="payments-terms-link"
      href={termsLink}
      text={linkText}
      title={linkText}
      color="textSecondary"
      variant="caption"
      rel="noreferrer noopener"
      target="_blank"
    />
  );
};
