import * as Sentry from '@sentry/react';
import { BrowserTracing } from '@sentry/tracing';
import { CONFIG_SENTRY, APP_VERSION } from 'constants/index';

/**
 *     ( sentry.io)
 */
export const initReportError = () => {
  Sentry.init({
    dsn: CONFIG_SENTRY.DSN,
    integrations: [new BrowserTracing()],
    tracesSampleRate: CONFIG_SENTRY.TRACES_RATE,
    normalizeDepth: 7,
    environment: CONFIG_SENTRY.ENVIRONMENT,
    release: `publishers@${APP_VERSION}`,
  });
};
