import { useAppSelector } from 'store/hooks';

import type { PATHS } from 'routes';
import type { MenuBlockProps } from '../types';

import { MenuItem } from './menu-item';
import { MenuGroup } from './menu-group';
import * as Styled from '../../styles';

const getTitleDataTestid = (title: string) =>
  `sidebar-title-${title.toLowerCase().replace(/\s/, '')}`;

export const MenuBlock = (props: MenuBlockProps) => {
  const { routesMap, currentPath } = props;
  const isSidebarCollapsed = useAppSelector(
    (state) => state.sitePage.isSidebarCollapsed,
  );

  const renderMenuItem = (path: PATHS) => {
    const route = routesMap.get(path);

    if (!route) {
      return null;
    }

    if (route.meta?.isGroup) {
      return (
        <MenuGroup
          key={path}
          route={route}
          currentPath={currentPath}
          routesMap={routesMap}
        />
      );
    }

    return (
      <MenuItem
        key={path}
        path={path}
        route={route}
        currentPath={currentPath}
      />
    );
  };

  switch (props.type) {
    case 'border':
      return <Styled.ItemTopBorder />;

    case 'padding':
      return <Styled.ItemPadding />;

    case 'group-with-title':
      return (
        <>
          <Styled.MenuTitle data-testid={getTitleDataTestid(props.title)}>
            {!isSidebarCollapsed && (
              <Styled.MenuTitleText>{props.title}</Styled.MenuTitleText>
            )}
          </Styled.MenuTitle>
          {props.routes.map(renderMenuItem)}
        </>
      );

    default:
      return <>{props.routes.map(renderMenuItem)}</>;
  }
};
