import { useEffect, useState } from 'react';
import type { CellRenderProps } from '@development/ui-kit';
import { OpenInNew } from '@material-ui/icons';
import { format } from 'date-fns';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import {
  initTicketsList,
  ITEMS_PER_PAGE,
  updateTicketsList,
  updateTicketsPage,
} from 'old-reducer/tickets/thunk';
import type { TableData, TableFields } from 'old-component/filtered-list';

import { selectAllTicketTopics } from 'store/ticket-topics/selectors';
import { selectAllTicketStatuses } from 'store/ticket-statuses/selectors';

import { v2 } from '../../../constants';

import type { FieldTypesMap } from '../types';
import { TicketFields } from '../types';
import { OpenTicketLink } from './styles';
import { DateTimeCell } from './date-time-cell';
import { DepartmentCell } from './department-cell';
import { StatusCell } from './status-cell';

const useTicketsTableFields = () => {
  const { currentType, currentStatus } = useAppSelector(
    (state) => state.ticketsReducer,
  );

  const ticketTopics = useAppSelector(selectAllTicketTopics);
  const ticketStatuses = useAppSelector(selectAllTicketStatuses);

  const [localType, updateType] = useState(currentType);
  const [localStatus, updateStatus] = useState(currentStatus);

  const fields: TableFields<FieldTypesMap> = {
    [TicketFields.createdOn]: {
      caption: 'Created on',
      width: '161px',
      cellRender: (data: CellRenderProps['cellData']) => (
        <DateTimeCell datetime={data.value} />
      ),
    },
    [TicketFields.updatedOn]: {
      caption: 'Updated on',
      width: '161px',
      cellRender: (data: CellRenderProps['cellData']) => (
        <DateTimeCell datetime={data.value} />
      ),
    },
    [TicketFields.department]: {
      caption: 'Type',
      availableValues: ticketTopics,
      currentValue: localType,
      onChange: updateType,
      filterWidth: 370,
      minWidth: '75px',
      cellRender: (data: CellRenderProps['cellData']) => (
        <DepartmentCell id={data.value} />
      ),
    },
    [TicketFields.status]: {
      caption: 'Status',
      availableValues: ticketStatuses,
      currentValue: localStatus,
      onChange: updateStatus,
      filterWidth: 200,
      cellRender: (data: CellRenderProps['cellData']) => (
        <StatusCell idStatus={data.value} />
      ),
      width: '100px',
    },
    [TicketFields.subject]: {
      caption: 'Subject',
      minWidth: '150px',
    },
    [TicketFields.link]: {
      caption: '',
      width: '55px',
      cellRender: (data: CellRenderProps['cellData']) => (
        <OpenTicketLink
          href={data.value}
          target="_blank"
          data-testid={`support__open-ticket-button-${data.rowIndex}`}
        >
          <OpenInNew />
        </OpenTicketLink>
      ),
    },
  };

  const resetLocalState = () => {
    updateStatus(currentStatus);
    updateType(currentType);
  };

  return {
    fields,
    localType,
    localStatus,
    resetLocalState,
  };
};

const useTicketsData = (): TableData<FieldTypesMap>[] => {
  const { items } = useAppSelector((state) => state.ticketsReducer);

  return items.map((item, index) => ({
    ID: index,
    [TicketFields.updatedOn]: format(
      new Date(item.last_updated_on),
      'dd.MM.yyyy HH:mm',
    ),
    [TicketFields.createdOn]: format(
      new Date(item.created_on),
      'dd.MM.yyyy HH:mm',
    ),
    [TicketFields.status]: item.status,
    [TicketFields.department]: item.department,
    [TicketFields.subject]: item.title,
    [TicketFields.link]: `${v2}/ticket/${item.id}/`,
  }));
};

export const useTicketsInfo = () => {
  const dispatch = useAppDispatch();

  const { currentPage, totalItems, isLoading } = useAppSelector(
    (state) => state.ticketsReducer,
  );

  const dataSource = useTicketsData();
  const { fields, localStatus, localType, resetLocalState } =
    useTicketsTableFields();

  useEffect(() => {
    dispatch(initTicketsList());
  }, [dispatch]);

  const applyFilter = () => {
    dispatch(updateTicketsList(localStatus, localType));
  };

  const changePage = async (pageNumber: number) => {
    await dispatch(updateTicketsPage(pageNumber));
    resetLocalState();
  };

  return {
    fields,
    dataSource,
    applyFilter,
    changePage,
    totalItems,
    currentPage,
    isLoading,
    itemsPerPage: ITEMS_PER_PAGE,
  };
};
