import { Dialog, DialogActions, DialogTitle } from '@material-ui/core';
import { Button } from '@development/ui-kit';

import { DialogStepsEnum } from 'old-enums/dialog-steps-enum';
import * as Styled from './styles';

import type { AlertWrapProps } from './types';

export const Alert = ({ children, data, step, hideDialog }: AlertWrapProps) => (
  <Dialog open={step !== DialogStepsEnum.WAIT} onClose={() => hideDialog()}>
    <DialogTitle>{data.title}</DialogTitle>
    <Styled.DialogContentStyled>{children}</Styled.DialogContentStyled>
    <DialogActions>
      <Button
        dataTestId="alert-wrap__got-it-button"
        variant="outlined"
        onClick={() => hideDialog()}
      >
        Got it
      </Button>
    </DialogActions>
  </Dialog>
);
