import type { Thunk } from 'store/types';
import { AlertSeverity } from 'old-enums/alert-severity';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import type { PlainObject } from 'types/utils';
import { StatusCode } from 'old-enums/status-code';
import { getHeaders } from 'utils/http';
import { transformErrors } from 'utils/transform/transform-errors';
import { snackbarCommonError } from 'old-reducer/snackbar/constants';
import { showSnackbar } from '../snackbar';
import {
  hideUploadDialog,
  loadUploadDialog,
  setDocuments,
  setInitialStateToUploadForm,
  setPayments,
  setPayouts,
  setServerValidationErrors,
  setSignature,
  showUploadDialog,
  startDocumentsLoading,
  startPaymentsLoading,
  startPayoutsLoading,
  startSignatureLoading,
  startUploadModalLoading,
  stopDocumentsLoading,
  stopPaymentsLoading,
  stopPayoutsLoading,
  stopSignatureLoading,
  stopUploadModalLoading,
  togglePaymentDateInfo,
  updateDocuments,
} from '../payments-page';
import type { Document, Payment, PaymentsPageStore, Payout } from './types';

export const getPayments = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token: userToken },
  } = getState();

  dispatch(startPaymentsLoading());

  try {
    const response = await window.fetch('/api/payments', {
      headers: getHeaders(userToken),
    });

    if (response.status === StatusCode.SUCCESS_OK) {
      const { data } = (await response.json()) as {
        data: { items: Payment[] };
      };
      const { items }: { items: Payment[] } = data;

      dispatch(setPayments(items));
    } else if (response.status !== StatusCode.CLIENT_ERROR_NOT_FOUND) {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(stopPaymentsLoading());
  }
};

