export const uploadDocuments =
  (formData: FormData): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token: userToken },
    } = getState();

    dispatch(startUploadModalLoading());
    dispatch(loadUploadDialog());

    try {
      const response = await window.fetch('/api/documents', {
        headers: getHeaders(userToken),
        method: 'POST',
        body: formData,
      });

      if (response.status === StatusCode.CLIENT_ERROR_UNPROCESSABLE_ENTITY) {
        const { errors, message }: { errors: PlainObject; message: string } =
          await response.json();

        dispatch(showUploadDialog());
        dispatch(showSnackbar({ message }));

        const preparedErrors = transformErrors(errors);

        dispatch(setServerValidationErrors(preparedErrors));

        return;
      }

      if (
        [
          StatusCode.CLIENT_ERROR_PAYLOAD_TOO_LARGE,
          StatusCode.CLIENT_ERROR_FORBIDDEN,
          StatusCode.SERVER_ERROR_INTERNAL,
        ].includes(response.status)
      ) {
        const { message }: { message: string } = await response.json();
        dispatch(showUploadDialog());
        dispatch(showSnackbar({ message }));

        return;
      }

      const { data } = (await response.json()) as {
        data: { items: Document[] };
      };
      const { items }: { items?: Document[] } = data;

      dispatch(setDocuments(items || []));
      dispatch(hideUploadDialog());
      dispatch(setInitialStateToUploadForm());
    } catch {
      // noop
    } finally {
      dispatch(stopUploadModalLoading());
    }
  };

export const removeDocument =
  (documentId: number): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token: userToken },
    } = getState();

    dispatch(startDocumentsLoading());

    try {
      const response = await window.fetch(
        `/api/document/${documentId}/hidden-update`,
        {
          headers: getHeaders(userToken),
          method: 'PUT',
        },
      );

      if (response.status === StatusCode.SUCCESS_OK) {
        dispatch(updateDocuments(documentId));
      } else if (response.status !== StatusCode.CLIENT_ERROR_NOT_FOUND) {
        dispatch(showSnackbar(snackbarCommonError));
      }
    } catch {
      dispatch(showSnackbar(snackbarCommonError));
    } finally {
      dispatch(stopDocumentsLoading());
    }
  };

export const getSignature = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token: userToken },
  } = getState();

  dispatch(startSignatureLoading());

  try {
    const response = await window.fetch('/api/documents/user-signature', {
      headers: getHeaders(userToken),
    });

    if (response.status === StatusCode.SUCCESS_OK) {
      const { data } = (await response.json()) as {
        data: { signature: PaymentsPageStore['userSignature'] };
      };

      const { signature }: { signature: PaymentsPageStore['userSignature'] } =
        data;
      dispatch(setSignature(signature));
    } else if (response.status !== StatusCode.CLIENT_ERROR_NOT_FOUND) {
      dispatch(showSnackbar(snackbarCommonError));
    }
  } catch {
    dispatch(showSnackbar(snackbarCommonError));
  } finally {
    dispatch(stopSignatureLoading());
  }
};

export const saveSignature =
  (signature: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token: userToken },
    } = getState();
    const data = {
      signature,
    };

    try {
      const response = await window.fetch('/api/documents/user-signature', {
        headers: getHeaders(userToken),
        method: 'PUT',
        body: JSON.stringify(data),
      });

      if (response.status === StatusCode.SUCCESS_OK) {
        dispatch(setSignature(signature));
        dispatch(
          showSnackbar({
            message: SnackbarMessage.SUCCESS_SIGNATURE_SAVE,
            severity: AlertSeverity.SUCCESS,
          }),
        );
      } else if (response.status !== StatusCode.CLIENT_ERROR_NOT_FOUND) {
        dispatch(showSnackbar(snackbarCommonError));
      }
    } catch {
      dispatch(showSnackbar(snackbarCommonError));
    }
  };

