export const generateDocument =
  (link: string, documentName: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token: userToken },
    } = getState();

    try {
      const response = await window.fetch(link, {
        headers: getHeaders(userToken),
      });

      if (response.status === StatusCode.SUCCESS_OK) {
        const blobFile = await response.blob();
        const blobURL = URL.createObjectURL(blobFile);
        const linkTag = document.createElement('a');

        linkTag.href = blobURL;
        linkTag.download = `${documentName}.pdf`;

        document.body.appendChild(linkTag);

        linkTag.click();

        document.body.removeChild(linkTag);
        URL.revokeObjectURL(blobURL);
      } else if (response.status !== StatusCode.CLIENT_ERROR_NOT_FOUND) {
        dispatch(showSnackbar(snackbarCommonError));
      }
    } catch {
      dispatch(showSnackbar(snackbarCommonError));
    }
  };

export const togglePaymentDateInfoState =
  (isOpen: boolean): Thunk =>
  (dispatch) => {
    localStorage.setItem('paymentDateInfoExpanded', JSON.stringify(isOpen));
    dispatch(togglePaymentDateInfo(isOpen));
  };
