import { useAppSelector } from 'store/hooks';

import { TokenList } from './token-list';
import * as Styled from './styles';

export const InactiveTokensBlock = () => {
  const inactiveTokens = useAppSelector(
    (state) => state.apiTokenPage.inactiveTokens,
  );

  const renderInactiveTokensList = () => {
    if (inactiveTokens.length === 0) {
      return 'Nothing found';
    }

    return <TokenList tokens={inactiveTokens} />;
  };

  return (
    <Styled.Block>
      <Styled.BlockTitle variant="h3">Inactive tokens</Styled.BlockTitle>
      <Styled.BlockContent>{renderInactiveTokensList()}</Styled.BlockContent>
    </Styled.Block>
  );
};
