import {
  setLoadingState,
  setPinnedPosts,
  setPosts,
} from 'old-reducer/dashboard-page';
import type { Thunk } from 'store/types';
import { showSnackbar } from 'old-reducer/snackbar';
import { snackbarCommonError } from 'old-reducer/snackbar/constants';
import { getHeaders } from 'utils/http';
import type { Post } from './types';
import type { CollectionWithoutPagination, Page } from '../types';

export const loadPage =
  (page: number): Thunk =>
  async (dispatch, getState) => {
    const {
      dashboardPage: { posts },
      user,
    } = getState();

    // total = 12, limit = 10 -> maxPage = 2
    if (page < 1 || page > Math.ceil(posts.total / posts.limit)) {
      return;
    }

    dispatch(setLoadingState(true));

    try {
      let promisedPinnedPosts;

      // sonar  prettier ,       .
      if (page === 1) {
        promisedPinnedPosts = window.fetch('/api/dashboard/posts/pinned-post', {
          headers: getHeaders(user.token),
          method: 'GET',
        });
      } else {
        promisedPinnedPosts = Promise.resolve(null);
      }

      const [pinnedPostsResponse, postsResponse] = await Promise.all([
        //       .
        promisedPinnedPosts,
        window.fetch(`/api/dashboard/posts?page=${page}`, {
          headers: getHeaders(user.token),
          method: 'GET',
        }),
      ]);

      //      204 ...
      if (
        postsResponse.status >= 300 ||
        (pinnedPostsResponse && pinnedPostsResponse.status >= 300)
      ) {
        dispatch(showSnackbar(snackbarCommonError));

        return;
      }

      let promisedNewPinnedPosts;

      /// ...   .
      if (pinnedPostsResponse && pinnedPostsResponse.status === 200) {
        promisedNewPinnedPosts = pinnedPostsResponse.json() as Promise<{
          data: CollectionWithoutPagination<Post>;
        }>;
      } else {
        promisedNewPinnedPosts = Promise.resolve({ data: { items: [] } });
      }

      let promisedNewPosts;

      if (postsResponse && postsResponse.status === 200) {
        promisedNewPosts = postsResponse.json() as Promise<{
          data: Page<Post>;
        }>;
      } else {
        promisedNewPosts = Promise.resolve({
          data: {
            page: 0,
            total: 0,
            limit: 0,
            next: false,
            items: [],
          },
        });
      }

      const [newPinnedPosts, newPosts] = await Promise.all([
        promisedNewPinnedPosts,
        promisedNewPosts,
      ]);

      dispatch(setPosts(newPosts.data));
      dispatch(setPinnedPosts(newPinnedPosts.data));
    } catch {
      dispatch(showSnackbar(snackbarCommonError));
    } finally {
      dispatch(setLoadingState(false));
    }
  };
