import { createSlice, nanoid } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { DialogStatus } from 'old-enums/dialog-status';
import type { DocumentType } from 'old-enums/document-type';
import { initialState } from './payments/state';
import type {
  Payment,
  Payout,
  Document,
  PaymentsPageStore,
} from './payments/types';
import type { PlainObject } from '../types/utils';

const {
  actions: {
    setPayments,
    setPayouts,
    setDocuments,
    updateDocuments,
    setSignature,
    startDocumentsLoading,
    stopDocumentsLoading,
    startPaymentsLoading,
    stopPaymentsLoading,
    startPayoutsLoading,
    stopPayoutsLoading,
    startSignatureLoading,
    stopSignatureLoading,
    startUploadModalLoading,
    stopUploadModalLoading,
    togglePaymentDateInfo,
    showUploadDialog,
    hideUploadDialog,
    loadUploadDialog,
    setInitialStateToUploadForm,
    setFilesErrorText,
    setTypeErrorText,
    setDocumentType,
    setUploadErrors,
    setServerValidationErrors,
  },
  reducer: paymentsPageReducer,
} = createSlice({
  name: 'paymentsPage',
  initialState,
  reducers: {
    setPayments: (state, { payload: payments }: PayloadAction<Payment[]>) => {
      state.payments = payments.sort().reverse();
    },

    setPayouts: {
      prepare: (payouts: Payout[] | []) => {
        let payloadPayouts: Payout[] | [] = [];

        if (payouts.length !== 0) {
          payloadPayouts = payouts.map((payout: Payout) => {
            return {
              ...payout,
              id: nanoid(2),
            };
          });
        }

        return { payload: { payouts: payloadPayouts } };
      },
      reducer: (
        state,
        { payload: { payouts } }: PayloadAction<{ payouts: Payout[] | [] }>,
      ) => {
        state.payouts = payouts;
      },
    },

    setDocuments: {
      prepare: (documents: Document[] | []) => {
        let payloadDocuments: Document[] | [] = [];

        if (documents.length !== 0) {
          payloadDocuments = documents.sort((a, b) => a.type - b.type);
        }

        return { payload: { documents: payloadDocuments } };
      },
      reducer: (
        state,
        {
          payload: { documents },
        }: PayloadAction<{ documents: Document[] | [] }>,
      ) => {
        state.documents = documents;
      },
    },

    updateDocuments: (
      state,
      { payload: documentId }: PayloadAction<Document['id']>,
    ) => {
      state.documents = state.documents.filter(({ id }) => id !== documentId);
    },

    setSignature: (
      state,
      { payload: signature }: PayloadAction<PaymentsPageStore['userSignature']>,
    ) => {
      state.userSignature = signature;
    },

    startPaymentsLoading: (state) => {
      state.isLoading.payments = true;
    },

    stopPaymentsLoading: (state) => {
      state.isLoading.payments = false;
    },

    startPayoutsLoading: (state) => {
      state.isLoading.payouts = true;
    },

    stopPayoutsLoading: (state) => {
      state.isLoading.payouts = false;
    },

    startDocumentsLoading: (state) => {
      state.isLoading.documents = true;
    },

    stopDocumentsLoading: (state) => {
      state.isLoading.documents = false;
    },

    startSignatureLoading: (state) => {
      state.isLoading.signature = true;
    },

    stopSignatureLoading: (state) => {
      state.isLoading.signature = false;
    },

    startUploadModalLoading: (state) => {
      state.isLoading.uploadModal = true;
    },

    stopUploadModalLoading: (state) => {
      state.isLoading.uploadModal = false;
    },

    togglePaymentDateInfo: (
      state,
      { payload: isOpen }: PayloadAction<boolean>,
    ) => {
      state.isPaymentDateInfoExpanded = isOpen;
    },

    showUploadDialog: (state) => {
      state.uploadDialogStatus = DialogStatus.SHOW;
    },

    hideUploadDialog: (state) => {
      state.uploadDialogStatus = initialState.uploadDialogStatus;
    },

    loadUploadDialog: (state) => {
      state.uploadDialogStatus = DialogStatus.LOADING;
    },

    setInitialStateToUploadForm: (state) => {
      state.documentUploadForm.documentType =
        initialState.documentUploadForm.documentType;
      state.documentUploadForm.errors.type =
        initialState.documentUploadForm.errors.type;
      state.documentUploadForm.errors.files =
        initialState.documentUploadForm.errors.files;
    },

    setFilesErrorText: (
      state,
      { payload: errorText }: PayloadAction<string>,
    ) => {
      state.documentUploadForm.errors.files = errorText;
    },

    setTypeErrorText: (
      state,
      { payload: errorText }: PayloadAction<string>,
    ) => {
      state.documentUploadForm.errors.type = errorText;
    },

    setServerValidationErrors: (
      state,
      { payload: errors }: PayloadAction<PlainObject>,
    ) => {
      Object.keys(state.documentUploadForm.errors).forEach((key) => {
        state.documentUploadForm.errors[key] = errors[key];
      });
    },

    setDocumentType: (
      state,
      { payload: documentType }: PayloadAction<DocumentType>,
    ) => {
      state.documentUploadForm.errors.type =
        initialState.documentUploadForm.errors.type;
      state.documentUploadForm.documentType = documentType;
    },

    setUploadErrors: (
      state,
      { payload: hasUploaderErrors }: PayloadAction<boolean>,
    ) => {
      state.documentUploadForm.hasUploaderErrors = hasUploaderErrors;
    },
  },
});

export {
  paymentsPageReducer,
  setPayments,
  setPayouts,
  setDocuments,
  updateDocuments,
  setSignature,
  startDocumentsLoading,
  stopDocumentsLoading,
  startPaymentsLoading,
  stopPaymentsLoading,
  startPayoutsLoading,
  stopPayoutsLoading,
  startSignatureLoading,
  stopSignatureLoading,
  startUploadModalLoading,
  stopUploadModalLoading,
  togglePaymentDateInfo,
  showUploadDialog,
  hideUploadDialog,
  loadUploadDialog,
  setInitialStateToUploadForm,
  setFilesErrorText,
  setTypeErrorText,
  setDocumentType,
  setUploadErrors,
  setServerValidationErrors,
};
