import { memo } from 'react';

import { Typography } from 'old-component';
import * as Styled from './styles';

import type { DescriptionItem } from '../types';

type Props = DescriptionItem & { width?: number };

export const Item = memo(
  ({ description, image, index, title, width }: Props) => (
    <Styled.ItemBlock>
      <Styled.Image>
        <img width={width} src={image} />
      </Styled.Image>
      <Styled.Info>
        <Styled.Title variant="subtitle1">
          {index !== undefined && (
            <Styled.ItemNumber>{index + 1}</Styled.ItemNumber>
          )}
          {title}
        </Styled.Title>
        <Typography variant="body2">{description}</Typography>
      </Styled.Info>
    </Styled.ItemBlock>
  ),
);
