/* eslint-disable react/jsx-props-no-spreading */
import { useCallback, useState } from 'react';
import { useAppDispatch, useAppSelector } from 'store/hooks';

import { TextField } from '@development/ui-kit';

import { OffersFilterName } from 'old-enums/offers-filter-name';
import {
  searchFieldDefaultValue,
  getOffersListData,
  setFilter,
  setSearchFilterData,
  setSearchInputText,
} from 'old-reducer/offers-page';

import type { ChangeEvent, KeyboardEvent } from 'react';
import * as Styled from './styles';

type RequestValueByEnter = string;
type RequestValueByClick = { text: string; offerId: number } | null;

type Props = {
  dataTestID: string;
};

const validateInputText = (value: string) => {
  const MAX_SEARCH_TEXT_LENGTH = 100;

  if (value.length > MAX_SEARCH_TEXT_LENGTH) {
    return `Search value must not exceed ${MAX_SEARCH_TEXT_LENGTH} characters`;
  }

  return null;
};

export const Search = ({ dataTestID }: Props) => {
  const { inputText, resultList } = useAppSelector(
    (state) => state.offersPage.search,
  );
  const sourceList = resultList.map(({ id, t_name }) => ({
    text: `${id} - ${t_name}`,
    offerId: id,
  }));
  const [errorText, setErrorText] = useState('');
  const dispatch = useAppDispatch();

  const handleChange = useCallback(
    (_event: ChangeEvent<{}>, value: string) => {
      dispatch(setSearchInputText(value));

      const validationError = validateInputText(value);

      if (validationError !== null) {
        setErrorText(validationError);

        return;
      }

      const valueWithoutSideSpaces = value.trim();

      if (valueWithoutSideSpaces.length !== 0) {
        setErrorText('');
        dispatch(
          setSearchFilterData(
            OffersFilterName.SEARCH_TEXT,
            valueWithoutSideSpaces,
          ),
        );
      }
    },
    [dispatch],
  );

  const onSearchByClick = useCallback(
    (value: RequestValueByClick) => {
      dispatch(setFilter(OffersFilterName.SEARCH_ID, value?.offerId));
      dispatch(
        setFilter(OffersFilterName.SEARCH_TEXT, searchFieldDefaultValue.search),
      );
    },
    [dispatch],
  );

  const onSearchByEnter = useCallback(
    (value: RequestValueByEnter) => {
      const validationError = validateInputText(value);

      if (validationError !== null) {
        setErrorText(validationError);

        return;
      }

      dispatch(setFilter(OffersFilterName.SEARCH_TEXT, value));
      dispatch(
        setFilter(OffersFilterName.SEARCH_ID, searchFieldDefaultValue.id),
      );
      dispatch(getOffersListData());
    },
    [dispatch],
  );

  const handleRequest = useCallback(
    (value: RequestValueByClick) => {
      setErrorText('');

      if (typeof value === 'object') {
        onSearchByClick(value);
      }

      dispatch(getOffersListData());
    },
    [dispatch, onSearchByClick],
  );

  const handleKeyPress = useCallback(
    (e: KeyboardEvent<HTMLDivElement>) => {
      const ENTER_KEY = 'Enter';

      if (e.key === ENTER_KEY) {
        onSearchByEnter(inputText);
      }
    },
    [onSearchByEnter, inputText],
  );

  return (
    <Styled.AutocompleteStyled
      clearOnEscape={false}
      clearOnBlur={false}
      forcePopupIcon={false}
      data-testid={dataTestID}
      inputValue={inputText}
      onKeyPress={(e) => handleKeyPress(e)}
      options={sourceList}
      getOptionLabel={(option) =>
        (option as NonNullable<RequestValueByClick>).text
      }
      onInputChange={handleChange}
      onChange={(_e, value) => handleRequest(value as RequestValueByClick)}
      renderInput={(params) => (
        <TextField
          dataTestId="search_offer"
          label="Search for offer ID or name"
          error={errorText}
          helperText={errorText || ''}
          variant="standard"
          {...params}
        />
      )}
    />
  );
};
