import React from 'react';

import * as Styled from './style';
import type { ManagerContactInfoProps } from './types';

export const ManagerContactInfo = ({
  avatar,
  email,
  name,
  skype,
  status,
}: ManagerContactInfoProps) => (
  <Styled.Wrapper>
    <Styled.Image alt={name} src={avatar} />
    <Styled.WrapperInfo>
      <Styled.Name>{name}</Styled.Name>
      {status ? <Styled.Status>{status}</Styled.Status> : null}
      {skype ? (
        <Styled.ContactLink
          href={`skype:${skype}?chat`}
          className="mdi mdi-skype"
          data-testid="personal-manager__skype-link"
        >
          {skype}
        </Styled.ContactLink>
      ) : null}
      {email ? (
        <Styled.ContactLink
          href={`mailto:${email}`}
          className="mdi mdi-email"
          data-testid="personal-manager__email-link"
        >
          {email}
        </Styled.ContactLink>
      ) : null}
    </Styled.WrapperInfo>
    <Styled.Delimiter />
  </Styled.Wrapper>
);
