import { FilteredList } from 'old-component/filtered-list';

import { SubmitNewTicket } from '../submit-button';
import { useTicketsInfo } from './useTicketsInfo';

export const TicketsTable = ({ toShowFilters }: { toShowFilters: boolean }) => {
  const {
    fields,
    dataSource,
    applyFilter,
    changePage,
    totalItems,
    currentPage,
    isLoading,
    itemsPerPage,
  } = useTicketsInfo();

  return (
    <FilteredList
      tableFields={fields}
      tableData={dataSource}
      updateData={applyFilter}
      updatePage={changePage}
      totalItems={totalItems}
      currentPage={currentPage}
      itemsPerPage={itemsPerPage}
      isLoading={isLoading}
      toShowFilters={toShowFilters}
    >
      <SubmitNewTicket />
    </FilteredList>
  );
};
