import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { initialState } from './dashboard/state';
import type { Post } from './dashboard/types';
import type { CollectionWithoutPagination, Page } from './types';

const {
  actions: { setLoadingState, setPosts, setPinnedPosts },
  reducer: dashboardPageReducer,
} = createSlice({
  name: 'dashboardPage',
  initialState,
  reducers: {
    setLoadingState: (
      state,
      { payload: isLoading }: PayloadAction<boolean>,
    ) => {
      state.isLoading = isLoading;
    },

    setPosts: (state, { payload: posts }: PayloadAction<Page<Post>>) => {
      state.posts = posts;
    },

    setPinnedPosts: (
      state,
      {
        payload: pinnedPosts,
      }: PayloadAction<CollectionWithoutPagination<Post>>,
    ) => {
      state.pinnedPosts = pinnedPosts;
    },
  },
});

export { dashboardPageReducer, setLoadingState, setPosts, setPinnedPosts };
