import { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { FooterLogin } from 'pages/signin/components/footer';
import { Header, HeaderSign } from 'old-component/header';
import { Manager } from 'old-component/personal-manager';
import { Sidebar } from 'old-component/sidebar';
import { SnackbarWrap } from 'old-component/snackbar/container';
import { PATHS, anonymousRoutes } from 'routes';
import type { MapStateToProps } from 'react-redux';
import type { AppState } from 'store/types';
import * as Styled from './styles';
import type { Props, StateProps, OwnProps } from './types';

const isSignUp = (path: string) =>
  PATHS.SIGNUP === path || PATHS.SIGNUP_TY === path;

const isResponsivePage = (path: string) =>
  isSignUp(path) || PATHS.SIGN_IN === path;
class PageComponent extends Component<Props> {
  componentDidMount() {
    const {
      title,
      match: { path },
    } = this.props;
    document.title = title ? `${title} @ Adsterra` : 'Adsterra';

    const viewport = document.createElement('meta');
    viewport.name = 'viewport';
    viewport.content = isResponsivePage(path)
      ? 'width=device-width, initial-scale=1.0'
      : 'width=1300';
    document.head.appendChild(viewport);
  }

  render() {
    const {
      component: PageContent,
      match: { path },
      title = '',
      isSidebarCollapsed,
    } = this.props;

    if (isSignUp(path)) {
      return <PageContent />;
    }

    if (anonymousRoutes.includes(path as PATHS)) {
      return (
        <Styled.AnonymousContainer>
          <HeaderSign />
          <PageContent />
          <FooterLogin />
        </Styled.AnonymousContainer>
      );
    }

    return (
      <div>
        <Sidebar />
        <Manager />
        <Styled.Container $isSidebarCollapsed={isSidebarCollapsed}>
          <Header title={title} />
          <PageContent />
        </Styled.Container>
        <SnackbarWrap />
      </div>
    );
  }
}

const mapStateToProps: MapStateToProps<StateProps, OwnProps, AppState> = (
  state,
) => ({
  isSidebarCollapsed: state.sitePage.isSidebarCollapsed,
});

// TODO  withRouter,      
export const Page = withRouter(connect(mapStateToProps)(PageComponent));
