import { SignUpPageContent } from 'features/signup';
import { useAppSelector } from 'store/hooks';
import { Redirect } from 'react-router-dom';
import { SignUpProgressEnum } from 'old-enums/sign-up-progress';

export const SignUp = () => {
  const { step } = useAppSelector((state) => state.signUp);

  if (step === SignUpProgressEnum.CONFIRM_STEP) {
    return <Redirect from="signup-ty" to="/signup-ty" />;
  }

  return <SignUpPageContent />;
};
