import type { ChangeEvent, KeyboardEvent } from 'react';

import * as Styled from 'pages/signin/styles';
import { useAppDispatch } from 'store/hooks';
import { entity, generalText } from 'constants/sign';
import { getSignIn, setUser, setAction } from 'old-reducer/sign-in';
import { SignInActionsEnum } from 'old-enums/sign-in-actions-enum';

import { DialogLink } from 'features/dialog-role/dialog-choose-role-link/dialog-choose-role-link';
import { ErrorMessage } from 'pages/signin/components';
import { Password } from 'old-component';

import { ENTER_KEY_CODE } from 'pages/signin/constants';

import { Typography, Link } from '@development/ui-kit';
import type { LogInProps } from './types';

export const LogIn = ({
  formError,
  loading,
  user,
  generalError,
}: LogInProps) => {
  const dispatch = useAppDispatch();

  const onBackButtonClick = () => {
    window.location.reload();
  };

  const onForgotPasswordButtonClick = () => {
    dispatch(setAction(SignInActionsEnum.FORGOT_PASSWORD));
  };

  const onLoginChange = (event: ChangeEvent<HTMLInputElement>) => {
    const loginValue = event.target.value.trim();

    dispatch(setUser('login', loginValue));
  };

  const onPasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
    const passwordValue = event.target.value.trim();

    dispatch(setUser('password', passwordValue));
  };

  const onLoginButtonClick = () => {
    dispatch(getSignIn());
  };

  const onSignInKeyDown = ({ code }: KeyboardEvent<HTMLInputElement>) => {
    if (code === ENTER_KEY_CODE) {
      onLoginButtonClick();
    }
  };

  return (
    <>
      <Typography variant="h2">
        Log in as {generalText.article[entity]}&nbsp;
        <Styled.UserTypeName>{generalText.name[entity]}</Styled.UserTypeName>
      </Typography>
      <Styled.NotUserTypeLink>
        <DialogLink variant="caption" />
      </Styled.NotUserTypeLink>
      {generalError.length < 100 ? (
        <>
          {generalError && <ErrorMessage errorText={generalError} />}
          <Styled.LoginField
            dataTestId="signin__login"
            name="login-signin"
            error={formError.login}
            helperText={formError.login}
            label="Login* / Email*"
            value={user.login}
            onChange={onLoginChange}
            onKeyDown={onSignInKeyDown}
            variant="outlined"
          />
          <Styled.PasswordContainer>
            <Password
              dataTestId="signin__password"
              name="signin"
              error={formError.password}
              helperText={formError.password}
              required
              value={user.password}
              onChange={onPasswordChange}
              onKeyDown={onSignInKeyDown}
              fullWidth
            />
          </Styled.PasswordContainer>
          <Styled.ForgotPasswordLink
            dataTestId="sign-in__forgot-password-link"
            text="Forgot Password?"
            variant="caption"
            onClick={onForgotPasswordButtonClick}
            color="textSecondary"
          />
          <Styled.LoginPageButton
            dataTestId="sign-in__submit-button"
            disabled={loading || !user.login || !user.password}
            color="tertiary"
            variant="contained"
            onClick={onLoginButtonClick}
            isLoading={loading}
          >
            Log in
          </Styled.LoginPageButton>
          <Styled.Note>
            <Typography variant="caption">
              Dont have {generalText.article[entity]}&nbsp;
              {generalText.name[entity]} account?&nbsp;
              <Link
                dataTestId="signin__dont-have-account"
                text="Sign up"
                color="secondary"
                href="/signup"
              />
            </Typography>
          </Styled.Note>
          <div className="inner" id="recaptchav2" />
        </>
      ) : (
        <Styled.ErrorContainer container flexDirection="column">
          <Styled.ErrorText>{generalError}</Styled.ErrorText>
          <Styled.LoginPageButton
            dataTestId="sign-in__back-button"
            disabled={loading}
            color="tertiary"
            variant="contained"
            onClick={onBackButtonClick}
            isLoading={loading}
          >
            Back
          </Styled.LoginPageButton>
        </Styled.ErrorContainer>
      )}
    </>
  );
};
