import { createIntercom } from 'lib/intercom/create-intercom';
import { IS_PRODUCTION } from 'old-common/is-production';
import { parseJWT } from 'utils/auth';
import { getHeaders } from 'utils/http';
import { setAmplitudeInstanceData } from 'lib/amplitude';
import { createRemarketingPixel } from 'lib/remarketing-pixel';
import { setUser } from '@sentry/browser';

import type { Action } from '@reduxjs/toolkit';
import type { Thunk } from 'store/types';

import { logout } from 'constants/index';
import { getUserTypeTitle } from 'old-enums/user-permission';
import { pushToGoogleTagManager } from 'lib/google-tag-manager';
import { setAutoLogin, setToken, setUserInfo } from './slice';

import type { AuthResponse } from './types';

export const getToken =
  (): Thunk<Action, Promise<boolean>> => async (dispatch) => {
    const tkn = document.cookie.replace(
      /(?:(?:^|.*;\s*)tkn3\s*=\s*([^;]*).*$)|^.*$/,
      '$1',
    );

    if (tkn !== '') {
      try {
        const response = await window.fetch(`/api/auth/${tkn}`);

        if (response.status === 200) {
          const {
            data: { token },
          }: AuthResponse = await response.json();

          const payload = parseJWT(token);

          if (payload != null) {
            dispatch(setToken(token));
            dispatch(setAutoLogin(!!payload.auto));

            if (!payload.auto) {
              createRemarketingPixel();
            }

            return true;
          }
        }
      } catch {
        // noop
      }
    }

    logout();

    return false;
  };

export const getUser =
  (): Thunk<Action, Promise<boolean>> => async (dispatch, getState) => {
    try {
      const {
        user: { token, isAutoLogin },
      } = getState();

      const response = await window.fetch('/api/user', {
        headers: getHeaders(token),
      });

      if (response.status === 200) {
        const { data } = await response.json();

        dispatch(setUserInfo({ data }));

        setUser({ email: data.email });

        if (IS_PRODUCTION) {
          const { intercom_app_id } = data;

          pushToGoogleTagManager({ userId: data.analytics_user_id });
          setAmplitudeInstanceData(data.amplitude_user_id, {
            Email: data.email,
            Id: data.id,
            Login: data.login,
            Type: 'Publisher',
            CPAPermissions: getUserTypeTitle(data.ida_permissions),
          });

          if (intercom_app_id !== null && !isAutoLogin) {
            createIntercom(intercom_app_id, data.id, data.name);
          }
        }

        return true;
      }
    } catch {
      // noop
    }

    return false;
  };

export const getManager =
  (): Thunk<Action, Promise<void>> => async (dispatch, getState) => {
    try {
      const { token } = getState().user;
      const response = await window.fetch('/api/manager', {
        headers: getHeaders(token),
      });

      if (response.status === 200) {
        const { data: manager } = await response.json();
        dispatch(setUserInfo({ manager }));
      }
    } catch {
      // noop
    }
  };

export const getAccountInfo = (): Thunk<Action, Promise<boolean>> => {
  return async (dispatch, getState) => {
    try {
      const { token } = getState().user;
      const response = await window.fetch('/api/user/account/info', {
        headers: getHeaders(token),
      });

      if (response.status === 200) {
        const data = await response.json();
        const accountInfo = {
          aa_request_status: data.data.custom_domain_request_status,
          account_active: !data.data.all_placements_not_in_use,
          custom_domain: data.data.custom_domain_assigned,
          has_popunder: data.data.has_popunder,
          news_counter: data.data.dashboard_view_counter,
        };

        dispatch(setUserInfo({ account_info: accountInfo }));

        return true;
      }
    } catch {
      // noop
    }

    return false;
  };
};
