import type { Dispatch, SetStateAction } from 'react';
import { useCallback, useState } from 'react';

export function useLocalStorageState<T>(
  key: string,
  possibleInitialValue: T | { (): T },
  serialize?: { (val: T): string },
  deserialize?: { (val: string): T },
): [T, Dispatch<SetStateAction<T>>] {
  const sze = serialize || ((val) => JSON.stringify(val));
  const dsze = deserialize || ((val) => JSON.parse(val) as T);

  const storedValue = window.localStorage.getItem(key);
  let initialValue: T;

  if (storedValue !== null) {
    initialValue = dsze(storedValue);
  } else {
    initialValue =
      possibleInitialValue instanceof Function
        ? possibleInitialValue()
        : possibleInitialValue;
  }

  const [val, setInternalSetVal] = useState(initialValue);
  const setVal = useCallback(
    (possibleNewVal: T | { (prevVal: T): T }) => {
      setInternalSetVal((v: T) => {
        const newVal =
          possibleNewVal instanceof Function
            ? possibleNewVal(v)
            : possibleNewVal;
        window.localStorage.setItem(key, sze(newVal));

        return newVal;
      });
    },
    //      .
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [],
  );

  return [val, setVal];
}
