import styled from 'styled-components';
import { FormControl, OutlinedInput } from '@material-ui/core';
import { withTheme } from '@material-ui/core/styles';
import { FileCopyOutlined } from '@material-ui/icons';

import type { WithTheme } from '@material-ui/core/styles';
import type { CopyIconProps } from './types';

export const TokenControl = withTheme(styled(FormControl)`
  &.MuiFormControl-root {
    margin-right: ${({ theme }: WithTheme) => theme.spacing(2)}px;
  }
`);

export const TokenInput = withTheme(styled(OutlinedInput)`
  &.MuiInputBase-root {
    padding-right: 0;
    width: 460px;
  }

  .MuiInputBase-input {
    height: 32px;
    padding: ${({ theme }: WithTheme) => theme.spacing(1, 2)};
  }
`);

export const CopyIcon = withTheme(styled(FileCopyOutlined)`
  color: ${({ disabled, theme }: CopyIconProps & WithTheme) =>
    disabled
      ? theme.palette.action.disabled
      : theme.palette.text.primary} !important;
`);
