import React from 'react';

import { ContentStatusConfirm } from '../content-status-confirm';
import { ContentStatusNotConfirm } from '../content-status-not-confirm';

import * as Styled from './style';

import type { ManagerContentProps } from './types';

export const ManagerContent = ({
  isStatusConfirm,
  isActive,
  newsCounter,
  avatar,
  email,
  name,
  skype,
  status,
}: ManagerContentProps) => {
  const isMultipleMessages = newsCounter > 1;
  const newMessageNotificationText = `You have ${
    isMultipleMessages ? 'new messages' : 'a new message'
  }!`;

  return (
    <Styled.Wrapper isActive={isActive} isHaveCounter={Boolean(newsCounter)}>
      {isStatusConfirm ? (
        <ContentStatusConfirm
          messageNotification={newMessageNotificationText}
          newsCounter={newsCounter}
        />
      ) : (
        <ContentStatusNotConfirm
          avatar={avatar}
          email={email}
          name={name}
          skype={skype}
          status={status}
          messageNotification={newMessageNotificationText}
          newsCounter={newsCounter}
        />
      )}
    </Styled.Wrapper>
  );
};
