import styled from 'styled-components';

import { withTheme } from '@material-ui/core';
import { themeColors } from 'styles/theme/constants';

import type { WithTheme } from '@material-ui/core/styles';

export const Wrapper = withTheme(styled.div`
  padding: ${({ theme }: WithTheme) => theme.spacing(3)}px;
  background: ${({ theme }: WithTheme) => theme.palette.tertiary.light};
  color: ${themeColors.textContrast};
`);

export const Text = withTheme(styled.p`
  color: ${themeColors.textContrast};
`);
