import { getHeaders } from 'utils/http';
import type { UserPermissionEnum } from 'old-enums/user-permission';

type PermissionResponse = {
  data: UserPermissionEnum[];
};

/**
 *   
 */
export const getUserPermissions = async (userToken: string) => {
  try {
    const response = await window.fetch('/api/user/permissions', {
      headers: getHeaders(userToken),
    });

    if (response.status === 200) {
      const { data } = (await response.json()) as PermissionResponse;

      return data;
    }

    return null;
  } catch {
    return null;
  }
};
