import { MenuItem } from '@material-ui/core';
import {
  getAllZoneCategories,
  getZoneCategoriesForNewZone,
  getZoneCategoryTitle,
  ZoneCategoryEnum,
} from 'old-enums/zone-category';

import * as Styled from './styles';

import type { Props } from './types';

export const SiteCategory = ({
  categoryId,
  isExistingSite,
  onZoneCategoryChange,
  isDirectLink = false,
}: Props) => {
  const arr = isExistingSite
    ? getAllZoneCategories()
    : getZoneCategoriesForNewZone();

  const options = arr.map((id) => {
    const categoryTitle =
      isDirectLink && categoryId === ZoneCategoryEnum.SOCIAL_BLACK
        ? 'Non-adult'
        : getZoneCategoryTitle(id);

    return (
      <MenuItem key={id} value={id}>
        {categoryTitle}
      </MenuItem>
    );
  });

  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: 300,
      },
    },
  };

  return (
    <Styled.SelectStyled
      data-testid="dialog__category-id"
      disabled={isExistingSite}
      name="category_id"
      value={categoryId}
      onChange={onZoneCategoryChange}
      MenuProps={MenuProps}
    >
      <MenuItem value={ZoneCategoryEnum.EMPTY}>Please select one</MenuItem>
      {options}
    </Styled.SelectStyled>
  );
};
