import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';

import {
  getRecaptchaToken,
  getRecaptchaTokenV2,
  renderRecaptchaV2,
} from 'lib/google-recaptcha';
import { setAuthCookie } from 'constants/index';
import { getHeaders } from 'utils/http';
import { SignInActionsEnum } from 'old-enums/sign-in-actions-enum';

import type { PlainObject } from 'types/utils';
import type { Thunk } from 'store/types';
import { checkAuth } from './common/thunk';

interface AuthResponse {
  data: {
    token: string;
  };
}

interface State {
  action: SignInActionsEnum;
  forgotPassword: {
    email: string;
  };
  form: {
    form_error: {
      email: string;
      login: string;
      password: string;
    };
    general_error: string;
    loading: boolean;
    message: string;
    recaptcha: {
      wasRendered: boolean;
    };
  };
  user: {
    login: string;
    password: string;
    fingerprint?: string;
  };
  context: {
    isAffiliateDomain: boolean;
  };
  confirmStep: {
    emailServices: {
      link: string;
    };
    email: string;
  };
}

const initialState: State = {
  action: SignInActionsEnum.SIGN_IN,
  forgotPassword: {
    email: '',
  },
  form: {
    form_error: {
      email: '',
      login: '',
      password: '',
    },
    general_error: '',
    loading: false,
    message: '',
    recaptcha: {
      wasRendered: false,
    },
  },
  user: {
    login: '',
    password: '',
    fingerprint: '',
  },
  context: {
    isAffiliateDomain: window.location.href.includes('affiliates'),
  },
  confirmStep: {
    emailServices: {
      link: '',
    },
    email: '',
  },
};

const {
  reducer: signInReducer,
  actions: {
    formError,
    formLoading,
    generalErrorSignin,
    setRecaptchaV2RenderState,
    setAction,
    setUser,
    resendPassword,
  },
} = createSlice({
  name: 'signIn',

  initialState,
  reducers: {
    formError: (
      state,
      { payload: errors }: PayloadAction<PlainObject<string>>,
    ) => {
      state.form.form_error = { ...initialState.form.form_error, ...errors };
      state.form.general_error = '';
      state.form.loading = false;
      state.form.message = '';
    },

    formLoading: (state) => {
      state.form.loading = true;
    },

    generalErrorSignin: (state, { payload: msg }: PayloadAction<string>) => {
      state.form.form_error = { ...initialState.form.form_error };
      state.form.general_error = msg;
      state.form.loading = false;
    },

    message: (state, { payload: msg }: PayloadAction<string>) => {
      state.form.form_error = { ...initialState.form.form_error };
      state.form.loading = false;
      state.form.message = msg;
    },

    setAction: (
      state,
      { payload: action }: PayloadAction<SignInActionsEnum>,
    ) => {
      state.action = action;
      state.form.form_error = { ...initialState.form.form_error };
      state.form.general_error = '';
      state.form.loading = false;
      state.form.recaptcha.wasRendered = false;
    },

    setUser: {
      prepare: (type: any, value: any) => ({ payload: { type, value } }),
      reducer: (
        state,
        { payload: { type, value } }: PayloadAction<{ type: any; value: any }>,
      ) => {
        if (type === 'email') {
          state.forgotPassword.email = value;

          return;
        }

        state.user[type] = value;
      },
    },

    setRecaptchaV2RenderState: (state) => {
      state.form.recaptcha.wasRendered = true;
      state.form.loading = false;
    },

    resendPassword: (
      state,
      {
        payload: { link, email },
      }: PayloadAction<{ link: string; email: string }>,
    ) => {
      state.confirmStep.emailServices.link = link;
      state.confirmStep.email = email;
    },
  },
});

export const showRecaptchaV2 = (): Thunk => async (dispatch, getState) => {
  const {
    signIn: {
      form: { recaptcha },
    },
  } = getState();

  if (!recaptcha.wasRendered) {
    await renderRecaptchaV2();
    dispatch(setRecaptchaV2RenderState());
  }
};

const getSignIn = (): Thunk => async (dispatch, getState) => {
  dispatch(formLoading());
  setTimeout(async () => {
    const {
      signIn: {
        user,
        form: { recaptcha },
      },
    } = getState();

    if (!user.login || !user.password) {
      const errors = ['login', 'password'].reduce(
        (acc, name) =>
          user[name] ? acc : { ...acc, [name]: 'This field is required' },
        {},
      );
      dispatch(formError(errors));

      return false;
    }

    try {
      const recaptchaToken = recaptcha.wasRendered
        ? await getRecaptchaTokenV2()
        : await getRecaptchaToken('sign_in_pub');

      const response = await window.fetch('/api/login', {
        body: JSON.stringify({
          ...user,
          token: recaptchaToken,
          isRecaptchaV2: recaptcha.wasRendered,
        }),
        headers: getHeaders(null, { hasBody: true }),
        method: 'post',
      });

      if (response.status === 200) {
        const {
          data: { token },
        }: AuthResponse = await response.json();

        setAuthCookie(token);
        dispatch(checkAuth());

        return true;
      }

      if (response.status === 401) {
        const {
          message: error,
          data: { switchingRecaptcha: shouldSwitchRecaptcha },
        } = await response.json();

        if (error) {
          dispatch(generalErrorSignin(error));

          if (
            shouldSwitchRecaptcha === true &&
            (error.includes('bot') || error.includes('incorrect'))
          ) {
            dispatch(showRecaptchaV2());
          }
        }

        return false;
      }

      if (response.status === 403) {
        const { message: error, data } = await response.json();

        if (!data || data.switchingRecaptcha === false) {
          dispatch(generalErrorSignin(error));
        } else if (data.confirmEmail != null && !data.confirmEmail) {
          dispatch(setAction(SignInActionsEnum.RESEND));
        } else if (data.reason === 'Declined') {
          const errorText =
            'This account is currently inactive. Check your inbox for more details or get in touch with us at publishers@adsterra.com';

          dispatch(generalErrorSignin(errorText));
        } else if (data.switchingRecaptcha === true) {
          dispatch(generalErrorSignin(error));
          dispatch(showRecaptchaV2());
        }
      }

      return false;
    } catch (ex: any) {
      // eslint-disable-next-line no-console
      console.warn(ex.toString());

      return false;
    }
  }, 200);
};

export {
  getSignIn,
  setAction,
  setUser,
  signInReducer,
  resendPassword,
  formLoading,
  formError,
  generalErrorSignin,
};
