import { nanoid } from '@reduxjs/toolkit';
import axios from 'axios';

import type { Thunk } from 'store/types';
import { getHeaders } from 'utils/http';
import type {
  Banner,
  ReferralsInfo,
  ReferralStatsItem,
  StatsFilter,
} from './types';
import { ReferralBannerExtension } from '../../old-view/referrals/referral-banner-extension';
import {
  addInviteLink,
  setBannerList,
  setFilterParams,
  setLoadingState,
  setReferralPagination,
  setReferralsInfo,
  setSelectedBanner,
  setStatsItems,
  setStatsPagination,
  setFilterPublishersList,
} from '../referrals';

import { initialFilter } from './state';
import { showSnackbar } from '../snackbar';
import { snackbarCommonError } from '../snackbar/constants';

const getPaginatedData = <T>(data: T[], page: number, perPage: number) =>
  data.filter((_: T, idx: number) => {
    const showFromItem = (page - 1) * perPage;
    const showToItem = showFromItem + (perPage - 1);

    return idx >= showFromItem && idx <= showToItem;
  });

const setRefListPagination =
  (page: number = 1): Thunk =>
  async (dispatch, getState) => {
    const {
      referralsPage: {
        referralsInfo: { referrals },
        refListPagination: { perPage },
      },
    } = getState();

    dispatch(
      setReferralPagination({
        total: Math.ceil(referrals.length / perPage),
        active: page,
        perPage,
        paginatedReferrals: getPaginatedData(referrals, page, perPage),
      }),
    );
  };

const setStatsListPagination =
  (page: number = 1): Thunk =>
  async (dispatch, getState) => {
    const {
      referralsPage: {
        stats,
        refListPagination: { perPage },
      },
    } = getState();

    dispatch(
      setStatsPagination({
        total: Math.ceil(stats.length / perPage),
        active: page,
        perPage,
        paginatedStats: getPaginatedData(stats, page, perPage),
      }),
    );
  };

const getBannersList =
  (type?: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    if (!type) {
      dispatch(setBannerList([]));
      dispatch(setSelectedBanner());

      return;
    }

    try {
      const response = await window.fetch('/api/referrals/banners', {
        headers: {
          ...getHeaders(token),
        },
      });

      if (response.status !== 200) {
        dispatch(showSnackbar(snackbarCommonError));

        return;
      }

      let { data } = await response.json();

      if (type === ReferralBannerExtension.GIF) {
        data = data.gif;
      } else {
        data = data.png;
      }

      dispatch(setBannerList(data));
      dispatch(setSelectedBanner(data[0]));
    } finally {
      dispatch(setLoadingState(false));
    }
  };

const getReferralsInfo = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  dispatch(setLoadingState(true));

  try {
    const invitesRequest = axios.get('/api/referrals/invites', {
      headers: getHeaders(token),
    });
    const referralsRequest = axios.get('/api/referrals', {
      headers: getHeaders(token),
    });

    const [invitesResponse, referralsResponse] = await Promise.all([
      invitesRequest,
      referralsRequest,
    ]);

    if (invitesResponse.status !== 200 || referralsResponse.status !== 200) {
      dispatch(showSnackbar(snackbarCommonError));

      return;
    }

    const {
      data: { data: invites },
    } = invitesResponse;
    const {
      data: { data: referrals },
    } = referralsResponse;

    const referralsInfo: ReferralsInfo = {
      invites: invites.items,
      referrals: referrals.items,
      userInviteLimit: invites.user_invite_limit,
    };

    dispatch(setReferralsInfo(referralsInfo));
    dispatch(setRefListPagination());
    dispatch(getBannersList(ReferralBannerExtension.PNG));
  } finally {
    dispatch(setLoadingState(false));
  }
};

const getBannerLink = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  dispatch(setLoadingState(true));

  try {
    const response = await window.fetch('api/referrals/invites', {
      headers: getHeaders(token),
      method: 'POST',
    });
    const { data } = await response.json();

    dispatch(addInviteLink(data));
  } finally {
    dispatch(setLoadingState(false));
  }
};

const getBannerFile =
  (banner: Banner, extension: string): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    window
      .fetch(`/api/referrals/banners/${banner.id}/${extension}`, {
        headers: {
          ...getHeaders(token),
        },
      })
      .then((response) => {
        if (response.status !== 200) {
          throw new Error();
        }

        return response;
      })
      .then((response) => response.blob())
      .then((blob) => {
        const blobURL = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = blobURL;
        link.download = `${banner.name}.${extension}`;
        document.body.appendChild(link);
        link.click();
      })
      .catch(() => {
        dispatch(showSnackbar(snackbarCommonError));
      });
  };

const setFilterPublishers = (): Thunk => async (dispatch, getState) => {
  const {
    user: { token },
  } = getState();

  const response = await window.fetch('/api/referrals/filter-parameters', {
    headers: {
      ...getHeaders(token),
    },
  });

  if (response.status !== 200) {
    dispatch(showSnackbar(snackbarCommonError));

    return;
  }

  const { data } = await response.json();

  dispatch(setFilterPublishersList(data.publishers));
};

const getReferralsStats =
  (params: StatsFilter = initialFilter, isCsv = false): Thunk =>
  async (dispatch, getState) => {
    const {
      user: { token },
    } = getState();

    dispatch(setLoadingState(true));
    dispatch(setFilterParams(params));

    const queryString = new URLSearchParams();
    Object.keys(params).forEach((key) => queryString.append(key, params[key]));

    try {
      const url = isCsv ? '/api/referrals/stats/csv' : '/api/referrals/stats';

      const response = await window.fetch(`${url}?${queryString.toString()}`, {
        headers: {
          ...getHeaders(token),
        },
      });

      if (response.status !== 200) {
        dispatch(showSnackbar(snackbarCommonError));
      }

      if (isCsv) {
        const csv = await response.text();
        const filename = (
          await response.headers.get('Content-Disposition')
        )?.split('=')[1];

        const link = document.createElement('a');
        link.href = `data:text/csv;charset=utf-8,${encodeURI(csv)}`;

        if (filename) {
          link.download = filename;
        }

        link.click();
      } else {
        const { data } = await response.json();

        data.items = data.items.map((item: ReferralStatsItem) => {
          if (item.id === undefined) {
            return {
              ...item,
              id: nanoid(2),
            };
          }

          return item;
        });

        dispatch(setStatsItems(data.items));
        dispatch(setStatsListPagination());
      }
    } finally {
      dispatch(setLoadingState(false));
    }
  };

const setFilter =
  (params: StatsFilter): Thunk =>
  async (dispatch) => {
    dispatch(setFilterParams(params));
  };

export {
  getBannerLink,
  getBannersList,
  getBannerFile,
  setFilterPublishers,
  getReferralsInfo,
  getReferralsStats,
  setFilter,
  setRefListPagination,
  setStatsListPagination,
};
