import { getHeaders } from 'utils/http';

export const fetchTicketTopics = async (token: string): Promise<Response> => {
  const queryString = new URLSearchParams();
  queryString.append('with_archive', '0');
  queryString.append('with_any', '1');

  return window.fetch(
    `api/dictionary/ticket/topics?${queryString.toString()}`,
    {
      method: 'GET',
      headers: getHeaders(token),
    },
  );
};
