import React from 'react';
import { connect } from 'react-redux';
import type { RouteComponentProps } from 'react-router-dom';
import { Redirect, withRouter } from 'react-router-dom';

import { CircularProgress, ImageList, ImageListItem } from '@material-ui/core';

import { Resend } from 'features/resend';
import { confirmEmail } from 'old-reducer/signup/thunk';
import type { AppState } from 'store/types';

import signUp from 'assets/images/signup/sign-up.svg';
import * as Styled from './style';

interface Dispatchers {
  confirmEmail(code: string): void;
}

interface Params {
  code: string;
}

export class ConfirmComponent extends React.Component<
  AppState['signUp'] & Dispatchers & RouteComponentProps<Params>
> {
  componentDidMount() {
    const {
      confirmEmail: confirm,
      match: {
        params: { code },
      },
    } = this.props;

    confirm(code);
  }

  render() {
    const {
      confirm: { button, redirect, text },
    } = this.props;

    if (redirect) {
      return <Redirect from="login" to="/login" />;
    }

    const output =
      text.length === 0 && !button ? (
        <Styled.LoadingContainer>
          <CircularProgress size={32} />
          <Styled.LoadingText>
            Processing
            <br />
            Please, do not close this window
          </Styled.LoadingText>
        </Styled.LoadingContainer>
      ) : (
        <Styled.Container>
          <div
            dangerouslySetInnerHTML={{ __html: text }} // eslint-disable-line react/no-danger
          />
          {button && <Resend />}
        </Styled.Container>
      );

    return (
      <div className="contentSign">
        <Styled.StyledPaper>
          <ImageList rowHeight={550} cols={2}>
            <ImageListItem>
              <div>
                <img src={signUp} alt="" />
              </div>
            </ImageListItem>
            <Styled.Tile>{output}</Styled.Tile>
          </ImageList>
        </Styled.StyledPaper>
      </div>
    );
  }
}

export const Confirm = withRouter(
  connect((state: AppState) => state.signUp, { confirmEmail })(
    ConfirmComponent,
  ),
);
