import { Dialog, Typography } from 'old-component';
import { Grid } from '@material-ui/core';
import { useCallback, useEffect, useState } from 'react';
import { uploadDocuments } from 'old-reducer/payments/thunk';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import {
  hideUploadDialog,
  setFilesErrorText,
  setInitialStateToUploadForm,
  setTypeErrorText,
} from 'old-reducer/payments-page';
import { DialogStatus } from 'old-enums/dialog-status';
import { dialogTexts, emptySelectOptionID, errorMessages } from './constants';
import { DocumentsUploader } from '../documents-uploader';
import { TypeSelect } from '../type-select';

export const UploadDialog = () => {
  const dispatch = useAppDispatch();
  const {
    uploadDialogStatus,
    documentType,
    hasUploaderErrors,
    filesErrorText,
  } = useAppSelector((state) => ({
    uploadDialogStatus: state.paymentsPage.uploadDialogStatus,
    documentType: state.paymentsPage.documentUploadForm.documentType,
    hasUploaderErrors: state.paymentsPage.documentUploadForm.hasUploaderErrors,
    filesErrorText: state.paymentsPage.documentUploadForm.errors.files,
  }));

  const [documentList, setDocumentList] = useState<Array<File>>([]);

  useEffect(() => {
    if (uploadDialogStatus === DialogStatus.HIDE) {
      setDocumentList([]);
    }
  }, [uploadDialogStatus]);

  const onUploadFiles = (files: Array<File>) => {
    setDocumentList(files);
  };

  const onCloseDialog = useCallback(() => {
    dispatch(setInitialStateToUploadForm());
    dispatch(hideUploadDialog());
  }, [dispatch]);

  const onUploadDocuments = useCallback(() => {
    const isEmptyFileList = documentList.length === 0;
    const isEmptyTypeSelected = documentType === emptySelectOptionID;

    if (hasUploaderErrors || isEmptyFileList || isEmptyTypeSelected) {
      if (isEmptyFileList) {
        dispatch(setFilesErrorText(errorMessages.noFilesToUpload));
      }

      if (isEmptyTypeSelected) {
        dispatch(setTypeErrorText(errorMessages.requiredFieldError));
      }

      return;
    }

    const formData = new FormData();

    documentList.forEach((file, index) => {
      formData.append(`file_${index}`, file);
    });

    formData.append('type', documentType.toString());

    dispatch(uploadDocuments(formData));
  }, [hasUploaderErrors, documentType, documentList, dispatch]);

  return (
    <Dialog
      onSubmit={onUploadDocuments}
      submitButtonText={dialogTexts.submitButtonText}
      cancelButtonText={dialogTexts.cancelButtonText}
      dataTestId="payments__upload-dialog"
      status={uploadDialogStatus}
      title={dialogTexts.title}
      onCancel={onCloseDialog}
      width={600}
    >
      <Grid container direction="column" spacing={2}>
        <TypeSelect />
        <DocumentsUploader onUploadFiles={onUploadFiles} />
        {filesErrorText && (
          <Grid item>
            <Typography variant="body2" color="error">
              {filesErrorText}
            </Typography>
          </Grid>
        )}
      </Grid>
    </Dialog>
  );
};
