import { useAppDispatch } from 'store/hooks';
import CopyToClipboard from 'react-copy-to-clipboard';
import { IconButton, InputAdornment } from '@material-ui/core';
import { Button } from '@development/ui-kit';
import { showSnackbar } from 'old-reducer/snackbar';
import { createToken } from 'old-reducer/api-token/thunk';
import { deactivateToken } from 'old-reducer/api-token-page';
import { AlertSeverity } from 'old-enums/alert-severity';
import { SnackbarMessage } from 'old-enums/snackbar-message';
import * as Styled from './styles';
import type { Props } from './types';

export const TokenGenerator = ({ currentToken, isLoading = false }: Props) => {
  const dispatch = useAppDispatch();
  const tokenValue = currentToken?.token ?? '';
  const placeholderText = isLoading ? 'Waiting...' : 'Generate token';
  const isCopyDisabled = isLoading || !tokenValue;

  const handleCreateTokenClick = () => {
    if (currentToken) {
      dispatch(deactivateToken(currentToken));
    }
    dispatch(createToken());
  };

  const handleCopyTokenClick = () => {
    dispatch(
      showSnackbar({
        message: SnackbarMessage.COPY_API_TOKEN,
        severity: AlertSeverity.SUCCESS,
      }),
    );
  };

  return (
    <div>
      <Styled.TokenControl>
        <Styled.TokenInput
          value={tokenValue}
          placeholder={placeholderText}
          readOnly
          disabled={isLoading}
          endAdornment={
            <InputAdornment position="end">
              <CopyToClipboard text={tokenValue} onCopy={handleCopyTokenClick}>
                <IconButton
                  data-testid="api__copy-to-clipboard-button"
                  disabled={isCopyDisabled}
                >
                  <Styled.CopyIcon disabled={isCopyDisabled} />
                </IconButton>
              </CopyToClipboard>
            </InputAdornment>
          }
        />
      </Styled.TokenControl>

      <Button
        dataTestId="api__generate-button"
        disabled={isLoading}
        size="large"
        variant="contained"
        onClick={handleCreateTokenClick}
      >
        Generate new token
      </Button>
    </div>
  );
};
