import { PASSWORD_MAX_LENGTH, PASSWORD_MIN_LENGTH } from 'constants/validation';
import type { yup } from 'lib/yup';
import { ErrorMessage } from './constants';

/**
 * ,     
 */
export function passwordRule(
  this: yup.StringSchema,
  { rangeErrorMessage, trimErrorMessage } = {
    rangeErrorMessage: ErrorMessage.RANGE,
    trimErrorMessage: ErrorMessage.TRIM,
  },
) {
  return this.strict()
    .required(ErrorMessage.REQUIRED)
    .range(PASSWORD_MIN_LENGTH, PASSWORD_MAX_LENGTH, rangeErrorMessage)
    .trim(trimErrorMessage);
}
